    var activePane = undefined;

    var previousEventTarget = undefined;
    var previousDropTarget = undefined;

    var previousHighlightElement = undefined;
    var previousDragClass = undefined;

    var previousWasPane = false;

    function removePreviousDragEffect()
    {
        if (previousHighlightElement)
        {
            g.removeClass(previousHighlightElement, 'itemDraggingAbove');
            g.removeClass(previousHighlightElement, 'itemDraggingBelow');
        }
    }

    self.handleDragOver = function (e)
    {
        // Only update the drop target once when the element that is being dragged over changes.
        if (previousEventTarget !== e.srcElement)
        {
            if (e.srcElement)
            {
                activePane = g.getPaneForElement(e.srcElement);
            }

            var eventTarget = e.srcElement;

            // Only update the drop target for certain element types.
            if (!eventTarget || !activePane || eventTarget.tagName !== 'SPAN' && eventTarget.tagName !== 'P' && eventTarget.tagName !== 'LI' && eventTarget.tagName !== 'UL')
            {
                // Skip Updating Drag Target
            }
            else
            {
                removePreviousDragEffect();

                var pane = g.getPaneForElement(eventTarget);
                try{g.PAssert(1329,pane, 'Must have a valid pane to update our drag target on');}catch(PERR){g.reportError(PERR);}

                var currentDropTarget = ko.dataFor(eventTarget);
                try{g.PAssert(1330,currentDropTarget, 'Should always have a valid drop target');}catch(PERR){g.reportError(PERR);}

                var highlightAbove = undefined;

                var isPane = false;
                if (currentDropTarget instanceof VMPane)
                {
                    isPane = true;

                    if (previousDropTarget)
                    {
                        currentDropTarget = previousDropTarget;
                    }
                    else
                    {
                        currentDropTarget = g.getLastPaneItem(currentDropTarget);
                    }

                    highLightAbove = (e.diffY < 0 ? true : false);
                }
                else if (currentDropTarget instanceof VMLI)
                {
                    if (!pane.isTimeline() && (currentDropTarget.isFullscreen() || eventTarget.tagName === 'LI'))
                    {
                        if (previousDropTarget)
                        {
                            currentDropTarget = previousDropTarget;
                            highlightAbove = (e.diffY < 0 || previousWasPane ? true : false);
                        }
                    }
                    else
                    {
                        highlightAbove = true;
                    }
                }
                else
                {
                    // Default to the original position of the item
                    currentDropTarget = self.getDropItem(e);
                }

                try{g.PAssert(1331,currentDropTarget, 'Should always have a valid drop target');}catch(PERR){g.reportError(PERR);}

                previousHighlightElement = pane.isTimeline() ? undefined : currentDropTarget.getSpan(pane.id);
                previousDragClass = (highlightAbove ? 'itemDraggingAbove' : 'itemDraggingBelow');

                // We don't support drop onto timeline right now
                if (!pane.isTimeline())
                {
                    g.addClass(previousHighlightElement, previousDragClass);
                }

                //log(previousDragClass, previousHighlightElement);

                try{g.PAssert(1332,!(currentDropTarget instanceof VMPane), 'Drop target shouldnt be a pane');}catch(PERR){g.reportError(PERR);}

                previousDropTarget = currentDropTarget;
                previousEventTarget = eventTarget;
                previousWasPane = isPane;
            }
        }

        if (activePane)
        {
            var topMin = 70;
            var topMax = 110;

            var bottomMin = platform.windowHeight() - 40;
            var bottomMax = platform.windowHeight() + 40;

            // Top drag-target for scrolling pane
            if (e.offsetY < topMax)
            {
                var speed = -(1 - g.computeScrollSpeed(topMin, topMax, e.offsetY));

                g.repeatScroll(speed, activePane);
            }
            else if (e.offsetY > bottomMin && e.offsetY < bottomMax)
            {
                var speed = g.computeScrollSpeed(bottomMin, bottomMax, e.offsetY);

                g.repeatScroll(speed, activePane);
            }
            else
            {
                g.cancelRepeatScroll();
            }
        }
        else
        {
            g.cancelRepeatScroll();
        }

        return false;
    };

    self.handleDropItem = function (movingItem, e)
    {
        //log('DROP: ', movingItem);

        g.cancelRepeatScroll();

        if (movingItem !== previousDropTarget)
        {
            // First check to see if we are dropping an item into its own subtree
            var parent = previousDropTarget.parent();
            while (parent)
            {
                if (parent === movingItem)
                {
                    return;
                }

                parent = parent.parent();
            }

            // Second check if this item is being dropped at the same location it started at.
            if (g.getNextItem(movingItem, g.focusedPane) === previousDropTarget && previousDragClass === 'itemDraggingAbove')
            {
                return;
            }

            var pane = g.getPaneForElement(previousEventTarget);

            // We don't support drag/drop into timelines, ignore if it happens.
            if (!pane.isTimeline())
            {
                var previousParent = movingItem.parent();
                var previousIndex = movingItem.getIndex();

                var target = undefined;
                var targetIndex = -1;

                // Compute target information
                if (previousDropTarget.isFullscreen())
                {
                    target = previousDropTarget;
                    targetIndex = target.numChildren();
                }
                else
                {
                    target = previousDropTarget.parent();

                    if (previousDragClass === 'itemDraggingAbove')
                    {
                        targetIndex = previousDropTarget.getIndex();
                    }
                    else
                    {
                        targetIndex = previousDropTarget.getIndex() + 1;
                    }
                }

                // Perform drop/move action

                tracker.beginAction(); // BEGIN TRACKER <----

                //log('Dropping: ', targetIndex, target);

                try{g.PAssert(1333,target, 'Trying to move without a target');}catch(PERR){g.reportError(PERR);}
                try{g.PAssert(1334,!isNaN(targetIndex), 'Trying to move without a target index');}catch(PERR){g.reportError(PERR);}

                movingItem.moveTo({parent: target, index: targetIndex});

                tracker.performAction(TrackerType.Move, {
                    'itemID': movingItem.id,
                    'oldParent': previousParent.id,
                    'oldPosition': previousIndex,
                    'newParent': target.id,
                    'newPosition': targetIndex
                });

                tracker.miscAction({
                    type: TrackerType.Misc,
                    misc: TrackerMisc.DropItem,
                    data: [
                    {
                        'itemID': movingItem.id
                    }]
                });

                tracker.endAction(); // END TRACKER <----

                g.timeline.reloadTimelines();
            }
        }
    };

