    function handleReaderLoad(evt) {
        log(evt.target.result);
    }

    function handleDropFiles(e)
    {
        var file = e.dataTransfer.files[0];

        var reader = new FileReader();

        // init the reader event handlers
        reader.onload = handleReaderLoad;

        // begin the read operation
        reader.readAsDataURL(file);
    }

    self.getDropItem = function(e)
    {
        try{g.PAssert(1337,e.dataTransfer.item, 'We do not support HTML5 drag/drop');}catch(PERR){g.reportError(PERR);}

        return e.dataTransfer.item;
    };

    self.handleDrop = function(e)
    {
        e.stopPropagation();

        if (!previousEventTarget)
        {
            return;
        }

        removePreviousDragEffect();

        var item = self.getDropItem(e);
        if (item)
        {
            self.handleDropItem(item, e);
        }
        else
        {
            //TODO: This was to support html5 dragdrop. Take it out if we don't use that.
            var id = e.dataTransfer.getData('itemid');
            if (id)
            {
                var from = ko.dataFor(document.getElementById(id));

                self.handleDropItem(from, e);
            }
            else if (e.dataTransfer.files && e.dataTransfer.files.length > 0)
            {
                handleDropFiles(e);
                e.stopPropagation();
                e.preventDefault();
            }
        }
    };
