define('VMPicker',['ko', 'globals', 'util', 'platform', 'data', 'goog', 'VMDocument', 'VMDocSharing', 'tracker'],
function (ko, g, util, platform, d, goog, VMDocument, VMDocSharing, tracker) {
function VMPicker()
{
    this.files = ko.observableArray();
    this.permissions = ko.observableArray();
    this.isPublic = ko.observable();
    this.fileMap = { };
    this.isLoading = ko.observable(false);
    this.needPermission = ko.observable(false);//ko.observable(!goog.hasPermission(GScope.DriveFull));
    this.showError = ko.observable(false);

    this.loadCallbacks = [ ];

    this.init();
}

VMPicker.prototype =
{
    init: function()
    {
        var docShare = new VMDocSharing();

        g.menus.registerModal('shareDoc', docShare, docShare.open.bind(docShare), docShare.onClose.bind(docShare));
    },
    refreshFiles: function ()
    {
        if (DEBUG)
        {
            this.setFiles(g.test.files, 0);

            this.isLoading(false);
            //this.needPermission(false);
        }
        else
        {
            this.runOnFileLoad(this.markFileProperties.bind(this));

            gapi.client.load('drive', 'v2', function ()
            {
                this.loadFileList();
            }.bind(this));

            //this.needPermission(!goog.hasPermission(GScope.DriveFull));
        }
    },
    // If files == undefined, just populate observables
    setFiles: function(files, selectedIndex)
    {
        this.files.removeAll();

        for (var i = 0; i < files.length; i ++)
        {
            var file = files[i];
            var vmDoc = new VMDocument(file);

            vmDoc.isOpen = (i === selectedIndex);

            this.files.push(vmDoc);
        }
        this.setMinHeight();
    },
    loadFileList: function (cb)
    {
        if (!this.isLoading())
        {
            this.isLoading(true);
            this.showError(false);

            gapi.client.load('drive', 'v2', function ()
            {
                gapi.client.drive.files.list(
                {
                    // TODO: Specify required fields
                    'fields': 'items(appDataContents,createdDate,description,editable,etag,exportLinks,iconLink,id,indexableText,kind,labels,md5Checksum,mimeType,ownerNames,owners,parents,permissions,properties,quotaBytesUsed,shared,sharedWithMeDate,sharingUser,title,userPermission,version,writersCanShare),kind,nextLink,nextPageToken',
                    'q': "trashed=false and mimeType='" + goog.REALTIME_MIMETYPE + '.' + goog.APP_ID + "'"
                }).execute(function (results)
                {
                    // if (DEBUG)
                    // {
                    //     log('Picker Results: ', results);
                    // }

                    if (!results.items || results.error)
                    {
                        this.showError('Load Documents');
                    }
                    else
                    {
                        var selectIndex = 0;
                        var currentId = goog.getDocId();

                        for (var i = 0; i < results.items.length; ++i)
                        {
                            var item = results.items[i];

                            this.fileMap[item.id] = item;

                            if (currentId === item.id)
                            {
                                selectIndex = i;
                            }
                        }

                        this.setFiles(results.items, selectIndex);
                    }

                    this.isLoading(false);

                    if (cb)
                    {
                        cb(results);
                    }

                    this.onFilesLoaded(results);
                }.bind(this));
            }.bind(this));
        }
        else
        {
            if (DEBUG) log('Skipping file load due to already loading');
        }
    },
    markFileProperties: function(res)
    {
        if (res && res.items)
        {
            for (var i = 0; i < res.items.length; ++i)
            {
                var fileInfo = res.items[i];

                // Check if the previous default file has been marked.
                if (!fileInfo.properties && fileInfo.title === goog.LEGACY_DefaultDriveDocTitle)
                {
                    // Ensure the current user is the owner of their default file
                    if (fileInfo.userPermission.id === 'me' && fileInfo.userPermission.role === 'owner')
                    {
                        gapi.client.drive.properties.insert({
                            'fileId': fileInfo.id,
                            'resource':
                            {
                                'key': goog.DefaultProperty,
                                'value': goog.DefaultPropertyValue,
                                'visibility': 'PRIVATE'
                            }
                        }).execute(function (result)
                        {
                            if (result.error || result.kind !== 'drive#property')
                            {
                                // Ignore, if this fails we will update again later.
                                if (ShouldLog(LogLevels.Error)) log('Error adding default file property: ', result);

                                debugger;
                            }
                        });

                        break;
                    }
                }
            }
        }
    },
    onFilesLoaded: function(res)
    {
        if (this.loadCallbacks.length > 0)
        {
            for (var i = 0; i < this.loadCallbacks.length; ++i)
            {
                if (this.loadCallbacks[i])
                {
                    this.loadCallbacks[i](res);
                }
            }

            this.loadCallbacks = [ ];
        }
    },
    runOnFileLoad: function(cb)
    {
        this.loadCallbacks.push(cb);
    },
    requestOpenDocument: function(id)
    {
        var doc = this.getFile(id);

        if (doc)
        {
            this.openDocument(doc);
        }
        else
        {
            if (this.needPermission())
            {
                this.doRequestPermission(function ()
                {
                    this.openDocumentFromId(id);
                }.bind(this));
            }
            else
            {
                this.onFilesLoaded(function ()
                {
                    this.openDocumentFromId(id);
                }.bind(this));
            }
        }
    },
    openDocumentFromId: function(id, title)
    {
        var currentDoc = goog.getDocId();

        if (currentDoc !== id)
        {
            var skipSettings = [ Settings.docSharing, Settings.authScopes, Settings.shownTutorial, Settings.lastMessageSync ];

            d.clearData(function ()
            {
                goog.setDocInfo(id, title);

                if (util.hasURLParam('file'))
                {
                    util.removeURLParam('file', /*noReload*/true);
                }
                else if (util.hasURLParam('state'))
                {
                    util.removeURLParam('state', /*noReload*/true);
                }

                // TODO: Remove timeout?
                setTimeout(function ()
                {
                    g.reload();
                }, 500);
            }, skipSettings);
        }
        else
        {
            if (DEBUG) log('Currently open file selected to reopen.', currentDoc);
        }
    },
    openDocument: function(doc)
    {
        g.localDocChangeRequested = true;

        this.openDocumentFromId(doc.id, doc.title);
    },
    doRequestPermission: function(cb)
    {
        // goog.runAuthenticate([GScope.DriveFull], /*runImmediate*/false, /*retry*/false, function (token)
        // {
        //     this.needPermission(!goog.hasPermission(GScope.DriveFull));

        //     if (token && !token.error)
        //     {
        //         g.settings.set(Settings.docSharing, true);

        //         this.loadFileList(cb);
        //     }
        //     else
        //     {
        //         // TODO: Error Handling
        //     }
        // }.bind(this));
    },
    onRequestPermission: function(e)
    {
        this.doRequestPermission();
    },
    onTapReloadDocuments: function(e)
    {
        this.loadFileList();
    },
    // onTapDocument: function(doc)
    // {
    //     this.openDocument(doc);
    // },
    onTapDocument: {
        onClick: function(e)
        {
            var doc = ko.dataFor(e.srcElement);

            if (g.hasClass(e.srcElement, 'documentTitle'))
            {
                this.openDocument(doc);

                tracker.miscAction({
                    type: TrackerType.Misc,
                    misc: TrackerMisc.OpenDocument,
                    data: [
                    {
                        value: true
                    }]
                });
            }
            else
            {
                if (doc instanceof VMDocument)
                {
                    doc.onTapMenu();

                    e.stopImmediatePropagation();
                }
                else
                {
                    g.reportError('Incorrect document type: ' + g.elementToString(e.srcElement));
                }
            }
        }
    },
    onTapAdd: {
        onStart: g.handlerPreventStop,
        onClick: function()
        {
            if (g.vmMain && g.vmMain.isOnline())
            {
                var doc = new VMDocument();

                doc.isOwned(true);

                this.files.push(doc);
                this.setMinHeight();

                g.sendEvent('Menu', 'AddDocument');

                doc.openSharing(function(saved)
                {
                    if(!saved)
                    {
                        this.files.remove(doc);
                        this.setMinHeight();
                    }
                }.bind(this));

                tracker.miscAction({
                    type: TrackerType.Misc,
                    misc: TrackerMisc.AddDocument,
                    data: [
                    {
                        value: true
                    }]
                });
            }
            else
            {
                g.messageQueue.pushMessage({
                    text: 'You must be online to create a new document',
                    type: MessageType.Warning,
                    timeout: 8000
                });
            }
        }
    },
    getFile: function(id)
    {
        return this.fileMap[id];
    },
    removeFile: function(doc)
    {
        this.files.remove(doc);
        this.setMinHeight();

        if (doc.isOpen)
        {
            d.clearData(function ()
            {
                setTimeout(function ()
                {
                    g.reload();
                }, 0);
            });
        }
    },
    setMinHeight: function()
    {
        if(platform.mobile)
            return;
        var documentsList = document.getElementById('documentsList');
        if(!documentsList)
        {
            setTimeout(this.setMinHeight.bind(this), 0);
            return;
        }
        //log('o', documentsList.clientHeight);
        var height = documentsList.clientHeight;
        g.element('sidebarDocumentContainer').style.minHeight = (height + 50) + 'px';
    }
};

return VMPicker;
});
