define('VMTitle',['ko', 'globals', 'data', 'tracker', 'platform'],
function (ko, g, d, tracker, platform) {
function VMTitle(p)
{
    this.text = p.text;
    this.item = p.item;
    this.home = p.home;
    this.isTruncated = ko.observable(false);
    this.pane = p.pane;

    this.init(p);
}

VMTitle.prototype = {
    init: function()
    {
        this.text = this.item.getParsedText();
        if(this.text.length > 25)
        {
            this.text = this.text.substr(0,25);
            this.isTruncated(true);
        }
    },
    onTap:
    {
        onStart: function(e) {
            e.handled = true;
            g.vmMain.setFocusedPane(this.pane);
        },
        onClick: function(e) {
            g.focusedPane.onTapTitle.onClick.call(g.focusedPane, e, this.item);
        }
    }
};

return VMTitle;
});
