
if (!this.document)
{
    importScripts('lib/date.js');
    importScripts('DuchessHelpers.js');
}

function parseWorkerWrapper()
{

if (DuchessHelpersWrapper && !this.helperLibLoaded)
{
    DuchessHelpersWrapper(this.DuchessHelpers = this.DuchessHelpers || {});
}

if (dateWrapper && !this.dateLibLoaded)
{
    dateWrapper();
}

log = postMessage;

var contacts;

onmessage = function(e)
{
    if (e.data.contacts)
    {
        contacts = e.data.contacts;
    }

    if (e.data.items)
    {
        var startTime = Date.now();

        var results = new Array(e.data.items.length);
        for (var i = 0; i < e.data.items.length; i++)
        {
            var entry = e.data.items[i];

            results[i] = DuchessHelpers.parseText(
                entry.text,
                entry.prevDate,
                entry.isUpdate,
                contacts
                );
        }

        postMessage({ 'id': e.data.id, 'styles': results, 'done': true, 'start': startTime, 'end': Date.now() });
    }
    else
    {
        postMessage({ 'id': e.data.id, 'done': false, 'msg': 'ERROR: No items passed to the worker' });
    }
};

}

if (this.document)
{
    window.parseWorkerWrapper = parseWorkerWrapper;
}
else
{
    parseWorkerWrapper();
};
define("parseWorker", function(){});

