define('sounds',['globals', 'platform'],
function (g, platform) {
    var self = {};
    var bank = {};

    function addSource( sound, src )
    {
        var source = document.createElement( 'source' );
        source.src = src;
        sound.appendChild( source );
    }

    self.add = function(name)
    {
        self[name] = name;
        if(!bank[name])
        {
            var sound = document.createElement('audio');
            addSource(sound, 'sounds/' + name + '.mp3');
            //addSource(sound, '/sounds/' + name + '.ogg');
            sound.preload = 'auto';
            bank[name] = sound;
        }
    };

    self.play = function(name)
    {
        if (g.settings.get(Settings.noSounds))
            return;

        if (!bank[name])
        {
            log('Tried to play a sound that has not been loaded:', name);
            console.trace();
            return;
        }

        try
        {
            bank[name].play();
        }
        catch (err)
        {
            log('Failed to play audio', err);
        }
    };

    // TODO: Enable sounds
    // if (!platform.demo)
    // {
    //     self.add('complete');
    // }

    return self;
});
