define('util',[],
function() {

var self = { };

window.URLPart =
{
    Search: 1,
    Hash:   2
};

var URLParamMap =
{
    'demo':          URLPart.Hash,   // Demo
    'login':         URLPart.Hash,   // LandingPage
    'access_token':  URLPart.Hash,   // OAuth
    'error':         URLPart.Hash,   // OAuth
    'expires_in':    URLPart.Hash,   // OAuth
    'scope':         URLPart.Hash,   // OAuth
    'token_type':    URLPart.Hash,   // OAuth
    'data':          URLPart.Hash,   // Demo
    'script':        URLPart.Hash,   // Demo
    'dmode':         URLPart.Hash,   // Demo
    'jasmine':       URLPart.Hash,   // Debug
    'touch':         URLPart.Hash,   // Debug
    'file':          URLPart.Hash,   // GDrive
    'panes':         URLPart.Hash,   // Demo
    'offline':       URLPart.Hash,   // Debug
    'live':          URLPart.Hash,   // Debug
    'test':          URLPart.Hash,   // Debug
    'nolocal':       URLPart.Hash,   // Debug
    'nosync':        URLPart.Hash,   // Debug
    'rdebug':        URLPart.Hash,   // DebugAssist
    'rsession':      URLPart.Hash,   // DebugAssist
    'cacheError':    URLPart.Hash,   // PageUpdate
    'state':         URLPart.Search, // GDrive
};

function setURLPart(part, str)
{
    if (part === URLPart.Search)
    {
        window.location.search = str;
    }
    else if (part === URLPart.Hash)
    {
        window.location.hash = str;
    }
}

function getStringForPart(part)
{
    if (part === URLPart.Search)
    {
        return window.location.search;
    }
    else if (part === URLPart.Hash)
    {
        return window.location.hash;
    }

    return undefined;
}

function getURLPartForParam(param)
{
    return URLParamMap[param];
}

self.hasURLParam = function(key, value)
{
    var str = key + '=';
    if (value)
    {
        str += value;
    }

    var part = getURLPartForParam(key);
    if (!part) { log('Invalid URL Part'); debugger; }

    var rawString = getStringForPart(part);

    return (rawString.indexOf(str) >= 0);
}

self.getURLParam = function(key, forcePart)
{
    var part = forcePart ? forcePart : getURLPartForParam(key);

    if (part === URLPart.Search)
    {
        key = key.replace(/[\[]/, "\\\[").replace(/[\]]/, "\\\]");
        var regexS = "[\\?&]" + key + "=([^&#]*)";
        var regex = new RegExp(regexS);
        var results = regex.exec(window.location.href);

        if (results === null)
        {
            return undefined;
        }
        else
        {
            return decodeURIComponent(results[1].replace(/\+/g, " "));
        }
    }
    else if (part === URLPart.Hash)
    {
        var queryString = window.location.hash.substring(1);
        var regex = /([^&=]+)=([^&]*)/g;
        var m;

        while (m = regex.exec(queryString))
        {
            if (decodeURIComponent(m[1]) === key)
            {
                return decodeURIComponent(m[2]);
            }
        }

        return undefined;
    }
    else
    {
        log('Invalid URL part');
        debugger;
    }
};

self.insertURLParam = function(key, value, noReload)
{
    key = encodeURIComponent(key);
    value = encodeURIComponent(value);

    var part = getURLPartForParam(key);
    var rawString = getStringForPart(part);

    var params = rawString.substr(1).split('&');

    for (var i = params.length - 1; i >= 0; i --)
    {
        var param = params[i].split('=');

        if (param[0] == key)
        {
            param[1] = value;

            params[i] = param.join('=');

            break;
        }
    }

    if (i < 0)
    {
        params[params.length] = [key,value].join('=');
    }

    setURLPart(part, params.join('&'));

    if (part === URLPart.Hash && !noReload)
    {
        window.location.reload();
    }
};

self.removeURLParam = function(key, noReload)
{
    key = encodeURIComponent(key);

    var part = getURLPartForParam(key);
    var rawString = getStringForPart(part);

    var params = rawString.substr(1).split('&');

    for(var i = params.length - 1; i >= 0; i --)
    {
        var param = params[i].split('=');

        if (param[0] == key)
        {
            params.splice(i, 1);
            break;
        }
    }

    setURLPart(part, params.join('&'));

    if (part === URLPart.Hash && !noReload)
    {
        window.location.reload();
    }
};

if (DEBUG)
{
    window.getURLParam = self.getURLParam;
    window.insertURLParam = self.insertURLParam;
    window.removeURLParam = self.removeURLParam;
}


//
// Storage
//

var pck = (window.location.protocol === 'chrome-extension:');

self.__storage = !pck ? window.localStorage : chrome.storage.local;

function dummyStorageGetFn() { };

self.storage =
{
    getItem: (self.__storage.getItem || function (key, cb)
    {
        return this.get(key, cb || dummyStorageGetFn);
    }).bind(self.__storage),

    setItem: (self.__storage.setItem || function (key, value)
    {
        var obj = { }; obj[key] = value;

        self.__storage.set(obj);
    }).bind(self.__storage),

    removeItem: (self.__storage.removeItem || function (key, cb)
    {
        this.remove(key, cb);
    }).bind(self.__storage),
};


return self;

});

