    //
    // Per Pane State:
    // id, focus, mode, showarch, search, offset
    //
    self.addPane = function(pane, index)
    {
        try{g.PAssert(1374,pane, 'Must pass in a valid pane to update');}catch(PERR){g.reportError(PERR);}

        var paneState = g.settings.get(Settings.paneState);
        if (!paneState)
        {
            paneState = [ ];
        }

        try{g.PAssert(1375,paneState.length === self.panes().length, 'Pane state does not match currently loaded pane set');}catch(PERR){g.reportError(PERR);}

        if (index === undefined)
        {
            self.panes.push(pane);
        }
        else
        {
            self.panes.splice(index, 0, pane);
        }

        paneState.push({
            id: pane.id,
            focus: pane.item().id,
            mode: pane.mode(),
            showarch: pane.showArchived(),
            search: pane.vmSearch.search(),
            offset: 0
        });

        g.settings.set(Settings.paneState, paneState);
    };

    self.removePaneAtIndex = function(index)
    {
        try{g.PAssert(1376,index !== undefined, 'Must pass in a valid index to remove');}catch(PERR){g.reportError(PERR);}

        var paneState = g.settings.get(Settings.paneState);
        try{g.PAssert(1377,paneState, 'Pane state should always be valid when removing panes');}catch(PERR){g.reportError(PERR);}

        try{g.PAssert(1378,paneState.length === self.panes().length, 'Pane state does not match currently loaded pane set');}catch(PERR){g.reportError(PERR);}

        self.panes.splice(index, 1);

        paneState.splice(index, 1);

        g.settings.set(Settings.paneState, paneState);
    }

    self.afterLoaded = function()
    {
        var paneState;
        if (self.isDemoMode())
        {
            self.clearPaneState();
        }
        else
        {
            paneState = g.settings.get(Settings.paneState);
        }

        if (paneState)
        {
            for (var i = 0; i < paneState.length; ++i)
            {
                if(platform.mobile && i > 0)
                {
                    break;
                }
                var paneInfo = paneState[i];

                // Update stored ID for the pane, this will change per load
                paneInfo.id = nextPaneId;

                var item = d.getModel(paneInfo.focus);

                // When resetting the client it is possible that the hash will contain items that dont
                // exist. Handle this by switching them back to the root. This will also reset the scroll
                // position and search.
                if (!item)
                {
                    item = self.root();

                    paneInfo.focus = item.id;
                    paneInfo.offset = 0;
                    paneInfo.search = '';
                }

                try{g.PAssert(1379,item, 'Should always have a valid item at this point');}catch(PERR){g.reportError(PERR);}

                var pane = new VMPane({
                    id: nextPaneId++,
                    item: item,
                    mode: paneInfo.mode,
                    search: paneInfo.search,
                    offset: paneInfo.offset
                });

                if (paneInfo.showarch)
                {
                    pane.showArchived(true);
                }

                // TODO: Fix potential perf problems with this approach, replace with something
                //       that wont recreate DOM nodes while updating knockout

                // When loading panes from the current pane state do not call addPane as it would double add
                // the pane to the state.
                self.panes.push(pane);
                self.setFocusedPane(pane, /*forLoad*/true);
                self.zoomin(item, true);
            }

            if (self.panes().length !== 1)
            {
                self.setFocusedPane(self.panes()[0], /*forLoad*/true);
            }
        }

        if (self.isDemoMode() && g.demoPanes)
        {
            for(var i = 0; i < g.demoPanes.length; i ++)
            {
                self.addPane(new VMPane({
                    id: nextPaneId ++,
                    item: self.root(),
                    mode: +g.demoPanes[i]
                }));
            }

            var pane = self.panes()[0];
            g.focusedPaneID = pane.id;
            g.focusedPane = pane;
            g.focusedItem = pane.item();

            self.zoomin(pane.item(), true);
        }
        else if (self.panes().length === 0)
        {
            var pane = new VMPane({
                id: nextPaneId++,
                item: self.root()
            });

            self.addPane(pane);

            if (!platform.mobile && g.activeDisplayMode & DisplayMode.ForceTwoPane)
            {
                var pane2 = new VMPane({
                    id: nextPaneId++,
                    item: self.root(),
                    mode: PaneMode.Timeline
                });

                self.addPane(pane2);
            }

            // self.setFocusedPane('pane' + 0);
            // self.zoomin(self.root(), true);

            g.focusedPaneID = pane.id;
            g.focusedPane = pane;
            g.focusedItem = self.root();

            self.zoomin(self.root(), true);
        }

        if (platform.mobile)
        {
            // setTimeout(function ()
            // {
            g.phoneMenu = new VMPhoneMenu();
            g.keyboardToolbar = new VMKeyboardToolbar();
            // }, 0);
        }

        // self.root().zoomin({time: 0});

        // self.root(new VMLI({data: docRoot}));


        // if (fullscreen)
        // {
        //     if(fullscreen != self.root().id)
        //     {
        //         var newFullscreen = d.getModel(fullscreen);

        //         if (!newFullscreen)
        //         {
        //             newFullscreen = self.root();
        //             try{g.PAssert(1380,newFullscreen);}catch(PERR){g.reportError(PERR);}
        //         }
        //         // newFullscreen.zoomin({time: 0});
        //         self.zoomin(newFullscreen);
        //     }
        //     elsepS
        //     {
        //         // Hash is root so just clear it off
        //         location.hash = '';
        //     }
        // }
        // else
        // {
        //     self.zoomin(self.root());
        // }

        g.timeline.updateNotifications();

        //self.sendAnalytics();
    };

