    self.clearPaneState = function()
    {
        g.settings.reset(Settings.paneState);
    };

    self.updatePaneState = function(pane)
    {
        if (g.isRunningScript() || platform.mobile)
        {
            return;
        }

        try{g.PAssert(1381,pane, 'Must pass in a valid pane to update');}catch(PERR){g.reportError(PERR);}

        var paneState = g.settings.get(Settings.paneState);
        try{g.PAssert(1382,paneState, 'Pane state should always be valid when updating state');}catch(PERR){g.reportError(PERR);}

        try{g.PAssert(1383,paneState.length === self.panes().length, 'Pane state does not match currently loaded pane set');}catch(PERR){g.reportError(PERR);}

        var paneStateFound = false;
        for (var i = 0; i < paneState.length; ++i)
        {
            var info = paneState[i];

            if (info.id === pane.id)
            {
                info.focus = pane.item().id;
                info.mode = pane.mode();
                info.showarch = pane.showArchived();
                info.search = pane.vmSearch.search();
                //info.offset = pane.getScrollOffset();

                g.settings.set(Settings.paneState, paneState);

                paneStateFound = true;

                break;
            }
        }

        try{g.PAssert(1384,paneStateFound, 'Pane state should always be found');}catch(PERR){g.reportError(PERR);}
    };

    self.updatePaneScrollState = function()
    {
        if (g.isRunningScript() || platform.mobile)
        {
            return;
        }

        var paneState = g.settings.get(Settings.paneState);

        // Pane length could be 0 if at the login screen
        if (paneState && self.panes().length > 0)
        {
            try{g.PAssert(1385,paneState.length === self.panes().length, 'Pane state does not match currently loaded pane set');}catch(PERR){g.reportError(PERR);}

            for (var i = 0; i < paneState.length; ++i)
            {
                var info = paneState[i];
                var pane = self.panes()[i];

                info.offset = pane ? pane.getScrollOffset() : 0;
            }

            g.settings.set(Settings.paneState, paneState);
        }
    };

