    self.getFirstSelectedItem = function()
    {
        try{g.PAssert(1387,platform.mobile, 'Selection tracking only available from mobile');}catch(PERR){g.reportError(PERR);}

        if(self.selected().length > 0)
            return self.selected()[0];

        return undefined;
    };

    // selection must be an array
    self.setSelected = function(selection)
    {
        try{g.PAssert(1388,platform.mobile, 'Selection tracking only available from mobile');}catch(PERR){g.reportError(PERR);}

        selection = selection || [];
        self._clearIsSelected(selection);
        for(var i = 0; i < selection.length; i ++)
        {
            selection[i].isSelected(true);
        }
        self.selected(selection);
    };

    // newSelection must be an array
    self._clearIsSelected = function(newSelected)
    {
        try{g.PAssert(1389,platform.mobile, 'Selection tracking only available from mobile');}catch(PERR){g.reportError(PERR);}

        newSelected = newSelected || [];
        // Remove selection from old items that aren't in new items
        for(var i = 0; i < self.selected().length; i ++)
        {
            // If item is in the new selection, don't remove selection from it
            var selectedItem = self.selected()[i];
            if(newSelected.indexOf(selectedItem) < 0)
            {
                selectedItem.isSelected(false);
            }
        }
    };

    self.clearSelected = function(newSelected)
    {
        try{g.PAssert(1390,platform.mobile, 'Selection tracking only available from mobile');}catch(PERR){g.reportError(PERR);}

        self._clearIsSelected(newSelected);
        self.selected([]);
    };

    // Selection is only used on mobile platforms, desktop does not use this.
    if (platform.mobile)
    {
        document.onselectionchange = function(e)
        {
            var selection = g.getSelectionInfo();
            var itemsChanged = true;
            var arrToSelect;

            // This gets set by g.blurActiveElement
            if(g.ignoreSelectionChange)
            {
                g.ignoreSelectionChange = false;
                prevRange = undefined;
                return;
            }

            if (selection)
            {
                // On android, if a drag has started, ensure that no text is selected
                if (platform.android && g.dragging)
                {
                    g.clearSelection();

                    return;
                }

                var range = selection.selection.getRangeAt(0);
                itemsChanged = !prevRange || range.startContainer !== prevRange.startContainer || range.endContainer !== prevRange.endContainer;

                if (prevRange && !itemsChanged &&
                    range.startOffset == prevRange.startOffset &&
                    range.endOffset == prevRange.endOffset)
                {
                    return; // Nothing's changed so return out
                }

                if (range.startContainer.id === 'itemOptions' || range.startContainer.id === 'fullscreenClickGrabber')
                {
                    return;
                }

                prevRange = range;
                // var docRoot = $(range.startContainer).parents('.pane')[0];
                // var paneContent = $(range.startContainer).parents('.paneContent')[0];

                if (g.focusedPane && g.focusedPane.mode() === PaneMode.Normal)
                {
                    var origLIParent = g.getLIParent(range.startContainer);
                    var origStart = origLIParent ? ko.dataFor(origLIParent) : undefined;

                    var skipUpdates = !origStart || (!origStart instanceof VMPane && !origStart instanceof VMLI);
                    if (skipUpdates)
                    {
                        // Don't do anything because this isn't an item
                    }
                    else
                    {
                        g.restrictSelectionToText();

                        selection = g.getSelectionInfo();
                        range = selection.selection.getRangeAt(0);

                        var startLI = g.getLIParent(range.startContainer);
                        var endLI = g.getLIParent(range.endContainer);

                        if (startLI && endLI && !g.hasClass(startLI, 'emptyPaneItem') && !g.hasClass(endLI, 'emptyPaneItem'))
                        {
                            var item = ko.dataFor(startLI);

                            if (itemsChanged)
                            {
                                if (startLI !== endLI)
                                {
                                    var endItem = ko.dataFor(endLI);

                                    // Add all lines in the selection to arrToSelect
                                    arrToSelect = g.getItemsBetween(item, endItem, g.focusedPane);
                                }
                                else
                                {
                                    arrToSelect = [ item ];

                                    if (android.isEnabled())
                                    {
                                        android.updateInput(item);
                                    }
                                }
                            }
                        }
                    }
                }
            }
            else if (platform.android || platform.mobileie || (platform.ios && (!platform.app || platform.ipad)))
            {
                g.fireCustomEvent('closeKeyboard');
            }

            if(!selection)
            {
                prevRange = undefined;
            }

            // If there are items which have changed their selection state and the selection state matters.
            if (itemsChanged && !(platform.android || platform.mobileie))
            {
                // Set selection to new items
                self.setSelected(arrToSelect);
            }
        };
    }

    self.setFocusedPaneFromElement = function(element)
    {
        if (element)
        {
            var paneEl = g.hasClass(element, 'paneRoot') ? element : g.getPaneForElement(element).rootElement;

            var pane = ko.dataFor(paneEl);
            try{g.PAssert(1391,pane, 'Could not find matching pane');}catch(PERR){g.reportError(PERR);}

            self.setFocusedPane(pane);
        }
    };

    var hasCalledNonLoad = false;
    self.setFocusedPane = function(pane, forLoad)
    {
        if (g.focusedPaneID !== pane.id)
        {
            hasCalledNonLoad = !forLoad;

            g.focusedPaneID = pane.id;
            // var paneIndex = parseInt(id.replace(/\D/g, ''), 10);
            g.focusedPane = pane;//self.panes()[paneIndex];
            g.focusedItem = g.focusedPane.item();
        }

        // if (!forLoad && !platform.phone)
        // {
        //     g.menus.openHoverMenu(g.focusedItem);
        // }
    };

    self.zoomin = function(item, forLoad)
    {
        try{g.PAssert(1392,item, 'Must supply a valid item to zoom to');}catch(PERR){g.reportError(PERR);}

        var oldItem = g.focusedItem;
        self._zoom(item, forLoad);

        var pane = g.focusedPane;

        if(!forLoad)
        {
            g.sendEvent('Menu', 'ZoomIn');
        }

        if (!platform.mobile && pane.paneContent)
        {
            g.setSelection(pane.paneContent, 0, 0);
        }

        if (pane.rootElement)
        {
            pane.setScrollOffset();
        }
        else
        {
            setTimeout(function ()
            {
                pane.setScrollOffset();
            }, 0);
        }

        if (item.isCollapsed())
        {
            item.toggleCollapsed();
        }

        if (!forLoad)
        {
            tracker.performAction(TrackerType.Zoom, {
                itemID: item.id,
                oldItemID: oldItem.id,
                zoomOut: false,
                time: g.zoomTime
            });
        }
    };

    self.zoomout = function(item, zoomedOutFrom)
    {
        try{g.PAssert(1393,item, 'Must supply a valid item to zoom to');}catch(PERR){g.reportError(PERR);}
        var oldItem = g.focusedItem;

        self._zoom(item, false);

        g.sendEvent('Menu', 'ZoomOut');

        g.selectChildren(zoomedOutFrom, zoomedOutFrom.getParsedText().length, 0);

        g.scrollToTop(zoomedOutFrom.getSpan(), 0);

        tracker.performAction(TrackerType.Zoom, {
            itemID: item.id,
            oldItemID: oldItem.id,
            zoomOut: true,
            time: g.zoomTime
        });
    };

    self._zoom = function(item, forLoad)
    {
        try{g.PAssert(1394,item, 'Must supply a valid item to zoom to');}catch(PERR){g.reportError(PERR);}

        var oldItem = g.focusedItem;

        if (oldItem && oldItem.id === item.id)
        {
            // Already zoomed at this item, so do nothing
            return;
        }

        g.focusedPane.item(item);
        g.focusedPane.notifyOfZoom();

        g.focusedItem = item;

        if (!forLoad)
        {
            self.updatePaneState(g.focusedPane);
        }
    };

