    self.handleFullscreenClick = function(callback)
    {
        if (!fullscreenClickHandled)
        {
            g.setupClick(grabber, undefined,
            {
                onRightClick: function(e)
                {
                    grabber.style.display = 'none';

                    var realElem = document.elementFromPoint(e.clientX, e.clientY);

                    var pane = g.getPaneForElement(realElem);

                    if (pane)
                    {
                        pane.onTap.onRightClick({
                            srcElement: realElem,
                            pageX: e.x,
                            pageY: e.y,
                            preventDefault: function () { e.preventDefault(); },
                            stopPropagation: function () { e.stopPropagation(); },
                            stopImmediatePropagation: function() { e.stopImmediatePropagation(); }
                        });
                    }

                    grabber.style.display = '';
                },
                onEnd: function(e)
                {
                    self.cancelFullscreenClick();
                    callback();
                }
            });

            g.removeClass(grabber, 'none');

            fullscreenClickHandled = true;
        }
    };
    self.cancelFullscreenClick = function()
    {
        if (fullscreenClickHandled)
        {
            g.addClass(grabber, 'none');
            //g.removeClick(grabber);

            g.menus.closeAllMenus();

            fullscreenClickHandled = false;
        }
    };

