define('VMHelp',['ko', 'globals'],
function (ko, g) {

function VMHelp()
{
    this.isVisible = ko.observable(false);

    this.helpContentEle = undefined;
    this.iframe = undefined;

    this.init();
}

VMHelp.prototype = {
    init: function()
    {
        g.menus.registerModal('help', this);
    },
    onLoad: function(ele)
    {
        this.helpContentEle = ele;

        if (!this.iframe)
        {
            this.iframe = document.createElement('iframe');

            this.iframe.id = 'helpContentFrame';
            this.iframe.src = 'help.html';
            this.iframe.setAttribute('seamless', 'seamless');
        }

        this.helpContentEle.insertBefore(this.iframe, this.helpContentEle.firstElementChild);
    },
    onTapClose: function()
    {
        g.menus.closeModal('help');
    }
}

return VMHelp;
});
