define('VMMenus',['ko', 'globals', 'data', 'gdata', 'VMSearch', 'VMTitle', 'edit', 'VMLI', 'tracker', 'platform', 'VMDocument'],
function (ko, g, d, gdata, VMSearch, VMTitle, edit, VMLI, tracker, platform, VMDocument) {
function VMMenus()
{
    this.linkAddress = ko.observable();
    this.linkTitle = ko.observable('temporary title');
    this.itemMenuPhoneWidth = -60 * 4;
    this.rightMenuWidth = 50;
    this.phoneLastTop = 0;
    this.itemMenuPhone = undefined;
    this.itemMenuBG = undefined;
    this.itemMenuPhoneMode = ko.observable(0); // 0: Disabled, 1: Complete, 2: Archive, 3: Delete
    this.rightMenuPhone = undefined;
    this.rightMenuPhoneBG = undefined;
    this.lastSwipeMove = 0;
    this.lastSwipeMoveTime = 0;
    this.canClosePane = ko.observable(false);
    this.showArchivedMenuOption = ko.observable(false);
    this.archivedMenuText = ko.observable('');
    this.itemIsHeader = ko.observable(false);
    this.itemHasChildren = ko.observable(false);
    this.itemIsCollapsed = ko.observable(false);
    this.itemIsArchived = ko.observable(false);
    this.itemCanIndent = ko.observable(false);
    this.itemCanUnindent = ko.observable(false);
    this.itemCanZoom = ko.observable(false);
    this.lastZIndex = 37;
    this.contextMenuOpenedOn = ko.observable(undefined);
    this.isModalVisible = ko.observable(false);
    this.isModalOpen = ko.observable(false);
    this.modalOpened = undefined;
    this.modalOpenedBound = undefined;
    this.modalsBound = false;
    this.menusBound = false;

    this.init();
}

VMMenus.prototype = {
    init: function()
    {
        window.addEventListener('closeKeyboard', this.onCloseKeyboard.bind(this));
        g.addCustomEventListener('keyEscape', this.onEscapeKey.bind(this), 100);

        if(!platform.mobile)
        {
            this.isModalOpen.subscribe(function(newValue)
            {
                if(newValue)
                {
                    requestAnimationFrame(function()
                    {
                        requestAnimationFrame(function()
                        {
                            g.addClass(g.element('main'), 'modalOpen');
                            g.addClass(g.element('desktopMenu'), 'modalOpen');
                            g.addClass(g.element('desktopSidebar'), 'modalOpen');
                        });
                    });
                }

                else
                {
                    requestAnimationFrame(function()
                    {
                        g.removeClass(g.element('main'), 'modalOpen');
                        g.removeClass(g.element('desktopMenu'), 'modalOpen');
                        g.removeClass(g.element('desktopSidebar'), 'modalOpen');
                    });
                }
            });
        }
    },
    bind: function()
    {
        if (!this.menusBound)
        {
            this.menusBound = true;

            g.applyBindings(this, 'contextMenus');

            if (platform.mobile)
            {
                this.itemMenuPhone = g.element('itemMenuPhone');
                this.rightMenuPhoneBG = g.element('rightMenuPhoneBG');
                this.rightMenuPhone = g.element('rightMenuPhone');
                this.rightMenuPhoneItems = g.element('rightMenuPhoneItems');
                this.itemMenuBG = g.element('itemMenuBG');

                g.applyBindings(this, 'itemMenuPhone');
                g.applyBindings(this, 'rightMenuPhoneItems');
            }
        }
    },
    onEscapeKey: function()
    {
        if (this.isModalOpen())
        {
            return this.closeModalInternal(false);
        }
    },
    itemMenuPhoneClass: function()
    {
        var mode = this.itemMenuPhoneMode();
        if(mode === 0)
            return 'complete completeDisabled';
        else if(mode == 1)
            return 'complete';
        else if(mode == 2)
            return 'notVisible';
        // else if(mode == 2)
        //     return 'archive';
        // else
        //     return 'delete';
    },
    selectionHasChildren: function()
    {
        for(var i = 0; i < g.vmMain.selected().length; i ++)
        {
            if(g.vmMain.selected()[i].hasChildren())
                return true;
        }
        return false;
    },
    selectionIsHeader: function()
    {
        return g.vmMain.selected().length === 1 &&
                g.vmMain.selected()[0].isHeader();
    },
    openLinkMenu: function(target, address, left, top)
    {
        this.linkAddress(address);
        g.menuOnItem = target;

        var menuLink = document.getElementById('contextMenuLink');
        menuLink.style.left = left + 'px';
        menuLink.style.top = top + 'px';
        menuLink.style.display = 'block';

        g.vmMain.handleFullscreenClick((function()
        {
            this.closeLinkMenu();
        }).bind(this));
    },
    closeLinkMenu: function()
    {
        g.menuOnItem = undefined;
        this.linkAddress('');

        var menuLink = document.getElementById('contextMenuLink');
        menuLink.style.display = 'none';

        g.vmMain.cancelFullscreenClick();
    },
    onTapLink: {
        onClick: function(e)
        {
            // Link navigation is handled by the <a> itself so just close the menu here.
            this.closeLinkMenu();
        }
    },
    closeAllMenus: function()
    {
        this.linkAddress('');

        var menuLink = document.getElementById('contextMenuLink');
        menuLink.style.display = 'none';

        g.menuOnItem = undefined;
    },
    setupItemMenu: function(target)
    {
        g.element('menuGoto').style.display = g.focusedPane.isSearching ? 'block' : 'none';
    },
    onTapHover: {
        onStart: function(e) {
            e.preventDefault();
        },
        onClick: function(e) {
            var id = e.srcElement.id;

            g.sendEvent('Menu', id);
        }
    },
    // onToolbarOver: function(m, e)
    // {
    //     var text;
    //     switch(e.target.id)
    //     {
    //         case 'hoverPriority':
    //             text = 'Important (' + platform.cmdChar + ' + 1)';
    //             break;
    //         case 'hoverComplete':
    //             text = 'Complete (' + platform.cmdChar + ' + 6)';
    //             break;
    //         case 'hoverStar':
    //             text = 'Star (' + platform.cmdChar + ' + 8)';
    //             break;
    //         case 'hoverComment':
    //             text = 'Note (' + platform.cmdChar + ' + /)';
    //             break;
    //         case 'hoverHeader':
    //             text = 'Header';
    //             break;
    //         case 'hoverBold':
    //             text = 'Bold (' + platform.cmdChar + ' + B)';
    //             break;
    //         case 'hoverItalic':
    //             text = 'Italic (' + platform.cmdChar + ' + I)';
    //             break;
    //         case 'hoverUnderline':
    //             text = 'Underline (' + platform.cmdChar + ' + U)';
    //             break;
    //         case 'hoverOutdent':
    //             text = 'Unindent (Shift + Tab)';
    //             break;
    //         case 'hoverIndent':
    //             text = 'Indent (Tab)';
    //             break;
    //         case 'hoverArchive':
    //             text = 'Archive';
    //             break;
    //         case 'hoverDelete':
    //             text = 'Delete';
    //             break;
    //     }

    //     if(text)
    //         g.vmMain.showTooltip(e.target, text);
    //     else
    //         g.vmMain.hideTooltip();
    // },
    // onToolbarLeave: function(m, e)
    // {
    //     g.vmMain.hideTooltip();
    // },
    multiSelect: function(item)
    {
        item.isSelected(!item.isSelected());
        if(item.isSelected())
        {
            g.vmMain.selected.push(item);
        }
        else
        {
            g.vmMain.selected.splice(g.vmMain.selected.indexOf(item), 1);
        }
    },
    revealGestureMenu: function(item, e)
    {
        try{g.PAssert(1338,g.revealingMenu === false, 'Must not have already started a gesture');}catch(PERR){g.reportError(PERR);}
        g.revealingMenu = true;

        this.startedInMultiSelectMode = g.phoneMenu.multiSelectMode();

        g.menuOnItem = item;

        {
            var menuSpan = item.getSpan();

            var height = 40;

            var offsetTop = g.offset(menuSpan).top;

            var top = offsetTop - g.phoneMainY; // The 4 is from the padding-top

            // Center vertically with span
            var spanHeight = g.height(menuSpan) + 8; // 4 padding on top and bottom
            top += (spanHeight - height) / 2;

            var paneTop = top;

            // If top is different, set the style
            if (this.phoneLastTop !== paneTop)
            {
                this.phoneLastTop = paneTop;
            }
        }

        this.itemMenuPhone.style[platform.transition.style] = '';

        g.setTransformStyle(this.itemMenuPhone, 0, this.phoneLastTop);

        if (!this.startedInMultiSelectMode)
        {
            g.setTransformStyle(this.rightMenuPhone, 0, 0);
        }

        this.rightMenuThresh = (this.rightMenuWidth / 200) * platform.windowWidth.peek();
    },
    updateRevealGestureMenu: function (e)
    {
        try{g.PAssert(1339,g.revealingMenu === true, 'Must have already started a gesture to perform an update');}catch(PERR){g.reportError(PERR);}

        var xOffset = e.diffX;

        this.lastSwipeMove = e.xThisFrame;
        this.lastSwipeMoveTime = e.timeStamp;

        if (this.startedInMultiSelectMode)
        {
            xOffset -= this.rightMenuWidth;

            var offset = g.clamp(xOffset, -this.rightMenuWidth, 0);

            g.setTransformStyle(this.rightMenuPhone, offset, 0);
        }
        else
        {
            // Move menu into place
            var clampedOffset = g.clamp(xOffset, -this.rightMenuWidth, 0);
            g.setTransformStyle(this.itemMenuPhone, platform.windowWidth.peek() + clampedOffset, this.phoneLastTop);

            var multPoint = -this.rightMenuWidth - this.rightMenuThresh;

            // Scale the right menu background
            var scalePerc = g.clamp((-xOffset - this.rightMenuWidth) / this.rightMenuThresh, 0, 1);
            this.itemMenuBG.style[platform.transform.style] = 'scale3d(1,' + scalePerc + ', 1)';

            var offset = g.clamp(xOffset + this.rightMenuWidth + this.rightMenuThresh, -this.rightMenuWidth, 0);
            g.setTransformStyle(this.rightMenuPhone, offset, 0);

            var opacityPerc = g.clamp((-xOffset + multPoint) / (this.rightMenuWidth * 0.75), 0, 1);
            this.itemMenuPhone.style.opacity = (1 - opacityPerc);

            if(!g.isKeyboardOpen)
            {
                g.vmMain.setSelected( [ g.menuOnItem ] );
            }

            // Set complete icon color

            // if (xOffset < multPoint)
            // {
            //     log('set to 0', offset, multPoint);
            //     if (this.itemMenuPhoneMode.peek() !== 0)
            //     {
            //         this.itemMenuPhoneMode(0);
            //     }
            // }
            if (xOffset <= -this.rightMenuWidth)
            {
                if (this.itemMenuPhoneMode.peek() !== 1)
                {
                    this.itemMenuPhoneMode(1);
                }
            }
            else
            {
                if (this.itemMenuPhoneMode.peek() !== 0)
                {
                    this.itemMenuPhoneMode(0);
                }
            }
        }
    },
    stopRevealGestureMenu: function (e)
    {
        try{g.PAssert(1340,g.revealingMenu === true, 'Must have already started a gesture');}catch(PERR){g.reportError(PERR);}

        var transitionTime = 200;
        var xOffset = e.diffX;

        g.revealingMenu = false;

        var timeSinceMove = e.timeStamp - this.lastSwipeMoveTime;

        if (!this.startedInMultiSelectMode)
        {
            if (xOffset <= -this.rightMenuWidth - this.rightMenuThresh)
            {
                g.setTransform(this.rightMenuPhone, -this.rightMenuWidth, 0, 200, 'ease-out');

                g.phoneMenu.multiSelectMode(true);
                g.blurActiveElement(true);
                g.vmMain.setSelected( [ g.menuOnItem ] );
            }
            else
            {
                if (xOffset <= -this.rightMenuWidth)
                {
                    g.menuOnItem.isComplete(!g.menuOnItem.isComplete());
                }

                g.setTransform(this.rightMenuPhone, 0, 0, 200, 'ease-out');
                if(!g.isKeyboardOpen)
                {
                    g.vmMain.clearSelected();
                }
            }
        }
        else
        {
            xOffset -= this.rightMenuWidth;
            var offset = g.clamp(xOffset, -this.rightMenuWidth, 0);
            if(offset <= -this.rightMenuWidth / 2)
            {
                g.setTransform(this.rightMenuPhone, -this.rightMenuWidth, 0, 200, 'ease-out');
            }
            else
            {
                this.closeMultiSelect();
                g.vmMain.clearSelected();
            }
        }

        this.closePhoneMenu(transitionTime);
    },
    closeMultiSelect: function ()
    {
        // this.rightMenuPhoneBG.style.opacity = 0;
        g.setTransform(this.rightMenuPhone, 0, 0, 200, 'ease-out');
        g.phoneMenu.multiSelectMode(false);

        g.vmMain.clearSelected();
    },
    closePhoneMenu: function ()
    {
        var transitionTime = 200;

        // Move menu to right side of screen
        g.setTransform(this.itemMenuPhone, platform.windowWidth(), this.phoneLastTop, transitionTime, 'ease-out');

        g.menuOnItem = undefined;
    },
    onResize: function ()
    {
        if (!g.menuOnItem)
        {
            g.setTransform(this.itemMenuPhone, platform.windowWidth(), this.phoneLastTop);
        }
    },
    onTapMenuPhone: {
        onStart: g.handlerPreventStop,
        onClick: function(e)
        {
            if (g.vmMain.selected().length === 0)
                return;

            var id = e.srcElement.id;
            var fn;
            var val;
            switch (id)
            {
                case 'phoneMenuComplete':
                    fn = 'isComplete';
                    val = !g.vmMain.getFirstSelectedItem().isComplete();
                    break;
                case 'phoneMenuFlag':
                    fn = 'isFlagged';
                    val = !g.vmMain.getFirstSelectedItem().isFlagged();
                    break;
                case 'phoneMenuPriorityNone':
                    fn = 'priority';
                    val = VMLIFlag.None;
                    break;
                case 'phoneMenuPriorityHigh':
                    fn = 'priority';
                    val = g.vmMain.getFirstSelectedItem().priority() == VMLIFlag.P0 ? VMLIFlag.None : VMLIFlag.P0;
                    break;
                case 'phoneMenuPriorityMed':
                    fn = 'priority';
                    val = g.vmMain.getFirstSelectedItem().priority() == VMLIFlag.P1 ? VMLIFlag.None : VMLIFlag.P1;
                    break;
                case 'phoneMenuPriorityLow':
                    fn = 'priority';
                    val = g.vmMain.getFirstSelectedItem().priority() == VMLIFlag.P2 ? VMLIFlag.None : VMLIFlag.P2;
                    break;
                case 'phoneMenuZoom':
                    g.vmMain.zoomin(g.vmMain.getFirstSelectedItem());
                    this.closeMultiSelect();
                    break;
                case 'phoneMenuDelete':
                    fn = 'deleteSelf';
                    break;
                case 'phoneMenuCollapse':
                    if (this.selectionHasChildren())
                    {
                        for (var i = 0; i < g.vmMain.selected().length; i ++)
                        {
                            if (g.vmMain.selected()[i].hasChildren())
                            {
                                if (!val)
                                {
                                    val = !g.vmMain.selected()[i].isCollapsed();
                                }

                                g.vmMain.selected()[i].isCollapsed(val);
                            }
                        }
                    }
                    break;
            }

            if (fn)
            {
                for (var i = 0; i < g.vmMain.selected().length; i ++)
                {
                    g.vmMain.selected()[i][fn](val);
                }
            }

            if (id === 'phoneMenuDelete')
            {
                g.vmMain.clearSelected();
            }
        }
    },

    onTapComplete: {
        onClick: function(e) {
            g.menuOnItem.isComplete(!g.menuOnItem.isComplete());
       }
    },
    onTapPriority: {
        onClick: function(e) {
            var prevPri = g.menuOnItem.priority();

            var newPri = (prevPri !== VMLIFlag.None) ? VMLIFlag.None : VMLIFlag.P0;

            g.menuOnItem.priority(newPri);
       }
    },
    onTapCollapse: {
        onClick: function(e) {
            g.menuOnItem.toggleCollapsed();
       }
    },
    onTapDelete: {
        onClick: function(e) {
            tracker.beginAction(); // BEGIN TRACKER <----

            g.menuOnItem.deleteSelf();

            tracker.endAction(); // END TRACKER <----
       }
    },
    paneTapped: function()
    {
        if(g.menuOnItem)
        {
            this.closePhoneMenu(100);
        }
    },
    openContextMenu: function(offset, menu, toLeft)
    {
        if (g.autocomplete)
        {
            g.autocomplete.hide();
        }

        var styles = {
            visibility: 'visible',
            top: offset.top
        };

        var menuWidth = g.width(menu);
        if (!toLeft)
        {
            if (offset.left + menuWidth > platform.windowWidth())
            {
                toLeft = true;
            }
        }

        if (!toLeft)
        {
            styles['left'] = offset.left + 2;
        }
        else
        {
            styles['left'] = offset.left - menuWidth - 2;
        }

        var menuHeight = g.height(menu);
        if (offset.top + menuHeight > platform.windowHeight())
        {
            styles.top -= menuHeight;
        }

        g.css(menu, styles);

        // Move the right click menu into bounds if possible.
        var windowBounds = document.documentElement.getBoundingClientRect();
        var rect = menu.getBoundingClientRect();

        var autoRect = { left: rect.left, right: rect.right, top: rect.top, bottom: rect.bottom };

        var origin = g.adjustOriginToBeInsideBounds(windowBounds, autoRect);

        if (origin.x !== autoRect.left || origin.y !== autoRect.top)
        {
            // Grab the element offset from the completion element element
            menu.style.left = origin.x + 'px';
            menu.style.top = origin.y + 'px';
        }

        g.vmMain.handleFullscreenClick((function()
        {
            this.closeContextMenu(menu);
        }).bind(this));
    },
    closeContextMenu: function(menu)
    {
        g.css(menu, {visibility: 'hidden'});
        g.vmMain.cancelFullscreenClick();
    },
    registerModal: function(menu, bind, openFn, closeFn)
    {
        try{g.PAssert(1341,menu && bind, 'Must have a valid name and object to bind modal to');}catch(PERR){g.reportError(PERR);}

        this[menu + 'Item'] = ko.observable(bind);

        this[menu + 'Visible'] = ko.observable(false);
        this[menu + 'Loading'] = ko.observable(false);

        if (openFn)
        {
            this[menu + 'OpenFn'] = openFn;
        }
        if (closeFn)
        {
            this[menu + 'CloseFn'] = closeFn;
        }
    },
    openModal: function(menu)
    {
        try{g.PAssert(1342,!this.modalOpened, 'Only a single modal should be opened at a time');}catch(PERR){g.reportError(PERR);}

        if (this[menu + 'OpenFn'])
        {
            this[menu + 'OpenFn'].apply(this[menu + 'Item'](), Array.prototype.slice.call(arguments, 1));
        }

        if (!this.modalsBound)
        {
            g.applyBindings(this, 'modals');
            this.modalsBound = true;
        }

        var visibleVar = this[menu + 'Visible'];
        try{g.PAssert(1343,visibleVar, 'Visible variable does not exist');}catch(PERR){g.reportError(PERR);}

        this.modalOpened = menu;
        this.modalOpenedBound = this[menu + 'Item']();

        visibleVar(true);

        this.isModalVisible(true);
        this.isModalOpen(true);
    },
    closeModal: function(menu)
    {
        // Modals fade out when closed so multiple clicks could occurr
        if(!this.modalOpened)
            return;

        try{g.PAssert(1344,!menu || menu === this.modalOpened, 'Tried to close the wrong modal');}catch(PERR){g.reportError(PERR);}

        if (arguments.length > 1)
        {
            this.closeModalInternal.apply(this, Array.prototype.slice.call(arguments, 1));
        }
        else
        {
            this.closeModalInternal();
        }
    },
    closeModalInternal: function()
    {
        // Modals fade out when closed so multiple clicks could occurr
        if(!this.modalOpened)
            return;

        try{g.PAssert(1345,this.modalOpened, 'Tried to close when no modal was open');}catch(PERR){g.reportError(PERR);}

        var visibleVar = this[this.modalOpened + 'Visible'];
        try{g.PAssert(1346,visibleVar, 'Visible variable does not exist');}catch(PERR){g.reportError(PERR);}

        var currentModal = this.modalOpened;

        visibleVar(false);
        this.isModalOpen(false);

        this.isModalVisible(false);

        this.modalOpened = undefined;
        this.modalOpenedBound = undefined;
        this.modalInputOn = undefined;

        g.fireCustomEvent('closeKeyboard');

        if (this[currentModal + 'CloseFn'])
        {
            this[currentModal + 'CloseFn'].apply(this[currentModal + 'Item'](), arguments);
        }

        return false;
    },
    focusModalInputMobile: function(e, element)
    {
        try{g.PAssert(1347,platform.mobile, 'Should not call this from non-mobile');}catch(PERR){g.reportError(PERR);}

        if (!this.modalInputOn)
        {
            g.openKeyboardOn(e, element, false, function()
            {
                if(platform.app)
                {
                    this.modalOpenedBound.onKeyboardOpened(e);
                    requestAnimationFrame(function(){
                        requestAnimationFrame(function(){
                            element.focus();
                            g.enforceScroll();
                        });
                    });
                }
                else
                {
                    element.focus();
                }
            }.bind(this));
        }
        else if(this.modalInputOn != element)
        {
            element.focus();

            if(this.modalOpenedBound.onKeyboardOpened)
            {
                requestAnimationFrame(function(){
                    this.modalOpenedBound.onKeyboardOpened(e);
                }.bind(this));
            }

            e.preventDefault();
            e.stopPropagation();
            g.enforceScroll();
        }
        else
        {
            g.enforceScroll();
        }
    },
    onTapModal: {
        onStart: function(e)
        {
            if (platform.mobile && e.srcElement != this.modalInputOn && (g.isKeyboardInputElement(e.srcElement) || g.hasClass(e.srcElement, 'input')))
            {
                log('start', g.isKeyboardOpen);
                if (!g.isKeyboardOpen)
                {
                    e.preventDefault();
                    e.stopPropagation();
                }
            }
        },
        onClick: function(e)
        {
            var prevent = true;
            if (e.srcElement.id == 'modals')
            {
                this.closeModal();
                this.modalInputOn = undefined;
            }
            else if (e.srcElement == this.modalInputOn)
            {
                prevent = false;
            }
            else if (g.isKeyboardInputElement(e.srcElement) || g.hasClass(e.srcElement, 'input'))
            {
                if(platform.mobile)
                {
                    this.focusModalInputMobile(e, e.srcElement);
                }
                this.modalInputOn = e.srcElement;
            }
            else
            {
                this.modalInputOn = undefined;
            }
            if (prevent && platform.mobile)
            {
                e.preventDefault();
                e.stopPropagation();
            }
        }
    },
    onCloseKeyboard: function()
    {
        this.modalInputOn = undefined;
    },
    paneIsTimeline: function()
    {
        if (!this.contextMenuOpenedOn())
            return false;

        return this.contextMenuOpenedOn().isTimeline && this.contextMenuOpenedOn().isTimeline();
    },
    openItemMenu: function(item, e)
    {
        this.openContextMenu({ left: e.pageX, top: e.pageY }, g.element('contextMenuItem'), false);

        this.contextMenuOpenedOn(g.getPaneForElement(e.srcElement));

        var firstItem;
        var foundZoomCandidate = false;
        g.vmMain.applyToSelection(function(item, index, total)
        {
            try{g.PAssert(1348,item, 'Item must be defined');}catch(PERR){g.reportError(PERR);}

            if (item && index === 0)
            {
                firstItem = item;

                this.itemIsHeader(item.isHeader());
                this.itemHasChildren(item.isHeader() || item.hasChildren());
                this.itemIsCollapsed(item.isCollapsed());
                this.itemIsArchived(item.isArchived());

                this.itemCanIndent(item.getIndex() !== 0);
                this.itemCanUnindent(item.parent() !== g.focusedItem);
            }

            if (!foundZoomCandidate)
            {
                if (item.canZoom())
                {
                    foundZoomCandidate = true;

                    this.itemCanZoom(true);
                }
            }

            // If we've reached the end of the selection and did not find a valid item to zoom disable the option.
            if (!foundZoomCandidate && index === total - 1)
            {
                this.itemCanZoom(false);
            }

            return !foundZoomCandidate;
        }.bind(this), { passExtraInfo: true });
    },
    closeItemMenu: function()
    {
        this.closeContextMenu(g.element('contextMenuItem'));
        this.contextMenuOpenedOn(undefined);
    },
    itemContextText: function()
    {
        return this.itemIsCollapsed() ? 'Expand': 'Collapse';
    },
    itemArchivedText: function()
    {
        return this.itemIsArchived() ? 'Unarchive' : 'Archive';
    },
    hotkeyText: function(key)
    {
        return platform.windows ? (platform.cmdChar + '+' + key) : (platform.cmdChar + '' + key);
    },
    onTapItemMenu: {
        onStart: g.handlerPreventStop,
        onClick: function(e)
        {
            var id = e.srcElement.id;
            if (!id)
            {
                id = e.srcElement.parentNode.id;
            }

            switch (id)
            {
                case 'itemComplete':
                    g.vmMain.applyFnToSelection('isComplete', /*toggle*/true);
                    break;
                case 'itemFlag':
                    g.vmMain.applyFnToSelection('isFlagged', /*toggle*/true);
                    break;

                case 'itemIndent':
                    g.fireKeyEvent(/*ele*/undefined, {normCode: KeyCode.Tab, shiftKey: false});
                    break;
                case 'itemUnindent':
                    g.fireKeyEvent(/*ele*/undefined, {normCode: KeyCode.Tab, shiftKey: true});
                    break;

                case 'itemPriorityNone':
                    g.vmMain.applyPriorityToSelection(VMLIFlag.None);
                    break;
                case 'itemPriorityLow':
                    g.vmMain.applyPriorityToSelection(VMLIFlag.P2);
                    break;
                case 'itemPriorityMed':
                    g.vmMain.applyPriorityToSelection(VMLIFlag.P1);
                    break;
                case 'itemPriorityHigh':
                    g.vmMain.applyPriorityToSelection(VMLIFlag.P0);
                    break;

                // case 'itemFormatBold':
                //     g.vmMain.applyFormatToSelection('b');
                //     break;
                // case 'itemFormatItalic':
                //     g.vmMain.applyFormatToSelection('i');
                //     break;
                // case 'itemFormatUnderline':
                //     g.vmMain.applyFormatToSelection('u');
                //     break;

                case 'itemCollapse':
                    g.vmMain.applyToSelection(function(item)
                    {
                        item.toggleCollapsed();
                    });
                    break;
                case 'itemZoom':
                    g.vmMain.applyToSelection(function(item)
                    {
                        if (item.canZoom())
                        {
                            g.vmMain.zoomin(item);
                            return false;
                        }

                        return true;
                    });
                    break;

                case 'itemCopy':

                    break;
                case 'itemPaste':

                    break;
                case 'itemUndo':

                    break;
                case 'itemRedo':

                    break;

                case 'itemArchive':
                    var numChanged = 0;

                    var numChecked = g.vmMain.applyToSelection(function(item)
                    {
                        if (this.itemIsArchived())
                        {
                            if (item.parent().unarchiveChild(item))
                            {
                                numChanged++;
                            }
                        }
                        else
                        {
                            if (item.parent().archiveChild(item))
                            {
                                numChanged++;
                            }
                        }
                    }.bind(this));

                    if (numChanged > 0)
                    {
                        var z = (numChanged > 1 ? ' items.' : ' item.');
                        var verb = this.itemIsArchived() ? 'unarchived' : 'archived';

                        g.messageQueue.pushMessage({
                            text: 'Successfully ' + verb + ' ' + numChanged + z,
                            type: MessageType.Info,
                            action: MessageAction.Default,
                            timeout: 2000
                        });
                    }
                    break;

                case 'itemDelete':
                    g.vmMain.applyToSelection(function(item)
                    {
                        item.deleteSelf();
                    });
                    break;
                case 'contextMenuItem':
                    // Click on a disabled item
                    break;
                default:
                    try{g.PAssert(1349,false, 'Unknown context menu item: ', id);}catch(PERR){g.reportError(PERR);}
                    break;
            }

            g.sendEvent('ContextMenu', id);

            this.closeItemMenu();
        }
    }
};

return VMMenus;
});

