define('VMDebug',['ko', 'globals', 'platform', 'data', 'FileSaver', 'goog', 'dimport'],
function (ko, g, platform, d, FileSaver, goog, dimport) {
function VMDebug ()
{
    this.bugDescription = ko.observable('');

    this.mode = ko.observable('html');
    this.exportedContent = '';
    this.exportedPreview = ko.observable('');
    this.exportArchived = ko.observable(true);

    this.importName = ko.observable('JSON');
    this.importDisplayMessage = ko.observable('');
    this.importing = false;
    this.importer = new dimport();

    this.init();
}

VMDebug.prototype = {
    init: function()
    {
        this.exportArchived.subscribe(this.exportData.bind(this));

        g.menus.registerModal('reportBug', this, this.reportBug);
        g.menus.registerModal('importData', this);
        g.menus.registerModal('exportData', this, this.openExport.bind(this), this.closeExport.bind(this));

        if (DEBUG)
        {
            window.__exportCoverage = function(redirect)
            {
                if (window.__coverage__)
                {
                    g.XHR({
                        type: 'POST',
                        url: window.location.protocol + '//localhost:8000/debug/sendCoverage',
                        data: JSON.stringify(__coverage__),
                        cb: function (xhr)
                        {
                            log('Coverage Response: ', xhr);

                            if (redirect)
                            {
                                window.location = window.location.protocol + '//localhost:8000/report';
                            }
                        }
                    });
                }
                else
                {
                    log('No code coverage information found');
                }
            };
        }
    },
    reportBug: function ()
    {
        setTimeout(function ()
        {
            document.getElementById('bugDescription').focus();
        });
    },
    onTapSaveBug: function ()
    {
        var appInfo = { };
        g.getAppInfo(appInfo);

        var notes = this.bugDescription() + '\r\n\r\n\r\n' + JSON.stringify(appInfo, undefined, 3);

        var indexOfNewline = this.bugDescription().indexOf('\n');
        var name = this.bugDescription().substr(0, indexOfNewline >= 0 ?
                                indexOfNewline :
                                Math.min(this.bugDescription().length, 120));

        var bug =
            {
                'name': name,
                'notes': notes
            };

        bug.email = g.getUserIdentifier();

        // if (bugForm['sendData'].checked)
        // {
        //     bug.userData = d.toJSON(d.getRootItem());
        // }

        var sendBug = function ()
        {
            g.XHR({
                type: 'POST',
                url: window.location.protocol + '//api.moo.do/debug/reportIssue',
                data: bug,
                cb: function (xhr)
                {
                    log('XHR Response: ', xhr);

                    this.bugDescription('');
                }.bind(this)
            });
        }.bind(this);

        if (bug.email !== 'Unknown')
        {
            sendBug();
        }
        else
        {
            g.getClientInfo(function (info)
            {
                if (info)
                {
                    bug.email = info.ip;
                }

                sendBug();
            });
        }

        g.menus.closeModal('reportBug');

        g.messageQueue.pushMessage({
            text: 'Thanks for taking the time to send us feedback!',
            type: MessageType.Info,
            action: MessageAction.Default,
            timeout: 1500
        });

        g.blurActiveElement();
        g.vmMain.clearSelected();
    },
    onTapCancelBug: function()
    {
        g.menus.closeModal('reportBug');

        g.blurActiveElement();
        g.vmMain.clearSelected();
    },
    openExport: function()
    {
        this.exportData();

        this.exportKeyHandler = this.onExportKeydown.bind(this);

        document.addEventListener('keydown', this.exportKeyHandler, true);
    },
    closeExport: function()
    {
        document.removeEventListener('keydown', this.exportKeyHandler, true);

        this.exportKeyHandler = undefined;
    },
    onTapCloseExport: function()
    {
        g.menus.closeModal('exportData');
    },
    exportData: function()
    {
        var content;
        var preview;
        var root = d.getRootItem();

        if (this.mode() == 'html')
        {
            content = d.toHtml(root, this.exportArchived());
            preview = content;
        }
        else if (this.mode() == 'plain')
        {
            var tab = '&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;';
            content = d.toPlainText(root, /*indent*/0, this.exportArchived());
            preview = content.replace(/\n/g, '<br>').replace(/\t/g, tab);
        }
        else if (this.mode() == 'json')
        {
            content = d.toJSON(root, /*hasPrev*/false, this.exportArchived());
            preview = content;
        }

        this.exportedContent = content;
        this.exportedPreview(preview);
    },
    onTapHTML: function(e)
    {
        this.mode('html');
        this.exportData();
        this.highlightExportButton(e);
    },
    onTapPlain: function(e)
    {
        this.mode('plain');
        this.exportData();
        this.highlightExportButton(e);
    },
    onTapJSON: function(e)
    {
        this.mode('json');
        this.exportData();
        this.highlightExportButton(e);
    },
    highlightExportButton: function(e)
    {
        if (!e.srcElement)
        {
            e.srcElement = e.target;
        }

        var btnParent = document.getElementById('exportTypes');

        if (btnParent)
        {
            for (var i = 0; i < btnParent.children.length; ++i)
            {
                var btn = btnParent.children[i];

                if (g.hasClass(btn, 'button'))
                {
                    if (btn !== e.srcElement)
                    {
                        g.removeClass(btn, 'bSelected');
                    }
                    else
                    {
                        g.addClass(btn, 'bSelected');
                    }
                }
            }
        }
    },
    onTapOpen: function()
    {
        var content = this.mode() == 'plain' ? this.exportedPreview() : this.exportedContent;
        var newWin = open('url');

        newWin.document.write(content);
    },
    onTapSelect: function()
    {
        var range = document.createRange();
        range.selectNodeContents(document.getElementById('exportedContent'));

        var sel = window.getSelection();

        sel.removeAllRanges();
        sel.addRange(range);
    },
    onTapSaveExport: function()
    {
        var ext;
        if (this.mode() == 'html')
        {
            ext = 'html';
        }
        else if (this.mode() == 'plain')
        {
            ext = 'txt';
        }
        else if (this.mode() == 'json')
        {
            ext = 'json';
        }

        try
        {
            var blob = new Blob([this.exportedContent], {type: "text/plain;charset=utf-8"});
            saveAs(blob, 'moodo.' + ext);
        }
        catch (err)
        {
            g.reportError(err);
        }
    },
    onExportKeydown: function(e)
    {
        platform.normalizeKeys(e);

        if (platform.mac && e.metaKey || !platform.mac && e.ctrlKey)
        {
            if (e.normCode === KeyCode.A)
            {
                this.onTapSelect();

                e.preventDefault();
                e.stopPropagation();

                return false;
            }
        }
    },
    // showImportLogin: function()
    // {
    //     g.removeClass(document.getElementById('importLoginArea'), 'none');
    //     g.addClass(document.getElementById('importTextArea'), 'none');
    //     g.addClass(document.getElementById('importUploadArea'), 'none');
    // },
    // showImportText: function()
    // {
    //     g.addClass(document.getElementById('importLoginArea'), 'none');
    //     g.removeClass(document.getElementById('importTextArea'), 'none');
    //     g.addClass(document.getElementById('importUploadArea'), 'none');
    // },
    // showImportUpload: function()
    // {
    //     g.addClass(document.getElementById('importLoginArea'), 'none');
    //     g.addClass(document.getElementById('importTextArea'), 'none');
    //     g.removeClass(document.getElementById('importUploadArea'), 'none');
    // },
    closeImport: function ()
    {
        g.menus.closeModal('importData');
    },
    isAreaVisible: function(area)
    {
        switch (this.importName())
        {
            case 'Wunderlist':
                return (area === 'LoginArea');
            case 'Workflowy':
                return (area === 'LoginArea');
            case 'OPML':
                return (area === 'UploadArea');
            case 'JSON':
                return (area === 'UploadArea');
            case 'Text':
                return (area === 'TextArea');
            case 'Google Tasks':
                return (area === 'OAuthArea');
            case 'Trello':
                return (area === 'LoginArea');
            case 'Toodledo':
                return (area === 'OAuthArea');
            default:
                try{g.PAssert(1449,false, 'Must add valid importer');}catch(PERR){g.reportError(PERR);}
                break;
        }

        return false;
    },
    highlightImportButton: function(e)
    {
        if (!e.srcElement)
        {
            e.srcElement = e.target;
        }

        var btnParent = document.getElementById('importLocations');

        if (btnParent)
        {
            for (var i = 0; i < btnParent.children.length; ++i)
            {
                var btn = btnParent.children[i];

                if (btn !== e.srcElement)
                {
                    g.removeClass(btn, 'bSelected');
                }
                else
                {
                    g.addClass(btn, 'bSelected');
                }
            }
        }
    },
    onTapRunImportWunderlist: function(e)
    {
        this.importName('Wunderlist');
        this.highlightImportButton(e);
        //this.showImportLogin();
    },
    onTapRunImportWorkflowy: function(e)
    {
        this.importName('Workflowy');
        this.highlightImportButton(e);
        //this.showImportLogin();
    },
    onTapRunImportOPML: function(e)
    {
        this.importName('OPML');
        this.highlightImportButton(e);
        //this.showImportUpload();
    },
    onTapRunImportJSON: function(e)
    {
        this.importName('JSON');
        this.highlightImportButton(e);
    },
    onTapRunImportText: function(e)
    {
        this.importName('Text');
        this.highlightImportButton(e);
    },
    onTapRunImportGTasks: function(e)
    {
        this.importName('Google Tasks');
        this.highlightImportButton(e);
        //this.showImportOAuth();
    },
    onTapRunImportTrello: function(e)
    {
        this.importName('Trello');
        this.highlightImportButton(e);
    },
    onTapRunImportToodledo: function(e)
    {
        this.importName('Toodledo');
        this.highlightImportButton(e);
    },
    onTapRunImport: function()
    {
        if (this.importing)
        {
            return;
        }

        var onImportError = function (msg, err)
        {
            try
            {
                var message = msg;

                if (!message || !g.isString(message))
                {
                    message = 'There was an unexpected error, please double check your data';
                }

                this.importing = false;

                this.importDisplayMessage(message);

                g.reportError(err || msg, msg);
            }
            catch (errErr)
            {
                g.reportError(errErr);
            }
        }.bind(this);

        var loginName = document.getElementById('importEmail').value;
        var loginPW = document.getElementById('importPW').value;

        var importFn = undefined;
        switch (this.importName())
        {
            case 'Wunderlist':
                importFn = this.importer.wunderlist;
                break;
            case 'Workflowy':
                importFn = this.importer.workflowy;
                break;
            case 'OPML':
                importFn = this.importer.opml;
                break;
            case 'JSON':
                importFn = this.importer.json;
                break;
            case 'Text':
                importFn = this.importer.text;
                break;
            case 'Google Tasks':
                importFn = this.importer.gtasks;
                break;
            case 'Trello':
                importFn = this.importer.trello;
                break;
            case 'Toodledo':
                importFn = this.importer.toodledo;
                break;
            default:
                try{g.PAssert(1450,false, 'Must add valid importer');}catch(PERR){g.reportError(PERR);}
                break;
        }

        if (importFn)
        {
            this.importing = true;

            importFn.login(loginName, loginPW, function ()
            {
                importFn.import(function (data)
                {
                    importFn.save(data, function ()
                    {
                        this.importDisplayMessage('Your data was imported successfully!');

                        this.importing = false;

                        setTimeout(function ()
                        {
                            g.menus.closeModal('importData');
                        }.bind(this), 2000);
                    }.bind(this), onImportError);
                }.bind(this), onImportError);
            }.bind(this), onImportError);
        }
    },
    importTitle: function()
    {
        var name = this.importName();

        if (name !== 'OPML' && name !== 'JSON' && name !== 'Text')
        {
            return "Importing data from " + this.importName();
        }
        else
        {
            return 'Upload ' + name + ' File';
        }
    },
    importMessage: function()
    {
        return this.importDisplayMessage();
    }
};

return VMDebug;
});

