define('edit',['ko', 'globals', 'platform', 'android', 'VMLI', 'tracker'],
function (ko, g, platform, android, VMLI, tracker) {
    var self = { };

    var selection,
        range,
        rangeStartContainer,
        rangeEndContainer,
        rangeStart,
        rangeEnd;

    var keyCodesDown = [KeyCode.Tab, KeyCode.BackSpace, KeyCode.Delete, KeyCode.Period];
    var keyCodesArrows = [KeyCode.UpArrow, KeyCode.DownArrow, KeyCode.LeftArrow, KeyCode.RightArrow,
                          KeyCode.Home, KeyCode.End];

    var textSaveInfo = {};
    var tempInput;
    var lastKeypress;

    var lastEditSelect = 0;

    var StringNewLine = String.fromCharCode(10);

    function updateSelection()
    {
        if (!android.isEnabled())
        {
            selection = g.getSelectionInfo();

            if (!selection || selection.rangeCount === 0)
            {
                return true;
            }

            range = selection.selection.getRangeAt(0);
            rangeStartContainer = range.startContainer;
            rangeEndContainer = range.endContainer;
            rangeStart = range.startOffset;
            rangeEnd = range.endOffset;
        }
        else
        {
            if (!android.getCurrentVMLI())
            {
                return true;
            }

            selection = { };

            range = undefined;
            rangeStartContainer = rangeEndContainer = android.getCurrentSpan();
            rangeStart = android.getSelectionStart();
            rangeEnd = android.getSelectionEnd();
        }
    }

    self.updateAutocomplete = function(e)
    {
       var fromArrows = e.normCode && keyCodesArrows.indexOf(e.normCode) >= 0;

        // Autocomplete
        if (g.autocomplete)
        {
            var selection = g.getSelectionInfo();

            if (selection && selection.selection.rangeCount > 0)
            {
                var range = selection.selection.getRangeAt(0);

                var startLI = g.getLIParent(range.startContainer);
                var endLI = g.getLIParent(range.endContainer);
                if (startLI && startLI === endLI && range.startOffset === range.endOffset)
                {
                    if (range.startContainer.tagName !== 'UL')
                    {
                        var item = ko.dataFor(startLI);
                        var span = item.getSpan();

                        try{g.PAssert(1235,span, 'Items should always have a valid span if we are performing updates in a VMPane');}catch(PERR){g.reportError(PERR);}

                        if (span)
                        {
                            var offset = g.getSelectOffsetsInSpan(item, span, range);

                            if (offset.start === offset.end)
                            {
                                g.autocomplete.update({ item: item, start: offset.start, fromArrows: fromArrows });
                            }
                        }
                        else
                        {
                            g.autocomplete.hide();
                        }
                    }
                }
            }
        }
    };

    self.onTextInput = function(e)
    {
        // If the last character was space and text input is not space, this was an autocorrect
        // so replace the highlighted word with the data from the event.
        if(textSaveInfo.item && textSaveInfo.waitAutocorrect && e.data != ' ' && e.data != StringNewLine)
        {
            var item = textSaveInfo.item;
            // If autocorrect came from hitting enter, the tempInput has the text
            var span = textSaveInfo.waitAutocorrect == 'enter' ? tempInput : item.getSpan();
            var s = item.getParsedText();

            updateSelection();
            var offsets = g.getSelectOffsetsInSpan(item, span, range);

            var oldLength = offsets.endText - offsets.startText;
            var insertText = textSaveInfo.waitAutocorrect == 'space' ? ' ' : '';

            textSaveInfo.text = s.substr(0, offsets.startText) + e.data + insertText + s.substr(offsets.endText);
            textSaveInfo.start += e.data.length - oldLength;
            textSaveInfo.waitAutocorrect = undefined;
        }
    };

    self.keyup = function (element, e)
    {
        if(textSaveInfo.item)
        {
            tracker.beginAction(); // BEGIN TRACKER <----

            if(textSaveInfo.text != undefined)
            {
                textSaveInfo.item.setText(textSaveInfo.text, true);
            }

            if(textSaveInfo.nextItem)
            {
                g.selectChildren(textSaveInfo.nextItem, 0, 0);
            }
            else
            {
                g.selectChildren(textSaveInfo.item, textSaveInfo.start, 0);
            }

            self.updateAutocomplete(e);

            tracker.endAction(); // END TRACKER <----
        }

        textSaveInfo.item = undefined;
        textSaveInfo.nextItem = undefined;
        textSaveInfo.text = undefined;
        textSaveInfo.start = undefined;
        textSaveInfo.waitAutocorrect = undefined;

        // On mobile we want to scroll to the item after it's been created
        // so it's called here instead of in keydown
        if (e.normCode === KeyCode.Enter || e.normCode === KeyCode.PageUp || e.normCode === KeyCode.PageDown)
        {
            self.scrollTextIntoView();
        }

        try{g.PAssert(1236,e.stopImmediatePropagation, 'Should always be defined');}catch(PERR){g.reportError(PERR);}
        if (e.stopImmediatePropagation)
        {
            e.stopImmediatePropagation();
        }

        try{g.PAssert(1237,e.preventDefault, 'Should always be defined');}catch(PERR){g.reportError(PERR);}
        if (e.preventDefault)
        {
            e.preventDefault();
        }

        if (e.handled)
        {
            return true;
        }
    };

    self.scrollTextIntoView = function(direction, time)
    {
        try{g.PAssert(1238,direction !== 0, 'Cannot have a null scroll direction');}catch(PERR){g.reportError(PERR);}

        if (isNaN(time))
        {
            time = 200;
        }

        // TODO: Optimize number of times that we call this - can be somewhat expensive
        if (!g.isScrolling)
        {
            updateSelection();

            if (rangeEndContainer && rangeEndContainer.tagName !== 'UL' && rangeEndContainer.tagName !== 'LI')
            {
                //try{g.PAssert(1239,rangeStartContainer.nodeName == '#text', 'Expected SPAN, Actual: ' + rangeStartContainer.tagName);}catch(PERR){g.reportError(PERR);}
                var scrollSpan = g.getSpanParent(rangeEndContainer);

                // Grab either the previous or next item to scroll into view so the current item doesnt end up at the very bottom
                // or very top of the screen. If this is the first or last item we dont have a choice.
                var currentItem = ko.dataFor(scrollSpan);

                if (currentItem)
                {
                    var item;

                    if (direction < 0)
                    {
                        item = g.getPreviousItem(currentItem, g.focusedPane) || currentItem;
                    }
                    else if (direction > 0)
                    {
                        item = g.getNextItem(currentItem, g.focusedPane) || currentItem;
                    }

                    var targetSpan = item ? item.getSpan() : scrollSpan;

                    g.scrollIntoView(targetSpan, time);
                }
            }
        }
    };

    self.handleSelectAll = function(pane, e)
    {
        updateSelection();

        var firstItem = g.getFirstPaneItem(pane);
        var lastItem = g.getLastPaneItem(pane);

        g.selectMultiline(firstItem.getSpan(), 0, lastItem.getSpan(), lastItem.getParsedText().length);

        //lastEditSelect = g.getItemCount(firstItem, lastItem, pane);

        return false;
    };

    self.handleArrows = function(element, e)
    {
        g.restrictSelectionToText();
        updateSelection();

        // Up arrow
        if ((platform.mac && e.metaKey && e.normCode === KeyCode.UpArrow) ||
            (!platform.mac && e.ctrlKey && e.normCode === KeyCode.Home))
        {
            var firstItem = g.getFirstPaneItem(g.focusedPane);
            if(e.shiftKey) // Highlight from top of box to here
            {
                g.selectMultiline(firstItem.getSpan(), 0, rangeEndContainer, rangeEnd);
            }
            else // Select top of box
            {
                g.selectChildren(firstItem, 0, 0);
            }
            g.scrollIntoView(firstItem.getSpan());
            return false;
        }
        // Down arrow
        else if ((platform.mac && e.metaKey && e.normCode === KeyCode.DownArrow) ||
                 (!platform.mac && e.ctrlKey && e.normCode === KeyCode.End))
        {
            var lastItem = g.getLastPaneItem(g.focusedPane);

            if (e.shiftKey) // Highlight from here to bottom of box
            {
                g.selectMultiline(rangeStartContainer, rangeStart, lastItem.getSpan(), lastItem.getParsedText().length);
            }
            else // Select bottom of box
            {
                g.selectChildren(lastItem, lastItem.getParsedText().length, 0);
            }

            g.scrollIntoView(lastItem.getSpan());

            return false;
        }
        else if (!platform.mac && e.ctrlKey && e.normCode === KeyCode.UpArrow && rangeStartContainer)
        {
            var item = ko.dataFor(g.getSpanParent(rangeStartContainer));

            var focusItem;
            if (item.parent() === g.focusedItem)
            {
                if (!item.isFirstChild())
                {
                    try{g.PAssert(1240,item.getIndex() > 0, 'Should never hit this with the first item in the list');}catch(PERR){g.reportError(PERR);}

                    focusItem = item.parent().getChild(item.getIndex() - 1);
                }
            }
            else
            {
                while (item.parent() !== g.focusedItem) { item = item.parent(); }

                focusItem = item;
            }

            if (focusItem !== undefined)
            {
                g.selectChildren(focusItem, focusItem.getParsedText().length, 0);

                g.scrollIntoView(focusItem.getSpan());
            }

            return false;
        }
        else if (!platform.mac && e.ctrlKey && e.normCode === KeyCode.DownArrow && rangeEndContainer)
        {
            var item = ko.dataFor(g.getSpanParent(rangeEndContainer));

            var focusItem;
            if (item.parent() === g.focusedItem)
            {
                if (!item.isLastChild())
                {
                    focusItem = item.parent().getChild(item.getIndex() + 1);
                }
            }
            else
            {
                while (item.parent() !== g.focusedItem) { item = item.parent(); }

                if (!item.isLastChild())
                {
                    focusItem = item.parent().getChild(item.getIndex() + 1);
                }
            }

            if (focusItem !== undefined)
            {
                g.selectChildren(focusItem, focusItem.getParsedText().length, 0);

                g.scrollIntoView(focusItem.getSpan());
            }

            return false;
        }

        if (rangeStartContainer === undefined || rangeEndContainer === undefined)
        {
            // If there is no selection present bail out early
            return true;
        }

        if (e.normCode === KeyCode.LeftArrow || e.normCode === KeyCode.RightArrow)
        {
            updateSelection();

            var li = g.getLIParent(rangeStartContainer);
            var li2 = g.getLIParent(rangeEndContainer);
            if (li == li2)
            {
                var item = ko.dataFor(li);
                if (item.getParsedText().length === 0)
                {
                    if (e.normCode === KeyCode.LeftArrow)
                    {
                        var prev = g.getPreviousItem(item, g.focusedPane);
                        if (prev)
                        {
                            g.selectChildren(prev, prev.getParsedText().length, 0);
                        }
                    }
                    else if (e.normCode === KeyCode.RightArrow)
                    {
                        var next = g.getNextItem(item, g.focusedPane);
                        if (next)
                        {
                            g.selectChildren(next, 0, 0);
                        }
                    }

                    return false;
                }
            }
        }
        else if (platform.ie && (e.normCode === KeyCode.UpArrow || e.normCode === KeyCode.DownArrow))
        {
            updateSelection();

            if (e.normCode === KeyCode.UpArrow)
            {
                var oldItem = ko.dataFor(g.getLIParent(rangeStartContainer));

                var newItem = g.getPreviousItem(oldItem, g.focusedPane);
            }
            else if (e.normCode === KeyCode.DownArrow)
            {
                var oldItem = ko.dataFor(g.getLIParent(rangeEndContainer));

                var newItem = g.getNextItem(oldItem, g.focusedPane);
            }

            if (newItem)
            {
                if (rangeStartContainer === rangeEndContainer)
                {
                    var selOffset = g.getSelectOffsetsInSpan(undefined, rangeEndContainer, range);
                    var offset = Math.min(selOffset.start, newItem.getParsedText().length);
                }
                else
                {
                    var offset = newItem.getParsedText().length;
                }

                g.selectChildren(newItem, offset, 0);

                return false;
            }
        }

        self.updateAutocomplete(e);

        return true;
    };

    self.keydown = function (element, e)
    {
        // log('down', e);
        var ret = true;
        if (keyCodesDown.indexOf(e.normCode) >= 0)
        {
            ret = self.keypress(element, e, /*fromKeydown*/true);
        }
        else if (keyCodesArrows.indexOf(e.normCode) >= 0)
        {
            ret = self.handleArrows(element, e);
        }
        else if (platform.mac && e.metaKey || !platform.mac && e.ctrlKey)
        {
            if (e.normCode === KeyCode.A)
            {
                return self.handleSelectAll(element, e);
            }
            else if (e.normCode === KeyCode.Y || e.normCode === KeyCode.Z)
            {
                return self.keypress(element, e, /*fromKeydown*/true);
            }
        }

        // if (self.nextKeypressFormat)
        // {
        //     var keyCodeUnformat = [KeyCode.LeftArrow, KeyCode.UpArrow, KeyCode.RightArrow, KeyCode.DownArrow];
        //     if (keyCodeUnformat.indexOf(e.normCode) >= 0)
        //     {
        //         self.nextKeypressFormat = undefined;
        //     }
        // }

        switch (e.normCode)
        {
            case KeyCode.Enter:
                // On mobile scrollTextIntoView on Enter is called by keyUp. On Android the keyUp event
                // may not be processed so call it here.
                if (!platform.mobile || android.isEnabled())
                {
                    self.scrollTextIntoView();
                }

                break;
            case KeyCode.UpArrow:
                if(platform.mac && !e.metaKey)
                {
                    self.scrollTextIntoView(-1, 10);
                }
                break;
            case KeyCode.DownArrow:
                if(platform.mac && !e.metaKey)
                {
                    self.scrollTextIntoView(1, 10);
                }
                break;
            default:
                // TODO: Make this smarter?
                var validCharacter = (e.normCode >= 32 && e.normCode <= 90 && e.normCode !== KeyCode.Period) || e.normCode === KeyCode.Enter;
                // If it's a character and not a hotkey press, scroll into view
                if(validCharacter && !(platform.mac && e.metaKey) && !(!platform.mac && e.ctrlKey))
                {
                    self.scrollTextIntoView();
                }

                break;
        }

        return ret;
    };

    //
    //
    // <ul>
    //   <li>                         Case A
    //     <span>#text</span>
    //   </li>
    //   <li>                         Case B
    //     <span>#text</span>
    //     <!-- Comment -->
    //     <ul>
    //       <li>
    //         <span>#text</span>
    //       </li>
    //     </ul>
    //     <!-- Comment -->
    //   </li>
    // </ul>
    //
    self.keypress = function (element, e, fromKeydown)
    {
        // log('press', e);
        var keyCodes = [KeyCode.Enter];

        //console.log('Pressed: [' + String.fromCharCode(e.which) + ']  ' + 'Code: ' + e.normCode);
        try{g.PAssert(1241,e.stopPropagation, 'Should always be defined');}catch(PERR){g.reportError(PERR);}
        if(e.stopPropagation)
            e.stopPropagation();

        var returnValue = true;

        var validCharacter = (e.normCode >= 32 && e.normCode !== KeyCode.Period) || keyCodes.indexOf(e.normCode) >= 0;

        var skipCharacter = (platform.mobile && e.normCode === KeyCode.Delete) || (e.which === 0);

        //log('KeyPress: (fromKeydown: ' + fromKeydown + ') (validCharacter: ' + validCharacter + ') (normCode: ' + e.normCode + ')');

        // TODO: Optimize perf for updates/saving!
        if (!skipCharacter && (fromKeydown || validCharacter))
        {
            updateSelection();

            if (!selection && !android.isEnabled())
            {
                return true;
            }

            if (platform.mac && e.metaKey || !platform.mac && e.ctrlKey)
            {
                if (e.normCode === KeyCode.Y || e.normCode === KeyCode.Z)
                {
                    if (e.normCode === KeyCode.Z && !e.shiftKey)
                    {
                        tracker.undoAction();
                        g.sendEvent('Hotkey', 'Undo');
                    }
                    else
                    {
                        tracker.redoAction();
                        g.sendEvent('Hotkey', 'Redo');
                    }

                    e.handled = true;

                    try{g.PAssert(1242,e.preventDefault, 'Should always be defined');}catch(PERR){g.reportError(PERR);}
                    if(e.preventDefault)
                        e.preventDefault();

                    return true;
                }
                else
                {
                    var keyChar = String.fromCharCode(e.normCode);
                    //console.log(e.normCode, keyChar);
                    switch(keyChar)
                    {
                        case 'c':
                        case 'C':
                            e.handled = true;
                            return true;
                        case 'v':
                        case 'V':
                            e.handled = true;
                            return true;
                        case 'r':
                        case 'R':
                        case 'a':
                        case 'A':
                            e.handled = true;
                            return true;
                        default:
                            break;
                    }
                }
            }

            var item;

            // If selection is in the content UL and there are no items, this UL is empty or has no matching search results.
            // So add an item here and continue as normal.
            if (rangeStartContainer.nodeName == 'UL')
            {
                var pane = ko.dataFor(rangeStartContainer);
                if(!(pane instanceof VMLI))
                {
                    // item is the VMPane
                    item = pane.addItemToPane('', 0);
                    span = item.getSpan();
                    g.selectChildren(item, 0, 0);
                }
            }

            // Ensure that our selection range is valid and fully inside the <span> elements
            g.restrictSelectionToText();
            updateSelection();

            try{g.PAssert(1243,android.isEnabled() || rangeStartContainer.nodeName == '#text' || rangeStartContainer.nodeName == 'SPAN', 'Expected SPAN, Actual: ' + rangeStartContainer.tagName);}catch(PERR){g.reportError(PERR);}
            var startSpan = g.getSpanParent(rangeStartContainer);

            try{g.PAssert(1244,android.isEnabled() || rangeEndContainer.nodeName == '#text' || rangeEndContainer.nodeName == 'SPAN', 'Expected SPAN, Actual: ' + rangeEndContainer.tagName);}catch(PERR){g.reportError(PERR);}
            var endSpan = g.getSpanParent(rangeEndContainer);

            // console.log(rangeStart, range.startContainer, ' -- ', rangeEnd, range.endContainer);

            if (!startSpan || !endSpan)
            {
                e.stopPropagation();
                e.preventDefault();

                return false;
            }


            tracker.beginAction(); // BEGIN TRACKER <----


            // Multiselect
            if (startSpan != endSpan && e.normCode !== KeyCode.Tab)
            {
                returnValue = returnValue && self.handleMultiselect(e, startSpan, endSpan, range);
            }

            var specialKeys = [KeyCode.Tab, KeyCode.Enter, KeyCode.BackSpace, KeyCode.Delete];
            if (returnValue && specialKeys.indexOf(e.normCode) >= 0)
            {
                // TODO: Unduplicate work from spans above
                var span = g.getSpanParent(selection.node);
                try{g.PAssert(1245,android.isEnabled() || (span && span.tagName === 'SPAN'), 'Expected SPAN, Actual: ' + span.tagName);}catch(PERR){g.reportError(PERR);}

                if (span)
                {
                    // If we're holding shift and hitting backspace, unindent
                    var treatBackspaceLikeTab = false;
                    if (e.shiftKey && e.normCode === KeyCode.BackSpace)
                    {
                        var startOffset = g.getSelectOffsetsInSpan(undefined, span, range);

                        if (startOffset.start === 0)
                        {
                            treatBackspaceLikeTab = true;
                        }
                    }

                    if (e.normCode === KeyCode.Tab || treatBackspaceLikeTab)
                    {
                        returnValue = returnValue && self.handleTab(e, range);
                    }
                    else if (e.normCode === KeyCode.Enter)
                    {
                        returnValue = returnValue && self.handleEnter(e, span, range);
                    }
                    else if (e.normCode === KeyCode.BackSpace)
                    {
                        returnValue = returnValue && self.handleBackspace(e, span, range);

                        if (!textSaveInfo.item)
                        {
                            self.updateAutocomplete(e);
                        }
                    }
                    else if (e.normCode === KeyCode.Delete)
                    {
                        returnValue = returnValue && self.handleDelete(e, span, range);
                    }
                }
            }

            var keyCode = e.normCode; // TODO: Why are we using which for this particular case?
            if (e.normCode === KeyCode.Period)
            {
                keyCode = (e.shiftKey ? KeyCode.RAngle : KeyCode.Delete);
            }

            var character = String.fromCharCode(keyCode);

            if (returnValue && character && e.normCode !== KeyCode.BackSpace && e.normCode !== KeyCode.Enter && !e.ctrlKey)
            {
                updateSelection();

                try{g.PAssert(1246,g.getSpanParent(rangeStartContainer) == g.getSpanParent(rangeEndContainer));}catch(PERR){g.reportError(PERR);}

                var span = g.getSpanParent(selection.node);
                try{g.PAssert(1247,android.isEnabled() || span.tagName === 'SPAN', 'Expected SPAN, Actual: ' + span.tagName);}catch(PERR){g.reportError(PERR);}

                if (!item)
                {
                    item = g.getItemForElement(span);
                }

                try{g.PAssert(1248,item, 'Must be able to get a valid item from the selected span');}catch(PERR){g.reportError(PERR);}

                var offsets = g.getSelectOffsetsInSpan(item, span, range);

                var s;
                if (android.isEnabled())
                {
                    s = android.getInputText();
                }
                else
                {
                    s = item.getParsedText();
                }

                var onlyInsert = true;
                var newText = s.substr(0, offsets.startText) + character + s.substr(offsets.endText);

                if (offsets.startText !== offsets.endText)
                {
                    onlyInsert = false;

                    tracker.performAction(TrackerType.Remove, {
                        'itemID': item.id,
                        'position': offsets.startText,
                        'text': s.slice(offsets.startText, offsets.endText)
                    });
                }

                // item.transformFormats(offsets.start, 1);
                // if (self.nextKeypressFormat)
                // {
                //     item.toggleFormat({start: offsets.start, end: offsets.start + 1, tag: self.nextKeypressFormat});
                //     self.nextKeypressFormat = undefined;
                // }

                tracker.performAction(TrackerType.Insert, {
                    'itemID': item.id,
                    'position': offsets.startText,
                    'text': character
                });

                if (platform.ios)
                {
                    textSaveInfo.text = newText;
                    textSaveInfo.start = offsets.start + 1;
                    textSaveInfo.waitAutocorrect = (character == ' ') ? 'space' : undefined;
                    textSaveInfo.item = item;
                    returnValue = true;
                }
                else
                {
                    g.clearSelection();
                    item.setText(newText, /*setImmediate*/true);

                    g.selectChildren(item, offsets.start + 1, 0);
                    returnValue = false;

                    try{g.PAssert(1249,e.preventDefault, 'Should always be defined');}catch(PERR){g.reportError(PERR);}
                    if(e.preventDefault)
                        e.preventDefault();

                    self.updateAutocomplete(e);
                }
            }

            tracker.endAction(); // END TRACKER <----
        }

        lastKeypress = e.normCode;

        return returnValue;
    };

    self.handleMultiselect = function(e, startSpan, endSpan, range)
    {
        var arrToDelete = [ ];

        var levelsDeep = 0;
        var returnValue = true;

        var startLI = startSpan.parentNode;
        var endLI = endSpan.parentNode;

        try{g.PAssert(1250,startLI.tagName == 'LI', 'Expected LI, Actual: ', startLI);}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(1251,endLI.tagName == 'LI', 'Expected LI, Actual: ', endLI);}catch(PERR){g.reportError(PERR);}

        // Ensure that this actually is a multi-select scenario, we can do the same actions cheaper
        // through the non-multiselect functions.
        try{g.PAssert(1252,startLI != endLI);}catch(PERR){g.reportError(PERR);}


        var startItem = ko.dataFor(startLI);
        var endItem = ko.dataFor(endLI);
        var nextItem = startItem;

        var startOffset = g.getSelectOffsetsInSpan(startItem, startSpan, range);
        var endOffset = g.getSelectOffsetsInSpan(endItem, endSpan, range);

        // Track the number of fullscreen child headers that are affected
        var fscount = 0;

        // Add all lines in the selection to arrToDelete
        do
        {
            var nextItemOptions = { numLevels: 0 };
            nextItem = g.getNextItem(nextItem, g.focusedPane, nextItemOptions);

            if (nextItem.parent() === g.focusedPane.item() && nextItem.isHeader())
            {
                fscount++;
            }

            levelsDeep += nextItemOptions.numLevels;

            if (nextItem !== endItem || endOffset.end === endOffset.total)
            {
                arrToDelete.push(nextItem);
            }
        } while (nextItem !== endItem);

        // Check the total number of items as well as the total number of fullscreen child headers to determine if
        // the user should be prompted to confirm their action.
        var doAction = true;
        if (arrToDelete.length >= 16 && fscount >= 2)
        {
            if (!platform.packagedApp)
            {
                doAction = confirm('You have selected a large number of items to delete, please confirm you want to perform this action.');
            }
        }

        if (doAction)
        {
            var newStartText;

            // If starting or ending mid-text combine the items
            if (startOffset.start > 0 || endOffset.end < endOffset.total)
            {
                var startText = startItem.getParsedText();
                var textAdded = endItem.getParsedText().substring(endOffset.endText);

                // Set text to be the combination of the unselected regions of the first and last items
                newStartText = startText.substring(0, startOffset.startText) + textAdded;

                tracker.performAction(TrackerType.Remove, {
                    'itemID': startItem.id,
                    'position': startOffset.startText,
                    'text': startText.slice(startOffset.startText)
                });

                tracker.performAction(TrackerType.Insert, {
                    'itemID': startItem.id,
                    'position': startOffset.startText,
                    'text': textAdded
                });

                if (arrToDelete.indexOf(endItem) < 0)
                {
                    arrToDelete.push(endItem);
                }

                // If wholly replacing the text of the start object, reset the properties of the object to match the end.
                if (startOffset.start === 0)
                {
                    startItem.copyProperties(endItem);
                }
            }
            else
            {
                tracker.performAction(TrackerType.Remove, {
                    'itemID': startItem.id,
                    'position': 0,
                    'text': startItem.getParsedText()
                });

                // console.log('else', range.startOffset, range.endOffset, range.startContainer);
                newStartText = "";
            }

            // This performs the delete action, so if that was the key pressed, return false
            if (e.normCode === KeyCode.BackSpace || e.normCode === KeyCode.Delete || e.normCode === KeyCode.Enter)
            {
                returnValue = false;
            }

            // Move all children from the endItem to the startItem
            endItem.moveChildren(startItem, 0);

            // If the last LI is a sub-item, all subsequent sub-items need to be moved
            if (levelsDeep > 0)
            {
                var arrToMove = [ ];
                var next;

                if (endLI.children.length > 1)
                {
                    // TODO: Will need to change this from hardcoded children to finding them.
                    next = endLI.children[1].children[0];
                }
                else
                {
                    next = endLI.nextElementSibling;
                }

                var nextChild = next;
                while (levelsDeep > 0 && nextChild)
                {
                    levelsDeep--;

                    while (nextChild !== undefined && nextChild !== null)
                    {
                        arrToMove.push(nextChild);
                        nextChild = nextChild.nextElementSibling;
                    }

                    next = next.parentElement.parentElement;
                    nextChild = next.nextElementSibling;
                }

                // TODO: Can we optimize the moves of the children to avoid recomputing the templates each time?
                if (arrToMove.length > 0)
                {
                    var firstDelete = startItem;//ko.dataFor(arrToDelete[0]);
                    var parentToMoveTo = firstDelete.parent();// levelsDeep > 0 ? firstDelete.parent() : firstDelete;
                    var index = firstDelete.getIndex() + 1;

                    for (var i = 0; i < arrToMove.length; i++)
                    {
                        var item = ko.dataFor(arrToMove[i]);
                        var oldParent = item.parent();

                        item.moveTo({parent: parentToMoveTo, index: index});

                        var oldIndex = item.parent().getIndexOf(item);

                        tracker.performAction(TrackerType.Move, {
                            'itemID': item.id,
                            'oldParent': oldParent.id,
                            'oldPosition': oldIndex,
                            'newParent': parentToMoveTo.id,
                            'newPosition': index
                        });

                        index++;
                    }
                }
            }

            // TODO: Can we optimize the deletion of the children to avoid recomputing the templates each time?
            for (var i = 0; i < arrToDelete.length; i++)
            {
                var item = arrToDelete[i];
                try{g.PAssert(1253,item, 'Item must exist');}catch(PERR){g.reportError(PERR);}

                if (!item.isDeleted)
                {
                    item.parent().deleteChild(item);
                }
            }

            startItem.setText(newStartText, /*setImmediate*/true);

            // Set selection to the start of the selection
            g.selectChildren(startItem, startOffset.start, 0);
        }
        else
        {
            returnValue = false;
        }

        return returnValue;
    };

    self.handleTab = function(e, range)
    {
        if (android.isEnabled())
        {
            // Android will always be a single item selection
            var startItem = android.getCurrentVMLI();
            var endItem = android.getCurrentVMLI();
        }
        else
        {
            var startSpan = g.getSpanParent(range.startContainer);
            var endSpan = g.getSpanParent(range.endContainer);

            var startOffset = g.getSelectOffsetsInSpan(undefined, startSpan, range);
            var endOffset = g.getSelectOffsetsInSpan(undefined, endSpan, range);

            try{g.PAssert(1254,startSpan.parentNode.tagName == 'LI', 'Expected LI, Actual: ', startSpan.parentNode);}catch(PERR){g.reportError(PERR);}
            try{g.PAssert(1255,endSpan.parentNode.tagName == 'LI', 'Expected LI, Actual: ', endSpan.parentNode);}catch(PERR){g.reportError(PERR);}

            var startItem = ko.dataFor(startSpan.parentNode);
            var endItem = ko.dataFor(endSpan.parentNode);
        }

        var nextItem = startItem;

        var needUpdate = { };

        var itemsToTab = [ startItem ];
        var siblingsToMove = [ ];

        var parent;
        var newParent;

        var moved = { };
        var itemsHighlighted = { };

        itemsHighlighted[startItem.id] = true;

        if (startItem !== endItem)
        {
            // Add all lines in the selection to itemsToTab
            do
            {
                // TODO: Should this ignore items not in search?
                var nextItemOptions = { numLevels: 0, ignoreSearch: true };
                nextItem = g.getNextItem(nextItem, undefined, nextItemOptions);

                parent = nextItem.parent();
                itemsToTab.push(nextItem);
                itemsHighlighted[nextItem.id] = true;
            } while (nextItem !== endItem);
        }

        // Note: These variables need to be declared at this scope to support the for loop before
        var i, condition, increment;
        if (e.shiftKey)
        {
            var initial = function(){i = itemsToTab.length - 1;};
            var condition = function(){return i >= 0;};
            var increment = function(){i --;};
        }
        else
        {
            var initial = function(){i = 0;};
            var condition = function(){return i < itemsToTab.length;};
            var increment = function(){i ++;};
        }

        for (initial(); condition(); increment())
        {
            var item = itemsToTab[i];

            var parent = item.parent();

            var index = parent.items().indexOf(item);
            var ok;
            var u;

            if (e.shiftKey) // Unindent
            {
                ok = item.parent() != g.focusedItem;
                if(ok)
                {
                    newParent = item.parent().parent();

                    var parentItems = parent.items();
                    var numSiblings = parentItems.length - index - 1;

                    // Move this item's siblings that aren't selected or hidden to be its child
                    u = index + 1;
                    while(parentItems[index + 1])
                    {
                        var siblingItem = parentItems[index + 1];
                        // Note: Don't want to move hidden items to be its child as they should stay in their current list
                        if(!itemsHighlighted[siblingItem.id] && !g.hasClass(siblingItem.getElement(), 'hidden'))
                        {
                            needUpdate[siblingItem.parent().id] = siblingItem.parent();
                            needUpdate[item.id] = item;

                            siblingItem.moveTo({parent: item, underlying: true});
                            moved[siblingItem.id] = true;

                            tracker.performAction(TrackerType.Move, {
                                'itemID': siblingItem.id,
                                'oldParent': parent.id,
                                'oldPosition': u,
                                'newParent': item.id,
                                'newPosition': item.items().length - 1
                            });
                        }
                        else
                        {
                            index ++;
                        }

                        u ++;
                    }

                    // Item's parent is going to be tabbed so don't need to move this one
                    if(itemsHighlighted[parent.id] >= 0 && !(parent.parent() && parent.parent() == g.focusedItem))
                    {
                        continue;
                    }

                    // It should be added as the next item after the current parent
                    var parentIndex = newParent.items().indexOf(parent);

                    needUpdate[item.parent().id] = item.parent();
                    needUpdate[newParent.id] = newParent;

                    //log('move', item.text, parent.text, parentIndex);
                    item.moveTo({parent: newParent, index: parentIndex + 1, underlying: true});
                    moved[item.id] = true;

                    tracker.performAction(TrackerType.Move, {
                        'itemID': item.id,
                        'oldParent': parent.id,
                        'oldPosition': index,
                        'newParent': newParent.id,
                        'newPosition': parentIndex + 1
                    });
                }
            }
            else // Indent
            {
                ok = index > 0;
                if (ok)
                {
                    // New parent is the item above this one
                    newParent = parent.items()[index - 1];
                    // Keep going up while the item does not match search
                    while(g.hasClass(newParent.getElement(), 'hidden'))
                    {
                        index --;
                        if(index <= 0)
                        {
                            // No previous items matching search, so can't indent
                            return false;
                        }
                        newParent = parent.items()[index - 1];
                    };

                    // If the parent has already been tabbed, this will be undefined. Just move on.
                    if(!moved[parent.id])
                    {
                        needUpdate[item.parent().id] = item.parent();
                        needUpdate[newParent.id] = newParent;

                        item.moveTo({parent: newParent, underlying: true});
                        //log('move A', item.text);
                    }
                    moved[item.id] = true;

                    tracker.performAction(TrackerType.Move, {
                        'itemID': item.id,
                        'oldParent': parent.id,
                        'oldPosition': index,
                        'newParent': newParent.id,
                        'newPosition': newParent.items().length - 1
                    });
                }
                else
                {
                    newParent = parent;
                    moved[item.id] = moved[parent.id];
                }

                var moveSiblings = false;
                var parentMoved = item;
                while(parentMoved)
                {
                    if(moved[parentMoved.id])
                    {
                        moveSiblings = true;
                        break;
                    }
                    parentMoved = parentMoved.parent();
                }
                var siblingStartIndex = item.getIndex() + 1;
                if(moveSiblings)
                {
                    // Make all children siblings
                    var items = item.items();
                    var numMoved = 0;
                    for(u = 0; u < items.length; u ++)
                    {
                        var child = items[u];
                        // Only move siblings not within the selection
                        if(!itemsHighlighted[child.id])
                        {
                            // Leave hidden children where they are
                            if(g.hasClass(child.getElement(), 'hidden'))
                            {
                                continue;
                            }
                            needUpdate[child.parent().id] = child.parent();
                            needUpdate[item.parent().id] = item.parent();

                            var moveIndex = siblingStartIndex + numMoved;
                            child.moveTo({parent: item.parent(), index: moveIndex, underlying: true});

                            //log('move C', child.text, siblingStartIndex, moveIndex);
                            moved[child.id] = true;
                            tracker.performAction(TrackerType.Move, {
                                'itemID': child.id,
                                'oldParent': item.id,
                                'oldPosition': u,
                                'newParent': item.parent().id,
                                'newPosition': moveIndex
                            });
                            numMoved ++;
                            u --;
                        }
                    }
                }
            }
        }

        for (var itemId in needUpdate)
        {
            //log('Mutated: ', needUpdate[itemId]);
            needUpdate[itemId].items.valueHasMutated();
        }


        //log('-----');

        // Enumerate the modified DOM to find the selected elements
        // at the saved indices
        // for (i = 0; i < itemsToTab.length; i ++)
        // {
        //     if (i == toSelectStartIndex)
        //     {
        //         toSelectStart = itemsToTab[i].focusedSpan();
        //     }

        //     if (i == toSelectEndIndex)
        //     {
        //         toSelectEnd = itemsToTab[i].focusedSpan();
        //     }
        // }

        // Select the #text element off of the span
        if (!android.isEnabled())
        {
            if (startItem || endItem)
            {
                try{g.PAssert(1256,startItem && endItem);}catch(PERR){g.reportError(PERR);}

                g.selectMultiline(startItem.getSpan(), startOffset.start, endItem.getSpan(), endOffset.end);
            }
        }
        else
        {
            android.updateInputBounds(startItem);
        }

        e.handled = true;

        try{g.PAssert(1257,e.preventDefault, 'Should always be defined');}catch(PERR){g.reportError(PERR);}
        if (e.preventDefault)
        {
            e.preventDefault();
        }

        return false;
    };

    self.handleEnter = function(e, span, range)
    {
        //try{g.PAssert(1258,span.parentNode.tagName == 'LI', 'Expected LI, Actual: ' + span.parentNode.tagName);}catch(PERR){g.reportError(PERR);}

        var item = g.getItemForElement(span);
        var parent = item.parent();
        var index = parent.items().indexOf(item);

        var offsets = g.getSelectOffsetsInSpan(item, span, range);

        // When pressing enter, ensure that the previous editing values are saved before continuing. This
        // should happen after the selection offsets are grabbed and the current VMLI is used and before the
        // text of the currently edited item is cached locally.
        if (android.isEnabled())
        {
            android.finishInput(/*continueEditing*/true);
        }

        var text = item.getParsedText();

        var createText = '';

        var item2;

        var addToChild = false;
        var newParent = parent;

        var useNextItem = true;
        if (offsets.end === 0) // Start
        {
            // Since we are at the beginning of the line, insert the new item before this one
            if (item.isNumList())
            {
                item2 = new VMLI({ text: NumListPrefix}, {parent: newParent, forceSave: true });
            }
            else
            {
                item2 = new VMLI({ text: ''}, {parent: newParent, forceSave: true });
            }

            index--;
            useNextItem = false;
        }
        else if (offsets.start >= offsets.total) // End
        {
            // Add to child if this item has children or if it's an empty child of fullscreen header
            addToChild = !e.shiftKey && !item.isCollapsed() && (item.items().length > 0 || item.isHeader());

            if (addToChild)
            {
                newParent = item;
            }

            var newText = '';
            if (item.isNumList())
            {
                newText = NumListPrefix;
            }

            item2 = new VMLI({ text: newText}, {parent: newParent, forceSave: true });
        }
        else if (offsets.start === 0 && offsets.end === offsets.total) // All
        {
            item2 = new VMLI({ text: ''}, {parent: newParent, forceSave: true });
            item.setText('', /*setImmediate*/true);

            tracker.performAction(TrackerType.Remove, {
                'itemID': item.id,
                'position': 0,
                'text': text
            });
        }
        else // Somewhere in the middle
        {
            var firstText = offsets.start > 0 ? text.substring(0, offsets.startText) : '';
            secondText = offsets.endText < text.length ? text.substring(offsets.endText) : '';

            if (item.isNumList())
            {
                secondText = NumListPrefix + secondText;
            }

            item2 = new VMLI({ text: firstText }, { parent: newParent, forceSave: true });

            // var formatsClone = JSON.parse(JSON.stringify(item.formats));
            // item2.setFormats(formatsClone);
            // item2.removeFormatsAfter(offsets.start);

            // var newFormats = item.getFormatsAfter(offsets.end);
            item.setText(secondText, /*setImmediate*/true);
            // item.setFormats(newFormats);

            tracker.performAction(TrackerType.Remove, {
                'itemID': item.id,
                'position': offsets.startText,
                'text': text.substring(offsets.startText)
            });

            item2.copyProperties(item);

            index --;
            useNextItem = false;
        }

        item2.parseText(/*immediateParse*/true);
        //item.setHeaderFromTemp();

        if (addToChild)
        {
            item.insertItem(item2, 0);

            tracker.performAction(TrackerType.Create, {
                'itemID': item2.id,
                'parent': item.id,
                'position': 0,
                'text': item2.getParsedText()
            });
        }
        else
        {
            newParent.insertItem(item2, index + 1);

            // // If item had children, move children to item2 unless it's collapsed
            // if (!item.isCollapsed() && item.items().length > 0 && useNextItem)
            // {
            //     item.moveChildren(item2);
            // }

            // // parent.items.splice(index + 1, 0, item2);

            tracker.performAction(TrackerType.Create, {
                'itemID': item2.id,
                'parent': newParent.id,
                'position': (index + 1),
                'text': item2.getParsedText()
            });
        }

        var selectItem = useNextItem ? item2 : item;

        try{g.PAssert(1259,selectItem, 'Expected another item to exist');}catch(PERR){g.reportError(PERR);}

        if (platform.ios)
        {
            textSaveInfo.start = offsets.start;
            textSaveInfo.waitAutocorrect = 'enter';
            textSaveInfo.item = useNextItem ? item : item2;
            textSaveInfo.nextItem = useNextItem ? item2 : item;

            if (!tempInput)
            {
                tempInput = g.element('tempInput');
            }

            tempInput.innerHTML = textSaveInfo.item.getStyledText();

            g.selectMultiline(tempInput, offsets.start, tempInput, offsets.start);

            // setTimeout(function(){
            // var sel = window.getSelection();
            // var range = document.createRange();

            // log(offsets.start, tempInput.innerHTML);
            // range.setStart(tempInput, offsets.start);
            // range.setEnd(tempInput, offsets.start);

            // sel.removeAllRanges();
            // sel.addRange(range);
        // }, 0);


            return true;
        }
        else if (android.isEnabled())
        {
            android.updateInput(selectItem);

            try{g.PAssert(1260,e.preventDefault, 'Should always be defined');}catch(PERR){g.reportError(PERR);}
            if (e.preventDefault)
            {
                e.preventDefault();
            }

            return false;
        }
        else
        {
            g.selectChildren(selectItem, 0, 0);

            try{g.PAssert(1261,e.preventDefault, 'Should always be defined');}catch(PERR){g.reportError(PERR);}
            if (e.preventDefault)
            {
                e.preventDefault();
            }

            return false;
        }
    };

    self.handleBackspace = function(e, span, range)
    {
        //try{g.PAssert(1262,span.parentNode.tagName == 'LI', 'Expected LI, Actual: ' + span.parentNode.tagName);}catch(PERR){g.reportError(PERR);}

        //var item = ko.dataFor(span.parentNode);
        var item = g.getItemForElement(span);

        var parent = item.parent();

        // var index = parent.items().indexOf(item);
        // try{g.PAssert(1263,index >= 0);}catch(PERR){g.reportError(PERR);}

        var offsets = g.getSelectOffsetsInSpan(item, span, range);

        // When pressing backspace, ensure that the previous editing values are saved before continuing. This
        // should happen after the selection offsets are grabbed and the current VMLI is used and before the
        // text of the currently edited item is cached locally.
        if (android.isEnabled())
        {
            android.finishInput(/*continueEditing*/true);
        }

        if (platform.ie)
        {
            e.preventDefault();
            e.stopImmediatePropagation();
        }

        var text = item.getParsedText();

        var itemSelSpan = item.getSpan();
        var newText = '';
        var newTextOffset = 0;

        // Beginning of line
        if ((offsets.startText === 0 && offsets.endText === 0) || text === '')
        {
            // Cursor was at the start. Move this text to the end of the previous
            // item and delete it.
            var prev = g.getPreviousItem(item, g.focusedPane);
            var next = g.getNextItem(item, g.focusedPane);

            // Should not delete if prev is fullscreen
            if (prev && (!prev.isFullscreen() || (text === '' && next)))
            {
                //var numChildren = item.items().length;
                var prevTextLength = prev.getParsedText().length;

                //if (numChildren === 0)
                {
                    // If the item being deleted does not have children, just move the item text
                    // and remove the item. If however it does have children, the children need to
                    // be moved into the previous item starting at the index of the item being removed.
                    //
                    // This logic is handled within deleteChild which will automatically move any children
                    // of item to be children of prev at the index of the deleted item.
                    parent.deleteChild(item, prev, 0);

                    // Set text of above to line above + this text
                    if (text.length > 0 && text !== '')
                    {
                        if (prev.getParsedText().length === 0)
                        {
                            prev.copyProperties(item);
                        }
                        // TODO: Put formats back
                        // prev.appendFormats(item.formats, prevTextLength);
                        prev.setText(prev.getParsedText() + text, /*setImmediate*/true);

                        tracker.performAction(TrackerType.Insert, {
                            'itemID': prev.id,
                            'position': prevTextLength,
                            'text': text
                        });
                    }

                    // isFullscreen check is handling the case where the first item is being deleted
                    var selectItem = prev.isFullscreen() ? prev.getFirstChild() : prev;
                    var selectOffset = prev.isFullscreen() ? 0 : prevTextLength;

                    g.selectChildren(selectItem, selectOffset, 0);

                    return false;
                }
                // else
                // {

                //     var prevParent = prev.parent();
                //     var prevIndex = prevParent.getIndexOf(prev);
                //     var siblings = item.getSiblings();

                //     item.moveTo({parent: prevParent, index: prevIndex});

                //     if(prev.items().length > 0)
                //     {
                //         var moveSiblingsTo = item;
                //         var siblingIndex = 0;
                //         for(var i = 0; i < siblings.length; i ++)
                //         {
                //             siblings[i].moveTo({parent: moveSiblingsTo, index: siblingIndex + i});
                //         }
                //     }

                //     prev.deleteSelf();

                //     // Set text of this to line above + this text
                //     if (prev.getParsedText().length > 0 && prev.getParsedText() != '')
                //     {
                //         item.transformFormats(0, prev.getParsedText().length);
                //         item.appendFormats(prev.formats);
                //         item.setText(prev.getParsedText() + text, /*setImmediate*/true);
                //     }

                //     //item.setHeaderFromTemp();

                //     g.selectChildren(item, prevTextLength, 0);
                //     return false;
                // }

                // if (!prev.isFullscreen())
                // {
                //     g.scrollIntoView(g.getLIParent(prev.getSpan()));
                // }
                // else
                // {
                //     g.scrollIntoView(g.getLIParent(prev.items()[0].getSpan()));
                // }
            }
            else
            {
                return false;
            }
        }
        else if ((e.metaKey && offsets.startText === text.length)  || // Cmd key
          (offsets.startText === 0 && offsets.endText === text.length)) // All selected
        {
            // Cursor is at end and cmd + bksp or all text is selected.

            tracker.performAction(TrackerType.Remove, {
                'itemID': item.id,
                'position': 0,
                'text': item.getParsedText()
            });

            //item.clearFormats();
            newText = '';
            newTextOffset = 0;
        }
        else if (e.metaKey)
        {
            // Delete everything from the currently selected location on the line to the start of the line
            newText = text.substring(offsets.startText);

            tracker.performAction(TrackerType.Remove, {
                'itemID': item.id,
                'position': 0,
                'text': text.slice(0, offsets.startText)
            });

            //item.clearFormats();
            newTextOffset = 0;
        }
        else if ((platform.mac && e.altKey || !platform.mac && e.ctrlKey) && offsets.start === offsets.end)
        {
            // option + bksp while not selecting multiple characters should delete
            // the word. If it's the only word, set the text to empty.
            var endOfText = text.substring(offsets.startText);
            var seenALetter = false;
            var isHandled = false;
            for (var i = offsets.startText - 1; i >= 0 && !isHandled; i--)
            {
                // Delete the word after the space or special characters.
                // seenALetter is for trimming the spaces off the end.
                seenALetter = seenALetter || text[i] !== ' ';
                if(text[i] == ' ')
                {
                    if(text[i - 1] == ' ')
                        continue;
                }
                if (i === 0 || text[i] === ' ' || text[i] === '@')
                {
                    // If the special character is right before the cursor, delete it.
                    // Otherwise, delete up to it.
                    var subIndex = (i === 0 || i === offsets.startText - 1) ? i : i + 1;
                    if (seenALetter || text[i] !== ' ')
                    {
                        //item.transformFormats(subIndex, -(offsets.startText - subIndex));

                        tracker.performAction(TrackerType.Remove, {
                            'itemID': item.id,
                            'position': subIndex,
                            'text': text.slice(subIndex, offsets.startText)
                        });

                        newText = text.substring(0, subIndex) + endOfText;
                        newTextOffset = subIndex;

                        isHandled = true;
                    }
                }
            }

            if (!isHandled)
            {
                if (offsets.startText === text.length)
                {
                    if (offsets.start === 0)
                    {
                        //item.clearFormats();
                        newText = '';

                        tracker.performAction(TrackerType.Remove, {
                            'itemID': item.id,
                            'position': 0,
                            'text': text
                        });
                    }
                }
                else
                {
                    //item.transformFormats(offsets.startText, -offsets.startText);
                    tracker.performAction(TrackerType.Remove, {
                        'itemID': item.id,
                        'position': 0,
                        'text': text.slice(0, offsets.startText)
                    });

                    newText = endOfText;
                }

                newTextOffset = 0;
            }
        }
        // If backspacing the last character, put a TextSpace there
        // TODO: startText?
        // TODO: Does this even need to be here now that we have no TextSpace?
        else if (offsets.start === 1 && offsets.end === 1 && text.length === 1 && text[0] != '')
        {
            tracker.performAction(TrackerType.Remove, {
                'itemID': item.id,
                'position': 0,
                'text': item.getParsedText()
            });
            newText = '';
            newTextOffset = 0;
        }
        else
        {
            try{g.PAssert(1264,offsets.start >= 0);}catch(PERR){g.reportError(PERR);}

            // If there is some text selected, delete that text
            if (offsets.start != offsets.end)
            {
                //item.transformFormats(offsets.startText, -(offsets.end - offsets.start));

                tracker.performAction(TrackerType.Remove, {
                    'itemID': item.id,
                    'position': offsets.startText,
                    'text': text.slice(offsets.startText, offsets.endText)
                });

                newText = text.substr(0, offsets.startText) + text.substr(offsets.endText);
                newTextOffset = offsets.start;
            }
            else
            {
                var sub = 1;
                if (text.length > 1 && text[offsets.startText - 1] == '')
                {
                    sub++;
                }

                // Otherwise there is no text selected, delete the previous character
                newText = text.substr(0, offsets.startText - sub) + text.substr(offsets.startText);

                //item.transformFormats(offsets.end - 1, -1);

                tracker.performAction(TrackerType.Remove, {
                    'itemID': item.id,
                    'position': offsets.startText - sub,
                    'text': text.slice(offsets.startText - sub, offsets.startText)
                });

                newTextOffset = offsets.startText - sub ;
            }
        }

        // Don't do this if the last keypress was backspace because that means it's repeating
        // and we don't want to to delete words
        // TODO: It'd be nice to stop doing this once backspace is no longer being repeated.
        // The only way I can think of is a timeout and that's scary.
        if(platform.phone && lastKeypress !== KeyCode.BackSpace)
        {
            textSaveInfo.text = newText;
            textSaveInfo.start = newTextOffset;
            textSaveInfo.item = item;
            if(newText.length === 0)
            {
                // If the text will be empty, temporarily put a TextSpace in the style text
                // so the default action of deleting it won't break the html.
                item.setStyledText(TextSpace + ' ', true);
                g.selectChildren(item, 1, 0);
            }
            return true;
        }
        else
        {
            item.setText(newText, /*setImmediate*/true);
            g.selectChildren(item, newTextOffset, 0);
        }

        return false;
    };

    self.handleDelete = function (e, span, range)
    {
        var item = ko.dataFor(span.parentNode);

        var offsets = g.getSelectOffsetsInSpan(item, span, range);

        try{g.PAssert(1265,item);}catch(PERR){g.reportError(PERR);}

        var parent = item.parent();
        var index = item.getIndex();
        var text = item.getParsedText();

        var itemSelSpan = item.getSpan();

        // If the text is completely equal to the placeholder space
        if (text === '')
        {
            var refocusItem = g.getNextItem(item, g.focusedPane);
            var refocusIndex = 0;

            if (!refocusItem)
            {
                refocusItem = g.getPreviousItem(item, g.focusedPane);
                refocusIndex = refocusItem.getParsedText().length;
            }

            if (refocusItem && !refocusItem.isFullscreen())
            {
                if (item.hasChildren())
                {
                    parent.replaceChild(item, item.getFirstChild());
                }
                else
                {
                    // Delete self and focus the next item
                    parent.deleteChild(item, parent, index);
                }

                g.selectChildren(refocusItem, refocusIndex, refocusIndex);
                // g.scrollIntoViewIfNeeded(refocusItem);
            }
        }
        else if ((offsets.start === offsets.total && offsets.end === offsets.total) ||
            (offsets.start === offsets.total - 1 && offsets.end === offsets.total - 1 && span.textContent[offsets.endText] === ''))
        {
            // If the delete key is pressed while the caret is at the end of the line,
            // delete the next item and set this items text to be the union
            var next = g.getNextItem(item, g.focusedPane);

            if (next)
            {
                // Set text of above to line above + this text
                // TODO: Move around the formatting
                //item.appendFormats(next.formats, text.length);
                item.setText(text + next.getParsedText(), /*setImmediate*/true);

                tracker.performAction(TrackerType.Insert, {
                    'itemID': item.id,
                    'position': text.length,
                    'text': next.getParsedText()
                });

                next.parent().deleteChild(next, item, 0);
                g.selectChildren(item, offsets.total, 0);
            }
        }
        else if (offsets.start === 0 && offsets.end === 0 && text.length === 1 && text[0] != '')
        {
            // If there is a single character left being deleted, it needs to be replaced
            // with a palceholder space
            item.setText('', /*setImmediate*/true);

            tracker.performAction(TrackerType.Remove, {
                'itemID': item.id,
                'position': 0,
                'text': text
            });
        }
        else if ((platform.mac && e.altKey || !platform.mac && e.ctrlKey) && offsets.start === offsets.end)
        {
            // option + delete while not selecting multiple characters should delete
            // the word. If it's the only word, set the text to empty.
            var startOfText = text.substring(0, offsets.startText);
            var seenALetter = false;
            var isHandled = false;
            for (var i = offsets.startText; i < text.length && !isHandled; i ++)
            {
                // Delete the word after the space or special characters.
                // seenALetter is for trimming the spaces off the end.
                seenALetter = seenALetter || text[i] !== ' ';
                if (i === (text.length - 1) || text[i] === ' ')
                {
                    // If the special character is right before the cursor, delete it.
                    // Otherwise, delete up to it.
                    var subIndex = (text[i] === ' ' && text[offsets.startText] == ' ') ? i : i + 1;
                    if (seenALetter || text[i] !== ' ')
                    {
                        //item.transformFormats(offsets.startText + 1, -(subIndex - offsets.startText));
                        var newText = startOfText + text.substring(subIndex);
                        item.setText(newText, /*setImmediate*/true);

                        tracker.performAction(TrackerType.Remove, {
                            'itemID': item.id,
                            'position': subIndex,
                            'text': newText
                        });

                        g.selectChildren(item, offsets.start, 0);

                        isHandled = true;
                    }
                }
            }
        }
        else
        {
            // Either delete the selected text, or delete the character folliwng the caret
            if (offsets.start != offsets.end)
            {
                //item.transformFormats(offsets.start, -(offsets.end - offsets.start));
                var newText = text.substr(0, offsets.startText) + text.substr(offsets.endText);
                item.setText(newText, /*setImmediate*/true);

                tracker.performAction(TrackerType.Remove, {
                    'itemID': item.id,
                    'position': offsets.startText,
                    'text': text.slice(offsets.startText, offsets.endText)
                });

                g.selectChildren(item, offsets.start, 0);
            }
            else
            {
                // Otherwise there is no text selected, delete the previous character
                //item.transformFormats(offsets.start, -1);
                var newText = text.substr(0, offsets.startText) + text.substr(offsets.startText + 1);
                item.setText(newText, /*setImmediate*/true);

                tracker.performAction(TrackerType.Remove, {
                    'itemID': item.id,
                    'position': offsets.startText,
                    'text': text.slice(offsets.startText, offsets.startText + 1)
                });

                g.selectChildren(item, offsets.start, 0);
            }
        }

        return false;
    };

    return self;
});
