define('android',['ko', 'globals', 'platform'],
function (ko, g, platform) {
    var self = { };

    var paneRoot;
    var paneScroller;
    var paneContent;
    var inputBox;
    var currentVMLI;

    var canSetInput = false;

    var enabled = false;
    self.isEnabled = function ()
    {
        return enabled;
    };

    self.getCurrentVMLI = function ()
    {
        return currentVMLI;
    };

    if (DEBUG)
    {
        window.__getCurrentVMLI = function ()
        {
            return currentVMLI;
        };
    }

    self.getCurrentSpan = function ()
    {
        if (currentVMLI)
        {
            return currentVMLI.getSpan();
        }

        return undefined;
    };

    self.isTargetBox = function(e)
    {
        return inputBox === (e.target || e.srcElement);
    };

    self.getInputBox = function ()
    {
        return inputBox;
    };

    self.getSelectionStart = function ()
    {
        return inputBox.selectionStart;
    };

    self.getSelectionEnd = function ()
    {
        return inputBox.selectionEnd;
    };

    self.setPaneRoot = function(pane)
    {
        if (pane.rootElement !== undefined)
        {
            enabled = true;
        }
        else
        {
            enabled = false;

            return;
        }

        if (pane.rootElement !== paneRoot)
        {
            // Grab DOM elements that need event listeners attached
            paneRoot = pane.rootElement;
            paneScroller = pane.scrollElement;
            paneContent = pane.paneContent;

            // paneRoot.addEventListener('textInput', function (e)
            // {
            //     log('TextInput: ', e.data.length, e);
            // });

            // paneRoot.addEventListener('compositionstart', function (e)
            // {
            //     log('CompositionStart: ', e);
            // });

            // paneRoot.addEventListener('compositionupdate', function (e)
            // {
            //     log('CompositionUpdate: ', e);
            // });

            // paneRoot.addEventListener('compositionend', function (e)
            // {
            //     log('CompositionEnd: ', e.data.length, e);
            // });

            // paneRoot.addEventListener('DOMCharacterDataModified', function (e)
            // {
            //     if (e.prevValue === TextSpace && e.newValue === "")
            //     {
            //         require(['edit', 'tracker'], function (edit, tracker)
            //         {
            //             var range = window.getSelection().getRangeAt(0);
            //             var spanEl = range.startContainer

            //             // If the selection container is a text node, grab the parent span
            //             if (range.startContainer.nodeType === 3)
            //             {
            //                 spanEl = range.startContainer.parentNode;
            //             }

            //             tracker.beginAction(); // BEGIN TRACKER <----

            //             edit.handleBackspace(e, range.startContainer, range);

            //             tracker.endAction(); // END TRACKER <----
            //         });
            //     }
            //     else
            //     {
            //         log(e);
            //     }
            // });

            inputBox = document.createElement('textarea');

            inputBox.id = 'androidInput';
            inputBox.style.visibility = 'hidden';

            paneRoot.appendChild(inputBox);

            // if (DEBUG)
            // {
            //     var mutID = 0;
            //     var observer = new MutationObserver(function (mutations)
            //     {
            //         mutations.forEach(function (mutation)
            //         {
            //             console.log(mutID + ' - Mutation: ', mutation.type + '  : ', mutation);
            //         });

            //         mutID++;
            //     });

            //     var config = { childList: true, characterData: true, characterDataOldValue: true, subtree: true };

            //     observer.observe(inputBox, config);
            // }

            inputBox.onfocus = function (e)
            {
                if (DEBUG) log('InputBox Focus', e);

                // If an element is being dragged, dont start editing
                if (!g.dragging)
                {
                    //g.inputJustBlurred = false;

                    if (!g.isKeyboardOpen)
                    {
                        g.keyboardToolbar.show();
                    }
                }
            };

            inputBox.onblur = function (e)
            {
                if (DEBUG) log('InputBox Blur: ', e);

                if (!e.relatedTarget || e.relatedTarget.id === 'searchPhone')
                {
                    self.finishInput();

                    //g.inputJustBlurred = true;

                    g.keyboardToolbar.hide();
                }
            };

            var edit;
            var tracker;

            inputBox.oninput = function (e)
            {
                try{g.PAssert(1058,currentVMLI, 'VMLI must be selected in order to get input events');}catch(PERR){g.reportError(PERR);}

                if (currentVMLI)
                {
                    var isBackspace = !self.getBoxHasTextSpace();

                    if (isBackspace)
                    {
                        edit = edit || require('edit');
                        tracker = tracker || require('tracker');

                        try{g.PAssert(1059,edit && tracker, 'Should always be defined');}catch(PERR){g.reportError(PERR);}

                        tracker.beginAction(); // BEGIN TRACKER <----

                        edit.handleBackspace(e, currentVMLI.getSpan(), /*range*/undefined);

                        tracker.endAction(); // END TRACKER <----
                    }
                }
            };

            function enforceSelection()
            {
                try{g.PAssert(1060,currentVMLI, 'VMLI must be selected in order to enforce selection offset');}catch(PERR){g.reportError(PERR);}
                try{g.PAssert(1061,self.getBoxHasTextSpace(), 'Editor must always be prefixed with TextSpace');}catch(PERR){g.reportError(PERR);}

                // Move selection to after the TextSpace
                if (inputBox.selectionStart === 0)
                {
                    inputBox.selectionStart = 1;
                }
            }

            if (!platform.ie)
            {
                inputBox.ontouchend = function (e)
                {
                    setTimeout(enforceSelection, 30);
                };
            }

            canSetInput = (inputBox.selectionStart !== null && inputBox.selectionStart !== undefined) && (inputBox.selectionEnd !== null && inputBox.selectionEnd !== undefined)



            log('---- Android Editing Enabled ----');
        }
    };

    var wasHeader = false;
    var isInputVisible = false;
    self.updateInput = function(vmli)
    {
        try{g.PAssert(1062,self.isEnabled(), 'Must be enabled to update input');}catch(PERR){g.reportError(PERR);}

        if (DEBUG) console.trace();

        if (DEBUG) log('Android UpdateInput');

        var sel = window.getSelection();
        var selOffset = undefined;

        if (sel.rangeCount > 0 && sel.getRangeAt(0).startContainer.nodeType === Node.TEXT_NODE)
        {
            var oldRange = sel.getRangeAt(0);

            selOffset = g.getSelectOffsetsInSpan(vmli, vmli.getSpan(), oldRange, /*forceSelection*/true);

            if (DEBUG) log('Sel Offset: ', selOffset, sel, sel.getRangeAt(0).startContainer);
        }
        else
        {
            if (DEBUG) log('No Selection Set - Android');
        }

        var openingInput = false;
        if (!isInputVisible)
        {
            isInputVisible = true;

            openingInput = true;

            inputBox.style.visibility = 'visible';
        }

        if (!g.isKeyboardOpen)
        {
            g.keyboardToolbar.show();
        }

        var itemChanged = (vmli !== currentVMLI);

        if (itemChanged || (vmli && vmli.isHeader() !== wasHeader))
        {
            // If there was an item being edited previously, ensure that its text is saved when switching to
            // editing a new item.
            if (currentVMLI && itemChanged)
            {
                self.finishInput(/*continueEditing*/true);
            }

            currentVMLI = vmli;

            wasHeader = vmli.isHeader();

            self.updateInputText(vmli);

            self.updateInputBounds(vmli);
        }

        if (openingInput)
        {
            inputBox.focus();
        }

        if (selOffset !== undefined && canSetInput)
        {
            // +1 Comes from TextSpace that is guaranteed to be at the front of the textarea
            inputBox.selectionStart = selOffset.start + 1;
            inputBox.selectionEnd = selOffset.end + 1;

            if (DEBUG) log('Set InputBox Selection: ' + (selOffset.start + 1), (selOffset.end + 1));
        }
    };

    self.updateInputText = function(vmli)
    {
        if (!vmli || !currentVMLI || vmli.id !== currentVMLI.id)
        {
            return;
        }

        var displayText = currentVMLI.getParsedText();

        if (DEBUG && DEBUG)  log('Update Input Text: ' + displayText);

        self.setBoxValue(displayText);
    };

    self.insertText = function(offset, text)
    {
        var val = self.getBoxValue();

        var newText = val.substr(0, offset) + text + val.substr(offset);

        self.setBoxValue(newText);

        if (canSetInput)
        {
            // +1 Comes from the TextSpace guaranteed to be at the beginning of the TextArea
            inputBox.selectionStart = offset + text.length + 1;
            inputBox.selectionEnd = offset + text.length + 1;
        }
    };

    self.getInputText = function()
    {
        try{g.PAssert(1063,self.isEnabled(), 'Must be enabled to get input');}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(1064,currentVMLI, 'Android input must be active to get input text');}catch(PERR){g.reportError(PERR);}

        if (!currentVMLI)
        {
            return '';
        }

        return self.getBoxValue();
    };

    var lastHeight = 0;
    self.updateInputBounds = function(vmli)
    {
        if (!vmli || !currentVMLI || vmli.id !== currentVMLI.id)
        {
            return;
        }

        if (DEBUG) log('Android UpdateInputBounds');

        var elem = vmli.getSpan();
        var bRect = elem.getBoundingClientRect();

        // The width of the left-hand colorized indicator
        var leftOffset = 8;

        // TODO: Remove magic number 41
        var topOffset = 40;

        var scrollTop = paneScroller.scrollTop;

        g.copyStyles(elem, inputBox, ['paddingLeft', 'paddingTop', 'paddingRight', 'paddingBottom', 'width', 'height', 'fontSize', 'lineHeight']);

        inputBox.style.top = Math.ceil(bRect.top + scrollTop - topOffset) + "px";
        inputBox.style.marginLeft = leftOffset + 'px';

        inputBox.style.paddingLeft = (parseInt(inputBox.style.paddingLeft) - leftOffset) + 'px';

        lastHeight = inputBox.scrollHeight;
    };

    self.checkAndroidBounds = function()
    {
        try{g.PAssert(1065,currentVMLI, 'When updating Android bounds we should always have a current VMLI');}catch(PERR){g.reportError(PERR);}

        var boxHeight = inputBox.scrollHeight;

        if (boxHeight !== lastHeight)
        {
            var resultText = self.getBoxValue();

            enabled = false;

            if (currentVMLI)
            {
                if (currentVMLI.getRawText() !== resultText)
                {
                    currentVMLI.setText(resultText, /*immediateParse*/true, /*fromAndroidEditing*/true);
                }

                var cStyle = window.getComputedStyle(currentVMLI.getSpan());

                inputBox.style.height = cStyle ? cStyle.height : '16px';
            }

            enabled = true;

            lastHeight = boxHeight;
        }
    };

    self.flushInput = function()
    {
        var resultText = self.getBoxValue();

        if (DEBUG && DEBUG) log('Android Flush Input: ' + resultText);

        // TODO: Only set text from android input when it has actually changed
        if (currentVMLI.getRawText() !== resultText)
        {
            currentVMLI.setText(resultText, /*immediateParse*/true, /*fromAndroidEditing*/true);
        }
    };

    self.finishInput = function(continueEditing)
    {
        try{g.PAssert(1066,self.isEnabled(), 'Must be enabled to finish input');}catch(PERR){g.reportError(PERR);}

        if (!currentVMLI)
        {
            return;
        }

        if (isInputVisible && !continueEditing)
        {
            isInputVisible = false;

            inputBox.style.visibility = 'hidden';
        }

        self.flushInput();

        if (DEBUG) log('Android Finished Input');

        currentVMLI = undefined;
    };

    self.getBoxHasTextSpace = function()
    {
        var rawText = inputBox.value;

        if (rawText.length === 0 || rawText[0] !== TextSpace)
        {
            try{g.PAssert(1067,rawText.indexOf(TextSpace) < 0, 'If TextSpace is not at the front of the editor, it shouldnt appear in the rest of the string');}catch(PERR){g.reportError(PERR);}

            return false;
        }

        return true;
    };

    self.getBoxValue = function()
    {
        var rawText = inputBox.value;

        var outText = rawText.replace(TextSpace, '');

        return outText;
    };

    self.setBoxValue = function(text)
    {
        if (!text || text.length === 0)
        {
            text = TextSpace;
        }

        var newText = text[0] === TextSpace ? text : (TextSpace + text);

        inputBox.value = newText;
    };

    self.passKeypress = function(e)
    {
        //log('Keypress: ', e, currentVMLI);

        if (!currentVMLI)
        {
            return false;
        }

        if (e.normCode === KeyCode.Enter || e.synthetic)
        {
            return true;
        }

        return false;
    };

    self.passKeydown = function(e)
    {
        //log('Keydown: ', e, currentVMLI);

        if (!currentVMLI)
        {
            return false;
        }

        if (e.normCode === KeyCode.Tab || e.normCode === KeyCode.BackSpace || e.synthetic)
        {
            return true;
        }

        return false;
    };

    return self;
});
