define('VMKeyboardToolbar',['ko', 'globals', 'data', 'edit', 'platform'],
function (ko, g, d, edit, platform) {
function VMKeyboardToolbar()
{
    this.buttons = ko.observableArray();
    this.priorityVisible = ko.observable();
    this.isVisible = ko.observable(false);
    this.element = undefined;
    this.aboutToShow = undefined;
    this.bottomPosition = undefined;

    this.init();
}

VMKeyboardToolbar.prototype = {
    init: function()
    {
        this.element = document.getElementById('mobileKeyboardToolbar');

        g.applyBindings(this, 'mobileKeyboardToolbar');

        window.addEventListener('closeKeyboard', this.hide.bind(this));
    },
    onTap: {
        onStart: g.handlerPreventStop,
        onClick: function(e)
        {
            var id = e.srcElement.id;
            if(id == 'mobileKeyboardToolbar')
            {
                e.preventDefault();
                e.returnFalse = true;
            }

            if(id == 'closeKeyboardButton')
            {
                g.blurActiveElement();
                g.vmMain.clearSelected();
            }
            else if(g.vmSearchPhone.isFocused())
            {
                var ch;
                if(id == 'priorityButton')
                    ch = '!';
                else if(id == 'dateButton')
                    ch = '@';
                else if(id == 'contactButton')
                    ch = '+';
                else if(id == 'tagButton')
                    ch = '#';
                g.vmSearchPhone.insert(ch);
            }
            else
            {
                if(id == 'outdentButton')
                    g.fireKeyEvent(/*ele*/undefined, {normCode: KeyCode.Tab, shiftKey: true});
                else if(id == 'indentButton')
                    g.fireKeyEvent(/*ele*/undefined, {normCode: KeyCode.Tab, shiftKey: false});
                // else if(id == 'headerButton')
                //     g.vmMain.makeSelectionHeader();
                else if(id == 'priorityButton')
                    this.priorityVisible(!this.priorityVisible());
                else if(id == 'pNoneButton')
                    g.vmMain.applyPriorityToSelection(VMLIFlag.None, /*toggle*/false);
                else if(id == 'p0Button')
                    g.vmMain.applyPriorityToSelection(VMLIFlag.P0, /*toggle*/true);
                else if(id == 'p1Button')
                    g.vmMain.applyPriorityToSelection(VMLIFlag.P1, /*toggle*/true);
                else if(id == 'p2Button')
                    g.vmMain.applyPriorityToSelection(VMLIFlag.P2, /*toggle*/true);
                else if(id == 'pStarButton')
                    g.vmMain.applyFnToSelection('isFlagged', /*toggle*/true);
                else if(id == 'dateButton')
                {
                    g.vmMain.insertCharacter('@', true);
                }
                else if(id == 'contactButton')
                {
                    g.vmMain.insertCharacter('+', true);
                }
                else if(id == 'tagButton')
                {
                    g.vmMain.insertCharacter('#', true);
                }
            }

            e.stopImmediatePropagation(); //stops event bubbling
            e.preventDefault();
            e.returnFalse = true;
        }
    },
    preShow: function()
    {
        // Show sometimes has a timeout so this hides the floating menu immediately
        // g.element('phoneMenuFloating').style.visibility = 'hidden';

        if (DEBUG) log('Pre-Show Keyboard Menu');

        //console.profile('ShowKeyboard');

        // This prevents the toolbar being hidden by the blur event before it is shown
        // in the case where focus changes between the pane and search
        this.aboutToShow = true;
        this.priorityVisible(false);

        // TODO: Since we no longer have pull-to-add can this be removed?

        // If pull-to-add adds an item it does not blur/focus so we need to clear this
        // state manually
        setTimeout(function()
        {
            this.aboutToShow = false;
        }.bind(this), 100);
    },
    show: function ()
    {
        this.aboutToShow = false;

        if (DEBUG) log('Show Keyboard Menu');

        this.updatePosition();

        g.isKeyboardOpen = true;

        if (!platform.ie/* || (platform.ie && (platform.mobro || platform.winphone))*/)
        {
            this.isVisible(true);
        }
        // g.element('phoneMenuFloating').style.visibility = 'hidden';
        this.hideTopBarIfLandscape();


        if (g.vmSearchPhone.isFocused())
        {
            g.addClass(this.element, 'kbsearch');
        }
        else
        {
            g.removeClass(this.element, 'kbsearch');
        }
    },
    hide: function ()
    {
        if (this.aboutToShow || !g.isKeyboardOpen)
        {
            if (DEBUG) log('Hide Keyboard Menu [SKIPPED]');

            return;
        }

        //console.profileEnd();

        if (DEBUG) log('Hide Keyboard Menu');

        this.priorityVisible(false);

        g.isKeyboardOpen = false;

        this.hideTopBarIfLandscape();

        this.isVisible(false);
        // g.element('phoneMenuFloating').style.visibility = 'visible';
    },
    hideTopBarIfLandscape: function()
    {
        if(platform.orientation === Orientation.Landscape && g.isKeyboardOpen)
        {
            g.setTransform(g.element('container'), 0, -40);
            g.setTransform(g.element('phoneMenuTopWrapper'), 0, -40);

            if(g.vmMain.selected().length > 0)
            {
                var item = g.vmMain.selected()[0];
                g.scrollToTop(item.getSpan());
            }
        }
        else
        {
            g.setTransform(g.element('container'), 0, 0);
            g.setTransform(g.element('phoneMenuTopWrapper'), 0, 0);
        }
    },
    // virtualKeyboardHeight: function ()
    // {
    //     // It looks like this code is in a very specific ordering especially with setting scroll offsets
    //     // on window... don't change unless you're going to re-comment and know what this is actually doing.
    //     var sx = document.body.scrollLeft, sy = document.body.scrollTop;
    //     var naturalHeight = window.innerHeight;

    //     window.scrollTo(sx, document.body.scrollHeight);

    //     var keyboardHeight = naturalHeight - window.innerHeight + sy;

    //     log(sy, naturalHeight, window.innerHeight, keyboardHeight);
    //     window.scrollTo(sx, 0);

    //     return keyboardHeight;
    // },
    updatePosition: function (byResize)
    {
        // if(!platform.app)
        // {
        //     if (byResize && g.isKeyboardOpen)
        //     {
        //         document.activeElement.blur();
        //     }
        //     else
        //     {
                // var bottom;
                if(!platform.app)
                {
                    // bottom = this.virtualKeyboardHeight();
                    // bottom = 0;
                    // debugger;
                    if(platform.orientation == Orientation.Portrait)
                    {
                        // if(!platform.kbsizePortrait)
                        // {
                        //     platform.kbsizePortrait = bottom;
                        // }
                        platform.kbsize = platform.kbsizePortrait;
                    }
                    else
                    {
                        // if(!platform.kbsizeLandscape)
                        // {
                        //     platform.kbsizeLandscape = bottom;
                        // }
                        platform.kbsize = platform.kbsizeLandscape;
                    }

                    // // iOS 7 hides the address and navigation bars in landscape but only if it has ever been
                    // // in portrait, so add extra padding
                    // if(!byResize && platform.ios7 && platform.orientation === Orientation.Landscape && platform.hasBeenPortrait)
                    // {
                    //     bottom += 44;
                    // }
                }
                // else
                // {
                    // bottom = platform.kbsize;
                // }

                var bottom = platform.kbsize;


                // Set bottom position on keyboard
                if (bottom != this.bottomPosition)
                {
                    this.bottomPosition = bottom;
                    g.element('mobileKeyboardToolbar').style.bottom = this.bottomPosition + 'px';
                    // Toolbar is at the wrong place and lags a little bit before layout, so hide it and
                    // show it again in a little bit
                    if(byResize)
                    {
                        this.isVisible(false);
                        setTimeout(function() {
                            this.isVisible(true);
                        }.bind(this), 400);
                    }
                }
        //     }
        // }
    }
};

return VMKeyboardToolbar;
});
