self.scopes = [ GScope.Calendar, GScope.CalendarRO, GScope.Contacts, GScope.Drive, GScope.UserInfoEmail, GScope.DriveFull, GScope.UserInfoProfile, GScope.DriveInstall, GScope.DriveAppData ];

