var calendarListFields = "etag,items(description,etag,id,kind,location,selected,summary,summaryOverride,timeZone),kind,nextPageToken";

self.isLoaded = false;

self.init = function()
{
    // Grab the saved scope indices
    var savedScopesString = g.settings.get(Settings.authScopes);

    // If there are saved scopes, combine with the default set of required scopes
    if (savedScopesString)
    {
        var savedScopes;
        try
        {
            savedScopes = JSON.parse(savedScopesString);
        }
        catch (err)
        {
            g.reportError(err);
        }

        if (savedScopes)
        {
            for (var i = 0; i < savedScopes.length; ++i)
            {
                try{g.PAssert(1036,savedScopes[i] >= 0, 'Each saved scope should be a valid array index');}catch(PERR){g.reportError(PERR);}

                if (savedScopes[i] >= 0)
                {
                    self.requestScopes.push(self.scopes[savedScopes[i]]);
                }
            }

            // No reason to save the same scope multiple times
            self.requestScopes = ko.utils.arrayGetDistinctValues(self.requestScopes);

            if (DEBUG) log('Saved Scopes: ', savedScopes);
        }
    }
    else
    {
        self.requestScopes = self.requiredScopes;
    }

    authorizedScopes = self.requestScopes;

    // If custom redirect was for syncing contacts, setup contact sync
    if (platform.syncContacts)
    {
        if (util.getURLParam('access_token') && !util.hasURLParam('error'))
        {
            if (authorizedScopes.indexOf(GScope.Contacts) < 0)
            {
                authorizedScopes.push(GScope.Contacts);

                saveAuthorizedScopes();
            }

            g.settings.set(Settings.syncContacts, true);
        }
    }

    // Setup Analytics UserID feature
    var authGASub = self.authenticatedUserEmail.subscribe(function (newValue)
    {
        if (newValue !== undefined)
        {
            if (window.ga)
            {
                ga('set', '&uid', g.getAnonUserIdentifier());
            }

            authGASub.dispose();
            authGASub = undefined;
        }
    });
};

self.ensureRequiredScopesAreRequested = function()
{
    var needsAdditionalScopes = false;

    // If we're missing any of the required OAuth scopes, prompt the user to authenticate them.
    for (var i = 0; i < self.requiredScopes.length; ++i)
    {
        if (authorizedScopes.indexOf(self.requiredScopes[i]) < 0)
        {
            needsAdditionalScopes = true;

            break;
        }
    }

    if (needsAdditionalScopes)
    {
        self.runAuthenticate(self.requiredScopes, /*runImmediate*/true, /*retry*/false, function (token)
        {
            if (!token || token.error)
            {
                g.messageQueue.pushMessage({
                    text: 'We need additional permissions to give you the best experience, ' + platform.actionVerb() + ' here to authenticate.',
                    type: MessageType.Warning,
                    action: function ()
                    {
                        self.runAuthenticate(self.requiredScopes, /*runImmediate*/false, /*retry*/false, function (token2)
                        {
                            if (!token2 || token2.error)
                            {
                                g.messageQueue.pushMessage({
                                    text: 'There was an error authenticating, ' + platform.actionVerb() + ' here to try again.',
                                    type: MessageType.Error,
                                    action: self.ensureRequiredScopesAreRequested
                                });
                            }
                        });
                    }
                });
            }
        });
    }
};

var watchdogWait = 1000 * 60 * 16; // 16 minutes (in ms)
var watchdogInterval = undefined;
self.startWatchdog = function()
{
    if (!watchdogInterval)
    {
        watchdogInterval = setInterval(self.watchdogFn, watchdogWait);
    }
};

self.stopWatchdog = function()
{
    clearInterval(watchdogInterval);
    watchdogInterval = undefined;
};

var lastWatchdogTime = 0;
self.watchdogFn = function()
{
    var currentTime = Date.now();

    // When devices come back from sleep, they may fire multiple interval events at once. Limit
    // number of events that get fired when coming back into the foreground.
    if (lastWatchdogTime - currentTime >= watchdogWait)
    {
        try
        {
            var token = gapi.auth.getToken();

            if (token)
            {
                var expireTime = parseInt(token.expires_at) * 1000;

                if (currentTime > expireTime)
                {
                    if (ShouldLog(LogLevels.Error)) log('Active token is no longer valid: ' + currentTime + ' vs. ' + expireTime);

                    refreshAuthorization();

                    g.reportError(new Error('Watchdog found expired token on client'));
                }
                else
                {
                    gdata.poke();
                }
            }
            else
            {
                if (ShouldLog(LogLevels.Error)) log('No active token on client!');

                refreshAuthorization();

                g.reportError(new Error('Watchdog found client with no token set'));
            }
        }
        catch (err)
        {
            g.reportError(err);
        }
        finally
        {
            lastWatchdogTime = currentTime;
        }

        g.checkForRemoteChanges();
    }
};

self.hasPermission = function(scope)
{
    return (authorizedScopes.indexOf(scope) > -1);
};

//
// GAPI Functionality
//
var refreshAuthorizationTimer = undefined;
var authorizedUntil = undefined;
var refreshingAuth = true;
self.onFocusHandler = function()
{
    if (authorizedUntil && Date.now() > authorizedUntil)
    {
        refreshAuthorization();
    }
};

self.onBlurHandler = function()
{
    if (refreshAuthorizationTimer)
    {
        clearTimeout(refreshAuthorizationTimer);
    }
}

function getAccessTokenInternal()
{
    var token = undefined;

    try
    {
        var gapiToken = gapi.auth.getToken();

        if (gapiToken)
        {
            token = gapiToken.access_token;
        }
    }
    catch (err)
    {
        self.recoverFromError(err);
    }

    return token;
}

self.getAccessToken = function()
{
    var token = getAccessTokenInternal();

    if (!token)
    {
        self.recoverFromError();
    }

    return token;
};

