// We only want to create a single realtime doc for the user (for now). Ensure that we
// check if the user already has a doc created by us and use that instead of creating a
// new doc.
function openDefaultRealtimeFile(callback)
{
    g.vmMain.setGDriveStatus(DriveStatus.Connecting);

    gapi.client.load('drive', 'v2', function ()
    {
        if(!gapi.client.drive)
        {
            showErrorMessage();
            return;
        }

        var queryString = "(title='"+self.LEGACY_DefaultDriveDocTitle+"' or properties has {key='"+self.DefaultProperty+"' and value='"+self.DefaultPropertyValue+"' and visibility='PRIVATE'}) and 'me' in owners and trashed=false"

        gapi.client.drive.files.list(
        {
            // 'maxResults': 1, // TODO: Why does maxResults of 1 not work?
            'fields': docFields,
            'q': queryString
        }).execute(function (results)
        {
            if (!results || !results.items)
            {
                if (results && (results.code !== 200 && results.error))
                {
                    showErrorMessage();
                }
                else
                {
                    createDefaultRealtimeFile(callback);
                }
            }
            else
            {
                callback(results.items[0], /*newDoc*/false);
            }
        });
    });
}

function deleteRealtimeFile(fileId, callback)
{
    gapi.client.drive.files.delete({
        'fileId': fileId
    }).execute(callback);
}

