// If a doc has not been created for us yet, this will create one. For now, leave the doc
// visible through your google drive management page.
function createDefaultRealtimeFile(callback)
{
    gapi.client.load('drive', 'v2', function ()
    {
        gapi.client.drive.files.insert({
            'resource':
            {
                'mimeType': self.REALTIME_MIMETYPE,
                'title': self.DefaultDocTitle,
                'writersCanShare': false,
                'properties':
                [
                    {
                        'key': self.DefaultProperty,
                        'value': self.DefaultPropertyValue,
                        'visibility': 'PRIVATE'
                    }
                ]
            }
        }).execute(function (docInfo)
        {
            if (docInfo.code !== 200 && docInfo.error)
            {
                showErrorMessage();
            }
            else
            {
                var loadFileID = util.getURLParam('file');

                // In the case where a user has had a file shared with them and this is their first file,
                // after we have created their default file, open the requested shared file.
                if (loadFileID)
                {
                    try
                    {
                        try{g.PAssert(1050,gapi.drive, 'gapi.drive should exist');}catch(PERR){g.reportError(PERR);}

                        util.removeURLParam('file');

                        gapi.drive.realtime.load(loadFileID, onDocLoaded, onDocInitialized, onDocLoadError);
                    }
                    catch (err)
                    {
                        g.reportError(err);
                    }
                }
                else
                {
                    callback(docInfo, /*newDoc*/true);
                }
            }
        });
    });
}

function onFirstDocInitialized(doc)
