function saveAuthorizedScopes()
{
    if (DEBUG) log('Saving Scopes: ');

    var scopeArr = [ ];

    for (var i = 0; i < authorizedScopes.length; ++i)
    {
        var scopeIndex = self.scopes.indexOf(authorizedScopes[i]);

        //try{g.PAssert(1054,scopeIndex >= 0, 'Scopes should always be found');}catch(PERR){g.reportError(PERR);}

        var forceNoSave = noSaveScopes.indexOf(authorizedScopes[i]) >= 0;

        if (DEBUG && forceNoSave)
        {
            log('Skipping scope save: ', authorizedScopes[i]);
        }

        if (scopeIndex >= 0 && !forceNoSave)
        {
            scopeArr.push(scopeIndex);
        }

        if (DEBUG) log(' :', authorizedScopes[i]);
    }

    var saveString = JSON.stringify(scopeArr);

    g.settings.set(Settings.authScopes, saveString);
}

function handleContactsLoaded(numContacts)
{
    if (numContacts > 0)
    {
        g.vmMain.runParseWorkerNewContacts();
    }
}

self.authAndLoadContacts = function(cb)
{
    if (g.settings.get(Settings.syncContacts))
    {
        if (authorizedScopes.indexOf(GScope.Contacts) < 0)
        {
            self.runAuthenticate([GScope.Contacts], /*runImmediate*/true, /*retry*/true, function (token)
            {
                if (!token || token.error)
                {
                    authorizedScopes.push(GScope.Contacts);
                    saveAuthorizedScopes();

                    g.messageQueue.pushMessage({
                        text: 'Contact sync is enabled but permissions have been lost, ' + platform.verb() + ' here to find it again.',
                        type: MessageType.Error,
                        action: self.authAndLoadContacts
                    });

                    if (cb) { cb(false); }
                }
                else
                {
                    self.loadContacts(function (numContacts)
                    {
                        handleContactsLoaded(numContacts);

                        if (cb && numContacts > 0) { cb(true); }
                    });
                }
            });
        }
        else
        {
            self.loadContacts(function (numContacts)
            {
                handleContactsLoaded(numContacts);

                if (cb && numContacts > 0) { cb(true); }
            });
        }
    }
};

