self.checkTokenInHash = function()
{
    var setToken = false;

    if (util.hasURLParam('access_token'))
    {
        try
        {
            var scopeString = util.getURLParam('scope');

            var token =
            {
                access_token: util.getURLParam('access_token'),
                expires_in: util.getURLParam('expires_in'),
                scope: scopeString.split('+')
            };

            gapi.auth.setToken(token);

            setToken = true;

            util.removeURLParam('access_token', true);
            util.removeURLParam('expires_in', true);
            util.removeURLParam('scope', true);
            util.removeURLParam('token_type', true);
        }
        catch (err)
        {
            g.reportError(err);
        }
    }

    return setToken;
};

self.runAuthenticate = function (requestScopes, runImmediate, retry, callback)
{
    if (!window.gapi || !window.gapi.auth)
    {
        if (callback)
        {
            callback();
        }

        return false;
    }

    if (!self.gapiLoaded)
    {
        //g.events.emit('gapi_loaded');
        g.fireCustomEvent('gapiLoaded');

        self.gapiLoaded = true;
    }

    var currentToken = undefined;

    try
    {
        currentToken = gapi.auth.getToken();
    }
    catch (err)
    {
        g.reportError(err);
    }

    if (DEBUG) log('Request Scopes: ', requestScopes);
    if (DEBUG) log('Current OAuth2 Token: ', currentToken);

    var requiredScopesToRequest;
    if (!currentToken)
    {
        requiredScopesToRequest = requestScopes;
    }
    else
    {
        requiredScopesToRequest = currentToken.scope;

        if (!g.isArray(requiredScopesToRequest))
        {
            g.reportError(new Error('Scopes are not an array: ' + requiredScopesToRequest));

            if (g.isString(requiredScopesToRequest))
            {
                try
                {
                    requiredScopesToRequest = currentToken.scope.split(' ');
                }
                catch (err)
                {
                    g.reportError(err);
                }
            }
        }

        var numScopesAdded = 0;
        for (var i = 0; i < requestScopes.length; ++i)
        {
            if (currentToken.scope.indexOf(requestScopes[i]) < 0)
            {
                requiredScopesToRequest.push(requestScopes[i]);
                numScopesAdded++;
            }
        }

        // If there are no new scopes being requested, skip sending an authorization request
        if (numScopesAdded === 0)
        {
            requiredScopesToRequest = [ ];
        }
    }

    if (DEBUG) log('Require Scopes To Request: ', requiredScopesToRequest);

    if (requiredScopesToRequest.length > 0)
    {
        // Some scopes are a superset of others, prune scopes that duplicate permissions.
        if (requiredScopesToRequest.indexOf(GScope.DriveFull) >= 0)
        {
            requiredScopesToRequest.remove(GScope.Drive);
        }

        var userId = self.authenticatedUserEmail() || g.settings.get('activeUser');

        var config = {
            'client_id': self.CLIENT_ID,
            'scope': requiredScopesToRequest,
            'immediate': runImmediate,
            'user_id': userId,
            'authuser': runImmediate ? 0 : -1
        };

        if (platform.requireCustomRedirect && !runImmediate)
        {
            config['redirect_uri'] = platform.customRedirectString;

            if (platform.customRedirectState)
            {
                if (requiredScopesToRequest.indexOf(GScope.Contacts) >= 0)
                {
                    platform.customRedirectState.syncContacts = true;
                }

                config['state'] = JSON.stringify(platform.customRedirectState);
            }
        }

        if (DEBUG) log('AuthScopes: ', config.scope);

        refreshingAuth = true;

        if (DEBUG)
        {
            var tokenString = 'runAuthenticate: ' + (currentToken ? JSON.stringify(currentToken) : 'undefined');
            window.__AUTH_REQUESTS.push(g.getCurrentStack(tokenString));
        }

        gapi.auth.authorize(config, function (token)
        {
            refreshingAuth = false;

            if (DEBUG) log('Returned Scopes: ', token);

            if (DEBUG) log('GAPI Authorized: ', log.now());

            //log(Date.now(), ': GAPI Authorized', gapi.auth.getToken());

            if ((!token || token.error) && runImmediate && retry)
            {
                self.runAuthenticate(requiredScopesToRequest, /*runImmediate*/false, /*retry*/false, callback);
            }
            else
            {
                if (token && !token.error)
                {
                    if (platform.app || util.hasURLParam('login', 'true'))
                    {
                        if (g.vmSetup)
                        {
                            g.vmSetup.setLoginState(LoginState.LOGGED_IN);
                        }
                        else
                        {
                            g.setLoginState(LoginState.LOGGED_IN);
                        }

                        self.loadClientDefaultFile();
                    }
                }

                handleAuthorization(token, callback);
            }
        });
    }
    else
    {
        if (ShouldLog(LogLevels.Error)) log('Authorization request with no scopes: ', requiredScopesToRequest);

        if (callback)
        {
            callback(currentToken);
        }
    }

    return true;
};

// Handle an authorization error. This may mean that we log the user out as it was a critical failure,
// otherwise we should display a message complaining about the failure. If this function returns true,
