define('AutocompleteDates',['ko', 'data', 'globals', 'platform'],
function (ko, d, g, platform) {
function AutocompleteDates(p)
{
    this.soft = ['now', 'soon', 'later'];
    this.softDays = ['today', 'tomorrow'];
    this.dayModifiers = ['next'];
    this.days = ['monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday', 'sunday'];
    this.months = ['january', 'february', 'march', 'april', 'may', 'june', 'july', 'august', 'september', 'october', 'november', 'december'];

    this.dict = this.soft.concat(this.softDays).concat(this.dayModifiers).concat(this.days).concat(this.months);

    this.NextWordOptions = {
        None: 0,
        DayName: 1,
        Days: 2,
        Time: 4
    };

    this.init();
}

AutocompleteDates.prototype = {
    init: function()
    {
    },
    findDatesLike: function(like)
    {
        var lower = like.toLowerCase();
        var results = [];

        for (var i = 0; i < this.dict.length; ++i)
        {
            if (this.dict[i].indexOf(lower) === 0)
            {
                results.push({index: 0, object: {id: this.dict[i], text: this.dict[i]}});
            }
        }
        return results;
    },
    findExact: function(text)
    {
        if(this.dict.indexOf(text) >= 0)
        //     return {index: 0, id: text, text: text};
            return this.nextWordOptions(text) == this.NextWordOptions.None;
        return false;
    },
    nextWordOptions: function(text)
    {
        // if(text.lastIndexOf(' ') == text.length - 1)
        // {
        var words = text.split(' ');
        var lastWord = words[words.length - 1];
        if(lastWord == '')
            lastWord = words[words.length - 2];
        // log('last word', lastWord, words);

        if(this.soft.indexOf(lastWord) >= 0)
            return this.NextWordOptions.None;
        if(this.softDays.indexOf(lastWord) >= 0)
            return this.NextWordOptions.Time;
        if(this.dayModifiers.indexOf(lastWord) >= 0)
            return this.NextWordOptions.DayName;
        if(this.days.indexOf(lastWord) >= 0)
            return this.NextWordOptions.Time;
        if(this.months.indexOf(lastWord) >= 0)
            return this.NextWordOptions.Days;

        var isInt = parseInt(lastWord, 10);
        // log('is int', isInt);
        if(!isNaN(isInt))
            return this.NextWordOptions.Time;

        if(lastWord.indexOf(':') >= 0)
            return this.NextWordOptions.Time;
        // }
        return this.NextWordOptions.None;
    }
};

return AutocompleteDates;
});
