    shouldNotify: function(item, now)
    {
        if(!now)
            now = new Date();

        return !item.isComplete() && item.date() && item.date() > now && !item.allDayDate && !item.isArchived();
    },
    updateNotifications: function(item)
    {
        if ((platform.ios || platform.android) && platform.app && !g.isDemoMode() && g.vmSettings.enabledNotifications())
        {
            var num = 0;
            var now = new Date();
            if (item)
            {
                if(this.shouldNotify(item, now))
                {
                    this.itemsToSchedule[item.id] = item;
                    num = 1;
                }
            }
            else
            {
                var self = this;

                g.searchVMLIs({
                    item: g.vmMain.root(),
                    includeArchived: false,
                    comparator: function(item)
                    {
                        return self.shouldNotify(item, now);
                    },
                    action: function(item)
                    {
                        num ++;
                        self.itemsToSchedule[item.id] = item;
                    }
                });
            }

            if (this.scheduleTimeout)
            {
                clearTimeout(this.scheduleTimeout);
            }

            if (num > 0)
            {
                this.scheduleTimeout = setTimeout(this.sendNotifications.bind(this), 500);
            }
        }
    },
    clearNotifications: function()
    {
        platform.sendToApp('scheduledItems', '[]');
    },
    sendNotifications: function()
    {
        var json = '[';
        var first = true;

        for (var prop in this.itemsToSchedule)
        {
            if (this.itemsToSchedule.hasOwnProperty(prop))
            {
                var item = this.itemsToSchedule[prop];

                if (item.date())
                {
                    //log('schedule', item.date(), item.allDayDate);

                    if (!first)
                    {
                        json += ',';
                    }

                    json += item.getBasicJSON();
                    first = false;
                }
            }
        }

        json += ']';
        platform.sendToApp('scheduledItems', json);
        this.itemsToSchedule = {};
    },
    notifyPositionChanged: function(item)
    {
        for (var i = 0; i < g.vmMain.panes().length; ++i)
        {
            g.vmMain.panes()[i].notifyPositionChanged(item);
        }
    },
    notifyStateChanged: function(item, type)
    {
        if (item.date() && type == PropChangeType.Date)
        {
            this.dateUpdated(item);
        }
        else
        {
            for (var i = 0; i < g.vmMain.panes().length; ++i)
            {
                g.vmMain.panes()[i].notifyStateChanged(item, type);
            }
        }
    },
