define('VMAutocomplete',['ko', 'data', 'globals', 'platform', 'VMContact', 'VMAutocompleteItem', 'AutocompleteDates', 'tracker'],
function (ko, d, g, platform, VMContact, VMAutocompleteItem, AutocompleteDates, tracker) {
function VMAutocomplete()
{
    this.items = ko.observableArray();
    this.isVisible = ko.observable(false);
    this.element = undefined;
    this.trackingItem = undefined;
    this.trackingTag = undefined;
    this.selectedIndex = ko.observable(-1);
    this.exactMatch = ko.observable(false);
    this.text = ko.observable('');

    this.autocompleteDates = new AutocompleteDates();

    this.nextDateWord = ko.observable(this.autocompleteDates.NextWordOptions.None);

    this.lastX = 0;
    this.lastY = 0;

    this.Modes = { Contact: 0, Date: 1 };
    this.mode = ko.observable(-1);

    this.init();
}
VMAutocomplete.prototype =
{
    getDateText: function(index)
    {
        switch(index)
        {
            case 1:
                return index + 'st ';
            case 2:
                return index + 'nd ';
            case 3:
                return index + 'rd ';
            default:
                return index + 'th ';
        }
    },
    getMinWidth: function()
    {
        return (this.mode() === this.Modes.Contact) ? '350px' : '180px';
    },
    showAllDates: function()
    {
        return this.text() === '';
    },
    showDate: function()
    {
        return this.nextDateWord() == this.autocompleteDates.NextWordOptions.None ||
               this.nextDateWord() == this.autocompleteDates.NextWordOptions.DayName;
    },
    showDays: function()
    {
        return this.nextDateWord() == this.autocompleteDates.NextWordOptions.Days;
    },
    showTime: function()
    {
        return this.nextDateWord() == this.autocompleteDates.NextWordOptions.Time;
    },
    showDates: function()
    {
        return platform.mobile && platform.app && this.mode() == this.Modes.Date && (this.text() === '' || this.nextDateWord());
    },
    showList: function()
    {
        return !this.exactMatch() && !this.showDates();
    },
    showPreview: function()
    {
        return this.selectedItem() && this.mode() == this.Modes.Contact && (!platform.mobile || this.exactMatch);
    },
    hasContent: function()
    {
        return this.showDates() || this.showList() || this.showPreview();
    },
    templatePreview: function(self, b)
    {
        return 'template-contact';// self.items()[this.selectedIndex()].templateName;
    },
    selectedItem: function()
    {
        if(this.selectedIndex() < 0)
            return undefined;
        else
            return this.items()[this.selectedIndex()];
    },
    init: function()
    {
        this.onkeydown = this.onkeydown.bind(this);

        this.onMouseover = this.onMouseover.bind(this);

        this.element = document.getElementById('autocomplete');

        g.applyBindings(this, 'autocomplete');

        window.addEventListener('closeKeyboard', this.hide.bind(this));
    },
    setMode: function(prefix)
    {
        if(prefix == DatePrefix)
            this.mode(this.Modes.Date);
        if(prefix == ContactPrefix)
            this.mode(this.Modes.Contact);
    },
    show: function(item, completionElement)
    {
        if (!this.isVisible())
        {
            var sel;
            if (!completionElement)
            {
                sel = document.getSelection();
                if (sel && sel.baseNode)
                {
                    var isContact = g.findParent(sel.baseNode, 'contact', 2);

                    completionElement =  isContact || g.findParent(sel.baseNode, 'date', 2);

                    if (isContact)
                    {
                        tracker.miscAction({
                            type: TrackerType.Misc,
                            misc: TrackerMisc.OpenContact,
                            data: [
                            {
                                info: item
                            }]
                        });
                    }
                }
            }

            var windowBounds = document.documentElement.getBoundingClientRect();
            var rect = this.element.getBoundingClientRect();

            var autoRect = { left: rect.left, right: rect.right, top: rect.top, bottom: rect.bottom };

            if (rect.width === 0 && rect.height === 0)
            {
                autoRect.right = 350;
                autoRect.bottom = 170;
            }

            if (completionElement)
            {
                var targetOrigin = completionElement.getBoundingClientRect();

                g.setOrigin(autoRect, targetOrigin.left, targetOrigin.bottom);

                var origin = g.adjustOriginToBeInsideBounds(windowBounds, autoRect, 20, 10, 10, 10);

                // Grab the element offset from the completion element element
                this.element.style.left = origin.x + 'px';
                this.element.style.top = origin.y + 'px';
            }
            else if (sel.rangeCount > 0)
            {
                // Grab the selection offset from the cursor if adding to a new completion element
                var targetOrigin = sel.getRangeAt(0).getClientRects()[0];
                targetOrigin.left -= 20; // TODO: Magic number?

                g.setOrigin(autoRect, targetOrigin.left, targetOrigin.bottom);

                var origin = g.adjustOriginToBeInsideBounds(windowBounds, autoRect, 20, 10, 10, 10);

                this.element.style.left = origin.x + 'px';
                this.element.style.top = origin.y + 'px';
            }
            else
            {
                // Unable to properly setup the element
                return;
            }

            this.isVisible(true);

            this.element.scrollTop = 0;

            if (platform.mobile && item)
            {
                var time = 150;
                var amtMoved = g.scrollToTop(item.getSpan(), time);
                var bounds = completionElement ? completionElement.getBoundingClientRect() : item.getSpan().getBoundingClientRect();
                var bottomOfItem = bounds.bottom - amtMoved;
                this.element.style.top = (bottomOfItem - 2) + 'px';
                if(this.mode() == this.Modes.Contact)
                    g.phoneMenu.contactMode(true);
                else if(this.mode() == this.Modes.Date)
                    g.phoneMenu.dateMode(true);

                g.keyboardToolbar.isVisible(false);

                var trans = platform.windowHeight() - platform.kbsize - bottomOfItem;
                g.setTransform(this.element, 0, trans);
                requestAnimationFrame(function(){
                    g.setTransform(this.element, 0, 0, time, 'ease-out');
                }.bind(this));
            }
        }
    },
    clear: function()
    {
        this.items.removeAll();
        this.trackingTag = false;
    },
    hide: function()
    {
        if (this.isVisible())
        {
            var time = 200;
            if(platform.mobile)
            {
                var trans = platform.windowHeight() - platform.kbsize;
                g.setTransform(this.element, 0, trans, time, 'ease-out');
                setTimeout(function(){
                    this.isVisible(false);
                    g.phoneMenu.contactMode(false);
                    if(g.isKeyboardOpen)
                    {
                        g.keyboardToolbar.isVisible(true);
                    }
                    this.clear();
                }.bind(this), time);
            }
            else
            {
                this.isVisible(false);
            }
        }
    },
    onkeydown: function(e)
    {
        if (this.items().length === 0 || !this.isVisible() || (this.exactMatch() && e.normCode !== KeyCode.Escape))
        {
            return true;
        }

        if (e.normCode === KeyCode.UpArrow || e.normCode === KeyCode.DownArrow)
        {
            if (this.items().length > 1)
            {
                if (this.selectedIndex() >= 0)
                {
                    this.items()[this.selectedIndex()].isSelected(false);
                }

                var newIndex = this.selectedIndex() + ((e.normCode == KeyCode.DownArrow) ? 1 : -1);
                if (newIndex < 0)
                {
                    this.selectedIndex(this.items().length + newIndex);
                }
                else if (newIndex >= this.items().length)
                {
                    this.selectedIndex(newIndex % this.items().length);
                }
                else
                {
                    this.selectedIndex(newIndex);
                }

                this.items()[this.selectedIndex()].isSelected(true);

                return false;
            }
            else
            {
                this.hide();
            }
        }
        else if (e.normCode === KeyCode.Enter)
        {
            if (this.selectedIndex() >= 0)
            {
                var item = this.items()[this.selectedIndex()];

                this.onTapped(item.text, true);
            }

            this.hide();

            return false;
        }
        else if (e.normCode === KeyCode.Escape)
        {
            this.hide();

            return false;
        }

        return true;
    },
    onTap:
    {
        onClick: function(e)
        {
            var item = ko.dataFor(e.srcElement);

            this.onTapped(item.text);
        }
    },
    onTapped: function(text, hide)
    {
        this.text(text);

        if (this.trackingItem)
        {
            this.trackingItem.onAutocompleteTapped(text, this.trackingTag);
        }

        if (hide || this.mode() === this.Modes.Contact || !platform.mobile)
        {
            this.hide();
        }
        // else if (!platform.mobile)
        // {
        //     var selection = g.getSelectionInfo();

        //     var offset = g.getSelectOffsetsInSpan(this.trackingItem, this.trackingItem.getSpan(), selection.selection.getRangeAt(0));

        //     this.update({ item: this.trackingItem, start: offset.start, fromArrows: false });
        // }
    },
    find: function(words, start, num)
    {
        if (start + num > words.length - 1) // Out of range
        {
            return false;
        }

        var text = words[start];

        if (isNaN(num)) // Initialize num to something valid
        {
            num = 0;
        }

        for (var i = 0; i < num; i ++) // Add subsequent words
        {
            if (!words[start+i+1])
            {
                break;
            }

            text += ' ' + words[start+i+1];
        }

        var compStr = (text.indexOf(':', text.length - 1) !== -1) ? text.substr(0, text.length - 1) : text;

        var results;
        if(this.mode() == this.Modes.Contact)
            results = this.findContact(compStr);
        else if(this.mode() == this.Modes.Date)
            results = this.findDate(compStr);

        if (results.length > 0)
        {
            // Check for matches including the next word
            // If they don't match, this is the best result
            return this.find(words, start, num + 1) ||
                   {start: start, num: num + 1, text: compStr, results: results};
        }

        return false;
    },
    findContact: function(name)
    {
        return d.findContactsLike(name);
    },
    findDate: function(text)
    {
        return this.autocompleteDates.findDatesLike(text);
    },
    findExact: function(text)
    {
        if (this.mode() == this.Modes.Contact)
        {
            return this.findContactExact(text);
        }
        else if (this.mode() == this.Modes.Date)
        {
            return this.findDateExact(text);
        }
    },
    findContactExact: function(name)
    {
        return d.findContactsExact(name);
    },
    findDateExact: function(text)
    {
        return this.autocompleteDates.findExact(text);
    },
    objectFromResult: function(result)
    {
        if (this.mode() == this.Modes.Contact)
        {
            return new VMContact(result);
        }
        else if (this.mode() == this.Modes.Date)
        {
            return new VMContact(result);
        }
    },
    getMinWordLength: function()
    {
        if (this.mode() == this.Modes.Contact)
        {
            return 1;
        }
        else if (this.mode() == this.Modes.Date)
        {
            return 1;
        }
    },
    getLineIndexFromWordIndex: function(words, wordIndex)
    {
        var lineIndex = 0;
        for(var i = 0; i < wordIndex; i ++)
        {
            lineIndex += words[i].length + 1;
        }
        return lineIndex;
    },
    update: function(params)
    {
        var item = params.item || this.trackingItem;
        var text = item.getParsedText();
        var selectionStart = params.start;
        var fromArrows = params.fromArrows;

        this.trackingItem = item;

        this.trackingTag = false;

        var startOffset = 0,
            i,
            lineIndex = 0,
            startWord = -1,
            startChar = -1;

        var words = text.split(' ');

        if (startOffset > 0)
        {
            words[0] = words[0].substr(startOffset);
        }

        // Find the word that the cursor is in
        for (i = 0; i < words.length; i ++)
        {
            // If this word is a tag and it's past selection start
            if ((words[i].startsWith(ContactPrefix) || words[i].startsWith(DatePrefix)) && lineIndex <= selectionStart)
            {
                startWord = i;
                startChar = lineIndex;
                this.setMode(words[i][0]);
            }

            lineIndex += words[i].length + 1;
        }

        var minWordLength = this.getMinWordLength();

        // Make sure selected word is longer than the minimum length for this type
        if (startWord >= 0)
        {
            this.clear();

            // Crop the tag character off the start word before searching
            words[startWord] = words[startWord].substr(1);

            var result = this.find(words, startWord);

            var addAmount = 1;

            // When pressing space (if typing more than just the first name of a contact) ensure that the
            // autocomplete dialog is not hidden.
            if (words[startWord + 1] === '')
            {
                addAmount++;
            }

            // If selection is within the range of the result word
            if(result && startChar + addAmount + result.text.length >= selectionStart)
            {
                var exactMatch = this.findExact(result.text);
                this.exactMatch(!!exactMatch);

                if(exactMatch)
                {
                    var options = { index: -1, text: exactMatch.text };
                    options.object = this.objectFromResult(exactMatch);
                    item = new VMAutocompleteItem(options);
                    this.items.push(item);
                    this.selectedIndex(0);
                    this.trackingTag = true;

                    if(!fromArrows && !platform.mobile)
                        this.show(this.trackingItem);
                }
                else
                {
                    // Valid matches either have multiple words, word length >= 3, or are an exact match
                    if(result && (result.num > 0))
                    {
                        this.trackingTag = {text: result.text,
                                           index: this.getLineIndexFromWordIndex(words, result.start) + 1,
                                           length: result.text.length};


                        // This is for autocomplete items to highlight the searched text
                        this.text(result.text);

                        var results = result.results;

                        var u, item, id;
                        for(i = 0; i < results.length; i ++)
                        {
                            var object = results[i].object;

                            var options = { index: i, text: object.text };
                            options.object = this.objectFromResult(object);
                            item = new VMAutocompleteItem(options);
                            this.items().push(item);
                        }

                        this.items.valueHasMutated();

                        this.selectedIndex(0);
                        this.items()[0].isSelected(true);
                    }
                    if(!fromArrows)
                        this.show(this.trackingItem);
                }
            }
        }

        this.nextDateWord(this.autocompleteDates.NextWordOptions.None);

        if(!this.trackingTag) // No results so remove all items
            this.hide();
    },
    onMouseover: function(item)
    {
        if (this.selectedIndex() >= 0)
        {
            this.items()[this.selectedIndex()].isSelected(false);
        }
        var index = this.items.indexOf(item);
        if(index >= 0)
        {
            this.selectedIndex(index);
            item.isSelected(true);
        }
    },
    onTapDate: {
        onStart: g.handlerPreventStop,
        onClick: function(e)
        {
            var el = e.srcElement;
            if(el.nodeName == 'SPAN')
            {
                var parentDiv = el.parentNode;
                if(g.hasClass(parentDiv, 'disabled'))
                    return;

                var text = el.id ? (el.id + ' ') : el.textContent;
                var next = this.autocompleteDates.nextWordOptions(text);
                this.nextDateWord(next);
                log('next', next, this.trackingTag);
                var hide = next == this.autocompleteDates.NextWordOptions.None;
                this.onTapped(text, hide);
                this.trackingTag.index += text.length;
            }
        }
    }
};

return VMAutocomplete;
});
