'use strict';
define('VMLI_Text',['ko', 'data', 'gdata', 'globals', 'tracker', 'android', 'DuchessHelpers'],
function (ko, d, gdata, g, tracker, android) {
var VMLI_Text = {
    _runAndGetSaveData : function (text, immediateParse)
    {
        //log('Set Text: \'' + text + '\'' + '  P: ' + immediateParse);

        var oldMetaData = this.getMetaTextSaveData();

        this.setRawText(text);

        this.parseText(immediateParse);

        var newMetaData = this.getMetaTextSaveData();

        // TODO: What if immediateParse is false - some of this info won't be available to save.


        var saveData = { };
        for (var key in newMetaData)
        {
            if (oldMetaData[key] !== newMetaData[key])
            {
                saveData[key] = newMetaData[key];
            }
        }

        return saveData;
    },
    setText: function (text, immediateParse, fromAndroidEditing)
    {
        var saveData = this._runAndGetSaveData(text, immediateParse);

        saveData['text'] = text;
        gdata.saveChanges(this, saveData);

        saveData['dateText'] = this.dateText;
        this.save(saveData, SaveFlag.Text); // Modified text

        if (immediateParse && !fromAndroidEditing && android.isEnabled())
        {
            android.updateInputText(this);
        }
    },
    // insertText: function (index, addedText, fullText)
    // {
    //     var saveData = this._runAndGetSaveData(fullText, /*immediateParse*/true);

    //     if (g.isEmpty(saveData))
    //     {
    //         gdata.insertText(this, index, addedText);

    //         saveData['text'] = fullText;
    //     }
    //     else
    //     {
    //         saveData['text'] = fullText;

    //         gdata.saveChanges(this, saveData, /*isText*/true);
    //     }

    //     saveData['dateText'] = this.dateText;
    //     this.save(saveData, 'text'); // Modified text
    // },
    parseText: function(immediateParse)
    {
        var styleInfo = undefined;
        if (immediateParse)
        {
            styleInfo = DuchessHelpers.parseText(
                this.getRawText(),
                this.getDateTime(),
                this.isLoaded,
                //this.formats,
                d.getContacts()
                );

            if(styleInfo.numContacts > 0)
            {
                tracker.miscAction({
                    type: TrackerType.Misc,
                    misc: TrackerMisc.AddContact,
                    data: [
                    {
                        itemID: this.id
                    }]
                });
            }
        }
        else
        {
            // styleInfo = {
            //     'lineStyle': DuchessHelpers.parseTextPrefixes(this.getRawText())
            // };
        }

        this.styleText(styleInfo, /*parsedFull*/immediateParse);
    },
    styleText: function(styleInfo, parsedFull, changedStyles)
    {
        try{g.PAssert(1075,!(!styleInfo && parsedFull), 'In a full parse, styleInfo should always be defined');}catch(PERR){g.reportError(PERR);}

        var styleChanges = false;

        var text;

        if (styleInfo)
        {
            if (styleInfo.styledText !== undefined)
            {
                text = styleInfo.styledText;
            }
            else if (styleInfo.text !== undefined)
            {
                text = styleInfo.text;
            }
            else
            {
                text = this.getRawText();
            }

            // After a full parse for this item, set the parsed text (may be undefined)
            if (parsedFull)
            {
                this.setParsedText(styleInfo.parsedText);
            }

            if (styleInfo.dates)
            {
                // TODO: Support multiple dates?
                if (styleInfo.dates.length === 0 && this.date())
                {
                    if (this.date())
                    {
                        this.allDayDate = undefined;
                        this.dateText = undefined;
                        this.date(null);

                        if (changedStyles)
                        {
                            styleChanges = true;
                            changedStyles.date = undefined;
                            changedStyles.dateText = undefined;
                            changedStyles.allDayDate = undefined;
                        }
                    }
                }

                for (var i = 0; i < styleInfo.dates.length; i ++)
                {
                    var dt = styleInfo.dates[i];

                    if (this.dateText !== dt.text)
                    {
                        this.dateText = dt.text;

                        if (changedStyles)
                        {
                            styleChanges = true;
                            changedStyles.dateText = dt.text;
                        }
                    }

                    // If there is a change in whether this item has a date or not or the date has changed
                    if (!dt.date || !this.date() || dt.date != this.date())
                    {
                        if (!!dt.date ^ !!this.date() || (this.date() && dt.date.getTime() !== this.date().getTime()))
                        {
                            if(dt.explicitTime)
                            {
                                dt.date._explicitTime = dt.explicitTime;
                            }

                            this.allDayDate = !dt.date._explicitTime;

                            this.date(dt.date);

                            if (changedStyles)
                            {
                                styleChanges = true;
                                changedStyles.date = dt.date;
                                changedStyles.allDayDate = dt.date._explicitTime;
                            }
                        }
                    }
                }
            }
            else if (this.date() && parsedFull)
            {
                // If there is no date string associated with the item, ensure that it is removed.
                this.dateText = undefined;
                this.allDayDate = undefined;
                this.date(null);

                if (changedStyles)
                {
                    styleChanges = true;
                    changedStyles.date = undefined;
                    changedStyles.dateText = undefined;
                    changedStyles.allDayDate = undefined;
                }
            }

            this.isStyled = styleInfo.hasStyle;
        }
        else
        {
            text = this.getRawText();
        }

        if (text.length === 0)
        {
            text = TextSpace;
            parsedFull = true;
        }

        //if (parsedFull)
        {
            this.setStyledText(text);
        }

        return styleChanges;
    },
    checkFormats: function(i, format2, preventDisable)
    {
        try{g.PAssert(1076,false, 'No formats');}catch(PERR){g.reportError(PERR);}
        return false;
        var format1 = this.formats[i];
        // Equal to existing format so remove it
        if(format2.start == format1.start && format2.end == format1.end)
        {
            if(!preventDisable)
            {
                this.formats.splice(i, 1);
                return {neg: true, add: false};
            }
        }
        // Format inside existing format
        else if(format2.start > format1.start && format2.end < format1.end)
        {
            var oldEnd = format1.end;
            format1.end = format2.start;
            format2.start = format2.end;
            format2.end = oldEnd;
        }
        // Format encapsulates existing format
        else if(format2.start < format1.start && format2.end > format1.end)
        {
            this.formats.splice(i, 1);
            return {neg: true};
        }
        // Format more before the start of formatted text
        else if(format2.start < format1.start && format2.end >= format1.start)
        {
            format1.start = format2.start;
            return {add: false};
        }
        // Format more after the end of formatted text
        else if(format2.start <= format1.end && format2.end > format1.end)
        {
            format1.end = format2.end;
            return {add: false};
        }
        // Unformat the end of formatted text
        else if(format2.start > format1.start && format2.end == format1.end)
        {
            if(!preventDisable)
            {
                format1.end = format2.start;
                return {add: false};
            }
        }
        // Unformat the start of formatted text
        else if(format2.start == format1.start && format2.end < format1.end)
        {
            if(!preventDisable)
            {
                this.formats[i].start = format2.end;
                return {add: false};
            }
        }
    },
    toggleFormat: function(format, preventDisable)
    {
        try{g.PAssert(1077,false, 'No formats');}catch(PERR){g.reportError(PERR);}
        return false;
        var add = true;
        for(var i = 0; i < this.formats.length; i ++)
        {
            if(this.formats[i].tag == format.tag)
            {
                var ret = this.checkFormats(i, format, preventDisable);
                if(ret && ret.add === false)
                {
                    add = false;
                }
                if(ret && ret.neg)
                {
                    i --;
                }
                if(i > 0 && this.formats[i - 1].tag == this.formats[i].tag)
                {
                    this.checkFormats(i - 1, this.formats[i], preventDisable);
                }
            }
        }
        if(add)
        {
            this.formats.push(format);
        }
        this.formats.sort(function (left, right)
        {
            return left.start - right.start;
        });
        this.saveFormats();
    },
    clearFormats: function()
    {
        try{g.PAssert(1078,false, 'No formats');}catch(PERR){g.reportError(PERR);}
        return false;
        if (this.formats.length !== 0)
        {
            this.formats.splice(0, this.formats.length);
            this.saveFormats();
        }
    },
    areFormatsEqual: function(formatsA, formatsB)
    {
        try{g.PAssert(1079,false, 'No formats');}catch(PERR){g.reportError(PERR);}
        return true;
        // If only one formats is undefined they are different
        if (!!formatsA ^ !!formatsB)
        {
            return false;
        }

        if (formatsA.length !== formatsB.length)
        {
            return false;
        }

        for (var i = 0; i < formatsA.length; ++i)
        {
            var entryA = formatsA[i];
            var entryB = formatsB[i];

            if (entryA.start !== entryB.start)
            {
                return false;
            }

            if (entryA.end !== entryB.end)
            {
                return false;
            }

            if (entryA.tag !== entryB.tag)
            {
                return false;
            }
        }

        return true;
    },
    setFormats: function(format, isFromRemote)
    {
        try{g.PAssert(1080,false, 'No formats');}catch(PERR){g.reportError(PERR);}
        return false;

        if (!this.areFormatsEqual(this.formats, format))
        {
            this.formats = format;

            this.parseText(/*immediateParse*/true);

            this.saveFormats(isFromRemote);
        }
    },
    appendFormats: function(newFormat, transform)
    {
        try{g.PAssert(1081,false, 'No formats');}catch(PERR){g.reportError(PERR);}
        return false;
        transform = !isNaN(transform) ? transform : 0;
        for(var i = 0; i < newFormat.length; i ++)
        {
            var fmt = newFormat[i];
            if(transform)
            {
                this.formats.push({start: fmt.start + transform, end: fmt.end + transform, tag: fmt.tag});
            }
            else
            {
                this.formats.push(fmt);
            }
        }
        this.formats.sort(function (left, right)
        {
            return left.start - right.start;
        });
        this.saveFormats();
    },
    getFormatsAfter: function(index)
    {
        try{g.PAssert(1082,false, 'No formats');}catch(PERR){g.reportError(PERR);}
        return false;
        var ret = [];
        for(var i = 0; i < this.formats.length; i ++)
        {
            var fmt = this.formats[i];
            if(fmt.start >= index)
            {
                ret.push({start: fmt.start - index, end: fmt.end - index, tag: fmt.tag});
            }
            else if(fmt.end >= index)
            {
                ret.push({start: 0, end: fmt.end - index, tag: fmt.tag});
            }
        }
        return ret;
    },
    removeFormatsAfter: function(index)
    {
        try{g.PAssert(1083,false, 'No formats');}catch(PERR){g.reportError(PERR);}
        return false;
        for(var i = 0; i < this.formats.length; i ++)
        {
            var fmt = this.formats[i];
            if(fmt.start > index)
            {
                this.formats.splice(i, 1);
                i --;
            }
            else if(fmt.end > index)
            {
                fmt.end = index;
            }
        }
        this.saveFormats();
    },
    transformFormats: function(index, transform)
    {
        try{g.PAssert(1084,false, 'No formats');}catch(PERR){g.reportError(PERR);}
        return false;
        if (this.formats.length === 0)
        {
            return;
        }

        var wasModified = false;

        for (var i = 0; i < this.formats.length; ++i)
        {
            var fmt = this.formats[i];

            if(fmt.start >= index)
            {
                fmt.start = Math.max(fmt.start + transform, index);
                wasModified = true;
            }

            if(fmt.end >= index)
            {
                // If the transform is larger than the space after the index, constrain it
                fmt.end += Math.max(transform, index - fmt.end);
                wasModified = true;
            }

            if(fmt.end <= 0 || fmt.start == fmt.end)
            {
                this.formats.splice(i, 1);
                i --;
                wasModified = true;
            }

            else
            {
                try{g.PAssert(1085,fmt.start >= 0);}catch(PERR){g.reportError(PERR);}
            }
        }

        if (wasModified)
        {
            this.saveFormats();
        }
    },
    saveFormats: function(isFromRemote)
    {
        try{g.PAssert(1086,false, 'No formats');}catch(PERR){g.reportError(PERR);}
        return false;
        try{g.PAssert(1087,false, 'PERF: Should not be modifying/saving formats as they are disabled');}catch(PERR){g.reportError(PERR);}

        if (!isFromRemote && this.data)
        {
            gdata.set(this.data, 'formats', this.formats);
        }
        this.save({formats: this.formats}, SaveFlag.Text);
    }
};
return VMLI_Text;
});

