define('VMOutline',['ko', 'globals', 'data', 'gdata', 'VMSearch', 'platform', 'tracker'],
function (ko, g, d, gdata, VMSearch, platform, tracker) {
function VMOutline(pane)
{
    this.pane = pane;
    this.root = g.vmMain.root();
    if(!platform.mobile)
    {
        this.isVisible = ko.observable(false);
    }
    this.init();
}

VMOutline.prototype = {
    init: function()
    {
        g.outline = this;

        if (platform.mobile)
        {
            g.ScreenManager.addScreen(this, Screens.Outline);
        }
    },
    getTopElement: function()
    {
        return document.getElementsByClassName('paneOutline')[0];
    },
    onTap:
    {
        onStart: function(e)
        {
            g.ScreenManager.checkSwipeStart(e);
        },
        onMove: function(e)
        {
            g.ScreenManager.checkSwipeMove(e);
        },
        onClick: function(e)
        {
            if (!platform.mobile || !g.ScreenManager.checkSwipeClick(e))
            {
                if (g.hasClass(e.srcElement, 'outlineText') || g.hasClass(e.srcElement.parentElement, 'outlineText'))
                {
                    var item = ko.dataFor(e.srcElement);

                    if(!platform.mobile)
                    {
                        g.vmMain.setFocusedPane(this.pane);
                    }

                    g.vmMain.zoomin(item);

                    if (platform.mobile)
                    {
                        g.ScreenManager.scrollToScreen(Screens.List);
                    }
                    else
                    {
                        g.focusedPane.toggleOutlineVisibility();
                    }

                    e.preventDefault();

                    g.sendEvent('Menu', 'OutlineZoom');
                }
            }
        },
        onEnd: function(e)
        {
            g.ScreenManager.checkSwipeEnd(e);
        }
    },
    onTapCollapse:
    {
        onStart: function(e)
        {
            if (g.hasClass(e.srcElement, 'outlineArrow'))
            {
                var li = e.srcElement.parentNode;

                if (g.hasClass(li, 'collapsed'))
                {
                    g.removeClass(li, 'collapsed');

                    g.sendEvent('Menu', 'OutlineMinimize');
                }
                else
                {
                    g.addClass(li, 'collapsed');

                    g.sendEvent('Menu', 'OutlineMaximize');
                }

                e.handled = true;
                e.preventDefault();
            }
        }
    },
    onScreenStartOpen: function()
    {
    },
    onScreenOpen: function()
    {
        if (g.isKeyboardOpen && !g.vmSearchPhone.isFocused())
        {
            g.blurActiveElement();
        }

        // g.vmMain.menuState(MenuState.Context);

        // Show context
        // g.vmMain.setTransform(g.contextX(), 180);

        // Animate context in
        //paneOutline.style.visibility = 'visible';

        // Workaround chrome on android rendering bug where the scrollbar stays visible on the pane
        // after the context pane is opened and hidden again. After hiding the pane, the overflowY
        // property is set to 'hidden' to prevent the scrollbar from staying visible on screen.
        // if (platform.android)
        // {
        //     var paneOutline = document.getElementsByClassName('paneOutline')[0];

        //     paneOutline.style.overflowY = 'scroll';
        //     //paneList.style.overflowY = 'hidden';
        // }

        tracker.miscAction({
            type: TrackerType.Misc,
            misc: TrackerMisc.ToggleOutline,
            data:
            {
                element: this.getTopElement()
            }
        });
    },
    onScreenUpdate: function(diff)
    {

    },
    onScreenClose: function()
    {
        // g.vmMain.menuState(MenuState.Main);
        // Hide context
        // g.vmMain.setTransform(0, 180);
        // Animate context out
        // g.setTransform(paneOutline, (-g.contextX() / 2), 0, 200, 'in');
        // setTimeout(function()
        // {
            //paneOutline.style.visibility = 'hidden';

        // if (platform.android)
        // {
        //     var paneOutline = document.getElementsByClassName('paneOutline')[0];

        //     paneOutline.style.overflowY = 'hidden';
        //     //paneList.style.overflowY = 'scroll';
        // }

        tracker.miscAction({
            type: TrackerType.Misc,
            misc: TrackerMisc.ToggleOutline,
            data:
            {
                element: this.getTopElement()
            }
        });

        // }, 200);
    },
    toggleDesktop: function()
    {
        var paneWidth = g.width(this.pane.element);
        var time = 150;

        if (this.isVisible())
        {
            g.setTransform(this.pane.outlineElement, -paneWidth * 1, 0, time, 'ease-out');
            g.setTransform(this.pane.paneContent, 0, 0, time, 'ease-out');
            setTimeout(function()
            {
                this.isVisible(!this.isVisible());
                this.pane.outlineElement.style.visibility = 'hidden';
            }.bind(this), time);
        }
        else
        {
            this.isVisible(!this.isVisible());
            this.pane.outlineElement.style.visibility = 'visible';
            g.setTransform(this.pane.outlineElement, -paneWidth * 1, 0);
            requestAnimationFrame((function(){
                g.setTransform(this.pane.outlineElement, 0, 0, time, 'ease-in-out');
                g.setTransform(this.pane.paneContent, paneWidth, 0, time, 'ease-in-out');
            }).bind(this));
        }

        tracker.miscAction({
            type: TrackerType.Misc,
            misc: TrackerMisc.ToggleOutline,
            data:
            {
                element: this.getTopElement()
            }
        });
    }
    // toggle: function()
    // {
    //     if(g.vmMain.isOutlineOut())
    //     {
    //         this.close();
    //     }
    //     else
    //     {
    //         this.open();
    //     }
    // },
};

g.VMOutline = VMOutline;
return VMOutline;
});

