define('VMSetup',['ko', 'data', 'globals', 'util', 'platform', 'goog', 'AScript'],
function (ko, d, g, util, platform, goog, AScript) {

function VMSetup ()
{
    this.loginState = ko.observable(LoginState.NONE);

    this.loginEnabled = ko.observable(true);
    this.loginStatus = ko.observable('');

    if (!goog.gapiLoaded && !DEBUG)
    {
        this.loginDisableTimeout = setTimeout(function()
        {
            this.loginEnabled(false);
            this.loginStatus('Must be online to login');
        }.bind(this), 1000);

        // g.events.on('gapi_loaded', (function()
        // {
        //     clearTimeout(this.loginDisableTimeout);
        //     this.loginEnabled(true);
        //     this.loginStatus('');
        // }).bind(this));

        window.addEventListener('gapiLoaded', function()
        {
            clearTimeout(this.loginDisableTimeout);
            this.loginEnabled(true);
            this.loginStatus('');
        }.bind(this), true);
    }

    //this.areAccountsSetup = ko.observable(true);
    //this.loggedInUser = ko.observable('');
    //this.passwordStatusText = ko.observable('');
}

VMSetup.prototype = {
    init: function()
    {
        if (!g.isDemoMode())
        {
            var loginState = g.getLoginState();

            this.setLoginState(loginState);

            g.applyBindings(this, 'setup');


            window.addEventListener('offline', function(evt)
            {
                log('Browser going offline.');

                g.vmMain.setGDriveStatus(DriveStatus.Offline);
            });

            window.addEventListener('online', function(evt)
            {
                log('Browser going online.');

                if (g.getLoginState() == LoginState.LOGGED_IN && !platform.offline)
                {
                    g.vmMain.setGDriveStatus(DriveStatus.Connecting);

                    var alreadyOnline = goog.goOnline();
                    log('Coming online. Already online:', alreadyOnline);
                }
            });

            if (DEBUG)
            {
                this.setLoginState(LoginState.LOGGED_IN);

                return LoginState.LOGGED_IN;
            }

            /*if (loginState == LoginState.LOGGED_OUT)
            {
                var forceLogin = util.getURLParam('login');

                if (forceLogin == 'true')
                {
                    this.userLogin();
                }
            }*/

            return loginState;
        }
        else
        {
            // if (!platform.mobile)
            // {
            //     ko.applyBindings(this, document.getElementById('authArea'));
            // }

            return LoginState.LOGGED_OUT;
        }
    },
    // Helper functions to reflect state information in the UI
    // _statusText: function()
    // {
    //     if (this.loginState() == LoginState.USER_LOGIN)
    //     {
    //         return 'Welcome back';
    //     }
    //     else if (this.loginState() == LoginState.USER_CREATE)
    //     {
    //         return 'Sign up';
    //     }
    //     else
    //     {
    //         return '';
    //     }
    // },
    // _statusTextColor: function ()
    // {
    //     if (this.loginState() == LoginState.USER_LOGIN)
    //     {
    //         return 'rgb(45, 230, 45)';
    //     }
    //     else
    //     {
    //         return '#ddd';
    //     }
    // },
    // _buttonText: function()
    // {
    //     if (this.loginState() == LoginState.USER_CREATE)
    //     {
    //         return 'Sign up';
    //     }
    //     else if(this.loginState() == LoginState.USER_LOGIN)
    //     {
    //         return 'Log in';
    //     }
    //     else
    //     {
    //         return 'Sign up / Log in';
    //     }
    // },
    logoutButtonText: function()
    {
        if (g.isDemoMode())
        {
            return 'Leave Introduction';
        }
        /*else if (this.loginState() == LoginState.OFFLINE)
        {
            return 'Login';
        }*/
        else
        {
            return 'Logout';
        }
    },
    setLoginState: function(state)
    {
        if (state != this.loginState())
        {
            g.setLoginState(state);

            this.loginState(state);
        }
    },
    //
    // User Authentication
    //
    userLogin: function ()
    {
        goog.runAuthenticate(goog.requiredScopes, /*runImmediate*/false, /*retry*/true, function (token)
        {
            if (token && !token.error)
            {
                // When loading for the first time, mark the last remote load as successful
                g.settings.set(Settings.localIntegrity, true);

                this.setLoginState(LoginState.LOGGED_IN);

                gapi.load('drive-realtime', function()
                {
                    goog.loadClientDefaultFile();
                });
            }
            else
            {
                log('Invalid token returned when calling userLogin');
            }
        }.bind(this));
    },
    openLogin: function ()
    {
        // g.removeClass(document.getElementById('setupContents'), 'trans');
        // setupContents.css({webkitTransform: 'translate3d(0px,0,0)'});

        this.loginState(LoginState.NONE);

        g.StopSpinner();
    },
    logout: function()
    {
        g.settings.reset();

        this.setLoginState(LoginState.LOGGED_OUT);

        d.clearData(function ()
        {
            if (util.hasURLParam('login'))
            {
                util.removeURLParam('login');
            }
            else if (!platform.app)
            {
                g.reload();
            }
            else if (platform.app)
            {
                platform.sendToApp('logout');
            }
        });
    },
    logoutClicked: function ()
    {
        if (g.isDemoMode())
        {
            // Leave demo
            util.removeURLParam('demo');
        }
        else
        {
            /*if (this.loginState() == LoginState.OFFLINE)
            {
                goog.goOnline();

                this.userLogin();
                g.vmMain.hideMenu();
            }
            else*/
            {
                var ok = true;
                for (var itemID in d.pendingRemoteUpdates)
                {
                    if (d.pendingRemoteUpdates.hasOwnProperty(itemID))
                    {
                        if (!platform.packagedApp)
                        {
                            ok = confirm('You have unsaved changes which will be lost if you log out. Are you sure?');
                        }

                        break;
                    }
                }

                if (ok)
                {
                    this.logout();
                }
            }
        }
    },
    isLocal: function()
    {
        return window.location.hostname == 'localhost';
    },
    onlineStatusToggle: function ()
    {
        platform.offline ? util.removeURLParam('offline') : util.insertURLParam('offline', 'true');
    },
    // startIntroduction: function ()
    // {
    //     // g.intro = new intro(function ()
    //     // {
    //     //     g.intro = undefined;
    //     // });

    //     // g.intro.run();
    // },
    runScript: function(id)
    {
        if (!g.AScript)
        {
            g.AScript = new AScript();
        }

        g.AScript.runScript(id, /*repeat*/true);
    },
    onTapLogin: function()
    {
        if (g.vmMain.isOnline())
        {
            this.userLogin();
        }
    },
    // onTapOffline: {
    //     onStart: function(e) {
    //         this.setLoginState(LoginState.OFFLINE);
    //     }
    // },
    onTapInput: {
        onClick: function(e) {
            if(platform.ios)
            {
                e.srcElement.focus();
                g.enforceScroll();
            }
        }
    },
    onTap: {
        onMove: function(e) {
            if(e.diffY < 0)
            {
                e.preventDefault();
            }
        }
    },
    // onTapDemo: function()
    // {
    //     util.insertURLParam('demo', 'true');
    // },
    onTapLanding: function()
    {
        window.location.pathname = '/about/';
    },
    onPrivacyPolicy: {
        onClick: function(e) {
            open('/privacy');
        }
    }
};

return VMSetup;
});

