define('VMTimelineBlock',['ko', 'globals', 'data', 'VMLI'],
function (ko, g, d, VMLI) {
function VMTimelineBlock(p)
{
    this.owner = p.owner;
    this.date = p.date;
    this.dateKey = p.dateKey;
    this.text = p.text;
    this.name = p.name;
    this.searchProvider = undefined;
    this.items = ko.observableArray();
    this.searchCount = ko.observable(0);
    this.hasDeferredUpdate = false;
    this.element = undefined;
    this.timeCount = ko.observable(0);
    this.isFlagged = p.isFlagged;
    this.isImportant = p.isImportant;
    this.isToday = false;

    this.init(p);
}

VMTimelineBlock.prototype = {
    showBlock: function ()
    {
        return ((this.items().length > 0) && this.searchCount() > 0);
    },
    specialClass: function ()
    {
        if(this.isFlagged)
            return 'flaggedBlock';
        if(this.isImportant)
            return 'importantBlock';
        if(this.isToday || this.isNow)
            return 'todayBlock';
    },
    showTime: function(ctx)
    {
        var item = ctx.$data;
        var index = ctx.$index;
        if(this.isNow)
            return false;

        // Force evaluation of showTime every time search is updated
        var tempValue = this.searchProvider.onUpdated();

        // Don't show time if no items in this block have a time
        if(this.timeCount() === 0 || this.isFlagged || this.isImportant || !this.searchProvider.isVisible(item))
            return false;

        var previousVisible = index() - 1;
        while(previousVisible > 0 && !this.searchProvider.isVisible(this.items()[previousVisible]))
        {
            previousVisible --;
        }
        // Show time if this is the first item
        if(previousVisible < 0)
            return true;

        var prevItem = this.items()[previousVisible];

        if(item.hasTime())
        {
            // Don't show time if same time as same time as previous
            if(this.compareDate(prevItem, item) === 0)
            {
                return false;
            }
        }
        else
        {
            // Don't show time if this and previous have no time
            if(!prevItem.hasTime())
                return false;
        }

        return true;
    },
    processDeferredUpdates: function()
    {
        if (this.hasDeferredUpdate)
        {
            this.hasDeferredUpdate = false;
            //this.items.valueHasMutated();
        }
    },
    init: function(p)
    {
        try{g.PAssert(1307,this.date);}catch(PERR){g.reportError(PERR);}

        if (!this.dateKey)
        {
            this.dateKey = this.date.clone().clearTime().getTime();

            if (p.name)
            {
                this.dateKey = this.dateKey + p.name;
            }
        }

        if (this.name)
        {
            this.text = this.name.toUpperCase();
            if(this.name == 'NOW')
                this.isNow = true;
        }
        else
        {
            this.text = '';
            if (this.date)
            {
                var timeDiff = Math.floor((this.date - Date.today()) / 86400000);

                switch (timeDiff)
                {
                    case -1:
                        this.text = 'YESTERDAY ';
                        break;
                    case 0:
                        this.text = 'TODAY ';
                        this.isToday = true;
                        break;
                    case 1:
                        this.text = 'TOMORROW ';
                        break;
                    default:
                        this.text = this.date.toString('dddd').toUpperCase();
                        break;
                }
            }

            this.text = this.text + ' ' + this.date.toString('M/d');
        }
    },
    comparePriority: function(left, right)
    {
        var lPri = left.priority();
        var rPri = right.priority();

        if (lPri > rPri)
        {
            return -1;
        }
        else if (lPri < rPri)
        {
            return 1;
        }

        // if (left.getRawText().startsWith(ImportantPrefix))
        // {
        //     if (right.getRawText().startsWith(ImportantPrefix))
        //     {
        //         return 0;
        //     }

        //     return -1;
        // }
        // else if (right.getRawText().startsWith(ImportantPrefix))
        // {
        //     return 1;
        // }

        return 0;
    },
    compareDate: function(left, right)
    {
        if (left.isComplete() && !right.isComplete())
        {
            return -1;
        }
        else if (!left.isComplete() && right.isComplete())
        {
            return 1;
        }

        if (left.isComplete() && right.isComplete())
        {
            return (left.dateCompleted > right.dateCompleted) ? 1 : -1;
        }

        // log(left._rawText, right._rawText, left.date(), right.date());
        if(left.date() && !right.date())
        {
            return -1;
        }
        else if(!left.date() && right.date())
        {
            return 1;
        }

        var leftDate = new Date(left.date());
        var leftTime = leftDate.toString('h:mmtt');

        var rightDate = new Date(right.date());
        var rightTime = rightDate.toString('h:mmtt');


        if (leftTime != '12:00am' && rightTime != '12:00am')
        {
            if(isNaN(leftDate.getYear()) || isNaN(rightDate.getYear()))
            {
                return 0;
            }

            return Date.compare(leftDate, rightDate);
        }
        else if (leftTime != '12:00am')
        {
            return -1;
        }
        else if (rightTime != '12:00am')
        {
            return 1;
        }

        return 0;
    },
    compareOrder: function(left, right)
    {
        // TODO: Should use a more specific version of getFirstItem that returns -1, 0, 1
        var first = g.getFirstItem(left, right);

        try{g.PAssert(1308,first !== undefined);}catch(PERR){g.reportError(PERR);}

        return ((first.id == left.id) ? -1 : 1);
    },
    compareItems: function(left, right)
    {
        // Sort by date if not a special block
        var cDate = !(this.isFlagged || this.isImportant) ? this.compareDate(left, right) : 0;

        if (cDate === 0)
        {
            var cPriority = !this.isFlagged ? this.comparePriority(left, right) : 0;
            if (cPriority === 0)
            {
                var cOrder = this.compareOrder(left, right);

                try{g.PAssert(1309,cOrder !== 0, "This should always result in a tie break");}catch(PERR){g.reportError(PERR);}

                return cOrder;
            }
            else
            {
                return cPriority;
            }
        }
        else
        {
            return cDate;
        }
    },
    // sortItems: function()
    // {
    //     this.items.sort(this.compareItems.bind(this));
    // },
    addItem: function(item)
    {
        if (this.items.indexOf(item) >= 0)
        {
            this.updateItem(item);

            return;
        }

        if (this.searchProvider.isMatched(item))
        {
            this.searchCount(this.searchCount() + 1);
            if(item.hasTime())
                this.timeCount(this.timeCount() + 1);

            if (DEBUG) log(item.id, ': New Count [', this.searchCount, ']');
        }

        //console.log('Insert Item: ', item.text);

        if (this.owner.deferModelUpdates)
        {
            this.items().insertSorted(item, this.compareItems.bind(this));
            this.hasDeferredUpdate = true;
        }
        else
        {
            this.items.insertSorted(item, this.compareItems.bind(this));
        }
    },
    removeItem: function(item)
    {
        if (this.searchProvider.isMatched(item))
        {
            this.searchCount(this.searchCount() - 1);
            if(item.hasTime())
                this.timeCount(this.timeCount() - 1);

            if (DEBUG) log(item.id, ': New Count [', this.searchCount, ']');
        }

        if (this.owner.deferModelUpdates)
        {
            this.items().remove(item);
            this.hasDeferredUpdate = true;
        }
        else
        {
            this.items.remove(item);
        }
    },
    updateItem: function(item)
    {
        // console.log('Update Item: ', item._rawText);

        if (this.owner.deferModelUpdates)
        {
            this.items().remove(item);
            this.items().insertSorted(item, this.compareItems.bind(this));
            this.hasDeferredUpdate = true;
        }
        else
        {
            this.items.remove(item);
            this.items.insertSorted(item, this.compareItems.bind(this));
        }

        var timeCount = 0;
        for(var i = 0; i < this.items().length; i ++)
        {
            if(this.items()[i].hasTime())
            {
                timeCount ++;
            }
        }
        this.timeCount(timeCount);
    },
    onLoad: function(element)
    {
        this.element = element;
    },
    notifySearchStateChanged: function(item, newValue)
    {
        // TODO: Does this really need to handle an invalid newValue?
        try{g.PAssert(1310,newValue !== undefined || this.searchProvider, 'Must supply a value or a valid search provider');}catch(PERR){g.reportError(PERR);}

        // If no new value is given, grab the latest from the search provider. This should only occur when
        // adding or removing items to this block.
        if (newValue === undefined)
        {
            newValue = this.searchProvider.isMatched(item);
        }

        if (DEBUG) log(item.id, ': Search State Changed to ', newValue);

        if (newValue === true)
        {
            this.searchCount(this.searchCount() + 1);

            if (item.hasTime())
            {
                this.timeCount(this.timeCount() + 1);
            }
        }
        else
        {
            this.searchCount(this.searchCount() - 1);

            if (item.hasTime())
            {
                this.timeCount(this.timeCount() - 1);
            }
        }

        if (DEBUG) log('  : New Count [', this.searchCount, ']');
    },
    setSearchProvider: function(vmSearch)
    {
        this.searchProvider = vmSearch;
    }
};

return VMTimelineBlock;
});

