define('AppCache',['ko', 'globals', 'platform'],
function (ko, g, platform) {

function AppCache()
{
    this.minTimeShowUpdate = 2000;
    this.timeToCancelButton = 2000;
	this.notifiedOfNew = false;
    this.isFinished = false;
    this.timeShownUpdatingMessage = undefined;
    // this.isVisible = ko.observable(true);
    this.showCancel = ko.observable(false);
    this.downloadingTimeout = 0;

    this.startStatus = window.applicationCache.status;
    this.continuedUpdate = false;
    this.updateActionDone = false;

    this.fnToRun = [ ];

    // Once the page has loaded and there was no appcache update on load, we periodically check
    // for updates on the fly. These updates should always post a new message instead of refreshing
    // the page to avoid interrupting the user's experience.
    this.dynamicUpdateCheck = false;

    // When doing a dynamic update check, some updates (those to data version) will require the user
    // to do an update. This will force them to reload their client.
    this.requiredUpdate = false;

    // Once an app update is found, keep track of whether there is a pending update that the user
    // has ignored.
    this.appUpdatePending = false;

    this.isShowingProgress = false;

	this.init();
}

AppCache.prototype = {
	init: function()
	{
        log('Cache Status: ' + this.startStatus);

        window.applicationCache.addEventListener('noupdate', this.onCacheLoadFinished.bind(this));
        window.applicationCache.addEventListener('cached', this.onFirstUpdateReady.bind(this));
        window.applicationCache.addEventListener('downloading', this.onUpdateDownloading.bind(this));
        window.applicationCache.addEventListener('updateready', this.onUpdateReady.bind(this));
        window.applicationCache.addEventListener('obsolete', this.onCacheLoadFinished.bind(this));
        window.applicationCache.addEventListener('error', this.onUpdateError.bind(this));
        // window.applicationCache.addEventListener('progress', this.onProgress.bind(this));

        if (window.applicationCache.status >= window.applicationCache.UPDATEREADY)
        {
            // this.notifyOfUpdate();
            // this.onUpdateReady();
        }
        else if (!document.documentElement.attributes.getNamedItem('manifest'))
        {
            this.onCacheLoadFinished();
        }
	},
    isVisible: function(value)
    {
        if (!g.isDemoMode())
        {
            g.messageQueue.pushMessage(MessageID.Updated);
        }
        else
        {
            if (value)
            {
                g.reload();
            }
        }
    },
    update: function()
    {
        if (window.applicationCache.status === window.applicationCache.IDLE)
        {
            applicationCache.update();
        }
    },
    checkForUpdate: function(forceUpdate)
    {
        this.requiredUpdate = !!forceUpdate;

        if (!this.appUpdatePending)
        {
            this.dynamicUpdateCheck = true;

            this.update();
        }
    },
    runOnFinish: function(cb, isTerminal)
    {
        try{g.PAssert(1478,this.fnToRun.length === 0, 'Only a single callback is allowed to be run at this point.');}catch(PERR){g.reportError(PERR);}

        // Either delay running the given callback until the app cache has finished updating or
        // run the callback async to give the same program execution to the calling API.
        if (!this.isFinished)
        {
            this.fnToRun.push(cb);
        }
        else
        {
            setTimeout(cb, 0);
        }
    },
    onUpdateReady: function()
    {
        log('Cache: New version available');
        this.appUpdatePending = true;

        if (this.startStatus === window.applicationCache.UNCACHED)
        {
            log('Cache Update from UNCACHED state. ERROR.');
        }

        // if (this.downloadingTimeout)
        // {
        //     clearTimeout(this.downloadingTimeout);
        // }

        this.onCacheLoadFinished();

        // var timeSinceUpdating = this.timeShownUpdatingMessage ? (Date.now() - this.timeShownUpdatingMessage) : 0;
        // if (timeSinceUpdating < this.minTimeShowUpdate)
        // {
        //     setTimeout(this.updateAction.bind(this), (this.minTimeShowUpdate - timeSinceUpdating));
        // }
        // else
        // {
            this.updateAction();
        // }
    },
    onFirstUpdateReady: function()
    {
        log('Cache: first update');
        // if (platform.mobile)
        // {
        //     this.closeMessage();
        // }

        // this.onCacheLoadFinished();
        // this.unNotifyOfUpdate();
    },
    onUpdateError: function(ev)
    {
        if (!platform.demo && (!g.vmMain || g.vmMain.isOnline()))
        {
            log('Update Error: ', ev);

            g.reportError(new Error('Error during app update'), ev);

            function pushMessage()
            {
                if (g.messageQueue)
                {
                    g.messageQueue.pushMessage({
                        text: 'Error updating app, ' + platform.actionVerb() + ' to retry.',
                        type: MessageType.Error,
                        action: MessageAction.Reload
                    });
                }
            }

            if (g.messageQueue)
            {
                pushMessage();
            }
            else
            {
                setTimeout(pushMessage, 5000);
            }
        }

        // if (platform.mobile)
        // {
        //     this.closeMessage();
        // }

        this.isVisible(false);
        this.onCacheLoadFinished();
    },
    onUpdateDownloading: function()
    {
        log('Cache: New version downloading');

        // this.notifyOfUpdate();
    },
    onCacheLoadFinished: function()
    {
        //try{g.PAssert(1479,!this.isFinished, 'Should only call cache load finished once.');}catch(PERR){g.reportError(PERR);}

        log('Cache: No update');

        if (!this.isFinished)
        {
            this.isFinished = true;

            // g.setAppCacheUpdateState(false);

            for (var i = 0; i < this.fnToRun.length; ++i)
            {
                this.fnToRun[i]();
            }
        }
    },
    // onProgress: function(e)
    // {
    //     if(e.lengthComputable)
    //     {
    //         if(!this.isShowingProgress)
    //         {
    //             this.isShowingProgress = true;
    //             g.removeClass(g.element('updateProgress'), 'hidden');
    //         }

    //         var perc = (e.loaded / e.total) * 100;
    //         g.element('updateProgress').value = perc;
    //     }
    // },
    notifyOfUpdate: function()
    {
        if (this.notifiedOfNew)
        {
            return;
        }

        this.notifiedOfNew = true;

        g.setAppCacheUpdateState(true);

        // Ensure that the onUpdateReady handler will be called after the downloading starts
        if (this.startStatus !== window.applicationCache.UNCACHED)
        {
            g.StopSpinner();

            // g.element('outerWrapper').style.display = 'none';
            // g.element('setupWrapper').style.display = 'none';

            this.isVisible(true);

            // this.timeShownUpdatingMessage = Date.now();

            // this.downloadingTimeout = setTimeout(function()
            // {
            //     this.downloadingTimeout = 0;
            //     this.showCancel(true);
            // }.bind(this), this.timeToCancelButton);
        }
    },
    // unNotifyOfUpdate: function()
    // {
    //     log('unNotify');
    //     // g.element('outerWrapper').style.display = '';
    //     // g.element('setupWrapper').style.display = '';

    //     this.isVisible(false);
    // },
    updateAction: function()
    {
        // if (this.dynamicUpdateCheck)
        // {
            if (this.requiredUpdate)
            {
                g.messageQueue.pushMessage(MessageID.RequiredUpdate);
            }
            else
            {
                this.isVisible(true);
                // g.messageQueue.pushMessage({
                //     text: 'New version available, ' + platform.actionVerb() + ' to load.',
                //     type: MessageType.Warning,
                //     action: MessageAction.Reload
                // });
            }
        // }
        // else
        // {
            // if (this.updateActionDone)
            // {
            //     return;
            // }

            // this.updateActionDone = true;

            // if (!this.continuedUpdate)
            // {
            //     g.reload();
            // }
            // else
            // {
                // this.isVisible(true);
                // g.messageQueue.pushMessage({
                //     text: 'New version available, ' + platform.actionVerb() + ' to load.',
                //     type: MessageType.Warning,
                //     action: MessageAction.Reload
                // });
            // }
        // }

        g.sendEvent('AppCache', 'Update');
    },
    // closeMessage: function()
    // {
    //     g.element('outerWrapper').style.display = '';
    //     g.element('setupWrapper').style.display = '';

    //     this.isVisible(false);
    // },
};

return AppCache;
});
