self.migrateData = function()
{
    // Possible Write Ops //

    var stopDocLoad = false;

    // When migrating data, ensure that the user is unable to undo any changes that we make.
    var requireLocalReload = false;
    var requireRemoteReload = false;

    var remoteVersion = self.root.get('dataVersion');
    if (remoteVersion < CURRENT_VERSION)
    {
        self.beginAction();

        log('Migrating to latest version: ', remoteVersion);

        try
        {
            switch (remoteVersion)
            {
                case 1:
                    migrateItemData_One(self.root);
                case 2:
                    migrateItemData_Two(self.root);
                case 3:
                    migrateItemData_Three(self.root);
                    requireRemoteReload = true;
                case 4:
                    migrateItemData_Four(self.root);
                default:
                    log('Remote Migration Completed');
                    break;
            }
        }
        catch (err)
        {
            requireRemoteReload = true;
            g.reportError(err);
        }

        self.endAction();

        self.model.getRoot().set('dataVersion', CURRENT_VERSION);

        log('Done Migrating Remote Data!', CURRENT_VERSION);
    }
    else if (remoteVersion > CURRENT_VERSION)
    {
        // When the remote version is larger than the local version, we need to update the app to the newest
        // version, otherwise the data will not be migrated or interpreted properly.
        requireLocalReload = true;

        // When the remote version is higher than we can upgrade to, stop loading the remote doc and ensure that
        // the page is refreshed with the newer version before loading the remote data.
        stopDocLoad = true;

        //try{g.PAssert(888,false, 'Remote version should never be larger than the client version except in cases where using an older version from the AppCache.');}catch(PERR){g.reportError(PERR);}
    }

    if (d.hasLocalData)
    {
        var localVersion = g.settings.get(Settings.localDataVersion) || 0;
        if (localVersion < CURRENT_VERSION)
        {
            try
            {
                switch (localVersion)
                {
                    case 0:
                        d.migrateLocalData_One();
                        requireLocalReload = true;
                        stopDocLoad = true;
                    case 4:
                        d.migrateLocalData_Two();
                    default:
                        log('Local Migration Completed');
                        break;
                }
            }
            catch (err)
            {
                requireLocalReload = true;
                g.reportError(err);
            }

            g.settings.set(Settings.localDataVersion, CURRENT_VERSION);

            log('Done Migrating Local Data!', CURRENT_VERSION);
        }
    }
    else
    {
        // If there is no local data loaded, the local data will be the same version as the remote data.
        g.settings.set(Settings.localDataVersion, remoteVersion);
    }

    if (requireLocalReload || requireRemoteReload)
    {
        if (requireLocalReload && !requireRemoteReload)
        {
            // Give the app cache a chance to complete its update before requesting a page reload.
            g.AppCache.runOnFinish(function ()
            {
                g.reload();
            }, /*isTerminal*/true);
        }
        else if (requireRemoteReload && !requireLocalReload)
        {
            self.registerSaveNotify(function ()
            {
                g.reload();
            });
        }
        else
        {
            var reqCount = 0;

            function reloadFn()
            {
                reqCount++;

                if (reqCount === 2)
                {
                    g.reload();
                }
            }

            g.AppCache.runOnFinish(reloadFn, /*isTerminal*/true);

            self.registerSaveNotify(reloadFn);
        }
    }

    return stopDocLoad;
};

function migrateItemData_Four(map)
{
    self.beginWriteAction();

    try{g.PAssert(889,map, 'Must have a valid root item to migrate');}catch(PERR){g.reportError(PERR);}

    // Skip migration if no contacts are loaded.
    if (d.contacts.length === 0)
    {
        log('Skipping remote data migration four');
        return;
    }

    var rootItem = map.get('root');
    //map.set('arch', map.createMap());

    function iterateLink(items)
    {
        if (items)
        {
            for (var i = 0; i < items.length; ++i)
            {
                var item = items.get(i);
                var text = self.get(item, 'text');

                if (text.indexOf('@') >= 0)
                {
                    var words = text.split(' ');

                    var lineIndex = 0;
                    for (var i = 0; i < words.length; ++i)
                    {
                        if (words[i].indexOf('@') === 0)
                        {
                            var matchedName = undefined;
                            var combined = words[i].substring(1);
                            for (var u = 1; u < 4; u ++)
                            {
                                var compStr = (combined.indexOf(':', combined.length - 1) !== -1) ? combined.substr(0, combined.length - 1) : combined;

                                if (d.contactsByName[compStr])
                                {
                                    matchedName = compStr;
                                }

                                if (i + u < words.length)
                                if (i + u < words.length)
                                {
                                    combined += ' ' + words[i + u];
                                }
                                else
                                {
                                    break;
                                }
                            }

                            if (matchedName)
                            {
                                try{g.PAssert(890,text[lineIndex] == '@', 'Explicitly matching @, should always match');}catch(PERR){g.reportError(PERR);}

                                var newText = text.substr(0, lineIndex) + '+' + text.substr(lineIndex + 1);

                                self.set(item, 'text', newText);
                                setRemoteTextVersion(item);
                            }
                        }

                        lineIndex += words[i].length + 1; // + 1 for space
                    }
                }

                iterateRemoteLink(item);
            }
        }
    }

    function iterateRemoteLink(remoteItem)
    {
        var collabItems = self.get(remoteItem, 'items');
        if (collabItems)
        {
            iterateLink(collabItems);
        }

        var collabArchivedItems = self.get(remoteItem, 'archivedItems');
        if (collabArchivedItems)
        {
            iterateLink(collabArchivedItems);
        }
    }

    iterateRemoteLink(rootItem);
}

function migrateItemData_Three(map)
{
    self.beginWriteAction();

    try{g.PAssert(891,map, 'Must have a valid root item to migrate');}catch(PERR){g.reportError(PERR);}

    var rootItem = map.get('root');
    //map.set('arch', map.createMap());

    function iterateLink(items)
    {
        if (items)
        {
            for (var i = 0; i < items.length; ++i)
            {
                var item = items.get(i);

                var text = self.get(item, 'text');
                if (text.startsWith('!'))
                {
                    self.set(item, 'priority', VMLIFlag.P0);
                    self.set(item, 'text', text.substr(1));

                    setRemoteTextVersion(item);
                }
                else if (text.startsWith('*'))
                {
                    self.set(item, 'isFlagged', true);
                    self.set(item, 'text', text.substr(1));

                    setRemoteTextVersion(item);
                }
                else if (text.startsWith('^'))
                {
                    self.set(item, 'isComplete', true);
                    self.set(item, 'text', text.substr(1));

                    setRemoteTextVersion(item);
                }

                iterateRemoteLink(item);
            }
        }
    }

    function iterateRemoteLink(remoteItem)
    {
        var collabItems = self.get(remoteItem, 'items');
        if (collabItems)
        {
            iterateLink(collabItems);
        }

        var collabArchivedItems = self.get(remoteItem, 'archivedItems');
        if (collabArchivedItems)
        {
            iterateLink(collabArchivedItems);
        }
    }

    iterateRemoteLink(rootItem);
}

function migrateItemData_Two(map)
{
    self.beginWriteAction();

    try{g.PAssert(892,map, 'Must have a valid root item to migrate');}catch(PERR){g.reportError(PERR);}

    var rootItem = map.get('root');

    function iterateLink(items)
    {
        if (items)
        {
            for (var i = 0; i < items.length; ++i)
            {
                var item = items.get(i);
                try{g.PAssert(893,item, 'Remote item must exist');}catch(PERR){g.reportError(PERR);}
                try{g.PAssert(894,!g.isString(item), 'New items must be collab maps not strings');}catch(PERR){g.reportError(PERR);}

                iterateRemoteLink(item);
            }
        }
    }

    function iterateRemoteLink(remoteItem)
    {
        var currentText = self.get(remoteItem, 'text');

        if (!g.isString(currentText))
        {
            self.set(remoteItem, 'text', currentText.getText());

            log('Updated Text: ', self.get(remoteItem, 'text'));
        }

        var collabItems = self.get(remoteItem, 'items');
        if (collabItems)
        {
            iterateLink(collabItems);
        }

        var collabArchivedItems = self.get(remoteItem, 'archivedItems');
        if (collabArchivedItems)
        {
            iterateLink(collabArchivedItems);
        }
    }

    iterateRemoteLink(rootItem);
};

function migrateItemData_One(map)
{
    self.beginWriteAction();

    try{g.PAssert(895,map, 'Must have a valid root item to migrate');}catch(PERR){g.reportError(PERR);}

    var rootItemID = map.get('root');
    var lookupTable = map.get('items');

    var rootItem = lookupTable.get(rootItemID);

    function iterateLink(items)
    {
        var newItems = [ ];

        if (items)
        {
            for (var i = 0; i < items.length; ++i)
            {
                var itemID = items.get(i);
                try{g.PAssert(896,g.isString(itemID), 'Must be a valid ID string');}catch(PERR){g.reportError(PERR);}

                var item = lookupTable.get(itemID);
                try{g.PAssert(897,item, 'Remote item must exist in DB');}catch(PERR){g.reportError(PERR);}
                try{g.PAssert(898,!g.isString(item), 'New items must be collab maps not strings');}catch(PERR){g.reportError(PERR);}

                newItems.push(item);

                iterateRemoteLink(item);
            }
        }

        return newItems;
    }

    function iterateRemoteLink(remoteItem)
    {
        var collabItems = self.get(remoteItem, 'items');
        if (collabItems)
        {
            var newItems = iterateLink(collabItems);

            try{g.PAssert(899,newItems.length === collabItems.length, 'New item list must be the same length as the old one');}catch(PERR){g.reportError(PERR);}

            if (newItems.length > 0)
            {
                collabItems.replaceRange(0, newItems);
            }
        }


        var collabArchivedItems = self.get(remoteItem, 'archivedItems');
        if (collabArchivedItems)
        {
            var newArchivedItems = iterateLink(collabArchivedItems);

            try{g.PAssert(900,newArchivedItems.length === collabArchivedItems.length, 'New item list must be the same length as the old one');}catch(PERR){g.reportError(PERR);}

            if (newArchivedItems.length > 0)
            {
                collabArchivedItems.replaceRange(0, newArchivedItems);
            }
        }
    }

    iterateRemoteLink(rootItem);

    map.set('root', rootItem);
};

//
// Batch actions to coalesce multiple data modifications into a single operation. This does
// not have transactional safety, only that the actions performed get sent together.
//
self.beginAction = function()
{
    if (self.model)
    {
        try{g.PAssert(901,!self.flushingDeferredActions, 'Should never begin a new action while flushing');}catch(PERR){g.reportError(PERR);}

        try{g.PAssert(902,!self.inCompoundOperation, 'Should not nest compound operations');}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(903,!self.wantCompoundOperation, 'Should not nest compound operations');}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(904,!self.isClosed, 'Document must be open to make changes');}catch(PERR){g.reportError(PERR);}

        if (DEBUG) log('<<<< Request Action >>>>');
        self.wantCompoundOperation = true;
    }
};

var ensureActionTimeout = undefined;
self.beginWriteAction = function()
{
    if (self.model)
    {
        // If currently flushing deferred operations, ignore any starts to begin flushing
        if (!self.flushingDeferredActions)
        {
            try{g.PAssert(905,self.inCompoundOperation || self.wantCompoundOperation, 'If performing an action, we should have already requested a compound operation');}catch(PERR){g.reportError(PERR);}

            if (!self.inCompoundOperation && self.wantCompoundOperation)
            {
                self.flushDeferredActions();

                setTimeout(ensureWriteAction, 0);

                if (DEBUG) log('<<<< Begin Action >>>>');
                self.model.beginCompoundOperation();

                self.inCompoundOperation = true;
                self.wantCompoundOperation = false;
            }
            else
            {
                //log('<<<< Perform Action >>>>');
                try{g.PAssert(906,!self.wantCompoundOperation, 'Should not nest compound operations');}catch(PERR){g.reportError(PERR);}
            }
        }
    }
};

self.endAction = function()
{
    if (self.model)
    {
        try{g.PAssert(907,!self.flushingDeferredActions, 'Should never end an action while flushing');}catch(PERR){g.reportError(PERR);}

        try{g.PAssert(908,!self.isClosed, 'Document must be open to make changes');}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(909,self.inCompoundOperation || self.wantCompoundOperation, 'If ending an action, a beginning should have happened');}catch(PERR){g.reportError(PERR);}

        try
        {
            if (self.inCompoundOperation)
            {
                self.model.endCompoundOperation();
                if (DEBUG) log('<<<< Ending Action >>>>');
            }
            else
            {
                if (DEBUG) log('<<<< Action Skipped >>>>');
            }
        }
        catch (err)
        {
            g.goog.recoverFromError(err);
        }
        finally
        {
            self.inCompoundOperation = false;
            self.wantCompoundOperation = false;

            clearTimeout(ensureActionTimeout);
        }
    }
};

function ensureWriteAction()
{
    try{g.PAssert(910,!self.inCompoundOperation, 'Should never be in a compound operation after sync block finishes');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(911,!self.wantCompoundOperation, 'Should never want a compound operation after a sync block finishes');}catch(PERR){g.reportError(PERR);}

    if (self.inCompoundOperation)
    {
        self.model.endCompoundOperation();
        if (DEBUG) log('<<<< Ensure Ending Action >>>>');

        g.reportError(new Error('Compound operation open after sync block finished'));
    }

    ensureActionTimeout = undefined;
}

// Default timeout for deferring actions
var DEFERRED_ACTION_TIMEOUT = 800;

// Track deferred actions on a per-item basis
var deferredActions = { };
var hasPendingDeferredActions = false;

// Setup a timer to flush deferred actions even if other actions that force a flush are never performed.
var flushDeferredActionsTimeout = undefined;

self.performDeferredAction = function(item, action)
{
    try{g.PAssert(912,item, 'Must supply a valid item to perform the action on');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(913,action, 'Must supply a valid action to be performed');}catch(PERR){g.reportError(PERR);}

    if (self.model)
    {
        try{g.PAssert(914,!self.inCompoundOperation, 'Cannot perform deferred action after a compound operation has started');}catch(PERR){g.reportError(PERR);}

        if (DEBUG)
        {
            // Verify only supported properties are being deferred

        }

        //log('Performing Deferred Action: ', action);

        if (deferredActions[item.id])
        {
            for (var prop in action)
            {
                deferredActions[item.id].changes[prop] = action[prop];
            }
        }
        else
        {
            deferredActions[item.id] = { item: item, changes: action };
        }

        hasPendingDeferredActions = true;

        clearTimeout(flushDeferredActionsTimeout);
        flushDeferredActionsTimeout = setTimeout(self.flushDeferredActions, DEFERRED_ACTION_TIMEOUT);
    }
};

self.flushDeferredActions = function()
{
    if (self.model && !self.isClosed)
    {
        try{g.PAssert(915,!self.flushingDeferredActions, 'Should never recursively call flushDeferredActions');}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(916,!self.inCompoundOperation, 'Must flush deferred actions before starting a compound operation');}catch(PERR){g.reportError(PERR);}

        if (hasPendingDeferredActions)
        {
            clearTimeout(flushDeferredActionsTimeout);
            flushDeferredActionsTimeout = undefined;

            self.flushingDeferredActions = true;

            try
            {
                self.model.beginCompoundOperation();

                try
                {
                    for (var id in deferredActions)
                    {
                        if (DEBUG) log('Flushing Deferred Action: ', deferredActions[id]);

                        saveChangesInternal(deferredActions[id].item, deferredActions[id].changes);
                    }

                    self.flushingDeferredActions = false;

                    hasPendingDeferredActions = false;
                    deferredActions = { };
                }
                catch (innerErr)
                {
                    g.reportError(err);
                }

                self.model.endCompoundOperation();
            }
            catch (err)
            {
                g.reportError(err);
            }
        }
    }
};

function attachEvents(item)
{
    // No Write Ops //

    try{g.PAssert(917,item, 'Must have a valid local item to attach events to');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(918,item instanceof VMLI, 'Must pass in a valid instace of a local item');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(919,item.data, 'Must have a valid remote item to attach events to');}catch(PERR){g.reportError(PERR);}

    item.data.addEventListener(gapi.drive.realtime.EventType.OBJECT_CHANGED, item.onObjectChanged.bind(item));
}

self.createNewItem = function (item, hasItems, hasArchivedItems)
{
    self.beginWriteAction();

    try{g.PAssert(920,item instanceof VMLI, 'Must pass in a valid instace of a local item');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(921,self.inCompoundOperation, 'Should always create an item inside of a compound operation');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(922,!self.isClosed, 'Document must be open to make changes');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(923,self.model, 'Need a valid model to create collaborative items');}catch(PERR){g.reportError(PERR);}

    try{g.PAssert(924,!item.data, 'When initializing a new item, it should have no gdata components already');}catch(PERR){g.reportError(PERR);}

    var map = self.model.createMap();
    item.data = map;

    if (DEBUG) log('Creating New Item: ', map.id);

    self.set(map, 'text', item.getRawText());

    if (hasItems)
    {
        var collabList = self.model.createList();
        self.set(map, 'items', collabList);
    }

    if (hasArchivedItems)
    {
        var collabArchiveList = self.model.createList();
        self.set(map, 'archivedItems', collabArchiveList);
    }

    self.set(map, 'dateCreated', item.dateCreated.getTime());

    item.id = map.id;

    if (!itemCache[map.id])
    {
        itemCache[map.id] = map;
    }

    return map.id;
};

self.initializeNewItem = function (item)
{
    self.beginWriteAction();

    try{g.PAssert(925,item instanceof VMLI, 'Must pass in a valid instace of a local item');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(926,!self.isClosed, 'Document must be open to make changes');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(927,self.inCompoundOperation, 'Should always initialize an item inside of a compound operation');}catch(PERR){g.reportError(PERR);}

    if (item.hasChildren())
    {
        var items = item.items();
        var collabList = ensureRemoteItemsArray(item);
        try{g.PAssert(928,collabList);}catch(PERR){g.reportError(PERR);}

        for (var i = 0; i < items.length; ++i)
        {
            try{g.PAssert(929,items[i].data, 'Item must have a valid remote to be added to a collab list');}catch(PERR){g.reportError(PERR);}
            collabList.push(items[i].data);
        }
    }

    if (item.hasArchivedChildren())
    {
        var archivedItems = item.archivedItems(/*underlying*/true);

        var collabArchivedList = ensureRemoteArchiveArray(item);

        try{g.PAssert(930,collabArchivedList);}catch(PERR){g.reportError(PERR);}

        for (var i = 0; i < archivedItems.length; ++i)
        {
            try{g.PAssert(931,archivedItems[i].data, 'Item must have a valid remote to be added to a collab list');}catch(PERR){g.reportError(PERR);}
            collabArchivedList.push(archivedItems[i].data);
        }
    }

    attachEvents(item);
};

//
// Given a VMLI, have it listen to gdrive events. This requires that the item has already loaded
// its data from gdrive.
//
self.attachEventsToItem = function(item)
{
    // No Write Ops //

    try{g.PAssert(932,item instanceof VMLI, 'Must pass in a valid local item');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(933,item.data, 'Must have loaded data from gdrive before attaching events to item');}catch(PERR){g.reportError(PERR);}

    attachEvents(item);
};

//
// Allow clients to register themselves to be notified when the undo/redo state changes for the loaded model.
//
var undoRedoStateChangeHandler;
self.registerForUndoRedoStateChange = function (handler)
{
    // No Write Ops //

    if (self.model)
    {
        self.model.addEventListener(gapi.drive.realtime.EventType.UNDO_REDO_STATE_CHANGED, handler.stateChangeForUndoRedo);

        // Initialize the client requesting updates for undo/redo state with values stored in the model.
        handler.stateChangeForUndoRedo({
            'canUndo': self.model.canUndo,
            'canRedo': self.model.canRedo
        });
    }
    else
    {
        undoRedoStateChangeHandler = handler;
    }
};

self.registerForSaveStateChange = function (handler)
{
    // No Write Ops //

    try{g.PAssert(934,self.doc);}catch(PERR){g.reportError(PERR);}
    self.doc.addEventListener(gapi.drive.realtime.EventType.DOCUMENT_SAVE_STATE_CHANGED, handler);
};

self.loadCollaborators = function ()
{
    var collabs = self.doc.getCollaborators();

    var selfInfo = undefined;
    for (var i = 0; i < collabs.length; ++i)
    {
        var collab = collabs[i];

        if (collab.isMe)
        {
            selfInfo = collab;
        }
    }

    self.authenticatedUser(selfInfo);
};

//
// Callbacks to give clients the ability to request a redo or undo of their last action.
//
self.undoAction = function ()
{
    self.flushDeferredActions();

    try{g.PAssert(935,!self.isClosed, 'Document must be open to make changes');}catch(PERR){g.reportError(PERR);}

    try{g.PAssert(936,self.model.canUndo);}catch(PERR){g.reportError(PERR);}

    if (DEBUG) log('>>>> Begin GData Undo <<<<');

    self.model.undo();

    if (DEBUG) log('>>>> End GData Undo <<<<');
};

self.redoAction = function ()
{
    self.flushDeferredActions();

    try{g.PAssert(937,!self.isClosed, 'Document must be open to make changes');}catch(PERR){g.reportError(PERR);}

    try{g.PAssert(938,self.model.canRedo);}catch(PERR){g.reportError(PERR);}

    if (DEBUG) log('>>>> Begin GData Redo <<<<');

    self.model.redo();

    if (DEBUG) log('>>>> End GData Redo <<<<');
};

self.getItemList = function(model, isArchived)
{
    self.beginWriteAction();

    try{g.PAssert(939,!(model instanceof VMLI), 'Must pass in a valid remote item');}catch(PERR){g.reportError(PERR);}

    if (isArchived)
    {
        return self.get(model, 'archivedItems');
    }
    else
    {
        return self.get(model, 'items');
    }
}

self.ensureItemList = function(item, isArchived)
{
    self.beginWriteAction();

    try{g.PAssert(940,item instanceof VMLI, "Must pass in a valid local item");}catch(PERR){g.reportError(PERR);}

    if (isArchived)
    {
        return ensureRemoteArchiveArray(item);
    }
    else
    {
        return ensureRemoteItemsArray(item);
    }
}

function ensureRemoteItemsArray(item)
{
    self.beginWriteAction();

    try{g.PAssert(941,item instanceof VMLI);}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(942,item.data, 'Must have valid remote item to attach events to and create');}catch(PERR){g.reportError(PERR);}

    var itemList = self.get(item.data, 'items');

    if (!itemList)
    {
        itemList = self.model.createList();
        self.set(item.data, 'items', itemList);

        //log('Creating a remote items array:', item.id);

        // itemList.addEventListener(gapi.drive.realtime.EventType.VALUES_ADDED, item.onListAdded.bind(item));
        // itemList.addEventListener(gapi.drive.realtime.EventType.VALUES_REMOVED, item.onListRemoved.bind(item));
        //itemList.addEventListener(gapi.drive.realtime.EventType.OBJECT_CHANGED, item.onObjectChanged.bind(item));
    }

    return itemList;
}

function ensureRemoteArchiveArray(item)
{
    self.beginWriteAction();

    try{g.PAssert(943,item instanceof VMLI);}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(944,item.data, 'Must have valid remote item to attach events to and create');}catch(PERR){g.reportError(PERR);}

    var archivedList = self.get(item.data, 'archivedItems');

    if (!archivedList)
    {
        archivedList = self.model.createList();
        self.set(item.data, 'archivedItems', archivedList);

        //log('Creating a remote archive array:', item.id);

        // archivedList.addEventListener(gapi.drive.realtime.EventType.VALUES_ADDED, item.onListAdded.bind(item));
        // archivedList.addEventListener(gapi.drive.realtime.EventType.VALUES_REMOVED, item.onListRemoved.bind(item));
        //archivedList.addEventListener(gapi.drive.realtime.EventType.OBJECT_CHANGED, item.onObjectChanged.bind(item));
    }

    return archivedList;
}

