self.canSave = function(item)
{
    if(!self.initialized)
        return;

    var data = (item instanceof VMLI) ? item.data : item;
    try{g.PAssert(945,data && !self.isClosed, 'Document must be open to make changes');}catch(PERR){g.reportError(PERR);}

    return (data && !self.isClosed) ? data : false;
};

self.deleteTree = function(item)
{
    self.beginWriteAction();

    try{g.PAssert(946,!(item instanceof VMLI), 'Must not be a VMLI, should be a remote item');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(947,!g.isString(item), 'Must pass in a valid remote item not an ID');}catch(PERR){g.reportError(PERR);}

    //log('Delete Tree: ', id);

    if (DEBUG) log('%cDelete tree from remote', 'color: red', item.id, self.get(item, 'text'));

    var items = self.get(item, 'items');

    if (items)
    {
        for(var i = 0; i < items.length; i ++)
        {
            var child = items.get(i);
            self.deleteTree(child);
        }
    }

    // TODO: Where are archived items handled during tree deletion?

    delete itemCache[item.id];
};

if (DEBUG && DEBUG)
{
    window.__REMOTE_VERSION_UPDATES = g.createRingBuffer(50);
    window.__LOCAL_VERSION_UPDATES = g.createRingBuffer(50);
    window.__MEM_VERSION_UPDATES = g.createRingBuffer(50);

    window.clearVersionUpdates = function()
    {
        window.__REMOTE_VERSION_UPDATES.reset();
        window.__LOCAL_VERSION_UPDATES.reset();
        window.__MEM_VERSION_UPDATES.reset();
    };
}

function setRemoteVersion(data)
{
    self.beginWriteAction();

    var v = self.getVersion(data, VersionType.Structure);
    v = v ? v + 1 : 1;

    if (DEBUG && DEBUG)
    {
        var verStr = self.getVersion(data, VersionType.Structure) + ' -> ' + v;
        window.__REMOTE_VERSION_UPDATES.push('VERSION: ' + data.id + ': ' + g.getCurrentStack(verStr));

        if (DEBUG) log('Setting remote version ' + data.id + ': ', verStr);
    }

    self.set(data, 'version', v);
}

function setRemoteTextVersion(data)
{
    self.beginWriteAction();

    var v = self.getVersion(data, VersionType.Text);
    v = v ? v + 1 : 1;

    if (DEBUG && DEBUG)
    {
        var verStr = self.getVersion(data, VersionType.Text) + ' -> ' + v;
        window.__REMOTE_VERSION_UPDATES.push('VERSIONTEXT: ' + data.id + ': ' + g.getCurrentStack(verStr));

        if (DEBUG) log('Setting remote TEXT version ' + data.id + ': ', verStr);
    }

    self.set(data, 'versionText', v);
}

//
// Notification that a single item has been added to the items array.
//
self.itemAdded = function(params)
{
    self.beginWriteAction();

    //log('--Add Remote Item: ', params);
    //if (DEBUG) log('%cAdded:', 'color: red', params.item.id, params.item.text, params.addedItem.text);

    var item = params.item;
    var addedItem = params.addedItem;
    var index = params.index;
    var isArchived = params.isArchived;
    var noVersion = params.noVersion;

    try{g.PAssert(948,item !== undefined && addedItem !== undefined && isArchived !== undefined, 'itemAdded is missing parameters');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(949,index >= -1 || index === undefined, 'Invalid index', index);}catch(PERR){g.reportError(PERR);}

    var itemData = self.canSave(item);
    if(!itemData)
        return;

    var addedData = addedItem.data;
    try{g.PAssert(950,addedData, 'Must have valid gdata for the item being inserted', addedData);}catch(PERR){g.reportError(PERR);}

    d.addPendingUpdate(item.id);
    d.addPendingUpdate(addedItem.id);

    var arr = self.ensureItemList(item, isArchived);
    try{g.PAssert(951,arr, 'Must have a valid remote list to add the item to');}catch(PERR){g.reportError(PERR);}

    if (index < 0 || isNaN(index) || index > arr.length)
    {
        arr.push(addedData);
    }
    else
    {
        arr.insert(index, addedData);
    }

    // var wasArchived = self.get(addedData, 'isArchived');
    // if (wasArchived !== isArchived)
    // {
    //     self.set(addedData, 'isArchived', isArchived);
    // }

    if (!noVersion)
    {
        setRemoteVersion(addedItem.data);
    }

    if (DEBUG) log('Item Added Locally: ', addedItem.id, self.getVersion(itemData, VersionType.Structure));
};

//
// Notification that one or more entries has been added to the items array.
//
self.itemsAdded = function(params)
{
    self.beginWriteAction();

    //if (DEBUG)  log('%cItems Added:', 'color: red', params.item.id, params);
    var item = params.item;
    try{g.PAssert(952,(item instanceof VMLI), 'Passed in item must be a VMLI');}catch(PERR){g.reportError(PERR);}

    var addedItems = params.addedItems;
    var index = params.index;
    var isArchived = params.isArchived;
    var noVersion = params.noVersion;

    try{g.PAssert(953,item !== undefined && addedItems !== undefined && isArchived !== undefined, 'itemsAdded is missing parameters');}catch(PERR){g.reportError(PERR);}

    var itemData = self.canSave(item);
    if(!itemData)
        return;

    var arr = self.ensureItemList(item, isArchived);
    try{g.PAssert(954,arr, 'Must have a valid remote list to add the item to');}catch(PERR){g.reportError(PERR);}

    // grab the google data objects out of the VMLIs to be inserted
    var addedArr = new Array(addedItems.length);
    for (var i = 0; i < addedItems.length; ++i)
    {
        addedArr[i] = addedItems[i].data;
        d.addPendingUpdate(addedItems[i].id);

        if (!noVersion && addedArr[i])
        {
            setRemoteVersion(addedArr[i]);
        }
    }

    d.addPendingUpdate(item.id);

    if (index < 0 || isNaN(index))
    {
        try{g.PAssert(955,isArchived, 'Only archived items should be inserted into a list by pushing');}catch(PERR){g.reportError(PERR);}

        arr.pushAll(addedArr);
    }
    else
    {
        arr.insertAll(index, addedArr);
    }

    if (DEBUG) log('Items Added Locally To', item.id, self.getVersion(itemData, VersionType.Structure));
};

//
// Notification that one or more entries has been removed from the items array.
//
self.itemRemoved = function(params)
{
    self.beginWriteAction();

    //if (DEBUG) log('%cItem(s) Removed:', 'color: red', params.item.id, params.item.text, params.index);
    var item = params.item;
    var index = params.index;
    var numToRemove = params.numToRemove;
    var isArchived = params.isArchived;
    var noDelete = params.noDelete;
    var noVersion = params.noVersion;

    try{g.PAssert(956,item !== undefined && index !== undefined && numToRemove !== undefined && isArchived !== undefined, 'itemRemoved is missing parameters');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(957,index >= 0, 'Must have a valid index to remove from');}catch(PERR){g.reportError(PERR);}

    var itemData = self.canSave(item);
    if(!itemData)
        return;

    if (isNaN(numToRemove))
    {
        numToRemove = 1;
    }

    var arr = self.getItemList(itemData, isArchived);
    try{g.PAssert(958,arr, 'Must have a valid remote list to remove the item from');}catch(PERR){g.reportError(PERR);}

    // Iterate through each item that is going to be removed. For each mark it as pending an update and
    // increment its version if necessary. If a delete is requested, perform that as well.
    for (var i = index; i < (index + numToRemove); i ++)
    {
        var deleted = arr.get(i);

        d.addPendingUpdate(deleted.id);

        if (!noVersion)
        {
            setRemoteVersion(deleted);
        }

        if (!noDelete)
        {
            self.deleteTree(deleted);
        }
    }

    d.addPendingUpdate(item.id);

    if (numToRemove === 1)
    {
        arr.remove(index);
    }
    else
    {
        arr.removeRange(index, index + numToRemove);
    }

    //removeRemoteArchiveArrayIfNecessary(itemData);

    //setRemoteVersion(itemData);

    if (DEBUG) log('Items Removed Locally From: ', item.id, index, numToRemove, self.getVersion(itemData, VersionType.Structure));
};

self.itemMoved = function(params)
{
    self.beginWriteAction();

    // if (DEBUG) log('%cMoved:', 'color: red', params);
    var item = params.item;
    var newInfo = params.newInfo;
    var oldParent = params.oldParent;
    var oldInfo = params.oldInfo;
    var noVersion = params.noVersion;

    try{g.PAssert(959,item !== undefined && newInfo !== undefined && oldParent !== undefined && oldInfo !== undefined, 'itemMoved is missing parameters');}catch(PERR){g.reportError(PERR);}

    try{g.PAssert(960,newInfo && newInfo.index >= -1);}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(961,newInfo && newInfo.isArchived !== undefined);}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(962,oldParent);}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(963,oldInfo && oldInfo.index >= 0);}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(964,oldInfo && oldInfo.isArchived !== undefined);}catch(PERR){g.reportError(PERR);}

    var itemData = self.canSave(item);
    if(!itemData)
        return;

    d.addPendingUpdate(item.id);

    self.itemRemoved({
        item: oldParent,
        index: oldInfo.index,
        numToRemove: 1,
        isArchived: oldInfo.isArchived,
        noDelete: true,
        noVersion: true // Only update the version info once when moving which will happen when adding the item
    });

    self.itemAdded({
        item: item.parent(),
        addedItem: item,
        index: newInfo.index,
        isArchived: newInfo.isArchived,
        noVersion: noVersion
    });

    if (DEBUG) log('Item Moved Locally: ', item.id, 'from', oldParent.id, 'to', item.parent().id);
};

self.itemArchived = function(item, itemArchived, index)
{
    try{g.PAssert(965,index >= 0, 'Must have a valid index to remove from');}catch(PERR){g.reportError(PERR);}

    self.beginWriteAction();

    var itemData = self.canSave(item);
    if(!itemData)
        return;

    d.addPendingUpdate(item.id);
    d.addPendingUpdate(itemArchived.id);

    var items = self.get(itemData, 'items');
    try{g.PAssert(966,items, 'Must be an items array present');}catch(PERR){g.reportError(PERR);}

    var archivedItems = ensureRemoteArchiveArray(item);
    try{g.PAssert(967,archivedItems, 'Must be an archived items array present');}catch(PERR){g.reportError(PERR);}

    if (DEBUG) log('Removing item [', index, ']: ', self.get(items.get(index), 'text'));
    items.remove(index);

    if (DEBUG) log('Reinserting at: ', items.length);
    archivedItems.push(itemArchived.data);

    setRemoteVersion(itemArchived.data);

    if (DEBUG) log('Item Archived Locally: ', itemArchived.id, self.getVersion(itemArchived, VersionType.Structure));
};

self.itemUnarchived = function(item, itemUnarchived, removeIndex, insertIndex)
{
    try{g.PAssert(968,removeIndex >= 0, 'Must have a valid index to remove from');}catch(PERR){g.reportError(PERR);}

    self.beginWriteAction();

    var itemData = self.canSave(item);
    if(!itemData)
        return;

    d.addPendingUpdate(item.id);
    d.addPendingUpdate(itemUnarchived.id);

    var items = self.get(itemData, 'items');
    try{g.PAssert(969,items, 'Must be an items array present');}catch(PERR){g.reportError(PERR);}

    var archivedItems = self.get(itemData, 'archivedItems');
    try{g.PAssert(970,archivedItems, 'Must be an archived items array present');}catch(PERR){g.reportError(PERR);}

    if (DEBUG) log('Removing item [', removeIndex, ']: ', self.get(archivedItems.get(removeIndex), 'text'));
    archivedItems.remove(removeIndex);

    if (DEBUG) log('Reinserting at: ', insertIndex);
    items.insert(insertIndex, itemUnarchived.data);

    // var removedArray = removeRemoteArchiveArrayIfNecessary(itemData);
    // if (DEBUG) log('Removed archive array from ', item.id);

    setRemoteVersion(itemUnarchived.data);

    if (DEBUG) log('Item Unarchived Locally: ', itemUnarchived.id, self.getVersion(itemUnarchived, VersionType.Structure));
};

