//
// Writes out the changes to a given item, only items that have been changed are written.
//
self.saveChanges = function(item, changes)
{
    if (self.inCompoundOperation)
    {
        saveChangesInternal(item, changes);
    }
    else
    {
        self.performDeferredAction(item, changes);
    }
};

function saveChangesInternal(item, changes)
{
    // if (DEBUG)
    // {
    //     window.__REMOTE_UPDATE_STACKS.push(g.getCurrentStack(changes));
    // }

    var itemData = self.canSave(item);
    if(!itemData)
        return;

    if (g.isEmpty(changes))
    {
        if (ShouldLog(LogLevels.Warning)) log('Skipped remote saving an empty object');
        return;
    }

    self.beginWriteAction();

    d.addPendingUpdate(item.id);

	for (var prop in changes)
	{
        try{g.PAssert(971,changes.hasOwnProperty(prop), 'Changes should only contain properties they own');}catch(PERR){g.reportError(PERR);}

        if (changes.hasOwnProperty(prop))
        {
            switch (prop)
            {
                case 'text':
                    try{g.PAssert(972,g.isString(changes['text']));}catch(PERR){g.reportError(PERR);}

                    self.set(itemData, 'text', changes['text']);
                    break;
                case 'date':
                    try{g.PAssert(973,!changes['date'] || typeof changes['date'] == 'number');}catch(PERR){g.reportError(PERR);}

                    self.set(itemData, 'date', changes['date']);
                    break;
                case 'dateCreated':
                    try{g.PAssert(974,typeof changes['dateCreated'] == 'number');}catch(PERR){g.reportError(PERR);}

                    self.set(itemData, 'dateCreated', changes['dateCreated']);
                    break;
                case 'dateCompleted':
                    try{g.PAssert(975,!changes['dateCompleted'] || typeof changes['dateCompleted'] == 'number');}catch(PERR){g.reportError(PERR);}

                    self.set(itemData, 'dateCompleted', changes['dateCompleted']);
                    break;
                case 'allDayDate':
                case 'priority':
                case 'isComplete':
                case 'isFlagged':
                    self.set(itemData, prop, changes[prop]);
                    break;
                case 'isCollapsed':
                case 'isArchived':
                    try{g.PAssert(976,false, 'These properties are not set this way');}catch(PERR){g.reportError(PERR);}

                    self.set(itemData, prop, changes[prop]);
                    break;
                case 'modifiedOfflineText':
                case 'dateText':
                    // Do Nothing - Not saved remotely
                    break;
                default:
                    try{g.PAssert(977,false, 'Attempting to remotely save an unknown field: ', prop);}catch(PERR){g.reportError(PERR);}
                    break;
            }
        }
    }

    // TODO: Are there cases where both the text and non-text versions need to be updated at the same time?
    // if (isText)
    // {
        setRemoteTextVersion(itemData);
    // }
    // else
    // {
    //     setRemoteVersion(itemData);
    // }

    if (DEBUG) log('Item Changes Saved:', item.getVersion(VersionType.Text), changes);

    // print();
    // if (self.savedTimeout)
    // {
    //     clearTimeout(self.savedTimeout);
    // }
};

