self.close = function(info)
{
    try{g.PAssert(978,!self.wantCompoundOperation, 'Closing the doc with a deferred compound operation');}catch(PERR){g.reportError(PERR);}

    if (self.doc && !self.isClosed)
    {
        log('---- Closing Remote Document! ----');

        self.isClosed = true;
        self.initialized = false;
        self.model = undefined;
        self.doc = undefined;
        self.root = undefined;

        g.messageQueue.pushMessage(MessageID.DriveError);

        g.vmMain.setGDriveStatus(DriveStatus.Offline);

        try
        {
            self.doc.close();
        }
        catch (err)
        {
            log('Error Closing Doc: ', err);
        }

        g.reportError(new Error('Document closed'), info);
    }
};

self.onInitialized = function(model)
{
    // Ops on Doc Create //
    log('Initializing', model);

    if (!d)
    {
        d = require('data');
    }

    if (!VMLI)
    {
        VMLI = require('VMLI');
    }

    // Create root item
    var home = model.createMap();
    var homeItems = model.createList();

    self.set(home, 'text', 'Home');
    self.set(home, 'items', homeItems);

    self.set(home, 'archivedItems', model.createList());
    self.set(home, 'dateCreated', Date.now());

    // Create default empty child
    var child = model.createMap();

    self.set(child, 'text', '');
    self.set(child, 'dateCreated', Date.now());

    // Add default child to root item
    homeItems.push(child);


    var root = model.getRoot();
    root.set('dataVersion', CURRENT_VERSION);
    root.set('root', home);

    // Create archived items depot
    //root.set('arch', model.createMap());
};

self.saveSetting = function(e)
{
    self.flushDeferredActions();

    try{g.PAssert(979,self.settingsRemote, 'Remote settings required to sync');}catch(PERR){g.reportError(PERR);}

    if (!e.data.isRemote)
    {
        if (DEBUG) log('SETTINGS: Saving settings to remote ', e.data);

        var value = e.data.value;
        var property = e.data.property;

        // Versions that get modified locally are negative.
        if (property.startsWith(g.settings.prefix))
        {
            value = Math.abs(value);

            g.settings.setVersion(property, value);
        }

        if (self.settingsLocal[property] !== value)
        {
            self.settingsLocal[property] = value;
            self.settingsRemote.set(property, value);
        }
    }
    else
    {
        if (DEBUG) log('SETTINGS: Skipping saving settings to remote', e.data);
    }
};

self.onRemoteSettingsLoad = function()
{
    if (g.settings.set(Settings.syncContacts, self.settingsLocal[Settings.syncContacts], self.settingsLocal[g.settings.prefix + Settings.syncContacts]))
    {
        g.goog.authAndLoadContacts();
    }

    if (g.settings.set(Settings.docSharing, self.settingsLocal[Settings.docSharing], self.settingsLocal[g.settings.prefix + Settings.docSharing]))
    {
        g.goog.authDocSharing();
    }

    window.addEventListener('settingsChange', self.saveSetting);
};

function onSettingsChanged(change)
{
    // TODO: Handle undo/redo?
    var wasChanged = false;
    for (var i = 0; i < change.events.length; ++i)
    {
        var ev = change.events[i];

        if (!ev.isLocal)
        {
            // Only consider properties changed when non-version ones do.
            if (!ev.property.startsWith(g.settings.prefix))
            {
                wasChanged = true;
            }

            self.settingsLocal[ev.property] = ev.newValue;

            g.fireCustomEvent('settingsChange_' + ev.property, ev.newValue);
        }
    }

    if (wasChanged)
    {
        self.onRemoteSettingsLoad();
    }
}

self.loadSettings = function()
{
    // No Write Ops //

    self.settingsLocal = { };
    self.settingsRemote = self.root.get('settings');

    // This is a write op, however the settigns field isnt shared by other changes and is flushed at will.
    if (!self.settingsRemote)
    {
        self.settingsRemote = self.model.createMap();
        self.root.set('settings', self.settingsRemote);
    }

    try
    {
        // Contacts
        self.settingsLocal[Settings.syncContacts] = self.settingsRemote.get(Settings.syncContacts);

        var syncContactsV = g.settings.prefix + Settings.syncContacts;
        self.settingsLocal[syncContactsV] = self.settingsRemote.get(syncContactsV);

        // Doc Sharing
        self.settingsLocal[Settings.docSharing] = self.settingsRemote.get(Settings.docSharing);

        var docSharingV = g.settings.prefix + Settings.docSharing;
        self.settingsLocal[docSharingV] = self.settingsRemote.get(docSharingV);

        // Theme
        self.settingsLocal[Settings.theme] = self.settingsRemote.get(Settings.theme);

        var themeV = g.settings.prefix + Settings.theme;
        self.settingsLocal[themeV] = self.settingsRemote.get(themeV);

        // Add event listener if settings are changed remotely
        self.settingsRemote.addEventListener(gapi.drive.realtime.EventType.OBJECT_CHANGED, onSettingsChanged.bind(self));
    }
    catch (err)
    {
        g.reportError(err);
    }
};

