self.poke = function()
{
    if (self.root && !self.isClosed)
    {
        try
        {
            var pk = self.root.get('poke');

            if (DEBUG) { log('Poke: ' + pk); }

            if (!pk)
            {
                self.root.set('poke', 1);
            }
            else
            {
                self.root.set('poke', pk + 1);
            }
        }
        catch (err)
        {
            self.setGDriveStatus(DriveStatus.Offline);

            self.isClosed = true;
        }
    }
    else if (self.isClosed)
    {
        log('Failed to poke closed document');
    }
};

window.__poke = self.poke;

self.rootChanged = function(e)
{
    if (e.ocHandled || e.isLocal || self.root.id !== e.target.id) { return false; }

    for (var i = 0; i < e.events.length; ++i)
    {
        switch (e.events[i].type)
        {
            case gapi.drive.realtime.EventType.VALUES_ADDED:
            case gapi.drive.realtime.EventType.VALUES_REMOVED:

                break;

            case gapi.drive.realtime.EventType.VALUE_CHANGED:
                {
                    if (e.property === 'dataVersion')
                    {
                        if (e.newValue > CURRENT_VERSION)
                        {
                            g.AppCache.checkForUpdate(/*forceUpdate*/true);
                        }
                        else if (e.newVersion < CURRENT_VERSION)
                        {
                            g.reportError(new Error('Remote setting dataVersion to invalid value' + e.newVersion));
                        }
                    }
                }
                break;

            case gapi.drive.realtime.EventType.TEXT_DELETED:
            case gapi.drive.realtime.EventType.TEXT_INSERTED:
            case gapi.drive.realtime.EventType.VALUES_SET:
            default:
                try{g.PAssert(980,false, 'Unexpected event type');}catch(PERR){g.reportError(PERR);}
                break;
        }
    }
};

self.isConnected = function()
{
    return self.initialized && !self.isClosed && !!self.model;
};

//
// Called once per document load when refreshing the page.
//
self.onLoad = function(doc)
{
    // No Write Ops //

    try
    {
        // TODO: Only clear selection if within the document, leave search selection
        //g.clearSelection();

        try{g.PAssert(981,self.isClosed, 'Document must not already be open when we are loading');}catch(PERR){g.reportError(PERR);}
        self.initialized = true;

        if (DEBUG) log('Loading document from gdrive', log.now());
        //console.timeStamp('Loading Remote Data');

        if (!d)
        {
            d = require('data');
        }

        if (!VMLI)
        {
            VMLI = require('VMLI');
        }

        self.doc = doc;
        self.model = doc.getModel();
        self.root = self.model.getRoot();

        self.root.addEventListener(gapi.drive.realtime.EventType.OBJECT_CHANGED, self.rootChanged);

        self.isClosed = false;

        self.registerForSaveStateChange(self.onSaveStateChange);

        // Ensure the remote schema is up to date
        var stopLoadingRemote = self.migrateData();

        if (!stopLoadingRemote)
        {
            self.loadSettings();

            self.rootItem = self.root.get('root');

            if (!self.rootItem)
            {
                g.messageQueue.pushMessage({
                    text: 'Document is corrupted, ' + platform.actionVerb() + ' to reload. If the problem persists, please contact support.',
                    type: MessageType.Fatal,
                    action: MessageAction.ResetClient
                });

                return;
            }

            //self.rootItem.addEventListener(gapi.drive.realtime.EventType.OBJECT_CHANGED, d.getModel(self.rootItem.id).onObjectChanged.bind(d.getModel(self.rootItem.id)));

            self.rootID = self.rootItem.id;

            self.itemsLoaded = false;

            //self.root.addEventListener(gapi.drive.realtime.EventType.OBJECT_CHANGED, onObjectChanged);


            // Debug only - print the corresponding information stored in google drive
            //printDriveItems(false);

            window.__remoteDoc = doc;

            if (DEBUG)
            {
                window.printRemote = (function () { self.print(/*force*/true); }).bind(self);

                self.print();
            }

            if (undoRedoStateChangeHandler)
            {
                self.registerForUndoRedoStateChange(undoRedoStateChangeHandler);
                undoRedoStateChangeHandler = undefined;
            }

            //VMCollab.init(self.doc);

            if (g.vmMain && g.vmMain.root())
            {
                g.vmMain.loadGDriveData();
            }
            else
            {
                g.requireGDriveDataCallback = true;
            }

            self.loadCollaborators();

            return true;
        }
        else
        {
            log('---- Skipping Document Load! ----');

            self.isClosed = true;
            self.initialized = false;

            self.doc = undefined;
            self.model = undefined;
            self.root = undefined;

            return false;
        }
    }
    catch (err)
    {
        g.reportError(err);
    }
};

self.getServerVersion = function()
{
    if (self.model && !self.isClosed)
    {
        return self.model.serverVersion;
    }

    return undefined;
};

// Replace registration with event listener for gdataSaved
var notifyOfSaveChange = [ ];
self.registerSaveNotify = function(fn)
{
    notifyOfSaveChange.push(fn);
}

var lastPending = false;
var lastSaving = false;
var saveTimeout;
self.onSaveStateChange = function(e)
{
    // No Write Ops //

    if (lastPending !== e.isPending || lastSaving !== e.isSaving)
    {
        if (DEBUG) log('%cStatus - Pending[' + e.isPending + '] Saving[' + e.isSaving + ']', 'color: green');

        if (!e.isSaving && !e.isPending)
        {
            g.vmMain.setGDriveStatus(DriveStatus.Saved);

            d.clearPendingUpdates();

            //g.events.emit('gdataSaved');
            clearTimeout(saveTimeout);
            saveTimeout = undefined;

            g.messageQueue.clearMessage(MessageID.ConnectionError);

            for (var i = 0; i < notifyOfSaveChange.length; ++i)
            {
                notifyOfSaveChange[i]();
            }
        }
        else if (navigator.onLine)
        {
            if (e.isSaving)
            {
                if (!e.isPending)
                {
                    g.vmMain.setGDriveStatus(DriveStatus.Saving);
                }

                clearTimeout(saveTimeout);

                if (platform.mobile)
                {
                    saveTimeout = setTimeout(self.onErrorSaving, 12000);
                }
                else
                {
                    saveTimeout = setTimeout(self.onErrorSaving, 8000);
                }
            }
        }
        else
        {
            g.vmMain.setGDriveStatus(DriveStatus.Offline);
        }

        lastPending = e.isPending;
        lastSaving = e.isSaving;
    }
};

self.onErrorSaving = function()
{
    //g.messageQueue.pushConnectionError();
    g.vmMain.setGDriveStatus(DriveStatus.Offline);
    saveTimeout = undefined;
};

if (DEBUG)
{
    window.remoteGets = { };
}

self.getVersion = function(map, type)
{
    try{g.PAssert(982,!(map instanceof VMLI), 'Must pass in a valid remote item');}catch(PERR){g.reportError(PERR);}

    if (DEBUG)
    {
        var field = ((type === VersionType.Structure) ? 'version' : 'versionText');
        window.remoteGets[field] = (window.remoteGets[field] ? window.remoteGets[field] + 1 : 1);
    }

    var v;
    switch (type)
    {
        case VersionType.Structure:
            v = map.get(g.translateFieldIn('version'));
            break;
        case VersionType.Text:
            v = map.get(g.translateFieldIn('versionText'));
            break;
        default:
            v = -2;
            try{g.PAssert(983,false, 'Invalid version type');}catch(PERR){g.reportError(PERR);}
            break;
    }

    return v ? v : 0;
};

self.get = function(map, field)
{
    try{g.PAssert(984,!(map instanceof VMLI), 'Must pass in a valid remote item');}catch(PERR){g.reportError(PERR);}

    if (DEBUG)
    {
        window.remoteGets[field] = (window.remoteGets[field] ? window.remoteGets[field] + 1 : 1);
    }

    return map.get(g.translateFieldIn(field));
};

self.set = function(map, field, value)
{
    self.beginWriteAction();

    try{g.PAssert(985,!(map instanceof VMLI), 'Must pass in a valid remote item');}catch(PERR){g.reportError(PERR);}

    if (value === undefined)
    {
        value = null;
    }

    return map.set(g.translateFieldIn(field), value);
};

self.delete = function(map, field)
{
    self.beginWriteAction();

    try{g.PAssert(986,!(map instanceof VMLI), 'Must pass in a valid remote item');}catch(PERR){g.reportError(PERR);}

    return map.delete(g.translateFieldIn(field));
};

