self.findInTree = function(item, searched)
{
    // No Write Ops //

    if (!searched)
    {
        searched = self.rootItem;
    }

    function findItemInTree(arr, isArchived)
    {
        if (!arr)
            return false;

        for (var i = 0; i < arr.length; i++)
        {
            var result = arr.get(i);

            if (result.id == item.id)
            {
                return { 'parent': searched, 'index': i, 'isArchived': isArchived };
            }

            var found = self.findInTree(item, result);
            if (found)
            {
                return found;
            }
        }

        return undefined;
    }

    var items = self.get(searched, 'items');

    var foundItem = findItemInTree(items, /*isArchived*/false);

    if (!foundItem)
    {
        var archivedItems = self.get(searched, 'archivedItems');

        foundItem = findItemInTree(archivedItems, /*isArchived*/true);
    }

    return foundItem;
}

return self;
