define('ScreenManager',['ko', 'globals', 'platform'],
function (ko, g, platform) {
function ScreenManager(owner)
{
    this.snapPoints = [ ];
    this.screens = [ ];

    this.activeScreen = -1;
    this.screenAtSwipeStart = -1;
    this._isSwiping = false;

    this.minOffset = 0;
    this.maxOffset = 0;

    this.owner = owner;

    this.useScroll = false;

    this.lastOffset = 0;

    this.updateFunctions = [ ];

    // this.snapComputed = false;

    this.paneWidth = 0.85;

    this.init();
}

if (platform.mobile)
{
    window.Screens =
    {
        Settings:   0,
        Outline:    1,
        List:       2,
        MAX:        2
    };
}
else
{
    window.Screens =
    {
        Outline:    0,
        List:       1,
        MAX:        1
    };
}

ScreenManager.prototype = {
    init: function()
    {
        this.activeScreen = Screens.MAX;
    },
    getActiveScreen: function()
    {
        return this.activeScreen;
    },
    setOwner: function(owner)
    {
        this.owner = owner;
    },
    isSwiping: function()
    {
        return this._isSwiping;
    },
    isScreenOpen: function(index)
    {
        return (this.activeScreen === index);
    },
    computeSnapPoints: function()
    {
        try{g.PAssert(1465,platform.mobile, 'Should only be computing snappoints on mobile');}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(1466,this.owner, 'Must have a valid owner to compute snap points');}catch(PERR){g.reportError(PERR);}

        // if (this.snapComputed)
        // {
        //     return;
        // }

        var totalPercent = this.paneWidth * 3;
        var snapPercents = [ 0, this.paneWidth, this.paneWidth * 2 ];

        var scrollWidth = this.useScroll ? this.owner.scrollWidth : (parseInt(g.width(this.owner)) * totalPercent);

        for (var i = 0; i < snapPercents.length; ++i)
        {
            this.snapPoints[i] = Math.round(snapPercents[i] * (scrollWidth / totalPercent));
            if (DEBUG) log('SnapPoint: [' + i + ']:' + this.snapPoints[i]);
        }

        this.offsetMin = 0;
        this.offsetMax = this.snapPoints[2];

        // this.snapComputed = true;
        this.lastOffset = this.offsetMax;
    },
    // snapToScreen: function(index)
    // {
    //     debugger;
    //     //try{g.PAssert(1467,this.activeScreen !== index, 'Do not set the current screen to the one that is already displayed');}catch(PERR){g.reportError(PERR);}
    //     try{g.PAssert(1468,index <= Screens.MAX, 'Must be a valid screen type');}catch(PERR){g.reportError(PERR);}

    //     this.computeSnapPoints();

    //     if (this.activeScreen !== index)
    //     {
    //         this.notifyScreenOfClose(this.activeScreen);
    //         this.notifyScreenOfOpen(index);
    //     }

    //     var lastActive = this.activeScreen;

    //     this.activeScreen = index;

    //     if (lastActive < 0)
    //     {
    //         return;
    //     }

    //     var snapOffset = this.snapPoints[index];

    //     if (DEBUG) log('Snapping To: ', snapOffset);

    //     this.notifyScreenOfScroll(this.activeScreen, snapOffset, 0);

    //     if (this.useScroll)
    //     {
    //         this.owner.scrollLeft = snapOffset;
    //     }
    //     else
    //     {
    //         for (var i = 0; i < this.screens.length; ++i)
    //         {
    //             var topEle = this.screens[i].getTopElement();

    //             g.setTransform(topEle, -snapOffset, 0, 0);
    //         }
    //     }

    //     this.lastOffset = snapOffset;
    //     log(snapOffset);

    //     return snapOffset;
    // },
    onResize: function()
    {
        if (platform.mobile)
        {
            var index = this.activeScreen;

            this.computeSnapPoints();

            var screenOffset = this.snapPoints[index];

            this.updateScreenOffset(index, screenOffset, 0);
            this.lastOffset = screenOffset;
        }
    },
    scrollToScreen: function(index)
    {
        //try{g.PAssert(1469,this.activeScreen !== index, 'Do not set the current screen to the one that is already displayed');}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(1470,index <= Screens.MAX, 'Must be a valid screen type');}catch(PERR){g.reportError(PERR);}

        // this.computeSnapPoints();

        if (this.activeScreen !== index)
        {
            this.notifyScreenOfClose(this.activeScreen);
            this.notifyScreenOfOpen(index);
        }

        var previousActiveScreen = this.activeScreen;

        this.activeScreen = index;

        var screenOffset = this.snapPoints[index];
        if (DEBUG) log('Scrolling To: ', screenOffset);

        this.notifyScreenOfScroll(previousActiveScreen, screenOffset, 200);

        this.updateScreenOffset(index, screenOffset, 200);

        this.lastOffset = screenOffset;

        return screenOffset;
    },
    updateScreenOffset: function(index, offset, time)
    {
        time = time || 0;
        if (this.useScroll)
        {
            if(time)
            {
                var screenOffset = this.snapPoints[index];
                g.scrollTo(screenOffset, time, this.owner, /*horiz*/true);
            }
            else
            {
                this.owner.scrollLeft = offset;
            }
        }
        else
        {
            // log('update', index, offset, offsetOnScreen);
            for (var i = 0; i < this.screens.length; ++i)
            {
                var topEle = this.screens[i].getTopElement();

                g.setTransform(topEle, -offset, 0, time, 'ease-out');

                var offsetOnScreen = offset - ((i - 1) * platform.windowWidth() * this.paneWidth);
                var perc = (offsetOnScreen / (platform.windowWidth() * this.paneWidth));
                topEle.style.opacity = g.clamp(0.4 + perc * 0.6, 0, 1);
            }
        }

        g.setTransform(g.element('headers'), -offset * (1/ this.paneWidth), 0, time);

        this.lastOffset = offset;
    },
    getCurrentScreenOffset: function()
    {
        if (this.useScroll)
        {
            return this.owner.scrollLeft;
        }
        else
        {
            return this.lastOffset;
        }
    },
    addScreen: function(scr, index)
    {
        try{g.PAssert(1471,index <= Screens.MAX, 'Must be a valid screen type');}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(1472,this.screens[index] === undefined, 'Already have a screen at the given index');}catch(PERR){g.reportError(PERR);}

        if (DEBUG) log('Adding Screen: ', index);

        this.screens[index] = scr;
    },
    getScreen: function(index)
    {
        var scr = this.screens[index];
        try{g.PAssert(1473,scr, 'Must have a valid screen');}catch(PERR){g.reportError(PERR);}

        return scr;
    },
    notifyScreenOfClose: function(index)
    {
        if (index !== -1)
        {
            var scr = this.getScreen(index);

            if (scr.onScreenClose)
            {
                scr.onScreenClose();
            }
        }
    },
    notifyScreenOfScroll: function(index, offset, time)
    {
        if (index !== -1)
        {
            for (var i = 0; i < this.screens.length; ++i)
            {
                var scr = this.getScreen(i);

                if (scr.onScreenScroll)
                {
                    scr.onScreenScroll(offset, time);
                }
            }
        }

        g.fireCustomEvent('screenScroll');
    },
    notifyScreenOfStartOpen: function(index)
    {
        if (index !== -1)
        {
            var scr = this.getScreen(index);

            if (scr.onScreenStartOpen)
            {
                scr.onScreenStartOpen();
            }
        }
    },
    notifyScreenOfOpen: function(index)
    {
        if (index !== -1)
        {
            var scr = this.getScreen(index);

            if (scr.onScreenOpen)
            {
                scr.onScreenOpen();
            }
        }
    },
    startSwipe: function()
    {
        if (!this._isSwiping)
        {
            this._isSwiping = true;

            if (this.activeScreen === 0)
            {
                this.notifyScreenOfStartOpen(1);
            }
            else if (this.activeScreen === Screens.MAX)
            {
                this.notifyScreenOfStartOpen(Screens.MAX - 1);
            }
            else
            {
                // Because we may not know the direction of the swipe, or it may change partway through,
                // notify both of the adjacent panes that they may open so they can update their content.
                this.notifyScreenOfStartOpen(this.activeScreen - 1);
                this.notifyScreenOfStartOpen(this.activeScreen + 1);
            }

            if (DEBUG) log('Starting Swipe');
        }
    },
    updateSwipe: function(diff)
    {
        try{g.PAssert(1474,this.isSwiping(), 'Must be swiping to perform an update');}catch(PERR){g.reportError(PERR);}

        if (platform.ios || platform.android || (platform.ie && platform.mobile))
        {
            var offset = this.getCurrentScreenOffset();

            var newOffset = Math.max(Math.min(offset + diff, this.offsetMax), this.offsetMin);

            // var offsetOnScreen = newOffset - (this.activeScreen * platform.windowWidth() * this.paneWidth);

            // log(offset, newOffset);

            if (offset !== newOffset)// && offsetOnScreen > 0)
            {
                this.updateScreenOffset(this.activeScreen, newOffset);
            }

            //console.log('Updating Swipe: ', newOffset, ' | ', e);
        }
    },
    endSwipe: function()
    {
        try{g.PAssert(1475,this.isSwiping(), 'Must be swiping to end a swipe');}catch(PERR){g.reportError(PERR);}

        // this.computeSnapPoints();

        var currentOffset = this.getCurrentScreenOffset(this.activeScreen);
        var snapIndex = -1;

        var minDiff = 100000;
        for (var i = 0; i < this.snapPoints.length; ++i)
        {
            var snapDiff = Math.abs(this.snapPoints[i] - currentOffset);

            if (snapDiff < minDiff)
            {
                snapIndex = i;
                minDiff = snapDiff;
            }
        }

        this.scrollToScreen(snapIndex);

        if (platform.ios)
        {
            g.preventClick();
        }

        if (this._isSwiping)
        {
            this._isSwiping = false;

            if (DEBUG) log('Ending Swipe');
        }
    },
    checkSwipeStart: function(e)
    {
        var returnValue = false;
        this.screenAtSwipeStart = this.activeScreen;

        // if (this.activeScreen !== Screens.List)
        // {
            if (platform.ie && !platform.mobile)
            {
                // if (platform.mobile)
                // {
                //     // TODO: Correct swiping on mobile IE
                //     return true;
                // }
            }
            else if (platform.mobile)
            {
                if (this.activeScreen !== Screens.List && e.eventX > this.paneWidth * platform.windowWidth())
                {
                    // this.startSwipe();

                    e.preventDefault();

                    returnValue = true;
                }
            }
        // }

        return returnValue;
    },
    checkSwipeMove: function(e)
    {
        var returnValue = false;

        // if (this._isSwiping)
        {
            if (platform.ie && !platform.mobile)
            {
                // if (platform.mobile)
                // {
                //     // TODO: Correct swiping on mobile IE
                //     return true;
                // }
            }
            else if (platform.mobile)
            {
                var hasHoriz = (this.activeScreen !== Screens.MAX && Math.abs(e.diffX) > 5) ||
                               (this.activeScreen === Screens.MAX && e.diffX > 5);//(Math.abs(e.diffX) > 5 && this.activeScreen !== 0 && this.activeScreen !== Screens.MAX) ||
                               // (e.diffX < 5 && this.activeScreen === 0) ||
                               // (e.diffX > 5 && this.activeScreen === Screens.MAX);

                if (DEBUG) log('SwipeMove: ', hasHoriz, this.isSwiping(), e.diffX, e.distanceY);

                if (this.isSwiping() || (Math.abs(e.distanceY) < 5) && hasHoriz)
                {
                    if (!this.isSwiping())
                    {
                        this.startSwipe();
                    }

                    this.updateSwipe(-e.xThisFrame);

                    e.preventDefault();
                    e.stopImmediatePropagation();

                    returnValue = true;
                    // log('true', hasHoriz);
                }
            }
        }

        return returnValue;
    },
    checkSwipeClick: function(e)
    {
        if (this.activeScreen !== Screens.List && e.eventX > this.paneWidth * platform.windowWidth())
        {
            this._isSwiping = false;
            this.scrollToScreen(this.activeScreen + 1);
            return true;
        }
        return this._isSwiping;
    },
    checkSwipeEnd: function(e)
    {
        var returnValue = false;

        if (this.activeScreen == this.screenAtSwipeStart)// && this.activeScreen !== Screens.List)
        {
            if (platform.ie && !platform.mobile)
            {
                // if (platform.mobile)
                // {
                //     // TODO: Correct swiping on mobile IE
                //     return true;
                // }
            }
            else if (platform.mobile)
            {
                if (this.isSwiping())
                {
                    this.endSwipe();
                }
            }
        }
        else if(this.activeScreen != this.screenAtSwipeStart)
        {
            returnValue = true;
            log('return value is true');
        }

        return returnValue;
    }
};

return ScreenManager;
});

