define('VMAutocompleteItem',['ko', 'data', 'globals', 'platform'],
function (ko, d, g, platform) {
function VMAutocompleteItem(p)
{
    this.id = p.id;
    this.text = p.text || '';
    this.object = p.object;
    this.index = p.index;
    this.isSelected = ko.observable(false);
    this.element = undefined;

    this.init();
}

VMAutocompleteItem.prototype = {
    init: function()
    {
    },
    html: function()
    {
        // Wrap a <b> tag around the autocomplete search part of the text
        var searchText = g.autocomplete.text();
        var index = this.text.toLowerCase().indexOf(searchText.toLowerCase());

        var newText = '';

        if (index >= 0)
        {
            newText = this.text.substr(0, index) + '<b>' + this.text.substr(index, searchText.length) + '</b>' + this.text.substr(index + searchText.length);;
        }

        return newText;
    },
    onLoad: function(element)
    {
        this.element = element;
    }
    // onTap:
    // {
    //     onClick: function(e)
    //     {
    //         g.autocomplete.onTapped(this.text);
    //     }
    // }
};

return VMAutocompleteItem;
});
