define('VMDocument',['ko', 'globals', 'platform', 'data', 'goog', 'tracker'],
function (ko, g, platform, d, goog, tracker) {
function VMDocument(p)
{
    this.file = p;
    this.id = p && p.id;
    this.title = ko.observable(p && p.title || '');
    this.shared = p && p.shared;
    this.isOpen = undefined;
    //this.isEditable = ko.observable(false);
    this.isOwned = ko.observable(false);

    this.init();
}

VMDocument.prototype =
{
    init: function()
    {
        if(!this.file)
        {
            return;
        }

        if (this.file.userPermission.id === 'me' && this.file.userPermission.role === 'owner')
        {
            this.isOwned(true);
        }
    },
    onTapMenu: function()
    {
        this.openSharing();

        g.sendEvent('Menu', 'OpenSharing');

        tracker.miscAction({
            type: TrackerType.Misc,
            misc: TrackerMisc.OpenDocSharing,
            data: [
            {
                value: true
            }]
        });
    },
    openSharing: function(callback)
    {
        g.menus.openModal('shareDoc', this, callback);
    },
    closeMenu: function()
    {
        g.menus.closeContextMenu(g.element('contextMenuDocument'));
    },
    onTapContextMenu: function(e)
    {
        var id = e.srcElement.id;
        if (!id)
        {
            id = e.srcElement.parentNode.id;
        }
        switch (id)
        {
            case 'documentDelete':

                break;
            case 'documentShare':

                break;
        }
        this.closeMenu();
    },
};

return VMDocument;
});

