define('platform',['ko', 'util'],
function(ko, util) {

window.Orientation =
{
    Portrait: 0,
    Landscape: 1
};

function platform()
{
    this.versionRequired = 4;
    this.requireCustomRedirect = false;
    this.b = document.documentElement || document.body;
    this.orientation = Orientation.Portrait;
    this.windowWidth = ko.observable(0);
    this.windowHeight = ko.observable(0);
    this.kbsize = undefined;
    this.kbsizePortrait = undefined;
    this.kbsizeLandscape = undefined;
    this.hasBeenPortrait = false;
    this.iframeApp = undefined;
    this.appState = undefined;

    this.isDevURL = (window.location.hostname === 'dev.moo.do');

    this.init();

    var useLocationHost = DEBUG || this.isDevURL || (this.app && this.debugApp) || (!this.app && this.standalone);

    this.customRedirectString = window.location.protocol + '//' + (useLocationHost ? window.location.host : 'www.moo.do');

    if (this.app)
    {
        this.customRedirectState =
        {
            app: true,
            appv: this.versionRequired
        };
    }

    if (this.app || this.android)
    {
        this.keyboardOpenTime = 200;
    }
    else if (this.winphone)
    {
        this.keyboardOpenTime = 200;
    }
    else if (this.mobileie)
    {
        this.keyboardOpenTime = 600;
    }
    else
    {
        this.keyboardOpenTime = 0;
    }
}

platform.prototype =
{
    getPlatformInfo: function(obj)
    {
        obj['os'] = this.OSName;
        obj['browser'] = this.browser;
        obj['appVersionRequired'] = this.versionRequired;
        obj['app'] = this.app;
        obj['standalone'] = this.standalone;
        obj['packagedApp'] = this.packagedApp;
        obj['requireCustomRedirect'] = this.requireCustomRedirect;
        obj['isMobile'] = this.mobile;
        obj['isPhone'] = this.phone;
        obj['isTouch'] = this.touch;
        obj['isTouchDevice'] = this.isTouchDevice;
        obj['usePointerEvents'] = this.usePointerEvents;
        obj['kbsize'] = this.kbsize;
        obj['appState'] = this.appState;
        obj['bodyClass'] = this.b.className;
        obj['rawUA'] = navigator.userAgent;
    },
    init: function()
    {
        this.initAppState();

        var userAgent = navigator.userAgent;

        var bodyClass = '';

        // OS Detection
        var OSName = 'Unknown OS';

        if (this.app)
        {
            bodyClass += " app";
        }

        if (userAgent.indexOf('Win') !== -1)
        {
            this.OSName = 'Windows';
            this.windows = true;

            if (userAgent.indexOf('Windows Phone') !== -1)
            {
                this.phone = true;
                this.touch = true;
                this.mobile = true;
            }
        }
        else if (userAgent.indexOf('iPad') !== -1)
        {
            this.OSName = 'iPad';
            this.ios = true;
            this.ipad = true;
            this.phone = true;
            this.touch = true;
            this.mobile = true;
        }
        else if (userAgent.indexOf('iPhone') !== -1)
        {
            this.OSName = 'iPhone';
            this.ios = true;
            this.iphone = true;
            this.phone = true;
            this.mobile = true;
            this.touch = true;
        }
        else if (userAgent.indexOf('iPod') !== -1)
        {
            this.OSName = 'iPod';
            this.ios = true;
            this.ipod = true;
            this.phone = true;
            this.mobile = true;
            this.touch = true;
        }
        else if (userAgent.indexOf('Mac') !== -1)
        {
            this.OSName = 'MacOS';
            this.mac = true;
        }
        else if (userAgent.indexOf('X11') !== -1)
        {
            this.OSName = 'UNIX';
            this.unix = true;
        }
        else if (userAgent.indexOf('Android') !== -1)
        {
            this.OSName = 'Android';
            this.android = true;
            this.touch = true;
            this.mobile = true;
            this.phone = true;

            bodyClass += " android";
        }
        else if (userAgent.indexOf('Linux') !== -1)
        {
            this.OSName = 'Linux';
            this.linux = true;
        }

        if (this.ios)
        {
            this.ios7 = userAgent.indexOf('OS 7') !== -1;
            this.ios70 = userAgent.indexOf('OS 7_0') !== -1;
            this.ios71 = userAgent.indexOf('OS 7_1') !== -1;

            if (this.ios7)
            {
                bodyClass += " ios7";
            }
        }

        this.majorVersion = 1000;
        this.minorVersion = 1000;

        if (userAgent.indexOf('MSIE') !== -1 || userAgent.indexOf('Trident') !== -1)
        {
            this.browser = 'Trident';
            this.ie = true;

            this.winphone = !!userAgent.match(/IEMobile/i);

            var re;
            if (this.winphone || navigator.appName === 'Microsoft Internet Explorer')
            {
                re = new RegExp("MSIE ([0-9]{1,}[\.0-9]{0,})");
            }
            else
            {
                re = new RegExp("Trident/.*rv:([0-9]{1,}[\.0-9]{0,})");
            }

            if (re.exec(userAgent) != null)
            {
                this.majorVersion = parseFloat(RegExp.$1);
            }
        }
        else if (userAgent.indexOf('Chrome') !== -1 || userAgent.indexOf('CriOS') !== -1)
        {
            this.browser = 'Chrome';
            this.isChrome = true;

            var re = new RegExp('Chrome/([0-9]{1,})\.');

            if (re.exec(userAgent) != null)
            {
                this.majorVersion = parseFloat(RegExp.$1);
            }
        }
        else if (userAgent.indexOf('Android') !== -1)
        {
            this.browser = 'Native Android';

            var re = new RegExp('Linux; Android ([0-9]{1,})\.([0-9]{1,})\.');

            if (re.exec(userAgent) != null)
            {
                this.majorVersion = parseFloat(RegExp.$1);
                this.minorVersion = parseFloat(RegExp.$2);
            }
        }
        else if (userAgent.indexOf('Firefox') !== -1)
        {
            this.browser = 'Firefox';

            var re = new RegExp('Firefox/([0-9]{1,})\.');

            if (re.exec(userAgent) != null)
            {
                this.majorVersion = parseFloat(RegExp.$1);
            }
        }
        else if (userAgent.indexOf('Safari') !== -1 || this.OSName === 'iPad' || this.OSName === 'iPhone' || this.OSName === 'iPod')
        {
            this.browser = 'Safari';

            var re;

            if (this.mobile)
            {
                re = new RegExp('OS ([0-9]{1,})\_([0-9]{1,})');
            }
            else
            {
                re = new RegExp('Version/([0-9]{1,})\.([0-9]{1,})\.');
            }

            if (re.exec(userAgent) != null)
            {
                this.majorVersion = parseFloat(RegExp.$1);
                this.minorVersion = parseFloat(RegExp.$2);
            }
        }
        else if (userAgent.indexOf('Opera') !== -1)
        {
            this.browser = 'Opera';
        }

        this.isTouchDevice = !!(('ontouchstart' in window) || window.navigator.msMaxTouchPoints);

        this.usePointerEvents = !!window.navigator.msPointerEnabled;

        log('Touch Device: ' + this.isTouchDevice + ' Pointer Events: ' + this.usePointerEvents);

        log('Browser: ' + this.browser + '  Major Version: ' + this.majorVersion + '  Minor Version: ' + this.minorVersion);

        if (this.isTouchDevice)
        {
            if (this.usePointerEvents)
            {
                // Use pointer events
                this.touchStartEvent = 'MSPointerDown';
                this.touchMoveEvent = 'MSPointerMove';
                this.touchEndEvent = 'MSPointerUp';
                this.touchCancelEvent = 'MSPointerCancel';
            }
            else
            {
                // Use touch events
                this.touchStartEvent = 'touchstart';
                this.touchMoveEvent = 'touchmove';
                this.touchEndEvent = 'touchend';
                this.touchCancelEvent = 'touchcancel';
            }
        }

        if (this.usePointerEvents)
        {
            this.downEvent = 'MSPointerDown';
            this.moveEvent = 'MSPointerMove';
            this.upEvent = 'MSPointerUp';
        }
        else
        {
            this.downEvent = 'mousedown';
            this.moveEvent = 'mousemove';
            this.upEvent = 'mouseup';
        }

        try
        {
            var forceFullUI = !!util.storage.getItem('forceFullUI');
        }
        catch (err)
        {
            // Do Nothing
        }

        // TODO: Being a touch device does not automatically mean phone/mobile
        if (util.hasURLParam('touch', 'false') || forceFullUI)
        {
            this.touch = false;
            this.phone = false;
            this.mobile = false;
            this.ios = false;
            this.android = false;
        }
        else if (this.isTouchDevice || util.hasURLParam('touch', 'true'))
        {
            this.touch = true;
            this.phone = true;
            this.mobile = true;
        }

        if (this.windows)
        {
            bodyClass += " windows";

            if (this.ie)
            {
                bodyClass += " ie";

                if (!this.demo)
                {
                    this.mobro = this.isMobro();
                }
            }
            else if (this.browser === 'Firefox')
            {
                bodyClass += " firefox";
            }
        }

        if (this.phone)
        {
            bodyClass += " phone";
            // if(!this.ios7 || this.app || this.browser != 'Safari')
            // {
                bodyClass += ' nonbodyscroll';
                this.bodyscroll = false;
            // }
            // else
            // {
            //     bodyClass += ' bodyscroll';
            //     this.bodyscroll = true;
            // }
        }

        if(this.ipad)
        {
            bodyClass += ' ipad';
        }

        this.mobileie = this.ie && this.mobile;

        if (this.mobile && !this.app)
        {
            this.calcKeyboardSize();
        }

        this.cmdChar = this.windows ? 'ctrl' : '⌘';

        this.noErrorStacks = platform.OSName === 'iPad' || platform.OSName === 'iPhone' || platform.OSName === 'iPod';

        this.packagedApp = (window.location.protocol === 'chrome-extension:');
        this.standalone = !!window.navigator.standalone || this.packagedApp || (window.navigator.userAgent.indexOf('FluidApp') >= 0);

        if (this.standalone || this.app || this.mobro)
        {
            this.requireCustomRedirect = true;
        }

        if (bodyClass && bodyClass.length > 0)
        {
            this.b.className += bodyClass;
        }

        this.transition = this.addPrefix('transition');
        this.animation = this.addPrefix('animation');
        this.transform = this.addPrefix('transform');
        this.transformOrigin = this.addPrefix('transformOrigin'); // This will not have a valid 'prop' value
        //this.transitionEnd = this.addPrefix('transitionEnd');

        window.requestAnimationFrame = window.requestAnimationFrame ||
                                   window.webkitRequestAnimationFrame ||
                                   window.mozRequestAnimationFrame ||
                                   window.msRequestAnimationFrame ||
                                   window.oRequestAnimationFrame ||
                                   function(callback){ window.setTimeout(callback, 1000/60); };

        if (this.ie)
        {
            document.documentElement.addEventListener("MSHoldVisual", function(e) { e.preventDefault(); }, false);
            document.documentElement.addEventListener("contextmenu", function(e) { e.preventDefault(); }, false);
        }

        document.documentElement.addEventListener('keydown', this.normalizeKeys, true);
        document.documentElement.addEventListener('keypress', this.normalizeKeys, true);
        document.documentElement.addEventListener('keyup', this.normalizeKeys, true);
    },
    isMobro: function()
    {
        var supported = null;

        try
        {
            new ActiveXObject("");
        }
        catch (e)
        {
            // FF has ReferenceError here
            errorName = e.name;
        }

        try
        {
            supported = !!new ActiveXObject("htmlfile");
        }
        catch (e)
        {
            supported = false;
        }

        if (errorName != 'ReferenceError' && supported==false)
        {
            supported = false;
        }
        else
        {
            supported =true;
        }

        return !supported;
    },
    calcKeyboardSize: function()
    {
        if(this.ios)
        {
            if(this.iphone || this.ipod)
            {
                this.kbsizePortrait = 216;
                this.kbsizeLandscape = 162;

                if(this.ios71)
                {
                    this.kbsizePortrait += 44;
                    this.kbsizeLandscape += 44;
                }
            }
            else if(this.ipad)
            {
                this.kbsizePortrait = 264;
                this.kbsizeLandscape = 352;

                this.kbsizePortrait += 44;
                this.kbsizeLandscape += 44;
            }
        }
        else if (this.android || this.winphone)
        {
            this.kbsizePortrait = 0;
            this.kbsizeLandscape = 0;
        }
        else if (this.mobileie)
        {
            this.kbsizePortrait = 369;
            this.kbsizeLandscape = 492;
        }

    },
    normalizeKeys: function(ev)
    {
        if (ev.normCode === undefined)
        {
            if (ev.keyCode === null || ev.keyCode === 0)
            {
                ev.normCode = ev.charCode != null ? ev.charCode : ev.which;
            }
            else
            {
                ev.normCode = ev.keyCode;
            }
        }
    },
    initAppState: function()
    {
        var appState = location.hash.substring(1);
        if (appState)
        {
            var regex = /([^&=]+)=([^&]*)/g,
                        m;
            while (m = regex.exec(appState))
            {
                if (decodeURIComponent(m[1]) === 'state')
                {
                    var uri = decodeURIComponent(m[2]);
                    if (uri)
                    {
                        try
                        {
                            appState = JSON.parse(uri);
                        }
                        catch (err)
                        {
                            log('Error parsing AppState: ', uri);

                            debugger;

                            appState = undefined;
                        }

                        if (appState)
                        {
                            this.app = (appState.app === true);
                            this.appv = appState.appv;
                            this.debugApp = appState.debug;
                            this.live = appState.live;
                            this.offline = appState.offline;
                            this.syncContacts = appState.syncContacts;
                            this.test = appState.test;
                            this.appState = appState;
                        }
                    }

                    break;
                }
            }
        }

        this.live = this.live || util.hasURLParam('live', 'true');
        this.test = DEBUG && (this.test || util.hasURLParam('test'));
        this.offline = this.offline || this.test || util.hasURLParam('offline', 'true') || (DEBUG && DEBUG);
        this.demo = util.hasURLParam('demo', 'true') || (DEBUG && DEBUG);
        this.script = util.hasURLParam('script');
    },
    addPrefix: function(p)
    {
        var prefix = '';
        var v = [ 'ms', 'webkit', 'moz', 'o' ];
        var s = this.b.style;
        if (typeof s[p] == 'string')
        {
            prefix = '';
        }
        else
        {
            for ( var i = 0; i < v.length; i++) {
                if (typeof s['-' + v[i] + '-' + p] == 'string') {
                    prefix = v[i];
                    break;
                }
            }
        }

        var styleString = (prefix.length > 0) ? p.charAt(0).toUpperCase() + p.slice(1) : p;

        if (prefix)
        {
            return { prop: '-' + prefix + '-' + p, style: prefix + styleString };
        }
        else
        {
            return { prop: p, style: styleString };
        }
    },
    blockClient: function ()
    {
        // switch (this.OSName)
        // {
        //     //case 'iPad':
        //     // case 'iPhone':
        //         //platformName = 'the ' + this.OSName;
        //         //break;
        //     // case 'Android':
        //     //     if (!DEBUG)
        //     //     {
        //     //         platformName = this.OSName;
        //     //     }
        //     //     break;
        // }

        // Features:
        //  calc
        //    Firefox: 16
        //    Chrome: 26
        //    IE: 9
        //    Safari: 6.1
        //    iOS: 6.0
        //    Android: 4.4
        //  webworkers (optional)
        //    Firefox: 4
        //    Chrome: 4
        //    IE: 10
        //    Safari: 4.0
        //    iOS: 5.0
        //    Android: 4.4
        //  AppCache
        //    Firefox: 4
        //    Chrome: 4
        //    IE: 10
        //    Safari: 4.0
        //    iOS: 3.2
        //    Android: 2.1
        //  localStorage
        //    Firefox: 4
        //    Chrome: 4
        //    IE: 8
        //    Safari: 4.0
        //    iOS: 3.2
        //    Android: 2.1
        //  indexedDB
        //    Firefox: 16
        //    Chrome: 24
        //    IE: 10
        //    Safari: 8.0
        //    iOS: 8.0
        //    Android: 4.4
        //  webSQL
        //    Firefox: N/A
        //    Chrome: 4
        //    IE: N/A
        //    Safari: 3.1
        //    iOS: 3.2
        //    Android: 2.1
        //  transform
        //    Firefox: 4
        //    Chrome: 4
        //    IE: 9
        //    Safari: 3.1
        //    iOS: 3.2
        //    Android: 2.1

        switch (this.browser)
        {
            case 'Firefox':
                if (this.majorVersion < 16)
                {
                    return { platform: 'Firefox', browser: this.browser, update: true };
                }
                break;
            case 'Chrome':
                if (this.majorVersion < 26)
                {
                    return { platform: 'Chrome', browser: this.browser, update: true };
                }
                break;
            case 'Native Android':
                if (this.majorVersion === 2 && this.minorVersion < 1 || this.majorVersion < 2)
                {
                    return { platform: 'Native Android', browser: 'NativeAndroid', update: true };
                }
                break;
            case 'Opera':
                return { platform: this.browser, browser: this.browser, update: false };
            case 'Trident':
                // Disable IE below 10 and mobile IE unless demo
                if (!DEBUG && !this.isDevURL)
                {
                    if (this.majorVersion < 10 || (this.phone && !this.demo))
                    {
                        return { platform: 'Internet Explorer', browser: this.browser, update: (this.majorVersion < 10) };
                    }
                }

                break;
            case 'Safari':
                if (this.mobile && this.majorVersion < 6)
                {
                    return { platform: 'Safari', browser: this.browser, update: true };
                }
                else if(!this.mobile && ((this.majorVersion == 6 && this.minorVersion < 1) || this.majorVersion < 6))
                {
                    return { platform: 'Safari', browser: this.browser, update: true };
                }
                break;
        }

        if (this.app)
        {
            if (this.appv < this.versionRequired)
            {
                // JS  expects a version great than app is sending
                log('App Version out of date.');

                return { version: this.versionRequired };
            }
            else if (this.appv > this.versionRequired)
            {
                // App is sending a greater version than JS expects
                log('App is newer than the javascript, forcing an app cache update');

                return { cacheUpdate: true, error: false };
            }
        }

        if (!DEBUG && '1403041791456' !== document.body.getAttribute('ver'))
        {
            // If the page has a version mismatch, do not infinitely loop page refresh.
            var cErr = util.hasURLParam('cacheError');
            require(['globals'], function (g)
            {
                g.reportError(new Error('VersionMismatch, CacheError: ' + cErr));
            });
            if (!cErr)
            {
                this.sendToApp('resetCache');
                return { cacheUpdate: true, error: true };
            }
        }
    },
    actionVerb: function ()
    {
        return this.touch ? 'tap' : 'click';
    },
    actionVerbCaps: function ()
    {
        return this.touch ? 'Tap' : 'Click';
    },
    updateOrientation: function ()
    {
        var orientation = (Math.abs(window.orientation) == 90) ? Orientation.Landscape: Orientation.Portrait;
        if(this.orientation == Orientation.Portrait)
        {
            this.hasBeenPortrait = true;
        }

        this.orientation = orientation;

        if(!this.app)
        {
            this.kbsize = this.orientation == Orientation.Portrait ? this.kbsizePortrait : this.kbsizeLandscape;
        }

        return this.orientation;
    },
    sendToApp: function(method, data)
    {
        // alert('Send To App: ' + data);

        if (this.app)
        {
            if (this.ios)
            {
                if(!this.iframeApp)
                {
                    this.iframeApp = document.createElement("IFRAME");
                    this.iframeApp.setAttribute("height", "1px");
                    this.iframeApp.setAttribute("width", "1px");
                    document.documentElement.appendChild(this.iframeApp);
                }

                var href = 'duchess://' + method;
                if(data !== undefined)
                {
                    href += '//' + data;
                }

                this.iframeApp.setAttribute("src", href);
            }
            else if (this.android && AppInterface !== undefined)
            {
                AppInterface.addNotification(data);
            }
        }
    }
};

return new platform();
});
