define('VMMessageQueue',['ko', 'globals', 'util', 'platform', 'data', 'VMMessage'],
function (ko, g, util, platform, d, VMMessage) {

//
// Messages can contain the following fields and are a plan object. They should not contain a 'clear' field
// as this is used internally. At some point we should switch to using a 'createMessage' function on the queue
// which would return a valid message object instead of directly passing in any old object.
//
// text: The message text to display.
// type: The type of message, defined in window.MessageType. This affects the styling of the message.
// action: What to do when the message itself is clicked/tapped (not ignored).
// block: Prevent further interaction with the app until the user has refreshed the page.
//

function VMMessageQueue()
{
	this.queue = ko.observableArray();
    this.remoteMessages = { };

    this.defaultMessages = [ ];
    this.defaultMessages[MessageID.Undefined] = undefined;


    this.hasConnectionError = false;
    this.hasFatalError = false;
    this.hasDocChange = false;
    this.hasOfflineWarning = false;
    this.hasOnlineInfo = false;
    this.hasCacheReset = false;
    this.hasRequiredUpdate = false;

    this.currentFilter = 0x0;

    this.FilterType =
    {
        Mobile:         0x1,
        Desktop:        0x2,
        App:            0x4,
    };

    this.init();
}

VMMessageQueue.prototype = {
    init: function ()
    {
        // setTimeout(function ()
        // {
            //g.removeClass(document.getElementById('overlayArea'), 'notVisible');

            // this.pushMessage({
            //     text: 'Debug message to test the message queue.',
            //     type: MessageType.Fatal,
            //     action: MessageAction.Refresh
            // });
        //}.bind(this), 100);

        g.applyBindings(this, 'overlayArea');

        if (!platform.demo && !DEBUG && !platform.offline)
        {
            setTimeout(this.updateRemoteMessages.bind(this), 5000);
        }

        this.computeFilter();
    },
    getMessageById: function(id)
    {
        if (this.defaultMessages[id] === undefined)
        {
            this.initMessageByid(id);
        }

        try{g.PAssert(1454,this.defaultMessages[id] !== undefined, 'After init message should always be defined');}catch(PERR){g.reportError(PERR);}

        return this.defaultMessages[id];
    },
    testMessageById: function(id)
    {
        return !!this.defaultMessages[id];
    },
    initMessageByid: function(id)
    {
        switch (id)
        {
            case MessageID.ConnectionError:
                this.defaultMessages[id] = new VMMessage({
                    text: 'Connection error detected, ' + platform.actionVerb() + ' here to reconnect.',
                    type: MessageType.Error,
                    action: MessageAction.Reload
                }, this);
                break;
            case MessageID.OfflineUndo:
                this.defaultMessages[id] = new VMMessage({
                    text: 'Offline undo/redo has been disabled temporarily',
                    type: MessageType.Info
                }, this);
                break;

            case MessageID.DriveError:
                this.defaultMessages[id] = new VMMessage({
                    text: 'There was an error connecting to Google Drive. Moo.do will reconnect automatically when Google Drive is available.',
                    type: MessageType.Error,
                    action: MessageAction.None
                }, this);
                break;

            case MessageID.FatalError:
                this.defaultMessages[id] = new VMMessage({
                    text: 'There was a fatal error in the application, ' + platform.actionVerb() + ' here to restart.',
                    type: MessageType.Error,
                    action: MessageAction.Reload
                }, this);
                break;
            case MessageID.DocChange:
                this.defaultMessages[id] = new VMMessage({
                    text: 'The active document was changed in another tab, ' + platform.actionVerb() + ' here to restart.',
                    type: MessageType.Fatal,
                    action: MessageAction.Reload,
                    block: true
                }, this);
                break;

            case MessageID.OfflineWarn:
                this.defaultMessages[id] = new VMMessage({
                    text: 'You are offline and not saving your changes locally or remotely, you must reconnect before closing the window or you will lose your changes.',
                    type: MessageType.Warning
                }, this);
                break;

            case MessageID.OnlineInfo:
                this.defaultMessages[id] = new VMMessage({
                    text: 'You have gone back online, your changes have been saved.',
                    type: MessageType.Info,
                    timeout: 10000
                }, this);
                break;

            case MessageID.CacheReset:
                this.defaultMessages[id] = new VMMessage({
                    text: 'To fix your issues we are going to clear the local cache. This will reload the page and your data, ' + platform.actionVerb() + ' here to continue.',
                    type: MessageType.Info,
                    action: MessageAction.ResetClient
                }, this);
                break;

            case MessageID.RequiredUpdate:
                this.defaultMessages[id] = new VMMessage({
                    text: 'A software update is required to continue, ' + platform.actionVerb() + ' to update.',
                    type: MessageType.RequiredUpdate,
                    action: MessageAction.Reload,
                    block: true
                }, this);
                break;

            case MessageID.Updated:
                this.defaultMessages[id] = new VMMessage({
                    text: 'An update was installed. ' + platform.actionVerbCaps() + ' to reload.',
                    type: MessageType.RequiredUpdate,
                    action: MessageAction.Reload,
                    bottom: true
                }, this);
                break;

            case MessageID.MobileOffline:
                this.defaultMessages[id] = new VMMessage({
                    text: 'OFFLINE',
                    type: MessageType.MobileOffline,
                    action: MessageAction.None,
                    bottom: true
                }, this);
                break;
            default:
                try{g.PAssert(1455,false, 'Invalid MessageID');}catch(PERR){g.reportError(PERR);}
                break;
        }
    },
    pushMessage: function (msg)
    {
        var vmMsg;
        if (msg instanceof VMMessage)
        {
            vmMsg = msg;
        }
        else if (g.isObject(msg))
        {
            vmMsg = new VMMessage(msg, this);
        }
        else if (g.isString(msg))
        {
            vmMsg = new VMMessage({ text: msg }, this);
        }
        else
        {
            vmMsg = this.getMessageById(msg);

            if (!vmMsg)
            {
                try{g.PAssert(1456,false, 'Invalid data passed to message creation');}catch(PERR){g.reportError(PERR);}

                log('Message not created properly', msg);
            }
        }

        if (vmMsg)
        {
            vmMsg.show();
        }

        return vmMsg;
    },
    clearMessage: function(msg)
    {
        var vmMsg;
        if (msg instanceof VMMessage)
        {
            vmMsg = msg;
        }
        else
        {
            if (this.testMessageById(msg))
            {
                vmMsg = this.getMessageById(msg);
            }
        }

        if (vmMsg)
        {
            vmMsg.hide();
        }

        return vmMsg;
    },
    onMessageShow: function(msg)
    {
        this.queue.push(msg);
    },
    onMessageHide: function(msg)
    {
        try{g.PAssert(1457,msg, 'Must pass in a valid message to be removed from the queue');}catch(PERR){g.reportError(PERR);}

        var index = this.queue.indexOf(msg);
        if (index < 0)
        {
            try{g.PAssert(1458,false, 'Trying to remove an invalid message from the queue');}catch(PERR){g.reportError(PERR);}
            return;
        }

        this.queue.splice(index, 1);
    },
    // clearMessages: function ()
    // {
    //     var q = this.queue();
    //     for (var i = 0; i < q.length; ++i)
    //     {
    //         q[i].hide(/*noNotify*/true);
    //     }

    //     this.queue([]);
    // },
    // replaceMessagesWith: function(msg)
    // {
    //     var q = this.queue();

    //     for (var i = 0; i < q.length; ++i)
    //     {
    //         q[i].hide(/*noNotify*/true);
    //     }

    //     this.queue([msg]);

    //     return msg;
    // },

    //
    // Remote Messages
    //

    computeFilter: function ()
    {
        if (platform.isMobile) { this.currentFilter |= this.FilterType.Mobile; }
        if (!platform.isMobile) { this.currentFilter |= this.FilterType.Desktop; }
        if (platform.app) { this.currentFilter |= this.FilterType.App; }
    },

    updateRemoteMessages: function()
    {
        if (!g.isDemoMode())
        {
            var lastMessageSync = g.settings.get(Settings.lastMessageSync);

            var syncDateString = '';
            if (lastMessageSync)
            {
                syncDateString = '?lastSync=' + new Date(lastMessageSync).toString("yyyy-MM-ddTHH:mm:ss");
            }

            var newSyncTime = new Date();

            g.XHR({
                type: 'GET',
                url: window.location.protocol + '//api.moo.do/debug/getMessages' + syncDateString,
                cb: function (xhr)
                {
                    if (xhr.status === 200)
                    {
                        g.settings.set(Settings.lastMessageSync, newSyncTime);

                        var messageData = JSON.parse(xhr.responseText);

                        if (messageData)
                        {
                            var messages = messageData.m;
                            for (var i = 0; messages && i < messages.length; ++i)
                            {
                                var newMsg = messages[i];

                                var currentMessage = this.remoteMessages[newMsg.id];

                                if (!currentMessage)
                                {
                                    var showMessage = true;

                                    if (newMsg.filter)
                                    {
                                        showMessage = ((newMsg.filter & this.currentFilter) !== 0);

                                        log('Remote Message Filter: ' + newMsg.filter + ' ' + this.currentFilter + ' ' + showMessage);
                                    }

                                    if (showMessage)
                                    {
                                        this.pushMessage({
                                            text: newMsg.data,
                                            type: newMsg.type,
                                            action: MessageAction.Default
                                        });
                                    }

                                    this.remoteMessages[newMsg.id] = newMsg;
                                }
                            }
                        }
                    }
                    else
                    {
                        log('Unable to get remote messages', xhr);
                    }
                }.bind(this)
            });
        }
    }
};

return VMMessageQueue;
});
