self.getTransitionClass = function(time)
{
    var trans;
    // Use the correct css class for this animation time
    switch(time)
    {
        case 50:
            trans = 'trans05';
            break;
        case 100:
            trans = 'trans1';
            break;
        case 200:
            trans = 'trans2';
            break;
        case 300:
            trans = 'trans3';
            break;
        case 400:
            trans = 'trans4';
            break;
        case 500:
            trans = 'trans5';
            break;
        case 1000:
            trans = 'trans10';
            break;
        default:
            console.log('ERROR: no time for ', time);
            break;
    }
    return trans;
};

self.setTransformStyle = function(element, x, y)
{
    try{self.PAssert(743,element, 'Must provide a valid element');}catch(PERR){g.reportError(PERR);}

    if (element)
    {
        try{self.PAssert(744,x !== undefined, 'Must supply a valid X offset');}catch(PERR){g.reportError(PERR);}
        try{self.PAssert(745,y !== undefined, 'Must supply a valid Y offset');}catch(PERR){g.reportError(PERR);}

        if (!platform.ie)
        {
            element.style[platform.transform.style] = 'translate3d(' + x + 'px, ' + y + 'px, 0px)';
        }
        else
        {
            element.style[platform.transform.style] = 'translate('+ x + 'px' + ', ' + y + 'px)';
        }
    }
};

self.setTransitionStyle = function(element, property, time, easing)
{
    try{self.PAssert(746,element, 'Must provide a valid element');}catch(PERR){g.reportError(PERR);}

    if (element)
    {
        easing = easing || 'ease';

        element.style[platform.transition.style] = property + ' ' + time + 'ms ' + easing;

        setTimeout(function()
        {
            element.style[platform.transition.style] = '';
        }, time);
    }
};

var IE_ANIM_WORKAROUND = platform.ie;

self.setTransform = function(element, x, y, time, easing)
{
    if (time > 0)
    {
        if (IE_ANIM_WORKAROUND)
        {
            setTransformStyle(element, x, y);
        }
        else
        {
            self.setTransitionStyle(element, platform.transform.prop, time, easing);

            self.setTransformStyle(element, x, y);
        }
    }
    else
    {
        if (x || y)
        {
            self.setTransformStyle(element, x, y);
        }
        else
        {
            element.style[platform.transform.style] = 'none';
        }
    }
};

self.reload = function()
{
    try{self.PAssert(747,!appCacheUpdating, 'Page should not be reloaded while waiting for an appcache update to finish.');}catch(PERR){g.reportError(PERR);}

    if (appCacheUpdating)
    {
        self.reportError(new Error('Page requesting reload while waiting for AppCache update.'));
    }

    window.location.reload();
};

self.isSetupPageOpen = false;
self.isMainPageOpen = false;
self.openMainPage = function()
{
    // TODO: This function should only set CSS classes if they need to change. As it is now,
    //       we generally set classes even when not necessary multiple times on page load.

    if (self.isSetupPageOpen)
    {
        self.isSetupPageOpen = false;
    }

    if (!self.isMainPageOpen)
    {
        self.isMainPageOpen = true;
    }

    var outer = document.getElementById('outerWrapper');
    var setup = document.getElementById('setupWrapper')

    if (outer && setup)
    {
        self.removeClass(outer, 'none');
        self.addClass(setup, 'none');
    }
};

