    shownTutorial: 'shownTutorial',             // track whether the tutorial was shown on this device
    gdocId: 'gdocId',                           // track the currently open document id
    gdocTitle: 'gdocTitle',                     // track the currently open document title
    activeUser: 'activeUser',                   // track the currently active google user
    noSounds: 'noSounds',                       // whether to play sounds
