self.applyBindings = function(model, eleID)
{
    var elem = document.getElementById(eleID);

    try{self.PAssert(772,model, 'A valid model must be defined when apply bindings');}catch(PERR){g.reportError(PERR);}
    try{self.PAssert(773,elem, 'A valid element must be defined when apply bindings');}catch(PERR){g.reportError(PERR);}

    if (elem && model)
    {
        try
        {
            ko.applyBindings(model, elem);
        }
        catch (err)
        {
            err.message += ' | Binding on: ' + self.elementToString(elem);
            self.reportError(err);

            throw err;
        }
    }
    else
    {
        var errStr = 'Invalid model or element passed to applyBindings | ';

        if (elem)
        {
            errStr += 'NoModel - Elem: ' + self.elementToString(elem);
        }
        else if (model)
        {
            errStr += 'NoElem - Elem: #' + eleID;
        }
        else
        {
            errStr += 'NoModelNoElem - Elem: #' + eleID;
        }

        self.reportError(new Error(errStr));
    }
};

