ko.bindingHandlers.check =
{
    init: function(element, valueAccessor, allBindingsAccessor, viewModel)
    {
        var obs = valueAccessor();
        if (obs())
        {
            self.addClass(element, 'checked');
        }

        self.setupClick(element, viewModel,
        {
            onClick: function(e)
            {
                obs(!obs());

                if (obs())
                {
                    self.addClass(element, 'checked');
                }
                else
                {
                    self.removeClass(element, 'checked');
                }

                e.preventDefault();
                e.stopPropagation();
            }
        });
    },
    update: function(element, valueAccessor, allBindingsAccessor, viewModel)
    {
        var obs = valueAccessor();

        if (obs())
        {
            self.addClass(element, 'checked');
        }
        else
        {
            self.removeClass(element, 'checked');
        }
    }
};

