self.setupMouseOver = function(element, value, isUpdate)
{
    try{self.PAssert(775,!platform.mobile);}catch(PERR){g.reportError(PERR);}

    if (self.tooltip)
    {
        if (!isUpdate)
        {
            self.tooltip.register(element, value);
        }
        else
        {
            self.tooltip.update(element, value);
        }
    }
    else
    {
        self.reportError(new Error('Tooltips registered before VMTooltip created'));
    }
};

