self.findFirstDiff = function(str1, str2, ignoreCase)
{
    if (!str1)
    {
        return str2.length;
    }
    else if (!str2)
    {
        return str1.length;
    }

    var minLen = Math.min(str1.length, str2.length);

    for (var i = 0; i < minLen; ++i)
    {
        if (ignoreCase)
        {
            if (str1.charAt(i).toLowerCase() !== str2.charAt(i).toLowerCase())
            {
                return i;
            }
        }
        else
        {
            if (str1.charAt(i) !== str2.charAt(i))
            {
                return i;
            }
        }
    }

    return Math.max(str1.length, str2.length);
};

self.findMatchingLength = function(str1, str2, ignoreCase)
{
    if (!str1 || !str2)
    {
        return 0;
    }

    var minLen = Math.min(str1.length, str2.length);

    for (var i = 0; i < minLen; ++i)
    {
        if (ignoreCase)
        {
            if (str1.charAt(i).toLowerCase() !== str2.charAt(i).toLowerCase())
            {
                return i;
            }
        }
        else
        {
            if (str1.charAt(i) !== str2.charAt(i))
            {
                return i;
            }
        }
    }

    return minLen;
};

