self.requestNotification = function(platform)
{
    var reqForm = document.forms['requestNotification'];

    var reqResult = document.getElementById('notificationResult');

    var email = reqForm['email'].value;

    if (email === undefined || email === null || email.length < 5)
    {
        reqResult.innerText = 'Please enter a valid email address.';
    }
    else
    {
        self.XHR({
            type: 'POST',
            url: window.location.protocol + '//api.moo.do/requestNotification',
            data:
            {
                email: email,
                platform: platform
            },
            cb: function (xhr)
            {
                if (xhr.status === 200)
                {
                    // Success
                    reqForm['email'].value = "";

                    reqResult.innerText = 'Thanks for your interest!';
                }
                else
                {
                    // Failure
                    reqResult.innerText = 'There was an error processing your request, please try again in a few minutes.';
                }
            }
        });
    }

    return false;
};

