self.handleCriticalError = function()
{
    hasCriticalError = true;

    if (!self.isDemoMode())
    {
        var errorNode = document.createElement('div');

        errorNode.id = 'criticalError';
        errorNode.textContent = 'There was a critical error while loading the app, ' + platform.actionVerb() + ' to reload.';

        if (platform.isTouchDevice)
        {
            errorNode.addEventListener(platform.touchStartEvent, function (e)
            {
                self.reload();
            }, true);
        }
        else
        {
            errorNode.addEventListener(platform.upEvent, function (e)
            {
                self.reload();
            }, true);
        }

        self.prependChild(document.body, errorNode);

        self.removeDOM('outerWrapper');
        self.removeDOM('setupWrapper');
        self.removeDOM('templates');
    }
    else
    {
        self.reload();
    }
};

