self.parseExtra = function(extra)
{
    var extraString;

    if (extra)
    {
        try
        {
            var info;
            if (extra instanceof KeyboardEvent)
            {
                info =
                {
                    type: extra.type,
                    currentTarget: self.elementToString(extra.currentTarget),
                    target: self.elementToString(extra.target || extra.srcElement),
                    keyCode: extra.keyCode,
                    normCode: extra.normCode,
                    ctrlKey: extra.ctrlKey,
                    shiftKey: extra.shiftKey,
                    metaKey: extra.metaKey,
                    altKey: extra.altKey,
                    tstamp: Date.now()
                };
            }
            else if (extra instanceof window.MouseEvent)
            {
                info =
                {
                    type: extra.type,
                    currentTarget: self.elementToString(extra.currentTarget),
                    relatedTarget: self.elementToString(extra.relatedTarget),
                    target: self.elementToString(extra.target || extra.srcElement),
                    clientX: extra.clientX,
                    clientY: extra.clientY,
                    ctrlKey: extra.ctrlKey,
                    shiftKey: extra.shiftKey,
                    metaKey: extra.metaKey,
                    altKey: extra.altKey,
                    which: extra.which,
                    button: extra.button,
                    tstamp: Date.now()
                };
            }
            else if (window.TouchEvent && extra instanceof window.TouchEvent)
            {
                info =
                {
                    type: extra.type,
                    currentTarget: self.elementToString(extra.currentTarget),
                    relatedTarget: self.elementToString(extra.relatedTarget),
                    target: self.elementToString(extra.target || extra.srcElement),
                    tstamp: Date.now()
                };

                var touch = extra.touches[0];

                if(touch)
                {
                    info.clientX = touch.clientX;
                    info.clientY = touch.clientY;
                }
            }
            else if (window.PointerEvent && extra instanceof window.PointerEvent)
            {
                info =
                {
                    type: extra.type,
                    currentTarget: self.elementToString(extra.currentTarget),
                    relatedTarget: self.elementToString(extra.relatedTarget),
                    target: self.elementToString(extra.target || extra.srcElement),
                    clientX: extra.clientX,
                    clientY: extra.clientY,
                    tstamp: Date.now()
                };
            }
            else if (extra instanceof window.Event)
            {
                info =
                {
                    type: extra.type,
                    currentTarget: self.elementToString(extra.currentTarget),
                    relatedTarget: self.elementToString(extra.relatedTarget),
                    target: self.elementToString(extra.target || extra.srcElement),
                    tstamp: Date.now()
                };
            }
            else if (extra instanceof Selection)
            {
                if (extra.rangeCount === 0)
                {
                    info = { rangeCount: 0 };
                }
                else
                {
                    var range = extra.getRangeAt(0);

                    var startElement = range.startContainer;
                    var startWasTextNode = false;
                    var startElementLength = 0;

                    var endElement = range.endContainer;
                    var endWasTextNode = false;
                    var endElementLength = 0;

                    if (startElement.nodeType === Node.TEXT_NODE)
                    {
                        startElementLength = startElement.length;
                        startElement = startElement.parentNode;
                        startWasTextNode = true;
                    }

                    if (self.hasClass(startElement, 'text'))
                    {
                        startElementLength = getElementLength(startElement);
                    }

                    if (endElement.nodeType === Node.TEXT_NODE)
                    {
                        endElementLength = endElement.length;
                        endElement = endElement.parentNode;
                        endWasTextNode = true;
                    }

                    if (self.hasClass(endElement, 'text'))
                    {
                        startElementLength = getElementLength(endElement);
                    }

                    info =
                    {
                        rangeCount: extra.rangeCount,
                        startContainer: self.elementToString(startElement),
                        startWasTextNode: startWasTextNode,
                        startOffset: range.startOffset,
                        startElementLength: startElementLength,
                        endContainer: self.elementToString(endElement),
                        endWasTextNode: endWasTextNode,
                        endOffset: range.endOffset,
                        endElementLength: endElementLength
                    };
                }
            }
            else
            {
                info = extra;
            }

            extraString = safeStringify(info);
        }
        catch (err)
        {
            log('Error parsing extra ' + err.message);
        }
    }

    return extraString;
};

window.__jsVersion = '1403041791456';

// Report javascript errors back to web site
self.handleJSError = function(msg, stack, extra, noMsg)
{
    if (!noMsg)
    {
        console.error('JS Error: ' + msg, '  Stack: ', stack, '  Extra: ', extra);
    }

    // Do not send errors to server in DEBUG mode or if there is no information to share.
    if (DEBUG || (!msg && !stack))
    {
        debugger;
    }
    else
    {
        try
        {
            var logs = DEBUG ? '' : logs = __LOG_BUFFER.ordered().join('\r\n');

            var user = self.getUserIdentifier();

            var extraString = self.parseExtra(extra);

            function reportError()
            {
                var appInfo = { };
                self.getAppInfo(appInfo);

                self.XHR({
                    type: 'POST',
                    url: window.location.protocol + '//api.moo.do/debug/reportError',
                    data:
                    {
                        msg: msg,
                        url: window.location.href,
                        stack: stack,
                        logs: logs,
                        info: JSON.stringify(appInfo),
                        user: user,
                        extra: extraString,
                        jsVersion: window.__jsVersion,
                        htmlVersion: document.body.getAttribute('ver')
                    },
                    cb: function(xhr)
                    {
                        if (xhr.status !== 200)
                        {
                            log('Failed to report remote error', xhr.responseText, xhr);

                            debugger;
                        }
                        else
                        {
                            log('Reported Remote Error: ', xhr.responseText);
                        }
                    }
                });
            }

            if (user !== 'Unknown')
            {
                reportError();
            }
            else
            {
                self.getClientInfo(function (info)
                {
                    if (info)
                    {
                        user = info.ip;
                    }

                    reportError();
                });
            }
        }
        catch (err)
        {
            log('Badness happened reporting errors to server: ', err);
            debugger;
        }
    }
}

// Ensure that all errors are logged even if we dont get actionable information on them.
window.onerror = function (errorMsg, url, lineNumber, colNumber, errorObj)
{
    if (errorObj)
    {
        log('Reported Global OnError: ' + errorMsg + ' Script: ' + url + ' Line: ' + lineNumber + ' Column: ' + colNumber);

        self.reportError(errorObj);
    }
    else
    {
        log('Unreported Global OnError: ' + errorMsg + ' Script: ' + url + ' Line: ' + lineNumber + ' Column: ' + colNumber);

        self.reportError(errorMsg);
    }
};

self.reportError = function(err, extra)
{
    if (err && !extra && !err.message && !err.stack)
    {
        extra = err;
        err = undefined;
    }

    if (!err)
    {
        err = { message: 'Undefined error passed to reporter', stack: self.getCurrentStack('') };
    }

    self.handleJSError(err.message, err.stack, extra);
};

