window.__CreateDebugSession = function(key)
{
    if (window.remote)
    {
        return 'Debug session already started';
    }

    var debugKey = key || generateRemoteDebugKey();

    window.rcon = window.console;

    self.handleJSError('Remote Debug Session', undefined, debugKey, /*noMsg*/true);

    self.addScript('http://jsconsole.com/remote.js?' + debugKey, 'rDebugScript');

    {
        var profileRunning = false;
        var profileIndex = 0;
        var profileName = '';

        window.rdbg =
        {
            startProfile: function (forceName)
            {
                try
                {
                    if (!profileRunning)
                    {
                        profileName = forceName ? forceName : ('RDBGProfile_' + profileIndex);
                        window.rcon.profile(profileName);

                        profileRunning = true;

                        return 'Profiler Started: ' + profileName;
                    }
                    else
                    {
                        return 'Profiler already running';
                    }
                }
                catch (err)
                {
                    return 'Error starting profile: ' + err.message;
                }
            },
            stopProfile: function ()
            {
                try
                {
                    if (profileRunning)
                    {
                        window.rcon.profileEnd(profileName);
                        profileRunning = false;

                        var retString = 'Profile Completed: ' + profileName;
                        profileName = '';
                        profileIndex++;

                        return retString;
                    }
                    else
                    {
                        return 'Profiler not running, use startProfile()';
                    }
                }
                catch (err)
                {
                    return 'Error stopping profile: ' + err.message;
                }
            }
        };
    }

    return 'Your remote debug session has started: ' + debugKey;
};

