var fnRegex = /function\s+([^(]*?)\s*\(([^)]*)\)/;

var supportSimpleStack = false;
self.getCurrentStack = function(message)
{
    if (supportSimpleStack || platform.noErrorStacks)
    {
        try
        {
            var msg = JSON.stringify(message);
        }
        catch (err)
        {
            log('Error stringifying stack message', err, message);
        }
    }

    if (supportSimpleStack)
    {
        var err = new Error(msg || message);

        if (err.stack)
        {
            return err.stack;
        }
    }

    supportSimpleStack = false;

    //if (DEBUG || platform.noErrorStacks)
    {
        try
        {
            var callstack = [ 'GenError: ' + (msg || message) ];
            var currentFunction = arguments.callee.caller;

            var DEPTH_LIMIT = 75;

            var depth = 0;
            while (currentFunction && (depth++ < DEPTH_LIMIT))
            {
                var fn = currentFunction.toString();

                var fnMatch = fnRegex.exec(fn);
                var fnName;
                if (fnMatch && fnMatch.length === 3 && fnMatch[1] !== '' && fnMatch[1] !== undefined)
                {
                    fnName = 'at ' + fnMatch[1] + '(' + fnMatch[2].replace(/[\r\n]/g, '') + ')';
                }
                else
                {
                    if (fnMatch && fnMatch.length === 3)
                    {
                        fnName = 'at <anonymous> (' + fnMatch[2].replace(/[\r\n]/g, '') + ')';
                    }
                    else
                    {
                        fnName = 'at <anonymous> (<unknown>)';
                    }
                }

                callstack.push(fnName);

                currentFunction = currentFunction.caller;
            }

            return callstack.join('\n');
        }
        catch (err)
        {
            log('Error generating stack trace', err);
        }
    }

    return '';
};

var remoteUpdateWait = 1000 * 60 * 20; // 20 minutes (in ms)
var lastUpdateTime = 0;
self.checkForRemoteChanges = function()
{
    var currentTime = Date.now();

    if (lastUpdateTime - currentTime >= remoteUpdateWait)
    {
        try
        {
            // Check for updated site version from AppCache
            self.AppCache.checkForUpdate();

            self.messageQueue.updateRemoteMessages();
        }
        catch (err)
        {
            self.reportError(err);
        }
        finally
        {
            lastUpdateTime = currentTime;
        }
    }
};

self.createRingBuffer = function(len)
{
    var p = 0;
    var b = [ ];
    var limit = len;

    return {
        get: function (i) { return b[i]; },
        push: function (e) { b[p] = e; p = (limit + p + 1) % limit; },
        count: function () { return p; },
        reset: function () { p = 0; b = [ ]; },
        buffer: function () { return b; },
        limit: function(lim) { limit = lim; },
        ordered: function () { return b.slice(p).concat(b.slice(0, p)); },
        entries: function ()
        {
            var arr = this.ordered();

            for (var i = 0; i < arr.length; ++i)
            {
                console.log('---- Entry [' + i + '] ----');
                console.log(arr[i]);
            }
        }
    };
}

self.wrapFn = function(func)
{
    function wrapper()
    {
        if (DEBUG)
        {
            return func.apply(this, arguments);
        }
        else
        {
            try
            {
                return func.apply(this, arguments);
            }
            catch (err)
            {
                var argString;

                try
                {
                    argString = JSON.stringify(arguments)
                }
                catch (err2)
                {
                    log('Unable to stringify args: ', arguments);
                }
                finally
                {
                    self.reportError(err, argString);
                }
            }
        }
    }

    return wrapper;
};

(function wrapAsyncCallbacks()
{
    var _helper = function _helperFn(fnName)
    {
        var originalFn = window[fnName];

        window[fnName] = function wrapAsyncFn()
        {
            var args = Array.prototype.slice.call(arguments);
            var originalCallback = args[0];

            if (typeof (originalCallback) === 'function')
            {
                args[0] = self.wrapFn(originalCallback);
            }

            if (originalFn.apply)
            {
                return originalFn.apply(this, args);
            }
            else
            {
                return originalFn(args[0], args[1]);
            }
        };
    };

    _helper('setTimeout');
    _helper('setInterval');
}());

