self.enumToString = function(en,value)
{
    for (var k in en)
        if (en[k] == value)
            return k;
    return null;
};

self.elementToString = function(ele, includeLength)
{
    if (ele)
    {
        var nodeString = ele.nodeName;

        if (ele.id)
        {
            nodeString += '#' + ele.id;
        }

        if (ele.className)
        {
            nodeString += '.' + ele.className;
        }

        if(includeLength && ele.innerText)
        {
            nodeString += ':' + ele.innerText.length;
        }

        return '[' + nodeString + ']';
    }
    else
    {
        return '[UNDEFINED]';
    }
};

