self.showArrowAt = function(top, left)
{
    var arrow = document.getElementById('iarrow');
    try{self.PAssert(780,arrow, 'Must have a valid highlight arrow');}catch(PERR){g.reportError(PERR);}

    arrow.style.display = 'block';

    var extraOffset = (platform.mobile ? -35 : 0);

    arrow.style.top = (top + extraOffset) + 'px';
    arrow.style.left = left + 'px';
};

self.hideHighlightElement = function ()
{
    var arrow = document.getElementById('iarrow');
    try{self.PAssert(781,arrow, 'Must have a valid highlight arrow');}catch(PERR){g.reportError(PERR);}

    arrow.style.display = 'none';
};

