if (DEBUG)
{
    var eventID = 1;

    if (DEBUG) window.__eventBuffer = self.createRingBuffer(100);

    function tagEvent(e, info, handlers)
    {
        if (!e.id)
        {
            e.id = eventID++;

            if (DEBUG) window.__eventBuffer.push(e);
        }

        if (DEBUG)
        {
            e.activeHandler = { owner: info, fn: handlers };

            if (e.handlers)
            {
                e.handlers.push(e.activeHandler);
            }
            else
            {
                e.handlers = [ e.activeHandler ];
            }
        }
    }

    window.__tagEvent = tagEvent;
}

if (DEBUG && DEBUG)
{
    function replaceFunction(obj, name)
    {
        var rep = obj.prototype[name];

        obj.prototype[name] = function ()
        {
            if (this.activeHandler)
            {
                log(this.type + '.' + name + ' ' + self.elementToString(this.target) + self.elementToString(this.activeHandler.owner));
            }
            else
            {
                log(this.type + '.' + name + ' ' + self.elementToString(this.target) + '[NOTTAGGED]');
            }

            var stack = self.getCurrentStack();
            var call = { fn: name, handler: (this.activeHandler ? this.activeHandler.owner : 'NOTTAGGED'), stack: stack };

            if (this.calls)
            {
                this.calls.push(call);
            }
            else
            {
                this.calls = [ call ];
            }

            rep.call(this);
        };
    }

    if (window.MouseEvent)
    {
        replaceFunction(MouseEvent, 'stopImmediatePropagation');
        replaceFunction(MouseEvent, 'stopPropagation');
        replaceFunction(MouseEvent, 'preventDefault');
    }

    if (window.TouchEvent)
    {
        replaceFunction(TouchEvent, 'stopImmediatePropagation');
        replaceFunction(TouchEvent, 'stopPropagation');
        replaceFunction(TouchEvent, 'preventDefault');
    }

    if (window.PointerEvent)
    {
        replaceFunction(PointerEvent, 'stopImmediatePropagation');
        replaceFunction(PointerEvent, 'stopPropagation');
        replaceFunction(PointerEvent, 'preventDefault');
    }
}

function fnCatch(cb)
{
    try{self.PAssert(782,cb, 'Must specify a callback');}catch(PERR){g.reportError(PERR);}

    return function dbgCatch()
    {
        if (DEBUG)
        {
            if (cb)
            {
                return cb.apply(undefined, arguments);
            }
        }
        else
        {
            try
            {
                if (cb)
                {
                    return cb.apply(undefined, arguments);
                }
            }
            catch (err)
            {
                self.reportError(err, arguments);
                throw err;
            }
        }
    };
}

var dblClickTime = 500;
self.setupClick = function(obj, item, params)
{
    try{self.PAssert(783,obj, 'Expecting an object to bind the click hanlders to.');}catch(PERR){g.reportError(PERR);}

    if (!obj)
    {
        log('Skipping setupClick because no valid DOM object was provided to bind to');

        return;
    }

    if (DEBUG) log('SetupClick: ' + self.elementToString(obj));

    if (!item)
    {
        item = obj;
    }

    var useCapture = false;
    if (self.isFunction(params))
    {
        params = { onClick: params };
    }
    else
    {
        if (params)
        {
            if (params.useCapture)
            {
                useCapture = params.useCapture;
            }
        }
    }

    //
    // Backend Event Handlers
    //

    function move(e)
    {
        //if (DEBUG) log('Move: ', e.id);

        var xThisFrame = e.eventX - this.lastX;
        var yThisFrame = e.eventY - this.lastY;

        if (xThisFrame !== 0 || yThisFrame !== 0)
        {
            var firstMove = !this.moved;

            this.lastX = e.eventX;
            this.lastY = e.eventY;

            e.distanceX = this.distanceX = this.distanceX + Math.abs(xThisFrame);
            e.distanceY = this.distanceY = this.distanceY + Math.abs(yThisFrame);

            if (this.moved || e.distanceX > self.ClickThreshold || e.distanceY > self.ClickThreshold)
            {
                this.moved = true;
                e.moved = this.moved;
            }

            if (params.onMove || params.onFirstMove)
            {
                e.diffX = e.eventX - this.startX;
                e.diffY = e.eventY - this.startY;

                e.xThisFrame = xThisFrame;
                e.yThisFrame = yThisFrame;

                e.xFromTopLeft = this.xFromTopLeft;
                e.yFromTopLeft = this.yFromTopLeft;

                e.startX = this.startX;
                e.startY = this.startY;

                e.startClientX = this.startClientX;
                e.startClientY = this.startClientY;

                e.startSrc = this.startSrc;

                if (firstMove && params.onFirstMove)
                {
                    params.onFirstMove.call(item, e);
                }

                if (params.onMove)
                {
                    var handled = false;
                    handled = params.onMove.call(item, e);

                    if (handled)
                    {
                        e.stopImmediatePropagation();
                    }
                }

                // if (e.overrides)
                // {
                //     var obj = e.overrides;
                //     for (var i in obj)
                //     {
                //         if (obj.hasOwnProperty(i))
                //         {
                //             try{self.PAssert(784,this[i], 'Trying to override a property that does not exist', i);}catch(PERR){g.reportError(PERR);}
                //             this[i] = obj[i];
                //         }
                //     }
                // }
            }
        }
    }

    function end(e)
    {
        if (DEBUG) log('End: ', e.id, self.elementToString(e.target), self.elementToString(obj));

        if (!e.srcElement)
        {
            e.srcElement = e.target;
        }

        var now = Date.now();

        var lastTouch = this.lastTouch || now;
        var delta = Math.max(0, now - lastTouch);

        if (!this.numClicks)
        {
            this.numClicks = 1;
        }
        else
        {
            this.numClicks++;
        }

        e.eventX = this.lastX;
        e.eventY = this.lastY;

        e.moved = this.moved;

        e.timeDelta = delta;

        e.diffX = (this.lastX - this.startX);
        e.diffY = (this.lastY - this.startY);

        e.startX = this.startX;
        e.startY = this.startY;

        e.xFromTopLeft = this.xFromTopLeft;
        e.yFromTopLeft = this.yFromTopLeft;

        e.startSrc = this.startSrc;

        if (delta < dblClickTime && delta > 0)
        {
            if(params.onDblClick && !this.moved)
            {
                params.onDblClick.call(item, e);
            }
        }
        if (isNaN(e.button) || e.button === 0)
        {
            this.lastTouch = now;

            if (params.onClick && !this.moved)
                params.onClick.call(item, e);
        }
        else if (e.button == 1)
        {
            if (params.onMiddleClick && !this.moved)
                params.onMiddleClick.call(item, e);
        }

        if ((isNaN(e.button) || e.button === 0) && params.onEnd)
        {
            params.onEnd.call(item, e);
        }

        setTimeout(function()
        {
            this.lastTouch = undefined;
            this.numClicks = undefined;
        }.bind(this), dblClickTime);
    }

    function cancel(e)
    {
        if (DEBUG) log('Cancel: ', e.id, self.elementToString(e.target), self.elementToString(obj));

        if (params.onCancel)
        {
            params.onCancel.call(item, e);
        }
    }

    function start(e)
    {
        if (DEBUG) log('Start: ', e.id, self.elementToString(e.target), self.elementToString(obj));

        e.timeStart = this.timeStart = Date.now();

        if (!e.srcElement)
        {
            e.srcElement = e.target;
        }

        this.startSrc = e.srcElement;

        this.moved = false;

        e.startX = this.startX = e.eventX;
        e.startY = this.startY = e.eventY;

        e.startClientX = this.startClientX = e.clientX;
        e.startClientY = this.startClientY = e.clientY;

        this.lastX = e.eventX;
        this.lastY = e.eventY;

        this.distanceX = 0;
        this.distanceY = 0;

        var offset = self.offset(e.srcElement);

        e.xFromTopLeft = this.xFromTopLeft = e.eventX - offset.left;
        e.yFromTopLeft = this.yFromTopLeft = e.eventY - offset.top;

        if ((isNaN(e.button) || e.button === 0) && params.onStart)
        {
            params.onStart.call(item, e);
        }

        if (!isNaN(e.changeLastY))
        {
            this.lastY += e.changeLastY;
        }
    }

    //
    // Touch Passthrough Handlers
    //

    function touchMove(e)
    {
        if (DEBUG) { tagEvent(e, obj, params); }

        //if (DEBUG) log('TouchMove: ', e.id);

        var touch = (platform.ie ? e : e.touches[0]);

        e.eventX = touch.pageX;
        e.eventY = touch.pageY;

        e.clientX = touch.clientX;
        e.clientY = touch.clientY;

        move.call(obj, e);

        if (e.returnFalse)
        {
            return false;
        }
    }

    var touchStartHandler = fnCatch(touchStart);
    var touchMoveHandler = fnCatch(touchMove);
    var touchEndHandler = fnCatch(touchUp);
    var touchCancelHandler = fnCatch(touchCancel);

    function touchUp(e)
    {
        if (DEBUG) { tagEvent(e, obj, params); }

        if (DEBUG) log('TouchUp: ', e.id, self.elementToString(e.target), self.elementToString(obj));

        document.removeEventListener(platform.touchMoveEvent, touchMoveHandler, useCapture);
        document.removeEventListener(platform.touchEndEvent, touchEndHandler, useCapture);
        document.removeEventListener(platform.touchCancelEvent, touchCancelHandler, useCapture);

        if (gesture)
        {
            gesture.stop();
        }

        end.call(obj, e);

        if (e.returnFalse)
        {
            return false;
        }
    }

    function touchCancel(e)
    {
        if (DEBUG) { tagEvent(e, obj, params); }

        if (DEBUG) log('TouchCancel: ', e.id, self.elementToString(e.target), self.elementToString(obj));

        document.removeEventListener(platform.touchMoveEvent, touchMoveHandler, useCapture);
        document.removeEventListener(platform.touchEndEvent, touchEndHandler, useCapture);
        document.removeEventListener(platform.touchCancelEvent, touchCancelHandler, useCapture);

        if (gesture)
        {
            gesture.stop();
        }

        cancel.call(obj, e);

        if (e.returnFalse)
        {
            return false;
        }
    }

    var gesture = undefined;

    // if (platform.ie && platform.isTouchDevice && DEBUG)
    // {
    //     function gestureEv(e)
    //     {
    //         log('Gesture: ' + e.type + ': ', self.elementToString(e.target), self.elementToString(obj), e);
    //     };

    //     obj.addEventListener('MSGestureStart', gestureEv, useCapture);
    //     obj.addEventListener('MSGestureEnd', gestureEv, useCapture);
    //     obj.addEventListener('MSGestureChange', gestureEv, useCapture);
    //     obj.addEventListener('MSGestureTap', gestureEv, useCapture);
    //     obj.addEventListener('MSGestureHold', gestureEv, useCapture);
    //     obj.addEventListener('MSInertiaStart', gestureEv, useCapture);

    //     gesture = new MSGesture();
    //     gesture.target = obj;
    // }

    function touchStart(e)
    {
        if (DEBUG) { tagEvent(e, obj, params); }

        if (e.handled)
        {
            return;
        }

        if (DEBUG) log('TouchStart: ', e.id, self.elementToString(e.target), self.elementToString(obj));

        this.lastInputTouch = true;
        // e.handled = true;

        var touch = (platform.ie ? e : e.touches[0]);

        e.eventX = touch.pageX;
        e.eventY = touch.pageY;

        e.clientX = touch.clientX;
        e.clientY = touch.clientY;

        start.call(obj, e);

        if (!e.ignore)
        {
            document.addEventListener(platform.touchMoveEvent, touchMoveHandler, useCapture);
            document.addEventListener(platform.touchEndEvent, touchEndHandler, useCapture);
            document.addEventListener(platform.touchCancelEvent, touchCancelHandler, useCapture);

            // Note: Don't want to stop propagation in start because taps on items
            // need to bubble up to content to take the default action

            if (gesture)
            {
                log('Add Gesture Pointer: ', e.id, self.elementToString(e.target), self.elementToString(obj));

                gesture.addPointer(e.pointerId);
            }
        }

        if (e.returnFalse)
        {
            return false;
        }
    }

    //
    // Mouse Passthrough Handlers
    //

    function mouseMove(e)
    {
        if (DEBUG) { tagEvent(e, obj, params); }

        if (this.lastInputTouch)
        {
            return;
        }

        //if (DEBUG) log('MouseMove: ', e.id);

        e.eventX = e.pageX;
        e.eventY = e.pageY;

        move.call(obj, e);

        if (e.returnFalse)
        {
            return false;
        }
    }

    var mouseDownHandler = fnCatch(mouseDown);
    var mouseMoveHandler = fnCatch(mouseMove);
    var mouseUpHandler = fnCatch(mouseUp);
    var contextMenuHandler = fnCatch(contextMenu);

    function mouseUp(e)
    {
        if (DEBUG) { tagEvent(e, obj, params); }

        if (e.handled || this.lastInputTouch)
        {
            return;
        }

        if (DEBUG) log('MouseUp: ', e.id, self.elementToString(e.target), self.elementToString(obj));

        document.removeEventListener(platform.moveEvent, mouseMoveHandler, useCapture);
        document.removeEventListener(platform.upEvent, mouseUpHandler, useCapture);

        if (e.button === 0)
        {
            end.call(obj, e);
        }

        if (e.returnFalse)
        {
            return false;
        }
    }

    function mouseDown(e)
    {
        if (DEBUG) { tagEvent(e, obj, params); }

        if (e.handled || this.lastInputTouch)
        {
            return;
        }

        if (DEBUG) log('MouseDown: ', e.id, self.elementToString(e.target), self.elementToString(obj));

        this.lastInputTouch = false;

        e.eventX = e.pageX;
        e.eventY = e.pageY;

        start.call(obj, e);

        if (!e.ignore)
        {
            if (e.button === 0)
            {
                document.addEventListener(platform.moveEvent, mouseMoveHandler, useCapture);
            }

            document.addEventListener(platform.upEvent, mouseUpHandler, useCapture);
        }

        if (e.returnFalse)
        {
            return false;
        }
    }

    function contextMenu(e)
    {
        if (DEBUG) { tagEvent(e, obj, params); }

        if (e.handled || this.lastInputTouch)
        {
            return;
        }

        if (DEBUG) log('ContextMenu: ', e.id, self.elementToString(e.target), self.elementToString(obj));

        if (!e.srcElement)
        {
            e.srcElement = e.target;
        }

        if (params.onRightClick)
        {
            params.onRightClick.call(item, e);
        }
    }

    if (platform.isTouchDevice)
    {
        obj.addEventListener(platform.touchStartEvent, touchStartHandler, useCapture);
    }
    else
    {
        obj.addEventListener(platform.downEvent, mouseDownHandler, useCapture);
    }

    if (platform.usePointerEvents && platform.mobile)
    {
        obj.addEventListener('mousedown', function (e)
        {
            if (DEBUG) { tagEvent(e, obj, params); }

            if (!android.isEnabled() || !android.isTargetBox(e))
            {
                e.preventDefault();
                e.stopImmediatePropagation();
            }

            return false;
        }, useCapture);
    }

    if (params.onRightClick && !platform.mobile)
    {
        obj.addEventListener('contextmenu', contextMenuHandler, useCapture);
    }

    return this;
};

var spinnerRunning = false;
self.StartSpinner = function()
{
    if (platform.mobile && !spinnerRunning)
    {
        spinnerRunning = true;

        self.removeClass(document.getElementById('loading'), 'hidden');
    }
};

self.StopSpinner = function()
{
    var spinner = document.getElementById('loading');

    if (spinnerRunning && spinner)
    {
        self.addClass(document.getElementById('loading'), 'hidden');
    }
};

