Array.prototype.removeAt = function(from)
{
    return this.splice(from, 1)[0];
};

Array.prototype.remove = function(obj)
{
    var index = this.indexOf(obj);

    if (index >= 0)
    {
        this.removeAt.call(this, index);
    }

    return index;
};

