Array.prototype.findInsertIndex = function (ele, comp)
{
    try{self.PAssert(785,ele, 'Require a valid element to insert');}catch(PERR){g.reportError(PERR);}
    try{self.PAssert(786,comp, 'Require a valid comparison function');}catch(PERR){g.reportError(PERR);}

    var low = 0;
    var high = this.length - 1;

    while (low <= high)
    {
        var i = (low + high) >> 1;
        var c = comp(this[i], ele);

        if (c < 0)
        {
            low = i + 1;
        }
        else if (c > 0)
        {
            high = i - 1;
        }
        else
        {
            return i;
        }
    }

    return low;
}

Array.prototype.insertSorted = function (ele, comp)
{
    var idx = this.findInsertIndex(ele, comp);

    this.splice.apply(this, [idx, 0, ele]);
};

ko.observableArray.fn.insertSorted = function (ele, comp)
{
    this.peek().insertSorted(ele, comp);

    this.valueHasMutated();
};

self.clamp = function(val, min, max)
{
    return Math.max(min, Math.min(max, val));
};

