function readyStateCallback(e, callback)
{
    e.onloadDone = false; //for Opera
    e.onload = function()
    {
        e.onloadDone = true;
        if (callback)
        {
            callback();
        }
    };

    e.onReadystatechange = function()
    {
        if (e.readyState === 'loaded' && !e.onloadDone)
        {
            e.onloadDone = true;

            if (callback)
            {
                callback();
            }
        }
    };
}

self.createPath = function(filename)
{
    return window.location.protocol + '//' + window.location.host + '/' + filename;
};

self.addScript = function(filename, id, callback)
{
    var e = document.createElement('script');
    e.type = 'text/javascript';
    e.src = filename;

    if (id)
    {
        e.id = id;
    }

    if (callback)
    {
        readyStateCallback(e, callback);
    }

    if (typeof(e) !== 'undefined')
    {
        document.getElementsByTagName('head')[0].appendChild(e);
    }
};

self.addStylesheet = function(filename, callback)
{
    var e = document.createElement('link');
    e.type = 'text/css';
    e.rel = 'stylesheet';
    e.href = filename;

    if (callback)
    {
        readyStateCallback(e, callback);
    }

    if (typeof(e) !== 'undefined')
    {
        document.getElementsByTagName('head')[0].appendChild(e);
    }
};

//
// Debug Helpers
//

function makeString(args)
{
    var str = '';

    for (var i = 0; i < args.length; ++i)
    {
        str += (' ' + safeStringify(args[i]));
    }

    return str;
}

if (DEBUG)
{

self.Assert = function(value)
{
    if (!value)
    {
        try
        {
            if (arguments.length > 1)
            {
                console.error.apply(console, Array.prototype.slice.call(arguments, 1));

                if (DEBUG && DEBUG && window.__handleAssertForTest)
                {
                    window.__handleAssertForTest(Array.prototype.slice.call(arguments, 1));
                }
            }
            else
            {
                console.error('Assert Failure!');

                if (DEBUG && DEBUG && window.__handleAssertForTest)
                {
                    window.__handleAssertForTest('Assert Failure');
                }
            }
        }
        catch (err)
        {
            // Ignore error
            console.error('Problem reporting Assert!');
        }

        debugger;
    }
};

}

self.PAssert = function(tag, value)
{
    if (!value)
    {
        var assertExtra = { isAssert: true, tag: tag };

        try
        {
            var argString = makeString(Array.prototype.slice.call(arguments, 2));

            if (arguments.length > 2)
            {
                log('Assert Failure: ' + argString);
            }
            else
            {
                log('Assert Failure');
            }

            self.reportError(new Error('Assert Failure: ' + argString), assertExtra);
        }
        catch (err)
        {
            log('Exception in PAssert: ', err);

            self.reportError(err, assertExtra);
        }
    }
};

//
// DOM Helpers
//

self.getSelectionInfo = function()
{
    var selection = window.getSelection();

    var node = selection.anchorNode;

    if (node)
    {
        return {node: (node.nodeType === Node.TEXT_NODE ? node.parentNode : node), selection: selection};
    }

    return undefined;
};

self.getSelectionRange = function()
{
    var selection = window.getSelection();

    if (selection.rangeCount > 0)
    {
        var range = selection.getRangeAt(0);

        return range;
    }

    return undefined;
};

