self.getItemForElement = function(el)
{
    if (android.isEnabled())
    {
        var currentVMLI = android.getCurrentVMLI();

        if (currentVMLI)
        {
            return currentVMLI;
        }
    }

    if (el && el !== document.body)
    {
        // If the node being requested is a #text node, ensure a valid element is used instead
        if (el.nodeType === Node.TEXT_NODE)
        {
            el = el.parentNode;
        }

        var vm = ko.dataFor(el);

        return (vm instanceof VMLI) ? vm : undefined;
    }

    return undefined;
};

self.getSelectedItem = function()
{
    var selection = window.getSelection();

    return self.getItemForElement(selection.anchorNode);
};

