self.clearSelection = function()
{
    if (DEBUG) log('Clear Selection');

    window.getSelection().removeAllRanges();
};

// Get all text node children under a given <span class='text'> element.
function getTextNodesIn(element)
{
    // TODO: Support nested styling - change to descendants instead of children?
    var nodes = [];

    function getTextNodes(node)
    {
        if (node)
        {
            if (node.nodeType === Node.TEXT_NODE)
            {
                nodes.push(node);
            }
            else
            {
                for (var i = 0; i < node.childNodes.length; ++i)
                {
                    var el = node.childNodes[i];
                    // if(el.tagName == 'B' || el.tagName == 'I' || el.tagName == 'U')
                    // {
                    //     if(isNaN(el.length))
                    //     {
                    //         el.length = 1;
                    //     }
                    //     nodes.push(el);
                    //     getTextNodes(el);
                    //     nodes.push(el);
                    // }
                    // else
                    // {
                        getTextNodes(el);
                    // }
                }
            }
        }
    }

    getTextNodes(element);

    return nodes;
};

self.selectElement = function (ele, selectEnd)
{
    if (ele.textContent.length > 0 && ele.textContent !== TextSpace)
    {
        var sel = window.getSelection();
        var range = document.createRange();

        var selStart = (selectEnd ? ele.textContent.length : 0);

        range.setStart(ele.childNodes[0], selStart);
        range.setEnd(ele.childNodes[0], ele.textContent.length);

        if (DEBUG) log('Set Selection: [' + selStart + ',' + ele.textContent.length + ']', ele.childNodes[0]);

        sel.removeAllRanges();
        sel.addRange(range);
    }
    else
    {
        ele.focus();

        if (DEBUG) log('Set Focus:', ele);
    }
};

// Given a <span class='text'> element, select text independent of styling given a starting
// offset and length. This is useful when used in conjunction with the functions below that
// give offsets of selected text based on span elements.
self.selectChildren = function(item, start, length)
{
    if (!item)
    {
        try{self.PAssert(787,false, 'Valid item and text required to perform selection');}catch(PERR){g.reportError(PERR);}

        return;
    }

    var maxLength = item.getParsedText().length - item.styleLength;
    var start = Math.min(start, maxLength);
    var end = Math.min(start + length, maxLength);

    var sel = window.getSelection();
    var range = document.createRange();

    var startElement = undefined;
    var startOffset = -1;

    var endElement = undefined;
    var endOffset = -1;
    var totalOffset = 0;

    // The element may be undefined in cases where we are undoing an item deletion or
    // are trying to select into an unloaded item.
    var element = item.getSpan();

    if (element)
    {
        var children = getTextNodesIn(element);

        for (var i = 0; i < children.length; ++i)
        {
            var childLength = children[i].length;
            if (startOffset === -1 && (totalOffset + childLength) >= start)
            {
                startElement = children[i];
                startOffset = start - totalOffset;
            }

            if ((totalOffset + childLength >= end))
            {
                endElement = children[i];
                endOffset = end - totalOffset;
                break;
            }
            totalOffset += childLength;
        }

        try{self.PAssert(788,startElement && startOffset >= 0);}catch(PERR){g.reportError(PERR);}
        try{self.PAssert(789,endElement && endOffset >= 0);}catch(PERR){g.reportError(PERR);}

        try{self.PAssert(790,startOffset <= startElement.length);}catch(PERR){g.reportError(PERR);}
        try{self.PAssert(791,endOffset <= endElement.length);}catch(PERR){g.reportError(PERR);}

        startOffset = (startOffset < 0) ? 0 : startOffset;
        endOffset = (endOffset < 0) ? 0 : endOffset;

        range.setStart(startElement, startOffset);
        range.setEnd(endElement, endOffset);

        if (DEBUG) log('Set Selection: [' + startOffset + ',' + endOffset + ']', startElement, endElement);

        sel.removeAllRanges();
        sel.addRange(range);
    }
};

// Given a <span class='text'> element, find the element that the offset maps to. This is
// useful when the span has styled text and the element may not be the span's firstChild.
function getOffsetChildElement(element, offset)
{
    var totalOffset = 0;
    var children = getTextNodesIn(element);

    for (var i = 0; i < children.length; ++i)
    {
        var childLength = children[i].length;
        if ((totalOffset + childLength) >= offset)
        {
            return { element: children[i], offset: offset - totalOffset };
        }

        totalOffset += childLength;
    }

    try{self.PAssert(792,false, 'Unable to find offset child element');}catch(PERR){g.reportError(PERR);}

    return undefined;
};

function getElementLength(element)
{
    var children = getTextNodesIn(element);

    var total = 0;

    for (var i = 0; i < children.length; ++i)
    {
        total += children[i].length;
    }

    return total;
}

self.getOffsetOfSubNode = function(element, subnode)
{
    var offset = 0;
    var children = getTextNodesIn(element);
    var childrenSub = getTextNodesIn(subnode);
    for (var i = 0; i < children.length; ++i)
    {
        if(children[i] == childrenSub[0])
        {
            break;
        }
        // var childLength = children[i].length;
        offset += children[i].length;
    }
    return offset;
};

// Given a starting element and offset and an ending element and offset, sets the current
// text selection to range.
self.selectMultiline = function(eStart, oStart, eEnd, oEnd)
{
    var retVal = false;

    var sel = window.getSelection();
    var range = document.createRange();

    var start = getOffsetChildElement(eStart, oStart);
    var end = getOffsetChildElement(eEnd, oEnd);

    try{self.PAssert(793,start && start.element && !isNaN(start.offset), 'Start element is invalid');}catch(PERR){g.reportError(PERR);}
    try{self.PAssert(794,end && end.element && !isNaN(end.offset), 'End element is invalid');}catch(PERR){g.reportError(PERR);}

    if (start && start.element && end && end.element)
    {
        range.setStart(start.element, start.offset);
        range.setEnd(end.element, end.offset);

        if (DEBUG) log('Set Selection: [' + start.offset + ',' + end.offset + ']', start.element, end.element);

        sel.removeAllRanges();
        sel.addRange(range);

        retval = true;
    }

    return retVal;
};

self.saveSelectionItem = function(range)
{
    if (!range)
    {
        var selection = self.getSelectionInfo();

        if (!selection || selection.rangeCount === 0)
        {
            return undefined;
        }

        range = selection.selection.getRangeAt(0);
    }

    var validSelectionInfo = true;

    if (range && range.startContainer && range.endContainer)
    {
        try
        {
            var startItem = self.getItemForElement(range.startContainer);
            var endItem = self.getItemForElement(range.endContainer);

            if (startItem && endItem)
            {
                var startOffsets = self.getSelectOffsetsInSpan(startItem, startItem.getSpan(), range);
                var endOffsets = self.getSelectOffsetsInSpan(endItem, endItem.getSpan(), range);

                var savedPane = self.getPaneForElement(startItem.getSpan());
            }
            else
            {
                validSelectionInfo = false;
            }
        }
        catch (err)
        {
            self.reportError(err);

            validSelectionInfo = false;
        }
    }
    else
    {
        validSelectionInfo = false;
    }

    var selData = undefined;
    if (validSelectionInfo)
    {
        selData = { start: startOffsets, end: endOffsets, startItem: startItem, endItem: endItem, pane: savedPane };
    }

    return selData;
};

var savedSelections = { };
self.saveSelection = function (name, range)
{
    savedSelections[name] = self.saveSelectionItem(range, name);
};

self.clearSavedSelection = function (name)
{
    savedSelections[name] = undefined;
};

self.restoreSavedSelection = function (name, forcePane)
{
    var selData = savedSelections[name];
    var restoreItem = undefined;

    if (selData && (!forcePane || forcePane === selData.pane))
    {
        try
        {
            restoreItem = self.restoreSelectionItem(selData);

            if (restoreItem)
            {
                self.scrollIntoView(restoreItem.getSpan(), 0);
            }
        }
        catch (err)
        {
            self.reportError(err);
        }

        self.clearSavedSelection(name);
    }

    return !!restoreItem;
};

self.restoreSelectionItem = function(selectionInfo)
{
    try{self.PAssert(795,selectionInfo, 'Must restore to a valid selection');}catch(PERR){g.reportError(PERR);}

    var startIsHidden = self.hasClass(selectionInfo.startItem.getElement(), 'hidden');
    var endIsHidden = self.hasClass(selectionInfo.endItem.getElement(), 'hidden');

    if (startIsHidden && !endIsHidden)
    {
        var setSelection = self.selectMultiline(selectionInfo.endItem.getSpan(), selectionInfo.end.end, selectionInfo.endItem.getSpan(), selectionInfo.end.end);

        return setSelection ? selectionInfo.endItem : undefined;
    }
    else if (!startIsHidden && endIsHidden)
    {
        var setSelection = self.selectMultiline(selectionInfo.startItem.getSpan(), selectionInfo.start.start, selectionInfo.startItem.getSpan(), selectionInfo.start.start);

        return setSelection ? selectionInfo.startItem : undefined;
    }
    else
    {
        var setSelection = self.selectMultiline(selectionInfo.startItem.getSpan(), selectionInfo.start.start, selectionInfo.endItem.getSpan(), selectionInfo.end.end);

        return setSelection ? selectionInfo.startItem : undefined;
    }

    return undefined;
};

// Gets both the start and end offsets relative to a <span class='text'> element. The offsets
// are relative to the text of the item the span corresponds to.
// TODO: Allow to accept multiple elements? (multi-line?)
self.getSelectOffsetsInSpan = function(item, element, range, forceSelection)
{
    var styleLength = (item ? item.styleLength : 0);
    var returnValue = { start: 0, startText: styleLength, end: 0, endText: styleLength, total: 0, totalText: styleLength };

    if (android.isEnabled() && !forceSelection)
    {
        returnValue.start = android.getSelectionStart();
        returnValue.startText = returnValue.start + styleLength;

        returnValue.end = android.getSelectionEnd();
        returnValue.endText = returnValue.end + styleLength;

        returnValue.total = android.getInputText().length;
        returnValue.totalText = returnValue.total + styleLength;
    }
    else
    {
        try{self.PAssert(796,element && element.tagName == 'SPAN', 'Expected SPAN, Actual: ', element);}catch(PERR){g.reportError(PERR);}

        if (element && element.textContent !== TextSpace && !(android.isEnabled() && element.textContent === ""))
        {
            var startElement = range.startContainer;
            var startOffset = range.startOffset;

            var endElement = range.endContainer;
            var endOffset = range.endOffset;

            //console.log('Start: ' + startOffset + '  End: ' + endOffset);

            try{self.PAssert(797,startElement.nodeName == '#text', 'Expected #text, Actual: ', startElement.nodeName);}catch(PERR){g.reportError(PERR);}

            var totalOffset = 0;
            var children = getTextNodesIn(element);

            // TODO: Early out for loop
            for (var i = 0; i < children.length; ++i)
            {
                if (children[i] == startElement)
                {
                    returnValue.start = totalOffset + startOffset;
                    returnValue.startText = totalOffset + startOffset + styleLength;
                }

                if (children[i] == endElement)
                {
                    returnValue.end = totalOffset + endOffset;
                    returnValue.endText = totalOffset + endOffset + styleLength;
                }

                totalOffset += children[i].length;
            }

            returnValue.total = totalOffset;
            returnValue.totalText = totalOffset + styleLength;
        }
    }

    return returnValue;
};

function hasValidClass(el)
{
    return self.hasClass(el, 'text') || (el.id === 'androidInput' && android.isEnabled());
}

// Find the closest <span class='text'> to el. All styling of text should be done using <p> or <a>
// elements, and only a span with a class of 'text' should be returned.
self.getSpanParent = function(el)
{
    if (android.isEnabled())
    {
        return android.getInputBox();
    }
    else if (self.hasClass(el, 'noItems'))
    {
        return undefined;
    }

    while (el && (el.tagName !== 'SPAN' || !hasValidClass(el)))
    {
        el = el.parentNode;
    }

    if (el && (hasValidClass(el)))
    {
        return el;
    }

    try{self.PAssert(798,false, 'This shouldn\'t happen, spans should always be found');}catch(PERR){g.reportError(PERR);}

    return undefined;
};

// Find the closest <span> to el. All styling of text should be done using <p>
// elements, and only a span with a class of "text" should be returned.
self.getLIParent = function(el)
{
    while (el && el.tagName !== 'LI')
    {
        el = el.parentNode;
    }

    if(el && !self.hasClass(el, 'item'))
    {
        //try{self.PAssert(799,false, 'This shouldn\'t happen, LIs should always be found');}catch(PERR){g.reportError(PERR);}
        return null;
    }

    return el;
};

// Given a VMLI, finds the visually previous item by searching the item tree.
self.getPreviousItem = function(item, pane, options)
{
    try{self.PAssert(800,item, 'Item must be specified');}catch(PERR){g.reportError(PERR);}

    if (!item)
    {
        return undefined;
    }

    var found = undefined;

    var isTest = options && options.isTest;
    var ignoreSearch = options && options.ignoreSearch;

    var prev = item.parent();

    if (!prev)
    {
        return undefined;
    }

    var index = prev.getChildIndex(item);

    if (index > 0)
    {
        while (!found)
        {
            var newChild = false;

            for (var i = index - 1; i >= 0; i--)
            {
                var prevChild = prev.getChild(i);
                if (prevChild.hasChildren() && !prevChild.isCollapsed())
                {
                    if (ignoreSearch || !pane || !self.hasClass(prevChild.getElement(pane.id), 'hidden'))
                    {
                        prev = prevChild;
                        index = prevChild.numChildren();
                        newChild = true;
                        break;
                    }
                }
                else
                {
                    if (ignoreSearch || !pane || !self.hasClass(prevChild.getElement(pane.id), 'hidden'))
                    {
                        found = prevChild;
                        newChild = true;
                        break;
                    }
                }
            }

            if (!newChild)
            {
                found = prev;
            }
        }
        try{self.PAssert(801,isTest || !found || found.isCollapsed() || item === self.getNextItem(found, pane, { isTest: true, ignoreSearch: ignoreSearch }), 'Previous/Next should always match');}catch(PERR){g.reportError(PERR);}
    }
    else
    {
        found = item.parent();
    }

    return found;
};

// Given a VMLI, finds the visually next item by searching the item tree.
self.getNextItem = function(item, pane, options)
{
    try{self.PAssert(802,item, 'Item must be specified');}catch(PERR){g.reportError(PERR);}

    if (!item)
    {
        return undefined;
    }

    var next = undefined;

    var isTest = options && options.isTest;
    var ignoreSearch = options && options.ignoreSearch;

    // If there are children, grab the first child
    if (item.hasChildren() && !item.isCollapsed())
    {
        var numChildren = item.numChildren();
        for (var i = 0; i < numChildren; ++i)
        {
            var candidate = item.getChild(i);
            if (ignoreSearch || !pane || !self.hasClass(candidate.getElement(pane.id), 'hidden'))
            {
                next = candidate;

                if (options)
                {
                    options.numLevels++;
                }

                break;
            }
        }
    }

    var cItem = item;
    while (!next)
    {
        var parent = cItem.parent();
        if (!parent)
        {
            break;
        }

        var index = parent.getChildIndex(cItem);
        var numChildren = parent.numChildren();
        if (!parent.isCollapsed())
        {
            for (var i = index + 1; i < numChildren; ++i)
            {
                var candidate = parent.getChild(i);
                try{self.PAssert(803,!pane || candidate.getElement(pane.id), 'next element should be visible in pane');}catch(PERR){g.reportError(PERR);}

                if (ignoreSearch || !pane || !self.hasClass(candidate.getElement(pane.id), 'hidden'))
                {
                    next = candidate;
                    break;
                }
            }
        }

        cItem = parent;

        if (options && !next)
        {
            options.numLevels--;
        }

        if (pane && cItem === pane.item())
        {
            break;
        }
    }

    try{self.PAssert(804,isTest || !next || item === self.getPreviousItem(next, pane, { isTest: true, ignoreSearch: ignoreSearch }), 'Previous/Next should always match');}catch(PERR){g.reportError(PERR);}

    return next;
};

self.getFirstPaneItem = function(pane)
{
    return self.getNextItem(pane.item());
};

self.getLastPaneItem = function(pane)
{
    var item = pane.item().getLastChild();
    for (; item.hasChildren(); item = item.getLastChild());

    try{self.PAssert(805,item, 'Must always have a valid pane item');}catch(PERR){g.reportError(PERR);}

    return item;
};

self.getItemCounts = function(start, end, pane)
{
    try{self.PAssert(806,start, 'Start must be a valid item');}catch(PERR){g.reportError(PERR);}
    try{self.PAssert(807,end, 'End must be a valid item');}catch(PERR){g.reportError(PERR);}

    var count = 0;
    var fullscreenChildCount = 0;

    var item = start;
    do
    {
        item = self.getNextItem(item, pane);
        try{self.PAssert(808,item, 'Items must exist');}catch(PERR){g.reportError(PERR);}

        if (item.parent() === pane.item() && item.isHeader())
        {
            fullscreenChildCount++;
        }

        count++;
    } while (item !== end)

    return { fscount: fullscreenChildCount, icount: count };
};

// This is inclusive of the start and end item.
self.getItemsBetween = function (start, end, pane)
{
    try{self.PAssert(809,start, 'Start must be a valid item');}catch(PERR){g.reportError(PERR);}
    try{self.PAssert(810,end, 'End must be a valid item');}catch(PERR){g.reportError(PERR);}

    var selected = [ start ];

    var item = start;
    while (item !== end)
    {
        item = self.getNextItem(item, pane);
        try{self.PAssert(811,item, 'Items must exist');}catch(PERR){g.reportError(PERR);}

        selected.push(item);
    }

    return selected;
};

// Given two items, determine which appears first in the tree
self.getFirstItem = function (item1, item2)
{
    if (item1.id == item2.id || item2.parent().id == item1.id)
    {
        return item1;
    }
    else if (item1.parent().id == item2.id)
    {
        return item2;
    }

    var p = item1;

    var prevP = undefined;
    do
    {
        var q = item2;

        var prevQ = undefined;
        do
        {
            if (p.id == q.id)
            {
                if (prevP === undefined)
                {
                    // item2 is an ancestor of item1
                    return item1;
                }
                else if (prevQ === undefined)
                {
                    // item1 is an ancestor of item2
                    return item2;
                }

                // TODO: These asserts arent really valid, edge cases that aren't handled can hit them
                try{self.PAssert(812,prevQ !== undefined, "Fix this assert by handling this edge case!");}catch(PERR){g.reportError(PERR);}

                try{self.PAssert(813,p.id == prevQ.parent().id, "Must share a common parent");}catch(PERR){g.reportError(PERR);}
                try{self.PAssert(814,p.id == prevP.parent().id, "Must share a common parent");}catch(PERR){g.reportError(PERR);}

                try{self.PAssert(815,prevP.id != prevQ.id, "If both previous items match, they should have been the common ancestor");}catch(PERR){g.reportError(PERR);}

                return (prevP.getIndex() < prevQ.getIndex())  ? item1 : item2;
            }

            prevQ = q;
        } while ((q = q.parent()) !== undefined)

        prevP = p;
    } while ((p = p.parent()) !== undefined)

    try{self.PAssert(816,false, 'One item must appear first in the tree');}catch(PERR){g.reportError(PERR);}

    return undefined;
}

self.animateTransform = function(element, options)
{
    var transform = options.transform;

    // If we want a delay, delay and call setTransform again without the delay set
    if(!isNaN(options.delay))
    {
        var delay = options.delay;
        options.delay = undefined;
        setTimeout(self.animateTransform, delay, element, options);

        return;
    }

    function onComplete()
    {
        if(options.clearTransform)
        {
            element.style[platform.transform.style] = '';
            element.style[platform.transformOrigin.style] = '';
        }
        if(options.onComplete)
        {
            options.onComplete();
        }
    }

    if (options.time)
    {
        var trans = self.getTransitionClass(options.time);
        self.addClass(element, trans);

        // After the animation is complete, remove the animation class
        setTimeout(function()
        {
            self.removeClass(element, trans);
            onComplete();
        }, options.time);
    }
    else
    {
        onComplete();
    }

    if (!isNaN(options.opacity))
    {
        element.style.opacity = options.opacity;
    }
    if(!transform && !isNaN(options.left) && !isNaN(options.top))
    {
        transform = 'translate3d(' + left + 'px,' + top + 'px,0)';
    }

    if(options.origin)
    {
        element.style[platform.transformOrigin.style] = options.origin;
    }

    element.style[platform.transform.style] = transform;
};

self.easeOut = function (t, b, c)
{
    return -c *(t)*(t-2) + b;
};

self.scrollTo = function(n, duration, element, horiz, cb)
{
    element = element || self.focusedPane.rootElement;

    var scrollVar = (horiz ? 'scrollLeft' : 'scrollTop');

    if (duration > 0)
    {
        self.isScrollingIntoView = true;

        var scrollValue = element[scrollVar];
        var startTime;
        var difference = n - scrollValue;

        if (DEBUG)
        {
            console.log('Animated Scrolling [' + scrollValue + '] -> [' + n + ']');
        }

        var lastValue = scrollValue;
        function scrollLoop(timestamp)
        {
            if (!startTime)
            {
                startTime = timestamp - 20;
            }

            var t = Math.min((timestamp - startTime) / duration, 1);

            var scrollOffset = self.easeOut(t, scrollValue, difference);
            element[scrollVar] = scrollOffset

            if (t == 1)
            {
                self.isScrollingIntoView = false;

                if (platform.ios)
                {
                    // Fix that weird rendering bug that doesn't move
                    // the cursor to where it's supposed to be
                    self.setSelectionToWhatItAlreadyIs();

                    if (cb)
                    {
                        cb();
                    }
                }

                return;
            }

            requestAnimationFrame(scrollLoop);
        }

        requestAnimationFrame(scrollLoop);
    }
    else
    {
        if (DEBUG)
        {
            log('Jump Scrolling [' + element[scrollVar] + '] -> [' + n + ']');
        }

        if (element)
        {
            element[scrollVar] = n;
        }
    }
};

self.computeScrollSpeed = function (min, max, val)
{
    return ((max - min) - (max - val)) / (max - min);
};

var lastState = { speed: 0, pane: undefined };
var runScroll = false;

function performScroll()
{
    var minSpeed = 2;
    var maxSpeedAdd = 12;

    var scroller = lastState.pane.getScroller();

    var diff = (minSpeed + (maxSpeedAdd * lastState.speed));

    scroller.scrollTop += diff;

    if (runScroll)
    {
        requestAnimationFrame(performScroll);
    }
}

self.repeatScroll = function (speed, pane)
{
    lastState.speed = speed;
    lastState.pane = pane;

    if (!runScroll)
    {
        runScroll = true;

        requestAnimationFrame(performScroll);
    }
};

self.cancelRepeatScroll = function ()
{
    runScroll = false;
};

var VMPane;
self.getPaneForElement = function (element)
{
    if (!VMPane)
    {
        VMPane = require('VMPane');
    }

    if (element)
    {
        var localContext;
        if (android && android.isEnabled() && element.id == 'androidInput')
        {
            localContext = ko.contextFor(android.getCurrentVMLI().getSpan());
        }
        else
        {
            localContext = ko.contextFor(element);
        }

        // In some cases (zooming in) we detach nodes from the DOM. In this case, VMLIs may be referring to stale
        // DOM elements and try to get the KO context for them.
        //try{self.PAssert(817,localContext, 'Need a valid KO context to find pane');}catch(PERR){g.reportError(PERR);}

        var paneCandidate = (localContext ? localContext.$parents[localContext.$parents.length - 2] : undefined);

        if (!paneCandidate && (localContext && localContext.$parents.length === 1))
        {
            paneCandidate = localContext.$data;
        }

        if (paneCandidate instanceof VMPane)
        {
            return paneCandidate;
        }
    }

    return undefined;
};

self.elementInPane = function (element)
{
    if (element)
    {
        var localContext = ko.contextFor(element);

        if (localContext)
        {
            var paneCandidate = localContext.$parents[localContext.$parents.length - 2];
            if (paneCandidate)
            {
                return self.hasClass(paneCandidate.element, 'pane');
            }
        }
    }

    return false;
};

self.scrollToTop = function(element, duration, options)
{
    if (element)
    {
        var callback = options && options.callback;
        var scrollView = self.findParent(element, 'scroller');

        if (scrollView)
        {
            var top = element.getBoundingClientRect().top;

            var scrollTop = scrollView.scrollTop;
            var amtMoved = top - self.topBoxHeight + ((platform.orientation == Orientation.Landscape) ? self.topBarHeight : 0);

            var n = scrollTop + amtMoved;

            self.scrollTo(n, duration, scrollView);

            return amtMoved || true;
        }
    }

    return false;
};

self.scrollIntoView = function(element, duration, options)
{
    var callback = options && options.callback;
    var noToolbar = options && options.noToolbar;
    if (isNaN(duration))
    {
        duration = 200; // Default time
    }

    var performedScroll = false;

    if (element)
    {
        var scrollView = self.findParent(element, 'scroller');

        if (scrollView)
        {
            var scrollBounds = scrollView.getBoundingClientRect();
            var scrollBottom = scrollBounds.bottom;

            var bounds = element.getBoundingClientRect();

            var extraMoveAmt = 10;
            var scrollTop = scrollView.scrollTop;
            var n;

            // TODO: This doesn't take into account horizontal scrollbars that may appear from having multiple panes
            if(platform.mobile)
            {
                if(!noToolbar)
                {
                    scrollBottom -= self.keyboardToolbarHeight;
                }
                // Don't do this on ios7 unless keyboard is not open
                if(!platform.ios || platform.ios71 || !self.isKeyboardOpen)
                {
                    if(platform.kbsize)
                    {
                        scrollBottom -= platform.kbsize;
                    }
                    else
                    {
                        // Don't know the actual keyboard height so we'll have to guess.

                        // TODO: Put a number for Android in here?
                        // if(platform.ios)
                        scrollBottom -= 248; // iOS keyboard size + keyboard toolbar height
                    }
                }
            }

            if(bounds.top < scrollBounds.top)
            {
                var item = ko.dataFor(element);
                if(!platform.mobile && item == self.getFirstPaneItem(self.focusedPane))
                    n = 0;
                else
                    n = scrollTop + bounds.top - scrollBounds.top - extraMoveAmt;
            }
            else if(bounds.bottom > scrollBottom)
            {
                n = scrollTop + bounds.bottom - scrollBottom + extraMoveAmt;
            }

            if(n !== undefined)
            {
                self.scrollTo(n, duration, scrollView);
                if(callback)
                {
                    if(duration > 0)
                    {
                        setTimeout(callback, duration);
                    }
                    else
                    {
                        callback();
                    }
                }
                performedScroll = true;
            }
        }
    }

    return performedScroll;
};

self.isKeyboardInputElement = function(ele)
{
    if(ele.type === 'checkbox')
        return false;
    if(ele.nodeName == 'INPUT' || ele.type == 'textarea')
        return true;
    return false;
};

self.openKeyboardOn = function(e, element, showKeyboard, focusFn, blurFn)
{
    try{self.PAssert(818,platform.mobile, 'Should only be opening keyboard on mobile devices');}catch(PERR){g.reportError(PERR);}

    var scrollView = self.findParent(element, 'scroller');
    var scrollStart = scrollView ? scrollView.scrollTop : 0;
    var performedScroll = false;

    if(showKeyboard)
        self.keyboardToolbar.preShow();

    if (!self.isKeyboardOpen)
    {
        // Scroll item into view first
        if (platform.app || platform.android || platform.mobileie)
        {
            var callback = function()
            {
                if(showKeyboard)
                    self.keyboardToolbar.show();
                else
                    self.isKeyboardOpen = true;
                if(focusFn)
                    focusFn();

                self.enforceScroll();
            };

            self.addClass(scrollView, 'padded');

            // Wait a frame to allow padding to take effect before scrolling
            performedScroll = self.scrollIntoView(element, platform.keyboardOpenTime, {callback: callback, noToolbar: !showKeyboard});
        }
        else
        {
            performedScroll = self.scrollIntoView(element, platform.keyboardOpenTime);
        }
    }
    else
    {
        log('keyboard open');
        // if(focusFn)
        //     focusFn();
    }

    if(!performedScroll || !platform.app || platform.android)
    {
        // focusFn needs to be called first so it will blur the previous element before
        // showing the keybaord
        if(focusFn)
            focusFn();
        if(showKeyboard)
            self.keyboardToolbar.show();
    }


    // TODO: Defining this here is not the sexiest thing
    self.onCloseKeyboard = function ()
    {
        self.addClass(scrollView, 'padded');

        self.scrollTo(scrollStart, platform.keyboardOpenTime, scrollView, false, function()
        {
            self.removeClass(scrollView, 'padded');
        });
        // if(showKeyboard)
        // {
        self.isKeyboardOpen = false;
        // }
        if(blurFn)
            blurFn();

        // editable.onblur = undefined;
        // self.fireCustomEvent('blur');
        window.removeEventListener('closeKeyboard', self.onCloseKeyboard);
        self.onCloseKeyboard = undefined;
    }
    window.addEventListener('closeKeyboard', self.onCloseKeyboard);

    // if(platform.ios && !platform.app)
    // {
    //     var editable = self.findParent(element, 'editable');
    //     try{self.PAssert(819,editable, 'Editable parent should exist');}catch(PERR){g.reportError(PERR);}
    //     editable.onblur = function()
    //     {
    //         self.fireCustomEvent('closeKeyboard');
    //         if (editable) { editable.onblur = undefined; }
    //     };
    // }


    // Prevent native tap handler since setting selection will bring up
    // the keyboard
    if (platform.ios)
    {
        self.preventClick();
    }

    e.preventDefault();
    e.stopImmediatePropagation();
    e.returnFalse = true;

    self.enforceScroll();
}

self.blurActiveElement = function(ignoreSelectionChange)
{
    self.ignoreSelectionChange = !!ignoreSelectionChange;
    self.fireCustomEvent('closeKeyboard');

    document.activeElement.blur();

    setTimeout(function(){
        self.ignoreSelectionChange = false;
    }, 100);
}

self.preventScrollPropagation = function (ele)
{
    // return true;

    // log(self.vmMain.isKeyboardOpen());
    // if(!self.vmMain.isKeyboardOpen())
    // {
    //     return true;
    // }
    if (ele.scrollHeight === ele.clientHeight)
    {
        return false;
    }
    else
    {
        if (ele.scrollTop === 0)
        {
            ele.scrollTop = 1;
        }
        else
        {
            if (ele.scrollTop + ele.offsetHeight >= ele.scrollHeight)
            {
                ele.scrollTop = ele.scrollHeight - ele.offsetHeight - 1;
            }
        }
    }

    return true;
};

self.setSelectionToWhatItAlreadyIs = function()
{
    var sel = window.getSelection();
    if (sel.rangeCount > 0)
    {
        var range = sel.getRangeAt(0);

        if (DEBUG) log('Set Selection to Same', range);

        sel.removeAllRanges();
        sel.addRange(range);
    }
};

self.setSelection = function(element, start, end)
{
    try{self.PAssert(820,element, 'Must supply a valid element to set selection on');}catch(PERR){g.reportError(PERR);}
    try{self.PAssert(821,start >= 0 && end >= 0, 'Must specify positive start and end offsets');}catch(PERR){g.reportError(PERR);}
    if (start < 0 || end < 0) { return false; }
    if (!element) { return false; }

    var retVal = true;
    var select;

    try
    {
        var children = getTextNodesIn(element);
        var range = document.createRange();
        select = children.length > 0 ? children[0] : element;

        range.setStart(select, start);
        range.setEnd(select, end);

        var sel = window.getSelection();

        if (DEBUG) log('Set Selection: [' + start + ',' + end + ']', select);

        sel.removeAllRanges();
        sel.addRange(range);
    }
    catch (err)
    {
        self.reportError(err, {
            element: self.elementToString(element, true),
            select: self.elementToString(select, true),
            start: start,
            end: end,
            children: (children.length > 0 ? self.elementToString(element, true) : undefined)
        });

        retVal = false;
    }

    return retVal;
};

// Takes the current DOM Range selection and restricts it to a valid set of selections that
// only contain text under <span class='text'> elements.
self.restrictSelectionToText = function()
{
    if (android && android.isEnabled())
    {
        return;
    }

    var selection,
        range,
        rangeStartContainer,
        rangeEndContainer,
        rangeStart,
        rangeEnd;

    var updateSelection = function()
    {
        selection = self.getSelectionInfo();
        if (!selection || selection.rangeCount === 0)
        {
            return true;
        }

        range = selection.selection.getRangeAt(0);
        rangeStartContainer = range.startContainer;
        rangeEndContainer = range.endContainer;
        rangeStart = range.startOffset;
        rangeEnd = range.endOffset;
    };

    updateSelection();

    if (!selection)
    {
        return;
    }

    try{self.PAssert(822,rangeStartContainer.nodeName == '#text' || rangeStartContainer.nodeName == 'SPAN', 'Expected #text or SPAN, Actual: ' + rangeStartContainer.nodeName);}catch(PERR){g.reportError(PERR);}
    try{self.PAssert(823,rangeEndContainer.nodeName == '#text' || rangeEndContainer.tagName == 'LI' || rangeEndContainer.tagName == 'UL' ||
                rangeEndContainer.tagName == 'SPAN', 'Expected LI, UL, #text, SPAN, Actual: ' + rangeEndContainer.tagName);}catch(PERR){g.reportError(PERR);}

    if(rangeStartContainer.nodeName === 'SPAN')
    {
        self.selectMultiline(rangeStartContainer, rangeStart, rangeEndContainer, rangeEnd);
        updateSelection();
    }

    // This is the case where the selection is at the end of the li and past the #comment block so
    // the rangeStartContainer's parent is the li and not the text span.
    if(rangeStartContainer.nodeName === '#text' &&
       rangeStartContainer.previousSibling &&
       rangeStartContainer.previousSibling.nodeName === '#comment')
    {
        try{self.PAssert(824,false, 'This case shouldnt be getting hit');}catch(PERR){g.reportError(PERR);}

        self.selectChildren(self.getSpanParent(rangeStartContainer), 0, rangeEnd);
        updateSelection();
    }

    if (rangeStartContainer !== rangeEndContainer && rangeEnd === 0 && rangeEndContainer.nodeName == 'P')
    {
        rangeEndContainer = rangeEndContainer.parentNode.parentNode;
        // log(rangeEndContainer);
        var prevLI = rangeEndContainer.previousElementSibling;
        try{self.PAssert(825,!prevLI || prevLI.tagName == 'LI');}catch(PERR){g.reportError(PERR);}

        if (!prevLI)
        {
            prevLI = rangeEndContainer.parentNode.parentNode;
        }
        // Preview sibling has children. The line before is its last child.
        else if (prevLI.children.length > 1)
        {
            var children = prevLI.children[1].children;
            prevLI = children[children.length - 1];
        }

        var textLength = ko.dataFor(prevLI).getParsedText().length;
        // log(rangeStartContainer, prevLI);
        self.selectMultiline(rangeStartContainer, rangeStart, prevLI, textLength);
    }

    // If the selection goes past it into the next LI, the line we want is the line before.
    // This happens when you triple click in the margin to the right of the text
    // or select multiple items ending past the margin.
    else if (rangeEndContainer.tagName === 'LI')
    {
        var prevLI = rangeEndContainer.previousElementSibling;
        try{self.PAssert(826,!prevLI || prevLI.tagName === 'LI');}catch(PERR){g.reportError(PERR);}

        if (!prevLI)
        {
            prevLI = rangeEndContainer.parentNode.parentNode;
            console.log(prevLI);
        }
        // Preview sibling has children. The line before is its last child.
        else if (prevLI.children.length > 1)
        {
            var children = prevLI.children[1].children;
            prevLI = children[children.length - 1];
        }

        var prevSpan = prevLI.firstChild;
        try{self.PAssert(827,prevSpan.tagName === 'SPAN', 'Expected SPAN, Actual: ' + prevSpan.tagName);}catch(PERR){g.reportError(PERR);}

        self.selectChildren(ko.dataFor(prevSpan), 0, prevSpan.textContent.length);
    }
    else if (rangeEndContainer.nodeName === 'UL')
    {
        // This happens when pressing ctrl+A when there is only a title for a fullscreen child
        var prevSpan = rangeEndContainer.parentElement.firstElementChild;
        try{self.PAssert(828,prevSpan.tagName === 'SPAN', 'Expected SPAN, Actual: ' + prevSpan.tagName);}catch(PERR){g.reportError(PERR);}

        self.selectChildren(ko.dataFor(prevSpan), 0, prevSpan.textContent.length);
    }
};

self.trimString = function (str)
{
    var str = str.replace(/^\s\s*/, ''),
        ws = /\s/,
        i = str.length;
    while (ws.test(str.charAt(--i)));
    return str.slice(0, i + 1);
};

self.enforceScroll = function(force, top)
{
    if (force || (((platform.ios || platform.android) && (!platform.app || platform.ios71)) && !platform.bodyscroll))
    {
        setTimeout(function() {window.scrollTo(0, top || 0);},0);
        requestAnimationFrame(function() {window.scrollTo(0, top || 0);});
        window.scrollTo(0, top || 0);
        document.body.scrollTop = 0;
    }
};

self.preventDefault = function(e)
{
    e.preventDefault();
};

self.preventClick = function()
{
    self.addClass(self.element('outerWrapper'), 'noClick');

    window.ontouchstart = self.preventDefault;

    requestAnimationFrame(function()
    {
        window.ontouchstart = undefined;
        self.removeClass(self.element('outerWrapper'), 'noClick');
    });
};

function searchVMLIArray(options, array)
{
    for (var i = 0; i < array.length; ++i)
    {
        if(options.comparator)
        {
            if (options.comparator(array[i]))
            {
                options.action(array[i]);
            }
        }
        else if(options.each)
        {
            options.each(array[i]);
        }

        options.item = array[i];
        // Recurse through children
        self.searchVMLIs(options);
    }
}

self.searchVMLIs = function(options)
{
    var startItem = options.item;
    if(!startItem)
    {
        startItem = self.vmMain.root();
    }

    if(options.headers)
    {
        searchVMLIArray(options, startItem.headers());
    }
    else
    {
        searchVMLIArray(options, startItem.items());
    }

    if (options.includeArchived && startItem.hasArchivedChildren())
    {
        searchVMLIArray(options, startItem.archivedItems(/*underlying*/true));
    }
};

self.getLastVisibleItem = function(root)
{
    var list = root.items();
    for(var i = list.length - 1; i >= 0; i --)
    {
        if(!self.vmSearchPhone.hiddenItems[list[i].id])
        {
            var lastItem = self.getLastVisibleItem(list[i]);
            if(lastItem)
                return lastItem;
            else
                return list[i];
        }
    }
    return undefined;
}

self.invertObject = function(obj)
{
    var target = {};
    for (var i in obj)
    {
        if (obj.hasOwnProperty(i))
        {
            target[obj[i]] = i;
        }
    }
    return target;
};

