'use strict';
define('VMLI_Drive',['globals', 'data', 'gdata', 'tracker'],
function (g, d, gdata, tracker) {

var VMLI_Drive = function(VMLI)
{
    function iterateRemoteLink(remoteItem)
    {
        gdata.cacheItem(remoteItem);

        iterateLink(gdata.get(remoteItem, 'items'));
        iterateLink(gdata.get(remoteItem, 'archivedItems'));
    }

    function iterateLink(itemArray)
    {
        if (!itemArray)
        {
            return;
        }

        for (var i = 0; i < itemArray.length; ++i)
        {
            var item = itemArray.get(i);
            try{g.PAssert(1088,item, 'Item must exist to get it from the array');}catch(PERR){g.reportError(PERR);}

            if (item)
            {
                var localItem = d.getModel(item.id);
                if (localItem)
                {
                    localItem.linkToGDrive(item);
                }
                else
                {
                    iterateRemoteLink(item);
                }
            }
            else
            {
                // Remove undefined/nulls from remote list
                itemArray.remove(i);

                i--;
            }
        }
    }

    return {
    linkToGDrive: function(data, overwriteFromRemote)
    {
        try{g.PAssert(1089,data, 'Must be passing in valid data');}catch(PERR){g.reportError(PERR);}

        if (this.data)
        {
            if (DEBUG) log('Attempting to initialize an item twice with remote data', this.id);

            return;
        }

        this.data = data;
        gdata.cacheItem(data);

        this.setRemoteVersion(VersionType.Structure, gdata.getVersion(data, VersionType.Structure));
        this.setRemoteVersion(VersionType.Text, gdata.getVersion(data, VersionType.Text));

        if (DEBUG) log('Item: ', this.getParsedText() + ' - Local: ' + this.getVersion(VersionType.Text) + '  Remote: ' + this.getRemoteVersion(VersionType.Text));

        if (overwriteFromRemote)
        {
            this.setVersion(VersionType.Structure, this.getRemoteVersion(VersionType.Structure));
            this.setVersion(VersionType.Text, this.getRemoteVersion(VersionType.Text));
        }

        iterateLink(gdata.get(data, 'items'));
        iterateLink(gdata.get(data, 'archivedItems'));
    },
    resetModified: function()
    {
        var i;
        try{g.PAssert(1090,this.data, 'Can only reset modified if remote data exists');}catch(PERR){g.reportError(PERR);}

        var remoteChanged = false;

        var versionChanges = { };

        // If modified offline and the local version does not match the remote version then the remotee
        // must have been changed. In this case, update the local version to match the remote and save it.

        if (!this.modifiedOffline && this.isRemoteVersionNewer(VersionType.Structure))
        {
            var remoteVersion = this.getRemoteVersion(VersionType.Structure);
            this.setVersion(VersionType.Structure, remoteVersion);

            versionChanges.version = remoteVersion

            remoteChanged = true;
        }
        else if (this.modifiedOffline)
        {
            //try{g.PAssert(1091,this.version >= this.versionRemote || this.version == this.versionRemote);}catch(PERR){g.reportError(PERR);}

            versionChanges.modifiedOffline = false;

            remoteChanged = true;
        }

        if (!this.modifiedOfflineText && this.isRemoteVersionNewer(VersionType.Text))
        {
            var remoteVersionText = this.getRemoteVersion(VersionType.Text);
            this.setVersion(VersionType.Text, remoteVersionText);

            versionChanges.versionText = remoteVersionText;

            remoteChanged = true;
        }
        else if (this.modifiedOfflineText)
        {
            //try{g.PAssert(1092,this.versionText >= this.versionRemoteText || this.versionText == this.versionRemoteText);}catch(PERR){g.reportError(PERR);}

            versionChanges.modifiedOfflineText = false;

            remoteChanged = true;
        }

        if (remoteChanged)
        {
            this.save(versionChanges, SaveFlag.None);
        }

        // Only need remote versions while resolving conflicts
        this.setRemoteVersion(VersionType.Structure, undefined);
        this.setRemoteVersion(VersionType.Text, undefined);

        var items = this.items();
        for (i = 0; i < items.length; ++i)
        {
            items[i].resetModified();
        }

        if (this.hasArchivedChildren())
        {
            var archivedItems = this.archivedItems(/*underlying*/true);
            for (i = 0; i < archivedItems.length; ++i)
            {
                archivedItems[i].resetModified();
            }
        }
    },
    initFromGDrive: function(data, parseChildren)
    {
        try{g.PAssert(1093,data, 'To initialize from gdrive a valid remote data object must be passed in');}catch(PERR){g.reportError(PERR);}

        if (!data)
        {
            g.reportError(new Error('FATAL ERROR - No remote data for item'));

            return;
        }

        // log('init', this.text);

        if (DEBUG && this.data)
        {
            try{g.PAssert(1094,this.data.id == data.id, 'If there is already valid data for this item, it shouldn\'t change');}catch(PERR){g.reportError(PERR);}
            try{g.PAssert(1095,this.id == data.id || this.id == 'root', 'If there is already valid data for this item, it shouldn\'t change');}catch(PERR){g.reportError(PERR);}
        }

        this.data = data;
        // Log color key: green = from remote, blue = to remote, pink = duplicating, orange = remove local, red = remove remote

        var needsFullSave = ((this.id === undefined) || this.forceModifiedItems);

        var remoteChanges = needsFullSave ? undefined : { };
        var needsParsing = false;

        this.id = data.id;

        try
        {
            needsParsing = this.resolveText(remoteChanges) || needsParsing;
            needsParsing = this.resolveMisc(remoteChanges) || needsParsing;

            if (needsParsing)
            {
                g.vmMain.addParseItem(this, /*isRemoteLoad*/true);

                g.vmMain.addModifiedItem(this);
            }

            this.resolveItems(remoteChanges, parseChildren);
        }
        catch (err)
        {
            g.reportError(err);

            if (REPORT_ACTIONS) { try { g.reportAction(RATAG.R, ResolveAction.Error, { msg: err.message, stacK: err.stack, item: this.getDebugData() }); } catch(sErr) { g.reportActionCrash(sErr); } }
        }

        if (needsFullSave)
        {
            try{g.PAssert(1096,remoteChanges === undefined, 'If doing a full save, remoteChanges should always be undefined');}catch(PERR){g.reportError(PERR);}

            //if (!d.hasLocalData)
            {
                this.setVersion(VersionType.Structure, this.getRemoteVersion(VersionType.Structure));
                this.setVersion(VersionType.Text, this.getRemoteVersion(VersionType.Text));
            }

            this.save(null, SaveFlag.None);
        }
        else if (!g.isEmpty(remoteChanges))
        {
            if (remoteChanges.isDeleted !== true)
            {
                remoteChanges.isDeleted = undefined;
            }

            this.save(remoteChanges, SaveFlag.None);
        }

        gdata.attachEventsToItem(this);
    },
    resolveText: function(remoteChanges)
    {
        // Keep track of whether or not the text has changed and needs to be re-parsed
        var needsParsing = false;

        //
        // Conflict resolution for text change
        //

        {
            if (this.modifiedOfflineText && !this.forceModifiedItems)
            {
                 // If local version is newer, set it on remote otherwise if remote is newer create a conflicted item.
                if (this.isLocalVersionNewer(VersionType.Text))
                {
                    if (DEBUG) log('%cSet text on remote', 'color: blue', this.getVersion(VersionType.Structure), this.getParsedText(), gdata.get(this.data, 'text'));

                    if (REPORT_ACTIONS) { try { g.reportAction(RATAG.A, ResolveAction.SetRemoteText, { text: this.getParsedText(), item: this.getDebugData() }); } catch(sErr) { g.reportActionCrash(sErr); } }

                    // var localText = this.getParsedText();
                    // var remoteText = gdata.get(this.data, 'text');

                    gdata.saveChanges(this, { text: this.getParsedText() });
                }
                else
                {
                    var remoteText = gdata.get(this.data, 'text');
                    try{g.PAssert(1097,remoteText !== undefined, 'Every item should have text associated with it from gdrive');}catch(PERR){g.reportError(PERR);}

                    var localText = this.getRawText();

                    if (DEBUG) log('%cModified offline but out of date. Creating a duplicate item.', 'color: pink', this.getVersion(VersionType.Structure), this.getParsedText(), gdata.get(this.data, 'text'));

                    var needDupe = false;

                    // Only dupe the text item if the remote text and local text have changed
                    // in a significant way.
                    //
                    // Ideally this will be replaced with a text diffing algorithm where we rarely
                    // need to dupe the text entries.
                    if (localText !== remoteText)
                    {
                        needDupe = true;
                    }

                    if (needDupe)
                    {
                        // Add a duplicated item after this one
                        var thisIndex = this.getIndex();

                        var newItem = new VMLI({text: this.getParsedText()}, {parent: this.parent(), forceSave: true});

                        this.parent().insertItem(newItem, thisIndex + 1);

                        // Set this to be the one from remote
                        this.setText(remoteText, /*immediateParse*/true);

                        if (REPORT_ACTIONS) { try { g.reportAction(RATAG.B, ResolveAction.DupeItem, { remoteText: remoteText, localText: this.getParsedText(), prevItem: this.getDebugData(), newItem: newItem.getDebugData() }); } catch(sErr) { g.reportActionCrash(sErr); } }

                        if (remoteChanges)
                        {
                            remoteChanges['text'] = this.getParsedText();
                        }

                        // TODO: Is this really necessary, we parse right above here
                        needsParsing = true;
                    }
                    else if (remoteChanges)
                    {
                        remoteChanges['modifiedOfflineText'] = false;
                    }

                    if (remoteChanges)
                    {
                        // Update local text version to match remote
                        remoteChanges['versionText'] = this.getRemoteVersion(VersionType.Text);
                    }
                }
            }
            else if (this.isRemoteVersionNewer(VersionType.Text))
            {
                var remoteText = gdata.get(this.data, 'text');
                try{g.PAssert(1098,remoteText !== undefined, 'Every item should have text associated with it from gdrive');}catch(PERR){g.reportError(PERR);}

                if (DEBUG) log('Text: [' + this.getVersion(VersionType.Text) + '] [' + this.getRemoteVersion(VersionType.Text) + ']: ' +  remoteText);

                if (this.getRawText() != remoteText)
                {
                    if (DEBUG) log('%cSet text from remote', 'color: green', this.getVersion(VersionType.Text), this.getParsedText(), gdata.get(this.data, 'text'));

                    if (REPORT_ACTIONS) { try { g.reportAction(RATAG.C, ResolveAction.SetLocalText, { oldText: this.getRawText(), newText: remoteText, item: this.getDebugData() }); } catch(sErr) { g.reportActionCrash(sErr); } }

                    this.setRawText(remoteText);

                    if (remoteChanges)
                    {
                        remoteChanges['text'] = this.getRawText();
                    }

                    needsParsing = true;
                }

                if (remoteChanges && this.getVersion(VersionType.Text) !== this.getRemoteVersion(VersionType.Text))
                {
                    // Update local text version to match remote
                    remoteChanges['versionText'] = this.getRemoteVersion(VersionType.Text);
                }
            }
        }

        return needsParsing;
    },
    resolveMisc: function(remoteChanges)
    {
        var useLocal = this.modifiedOfflineText && this.isLocalVersionNewer(VersionType.Text);

        //log('Size[' + this.data.size + ']: ' + this.data.keys());
        //inspectRemote(this.data.id);

        var needsParsing = false;

        var modLocal = false;

        // Only perform comparison on local vs remote data when one of the versions for the item is out of date.
        if (useLocal || this.isRemoteVersionNewer(VersionType.Structure) || this.isRemoteVersionNewer(VersionType.Text))
        {
            var localChanges = { };

            //
            // Check remote and local properties to see if they have changed.
            //

            // var formats = gdata.get(this.data, 'formats');
            // if (formats && formats.length > 0)
            // {
            //     // TODO: Conflict resolution for formats
            //     var incomingFormats = JSON.parse(JSON.stringify(formats));

            //     if (!this.areFormatsEqual(incomingFormats, this.formats))
            //     {
            //         if (useLocal)
            //         {
            //             localChanges['formats'] = this.formats;

            //             modLocal = true;
            //         }
            //         else
            //         {
            //             this.setFormats(incomingFormats, /*isFromRemote*/true);

            //             if (remoteChanges)
            //             {
            //                 remoteChanges['formats'] = incomingFormats;
            //             }

            //             needsParsing = true;
            //         }
            //     }
            // }

            // TODO: If an items is modified locally here, should it be pushing its date change at this point or wait?
            var remoteDate = gdata.get(this.data, 'date');
            if (remoteDate || this.date())
            {
                if (!this.date() || (this.date().getTime() !== remoteDate))
                {
                    if (useLocal && this.date())
                    {
                        localChanges['date'] = this.date().getTime();

                        modLocal = true;
                    }
                    else
                    {
                        // TODO: Conflict resolution for dates
                        this.date(new Date(remoteDate), /*fromRemote*/true);
                        this.date()._explicitTime = !!this.allDayDate;

                        if (remoteChanges)
                        {
                            remoteChanges['date'] = this.date().getTime();
                        }

                        needsParsing = true;
                    }
                }

                // AllDayDate should only be set if the date is also set
                var remoteAllDayDate = gdata.get(this.data, 'allDayDate');
                if (remoteAllDayDate)
                {
                    if (this.allDayDate !== remoteAllDayDate)
                    {
                        if (useLocal)
                        {
                            localChanges['allDayDate'] = this.allDayDate;

                            modLocal = true;
                        }
                        else
                        {
                            this.allDayDate = remoteAllDayDate;

                            if (remoteChanges)
                            {
                                remoteChanges['allDayDate'] = remoteAllDayDate;
                            }
                        }
                    }
                }
            }

            try{g.PAssert(1099,this.allDayDate === undefined || this.date() !== undefined, 'Cannot have an alldaydate with no date specified');}catch(PERR){g.reportError(PERR);}


            var remoteDateCreated = gdata.get(this.data, 'dateCreated');
            if (!this.dateCreated || !remoteDateCreated || this.dateCreated.getTime() !== remoteDateCreated)
            {
                if (useLocal)
                {
                    localChanges['dateCreated'] = this.dateCreated ? this.dateCreated.getTime() : undefined;

                    modLocal = true;
                }
                else
                {
                    var rDate = remoteDateCreated;
                    try{g.PAssert(1100,typeof remoteDateCreated !== 'string', 'Invalid date format stored');}catch(PERR){g.reportError(PERR);}

                    // if (typeof remoteDateCreated == 'string')
                    // {
                    //     rDate = (new Date(remoteDateCreated)).getTime();

                    //     gdata.saveChanges(this, { 'dateCreated': rDate });
                    // }

                    this.dateCreated = new Date(rDate);

                    if (remoteChanges)
                    {
                        remoteChanges['dateCreated'] = rDate;
                    }
                }
            }

            var remoteDateCompleted = gdata.get(this.data, 'dateCompleted');
            if (remoteDateCompleted || this.dateCompleted)
            {
                if (!this.dateCompleted || !remoteDateCompleted || this.dateCompleted.getTime() !== remoteDateCompleted)
                {
                    if (useLocal)
                    {
                        localChanges['dateCompleted'] = this.dateCompleted ? this.dateCompleted.getTime() : undefined;

                        modLocal = true;
                    }
                    else
                    {
                        var rDate = remoteDateCompleted;
                        try{g.PAssert(1101,typeof remoteDateCompleted !== 'string', 'Invalid date format stored');}catch(PERR){g.reportError(PERR);}

                        // if (typeof remoteDateCompleted == 'string')
                        // {
                        //     rDate = (new Date(remoteDateCompleted)).getTime();

                        //     gdata.saveChanges(this, { 'dateCompleted': rDate });
                        // }

                        this.dateCompleted = new Date(rDate);

                        if (remoteChanges)
                        {
                            remoteChanges['dateCompleted'] = rDate;
                        }
                    }
                }
            }

            var remotePriority = gdata.get(this.data, 'priority');
            if (remotePriority || this.priority())
            {
                if (this.priority() !== remotePriority)
                {
                    if (useLocal)
                    {
                        localChanges['priority'] = this.priority();

                        modLocal = true;
                    }
                    else
                    {
                        if (remotePriority === null)
                        {
                            remotePriority = VMLIFlag.None;
                        }

                        this.priority(remotePriority, /*fromRemote*/true);
                    }
                }
            }

            var remoteFlagged = gdata.get(this.data, 'isFlagged');
            if (remoteFlagged || this.isFlagged())
            {
                if (this.isFlagged() !== remoteFlagged)
                {
                    if (useLocal)
                    {
                        localChanges['isFlagged'] = this.isFlagged();

                        modLocal = true;
                    }
                    else
                    {
                        if (remoteFlagged === null)
                        {
                            remoteFlagged = false;
                        }

                        this.isFlagged(remoteFlagged, /*fromRemote*/true);
                    }
                }
            }

            var remoteComplete = gdata.get(this.data, 'isComplete');
            if (remoteComplete || this.isComplete())
            {
                if (this.isComplete() !== remoteComplete)
                {
                    if (useLocal)
                    {
                        localChanges['isComplete'] = this.isComplete();

                        modLocal = true;
                    }
                    else
                    {
                        if (remoteComplete === null)
                        {
                            remoteComplete = false;
                        }

                        this.isComplete(remoteComplete, /*fromRemote*/true);
                    }
                }
            }

            // TODO: We do not currently send isCollapsed state to the remote doc
            // var isCollapsed = (gdata.get(this.data, 'isCollapsed') === true);
            // if (isCollapsed !== !!this.isCollapsed())
            // {
            //     this.isCollapsed(isCollapsed);

            //     remoteChanges['isCollapsed'] = isCollapsed;
            // }

            if (useLocal)
            {
                try{g.PAssert(1102,modLocal === !g.isEmpty(localChanges), 'Verify modLocal is computed properly. It should track any changes to localChanges');}catch(PERR){g.reportError(PERR);}

                if (modLocal === true)
                {
                    gdata.saveChanges(this, localChanges);
                }
            }
        }

        return needsParsing;
    },
    resolveItemsHelper: function(resolveArchived, remoteChanges, parseChildren)
    {
        if (DEBUG) log('Resolving [', resolveArchived, ']: ',  this.getParsedText());

        var i,
            remoteItem,
            localIndex,
            localItem,
            localModel,
            saveItemLists;

        // Log color key:
        //   green = from remote
        //   blue = to remote
        //   brown: revert local change
        //   pink = duplicating
        //   orange = remove local
        //   red = remove remote

        var collabItemList = gdata.getItemList(this.data, resolveArchived);

        var itemArray;
        if (resolveArchived)
        {
            if (this.hasArchivedChildren())
            {
                itemArray = this.archivedItems(/*underlying*/true);
            }
            else
            {
                itemArray = [ ];
            }
        }
        else
        {
            itemArray = this.items();
        }

        try{g.PAssert(1103,itemArray, 'Item arrays must exist');}catch(PERR){g.reportError(PERR);}

        // Check remote items
        //
        // Cases:
        // 1. Normal = Exists in both places
        // 2. Added remotely = !localItem
        // 3. Deleted locally = modifiedOffline && localItem.isDeleted
        // 4. Moved remotely = !modifiedOffline
        // 5. Moved locally = modifiedOffline && !localItem.isDeleted
        for (i = 0; collabItemList && i < collabItemList.length; i++)
        {
            remoteItem = collabItemList.get(i);

            // In the past there have been instances of undefined/null items being improperly inserted into the
            // remote arrays. In this case, simply skip the item as it is invalid. It will be removed when checking
            // the remote integrity of the items array.
            if (!remoteItem)
            {
                try{g.PAssert(1104,remoteItem, 'Item in remote list does not exist');}catch(PERR){g.reportError(PERR);}
                continue;
            }

            try{g.PAssert(1105,gdata.loadSeen[remoteItem.id] === undefined || gdata.loadSeen[remoteItem.id] === false, 'Items can only be seen once on the remote.');}catch(PERR){g.reportError(PERR);}

            // Mark item as being seen on remote
            gdata.loadSeen[remoteItem.id] = this.id;

            var localIndex = this.getIndexOf(remoteItem.id, /*isArchived*/resolveArchived);

            if (localIndex >= 0)
            {
                // The normal case - the item has the same local and remote parent and archive status.
                localModel = itemArray[localIndex];
                localModel.initFromGDrive(remoteItem);
            }
            else
            {
                // If the item is not found as a child of the local parent, check to see if it is located elsewhere or
                // if it was added remotely.
                localItem = d.getItem(remoteItem.id);
                localModel = d.getModel(remoteItem.id);

                if (!localItem)
                {
                    // If there is no corresponding local item for the remote item, it must be a newly added remote item,
                    // it needs to be added locally.
                    if (DEBUG) log('%cAdding from remote', 'color: green', this.getRawText(), i, remoteItem.id, gdata.get(remoteItem, 'text'));

                    localModel = new VMLI(
                    {
                        data: remoteItem,
                        isArchived: resolveArchived
                    },
                    {
                        parent: this,
                        parse: parseChildren
                    });

                    itemArray.splice(i, 0, localModel);
                    saveItemLists = true;

                    if (REPORT_ACTIONS) { try { g.reportAction(RATAG.D, ResolveAction.AddLocalItem, { item: localModel.getDebugData(), parent: this.getDebugData(), index: i, isArchived: resolveArchived }); } catch(sErr) { g.reportActionCrash(sErr); } }

                    g.vmMain.addModifiedItem(localModel);
                }
                else
                {
                    // There is a corresponding local item for the remote item, however it has a different parent than the
                    // remote item and may have been deleted. Based on versioning determine where to move them item.
                    if (localItem.isDeleted)
                    {
                        try{g.PAssert(1106,!localModel, 'The local model should never exist when an item has been marked as deleted locally');}catch(PERR){g.reportError(PERR);}

                        // The local item has been deleted, but the item still exists in the remote, it must have been deleted locally.
                        try{g.PAssert(1107,localItem.modifiedOffline, 'How did it get deleted if it wasn\'t modified offline?');}catch(PERR){g.reportError(PERR);}

                        // Note: Have to specifically check versions instead of using isLocalVersionNewer because
                        // the model doesn't exist due to the item being marked as isDeleted.
                        if (gdata.getVersion(remoteItem, VersionType.Structure) == localItem.version)
                        {
                            // Not modified remotely
                            if (DEBUG) log('%cDeleting from remote', 'color: red', this.getRawText(), i, remoteItem.id, gdata.get(remoteItem, 'text'));

                            // Delete remotely
                            gdata.itemRemoved({
                                item: this,
                                index: i,
                                numToRemove: 1,
                                isArchived: resolveArchived
                            });

                            if (REPORT_ACTIONS) { try { g.reportAction(RATAG.E, ResolveAction.RemoveRemoteItem, { item: this.getChild(i).getDebugData(), parent: this.getDebugData(), index: i, isArchived: resolveArchived }); } catch(sErr) { g.reportActionCrash(sErr); } }

                            i--;
                        }
                        else
                        {
                            try{g.PAssert(1108,!localModel, 'The local model should never exist when an item has been marked as deleted locally');}catch(PERR){g.reportError(PERR);}

                            if (DEBUG) log('%cAdding deleted item back from remote', 'color: brown', this.getRawText(), i, remoteItem.id, gdata.get(remoteItem, 'text'));

                            // Modified remotely so add it back locally. Always err on the side of keeping data instead of removing it.
                            d.undeleteItem(localItem);

                            localModel = new VMLI(
                            {
                                data: remoteItem,
                                isArchived: resolveArchived
                            },
                            {
                                parent: this,
                                parse: parseChildren,
                                forceSave: true
                            });

                            itemArray.splice(i, 0, localModel);
                            saveItemLists = true;

                            if (REPORT_ACTIONS) { try { g.reportAction(RATAG.F, ResolveAction.AddBackDeletedItem, { item: localModel.getDebugData(), parent: this.getDebugData(), index: i, isArchived: resolveArchived }); } catch(sErr) { g.reportActionCrash(sErr); } }

                            g.vmMain.addModifiedItem(localModel);
                        }
                    }
                    else if (localModel && localModel.modifiedOffline)
                    {
                        // If the localItem was not marked as isDeleted the localModel must exist. Additionally the localModel will have
                        // a different parent than the remote item.

                        // Not modified remotely, so local changes will be applied.
                        if (localModel.isLocalVersionNewer(VersionType.Structure))
                        {
                            // It needs to be moved remotely but let the case where items are moved locally handle it when
                            // traversing the local items lists.
                            if (DEBUG) log('%cMoved from here locally. 0x90.', 'color: gray', this.getRawText(), i, remoteItem.id, gdata.get(remoteItem, 'text'));

                            if (REPORT_ACTIONS) { try { g.reportAction(RATAG.G, ResolveAction.LocalMoveCandidate, { item: localModel.getDebugData(), parent: this.getDebugData(), index: i, isArchived: resolveArchived }); } catch(sErr) { g.reportActionCrash(sErr); } }

                            // If the item is being left alone, don't mark as seen so that it will be handled later.
                            try{g.PAssert(1109,gdata.loadSeen[remoteItem.id] === this.id);}catch(PERR){g.reportError(PERR);}
                            gdata.loadSeen[remoteItem.id] = false;
                        }
                        else
                        {
                            // Modified remotely as well as locally, remote changes will be applied.
                            if (DEBUG) log('%cMoved from here locally but overridden by remote', 'color: brown', this.getRawText(), i, remoteItem.id, gdata.get(remoteItem, 'text'));

                            if (REPORT_ACTIONS) { try { g.reportAction(RATAG.H, ResolveAction.MoveLocalItem, { item: localModel.getDebugData(), oldParent: localModel.parent(), oldIndex: localModel.getIndex(), newParent: this.getDebugData(), newIndex: i, isArchived: resolveArchived }); } catch(sErr) { g.reportActionCrash(sErr); } }

                            // Move it back here
                            localModel.moveTo({
                                parent: this,
                                index: i,
                                isRemoteChange: true,
                                shouldBeArchived: resolveArchived
                            });

                            localModel.initFromGDrive(remoteItem);
                            saveItemLists = true;

                            g.vmMain.addModifiedItem(localModel);
                        }
                    }
                    // Moved remotely to here
                    else if (localModel)
                    {
                        // The local item was neither deleted nor modified so the item was moved remotely. Update the
                        // local model to reflect the remote move.
                        if (DEBUG) log('%cMoved to here by remote', 'color: green', this.getRawText(), i, remoteItem.id, gdata.get(remoteItem, 'text'));

                        if (REPORT_ACTIONS) { try { g.reportAction(RATAG.I, ResolveAction.MoveLocalItem, { item: localModel.getDebugData(), oldParent: localModel.parent().getDebugData(), oldIndex: localModel.getIndex(), newParent: this.getDebugData(), newIndex: i, isArchived: resolveArchived }); } catch(sErr) { g.reportActionCrash(sErr); } }

                        localModel.moveTo({
                            parent: this,
                            index: i,
                            isRemoteChange: true,
                            shouldBeArchived: resolveArchived
                        });

                        localModel.initFromGDrive(remoteItem);
                        saveItemLists = true;

                        g.vmMain.addModifiedItem(localModel);
                    }
                    else
                    {
                        if (REPORT_ACTIONS) { try { g.reportAction(RATAG.Q, ResolveAction.Error, { msg: 'Local model does not exist for local item that was not deleted', item: localModel.getDebugData(), localIndex: i, data: localItem }); } catch(sErr) { g.reportActionCrash(sErr); } }

                        g.reportError(new Error('Local model does not exist for local item that was not deleted.'));
                    }
                }
            }
        }

        if (d.hasLocalData)
        {
            // Check local items
            // Cases:
            // 1. Added locally = !gdata.item() && modifiedOffline
            // 2. Deleted remotely = !gdata.item() && !modifiedOffline
            // 3. Moved locally = gdata.item() && modifiedOffline
            // 4. Moved remotely = gdata.item() && !modifiedOffline
            for (i = 0; i < itemArray.length; i++)
            {
                localModel = itemArray[i];

                // Already processed this item in the remote check
                if (gdata.loadSeen[localModel.id] === this.id)
                {
                    continue;
                }

                // Item exists locally but not remotely
                remoteItem = localModel.data; //gdata.item(localModel.id);
                //try{g.PAssert(1110,remoteItem == gdata.item(localModel.id), 'Local model should always be up to date at this point');}catch(PERR){g.reportError(PERR);}

                // Doesn't exist on remote
                if (!remoteItem)
                {
                    // Added locally
                    if (localModel.modifiedOfflineText)
                    {
                        // Add to remote
                        if (DEBUG) log('%cAdding from local', 'color: blue', this.getParsedText(), i, localModel.id, localModel.getParsedText());

                        var childrenToRemove = [ ];

                        var localChildren;
                        if (resolveArchived)
                        {
                            if (localModel.hasArchivedChildren())
                            {
                                localChildren = localModel.archivedItems(/*underlying*/true);
                            }
                        }
                        else
                        {
                            localChildren = localModel.items();
                        }

                        if (localChildren)
                        {
                            for (var j = 0; j < localChildren.length; ++j)
                            {
                                // If the local child has valid remote data, it needs to be moved from its previous location
                                // to this item. Additionally the child should not have been moved remotely otherwise the
                                // remote move should be applied instead.
                                if (localChildren[j].data && localChildren[j].isLocalVersionNewer(VersionType.Structure))
                                {
                                    var inTree = gdata.findInTree(localChildren[j].data);
                                    try{g.PAssert(1111,inTree, 'Item should be found in tree');}catch(PERR){g.reportError(PERR);}

                                    if (inTree)
                                    {
                                        childrenToRemove.push({
                                            parent: inTree.parent,
                                            child: localChildren[j]
                                        });
                                    }
                                }
                            }
                        }

                        // Initialize the local item and create it remotely.
                        d.initializeItem(localModel);

                        gdata.itemAdded({
                            item: this,
                            addedItem: localModel,
                            index: i,
                            isArchived: resolveArchived
                        });

                        if (REPORT_ACTIONS) { try { g.reportAction(RATAG.J, ResolveAction.AddRemoteItem, { item: localModel.getDebugData(), parent: this.getDebugData(), index: i, isArchived: resolveArchived }); } catch(sErr) { g.reportActionCrash(sErr); } }

                        if (DEBUG) log('Moving Children: ', childrenToRemove);

                        // Any children that need to be moved into the newly created remote item need to be removed
                        // from their current parents.
                        for (var j = 0; j < childrenToRemove.length; ++j)
                        {
                            var removeData = childrenToRemove[j];
                            try{g.PAssert(1112,removeData, 'Must have valid data to remove');}catch(PERR){g.reportError(PERR);}

                            var isArchived = removeData.child.isArchived();

                            var remoteList = gdata.getItemList(removeData.parent, isArchived);
                            try{g.PAssert(1113,remoteList, 'The item has children, it must have a list');}catch(PERR){g.reportError(PERR);}

                            var index = remoteList.indexOf(removeData.child.data);
                            try{g.PAssert(1114,index >= 0, 'Remote item must be in the parents list');}catch(PERR){g.reportError(PERR);}

                            gdata.itemRemoved({
                                item: removeData.parent,
                                index: index,
                                numToRemove: 1,
                                isArchived: isArchived
                            });

                            if (REPORT_ACTIONS) { try { g.reportAction(RATAG.K, ResolveAction.RemoveRemoteItem, { item: removeData.child.getDebugData(), parent: d.getModel(removeData.parent.id).getDebugData(), index: index, isArchived: isArchived }); } catch(sErr) { g.reportActionCrash(sErr); } }
                        }

                        // After removing remote items from their current parents, initialize the new remote item which
                        // will add the items that were removed to its own items list.
                        localModel.initFromGDrive(localModel.data);
                    }
                    else
                    {
                        // Item was deleted remotely, delete from local db.
                        if (DEBUG) log('%cDeleted on remote', 'color: orange', this.getParsedText(), localModel.id, localModel.getParsedText(), this.getVersion(VersionType.Structure));

                        if (REPORT_ACTIONS) { try { g.reportAction(RATAG.L, ResolveAction.DeleteLocalItem, { item: localModel.getDebugData(), parent: this.getDebugData(), index: localModel.getIndex(), isArchived: resolveArchived }); } catch(sErr) { g.reportActionCrash(sErr); } }

                        try{g.PAssert(1115,d.getItem(localModel.id), "Trying to delete an item locally that doesn't exist locally");}catch(PERR){g.reportError(PERR);}

                        this.deleteChild(localModel);
                        i--;

                        saveItemLists = true;

                        g.vmMain.addModifiedItem(localModel);
                    }
                }
                else
                {
                    // Item exists on remote and moved locally to here
                    if (localModel.modifiedOffline)
                    {
                        // Modified more recently than remote
                        if (localModel.isLocalVersionNewer(VersionType.Structure))
                        {
                            if (DEBUG) log('%cMoved to here locally', 'color: blue', this.getParsedText(), i, remoteItem.id, gdata.get(remoteItem, 'text'));

                            var inTree = gdata.findInTree(remoteItem);
                            try{g.PAssert(1116,inTree, 'Item should be found in tree');}catch(PERR){g.reportError(PERR);}

                            if (inTree)
                            {
                                if (REPORT_ACTIONS)
                                {
                                    try
                                    {
                                        var oldParentModel = d.getModel(inTree.parent.id);

                                        g.reportAction(RATAG.M, ResolveAction.MoveRemoteItem, { item: localModel.getDebugData(), oldParent: oldParentModel ? oldParentModel.getDebugData() : undefined, oldIndex: inTree.index, newParent: localModel.parent().getDebugData(), newIndex: i, isArchived: resolveArchived });
                                    }
                                    catch(sErr)
                                    {
                                        g.reportActionCrash(sErr);
                                    }
                                }

                                // Move the item before initializing it to avoid a recursive hierarchy
                                gdata.itemMoved({
                                    item: localModel,
                                    newInfo: { 'index': i, 'isArchived': resolveArchived },
                                    oldParent: inTree.parent,
                                    oldInfo: inTree
                                });
                            }
                            else
                            {
                                if (REPORT_ACTIONS) { try { g.reportAction(RATAG.N, ResolveAction.Error, { msg: 'Item not found in local tree', item: localModel.getDebugData(), localIndex: i, data: localItem }); } catch(sErr) { g.reportActionCrash(sErr); } }
                            }

                            localModel.initFromGDrive(remoteItem);
                        }
                        else
                        {
                            // Modified more recently on remote, do nothing.
                            if (DEBUG) log('%cMoved to here locally but overridden by remote. 0x90.', 'color: gray', this.getParsedText(), i, remoteItem.id, gdata.get(remoteItem, 'text'));
                        }
                    }
                    else
                    {
                        // It needs to be moved locally but let the case to where it is moved handle it
                        if (DEBUG) log('%cMoved from here by remote. 0x90.', 'color: gray', this.getParsedText(), i, remoteItem.id, gdata.get(remoteItem, 'text'));
                    }
                }
            }

            if (!resolveArchived && collabItemList && (collabItemList.length > 0 || itemArray.length > 0))
            {
                // To merge two arrays of items in a sensible way without knowing the state of the array that
                // they diverged from the following steps are performed:
                //  1) Take the item list from the remote and create a temporary list with items whose local
                //     version is newer are removed. Items that are not in both lists should be removed as well.
                //  2) Reinsert the locally modified items into the temporary list at their local index.
                //  3) Compute moves needed to go from the local list to the current temporary list.
                //  4) Perform computed moves on the local list.
                //  5) Compute moves needed to go from the remote list to the current temporary list.
                //  6) Perform computed moves on the remote list.
                //  7) Items that were not in both local and remote lists can be readded to the end of the list
                //     or left in whatever place they were in before as they will be moved at a later step.

                var localModArray = [ ];
                var nonSharedArray = undefined;
                var tempArray = [ ];

                var remoteArray = collabItemList.asArray();

                var reorderFound = false;

                // Iterate over the remote array to base the destination list on. Any items that were not modified
                // locally should be copied over into an array which will then have the locally modified items merged
                // into them. Any items that aren't seen should be added to nonSharedArray as they are expected to remain
                // at the end of the array and moved later in the conflict resolution process.
                for (i = 0; i < remoteArray.length; ++i)
                {
                    var tempID = remoteArray[i].id;

                    var tempModel = d.getModel(tempID);
                    try{g.PAssert(1117,tempModel, 'If the remote ID exists the local item should have been created.');}catch(PERR){g.reportError(PERR);}

                    // If an item only exists remotely or was not modified locally, move it into the destination array.
                    var localIndex = this.getIndexOf(tempID);
                    if (!tempModel || !tempModel.modifiedOffline || !tempModel.isLocalVersionNewer(VersionType.Structure))
                    {
                        if (localIndex >= 0)
                        {
                            tempArray.push(tempID);

                            if (localIndex !== i)
                            {
                                reorderFound = true;
                            }
                        }
                        else
                        {
                            if (!nonSharedArray)
                            {
                                nonSharedArray = [ ];
                            }

                            log(this.id + ': Item in remote array not found in local array: ', tempID);
                            nonSharedArray.push(tempID);
                        }
                    }
                    else
                    {
                        if (localIndex >= 0)
                        {
                            localModArray.push(localIndex);
                        }
                        else
                        {
                            if (!nonSharedArray)
                            {
                                nonSharedArray = [ ];
                            }

                            log(this.id + ': Item in remote array not found in local array: ', tempID);
                            nonSharedArray.push(tempID);
                        }
                    }
                }

                // In the case that no local items were modified skip trying to match the local remote and destination arrays as
                // they are guarenteed to be equal at this point.
                if (reorderFound || localModArray.length > 0)
                {
                    if (DEBUG && DEBUG)
                    {
                        var tempArrayText = [ ];
                        tempArray.forEach(function (item) { tempArrayText.push(d.getModel(item)._rawText); });
                        log('Orig Remote Array: ', tempArrayText.join());
                    }

                    // In order to guarentee correct insertion order, make sure the insert indices are sorted in ascending order.
                    // The indices stored here are the expected final index of an item. Inserting in ascending order ensures that
                    // the index is correct relative to the final list.
                    localModArray.sort();

                    // Insert the locally modified items into the destination array.
                    for (i = 0; i < localModArray.length; i++)
                    {
                        var localItem = itemArray[localModArray[i]];

                        if (localItem)
                        {
                            tempArray.splice(localModArray[i], 0, localItem.id)
                        }

                        if (DEBUG && DEBUG)
                        {
                            var tempArrayText = [ ];
                            tempArray.forEach(function (item) { tempArrayText.push(d.getModel(item)._rawText); });
                            log('Add Local - Remote Array: ', tempArrayText.join());
                        }
                    }

                    // If there are any remote-only items, ensure that they end up at the end of the array as they should
                    // be moved by a later node in the tree.
                    if (nonSharedArray && nonSharedArray.length > 0)
                    {
                        tempArray = tempArray.concat(nonSharedArray);
                    }

                    if (DEBUG && DEBUG)
                    {
                        var remoteArrayText = [ ];
                        remoteArray.forEach(function(item) { remoteArrayText.push(d.getModel(item.id)._rawText); });

                        var localArrayText = [ ];
                        itemArray.forEach(function (item) { localArrayText.push(d.getModel(item.id)._rawText); });

                        var tempArrayText = [ ];
                        tempArray.forEach(function (item) { tempArrayText.push(d.getModel(item)._rawText); });

                        log('Remote Array: ', remoteArrayText.join());
                        log('Local Array: ', localArrayText.join());
                        log('Temp Array: ', tempArrayText.join());

                        var remoteArrayText = [ ];
                        collabItemList.asArray().forEach(function(item) { remoteArrayText.push(d.getModel(item.id)._rawText); });
                        log('  ROrig: ', remoteArrayText.join());
                    }

                    // Given a temp array, find any differences between the temp array and the remote array and remove them. This is
                    // done by insertion sort. Each move operation modifies the underlying remote data.
                    for (i = 0; i < tempArray.length; ++i)
                    {
                        var searchItem = tempArray[i];

                        // Only search the unsorted portion of the remote item list.
                        for (var j = i; j < collabItemList.length; ++j)
                        {
                            var findItem = collabItemList.get(j);

                            if (searchItem === findItem.id)
                            {
                                if (i !== j)
                                {
                                    var lModel = d.getModel(findItem.id);

                                    if (REPORT_ACTIONS) { try { g.reportAction(RATAG.O, ResolveAction.MoveRemoteItem, { item: lModel.getDebugData(), oldParent: this.getDebugData(), oldIndex: j, newParent: lModel.parent().getDebugData(), newIndex: i, isArchived: false }); } catch(sErr) { g.reportActionCrash(sErr); } }

                                    var oldInfo = { 'index': j, 'isArchived': false };
                                    var newInfo = { 'index': i, 'isArchived': false };

                                    gdata.itemMoved({
                                        item: lModel,
                                        newInfo: newInfo,
                                        oldParent: this,
                                        oldInfo: oldInfo
                                    });

                                    saveItemLists = true;

                                    g.vmMain.addModifiedItem(lModel);

                                    if (DEBUG && DEBUG)
                                    {
                                        var remoteArrayText = [ ];
                                        collabItemList.asArray().forEach(function(item) { remoteArrayText.push(d.getModel(item.id)._rawText); });
                                        log('   Sort: ', remoteArrayText.join());
                                    }
                                }

                                break;
                            }
                        }
                    }

                    if (DEBUG && DEBUG)
                    {
                        var localArrayText = [ ];
                        itemArray.forEach(function (item) { localArrayText.push(d.getModel(item.id)._rawText); });
                        log('  LOrig: ', localArrayText.join());
                    }

                    // Given a temp array, find any differences between the temp array and the local array and remove them. This is also
                    // done by insertion sort.
                    for (i = 0; i < tempArray.length; ++i)
                    {
                        var searchItem = tempArray[i];

                        for (var j = i; j < itemArray.length; ++j)
                        {
                            var findItem = itemArray[j];

                            if (searchItem === findItem.id)
                            {
                                if (i !== j)
                                {
                                    if (REPORT_ACTIONS) { try { g.reportAction(RATAG.P, ResolveAction.MoveLocalItem, { item: findItem.getDebugData(), oldParent: findItem.parent().getDebugData(), oldIndex: findItem.getIndex(), newParent: this.getDebugData(), newIndex: i, isArchived: false }); } catch(sErr) { g.reportActionCrash(sErr); } }

                                    findItem.moveTo({
                                        parent: this,
                                        index: i,
                                        isRemoteChange: true,
                                        shouldBeArchived: false
                                    });

                                    saveItemLists = true;

                                    g.vmMain.addModifiedItem(findItem);

                                    if (DEBUG && DEBUG)
                                    {
                                        var localArrayText = [ ];
                                        itemArray.forEach(function (item) { localArrayText.push(d.getModel(item.id)._rawText); });
                                        log('   Sort: ', localArrayText.join());
                                    }
                                }

                                break;
                            }
                        }
                    }
                }
            }

            // After ensuring items have been moved to the correct parents and any local-only items were created remotely, check
            // to see if any items that were moved locally within the same parent need to be moved remotely.
            // for (i = 0; i < itemArray.length; i++)
            // {
            //     localModel = itemArray[i];

            //     // Modified locally
            //     if (localModel.modifiedOffline && localModel.isLocalVersionNewer(VersionType.Structure))
            //     {
            //         try{g.PAssert(1118,localModel.data, 'Must have valid remote item');}catch(PERR){g.reportError(PERR);}
            //         var remoteIndex = this.getRemoteIndexOf(localModel.data, /*isArchived*/resolveArchived);

            //         // Item is in a different place
            //         if (remoteIndex >= 0 && remoteIndex !== i)
            //         {
            //             try{g.PAssert(1119,localModel.data === gdata.item(localModel.id), 'Local item should always have up to date remote at this point');}catch(PERR){g.reportError(PERR);}

            //             // Moved locally to here
            //             if (DEBUG) log('%cMoved locally to', 'color:ack green', this.getParsedText(), remoteIndex, i, localModel.id, localModel.getParsedText());

            //             var oldInfo = { 'index': remoteIndex, 'isArchived': resolveArchived };
            //             var newInfo = { 'index': i, 'isArchived': resolveArchived };

            //             gdata.itemMoved({
            //                 item: localModel,
            //                 newInfo: newInfo,
            //                 oldParent: this,
            //                 oldInfo: oldInfo
            //             });

            //             saveItemLists = true;

            //             g.vmMain.addModifiedItem(localModel);
            //         }
            //     }
            // }

            // // Now check for any items that were moved remotely to determine if they need to be reordered as well.
            // for (i = 0; i < itemArray.length; i++)
            // {
            //     localModel = itemArray[i];

            //     if (localModel.isRemoteVersionNewer(VersionType.Structure))
            //     {
            //         try{g.PAssert(1120,localModel.data, 'Must have valid remote item');}catch(PERR){g.reportError(PERR);}
            //         var remoteIndex = this.getRemoteIndexOf(localModel.data, /*isArchived*/resolveArchived);

            //         // Item is in a different place
            //         if (remoteIndex >= 0 && remoteIndex !== i)
            //         {
            //             try{g.PAssert(1121,localModel.data === gdata.item(localModel.id), 'Local item should always have up to date remote at this point');}catch(PERR){g.reportError(PERR);}

            //             // Moved remotely to here
            //             if (DEBUG) log('%cMoved by remote to', 'color: green', this.getParsedText(), remoteIndex, i, localModel.id, localModel.getParsedText);

            //             localModel.moveTo({
            //                 parent: this,
            //                 index: remoteIndex,
            //                 isRemoteChange: true,
            //                 shouldBeArchived: resolveArchived
            //             });

            //             saveItemLists = true;

            //             g.vmMain.addModifiedItem(localModel);
            //         }
            //     }
            // }
        }

        if (saveItemLists)
        {
            if (d.hasLocalData)
            {
                if (resolveArchived)
                {
                    if (this.hasArchivedChildren())
                    {
                        this.archivedItems().valueHasMutated();
                    }
                }
                else
                {
                    this.items.valueHasMutated();
                }
            }

            if (resolveArchived && !this.hasArchivedChildren())
            {
                try{g.PAssert(1122,itemArray.length > 0, 'If saving an archive array it should have data in it');}catch(PERR){g.reportError(PERR);}

                this.archivedItems()(itemArray);
            }

            if (remoteChanges)
            {
                remoteChanges['items'] = this.getItemsArrayForSave();
            }
        }
    },
    resolveItems: function(remoteChanges, parseChildren)
    {
        this.resolveItemsHelper(/*resolveArchived*/false, remoteChanges, parseChildren);
        this.resolveItemsHelper(/*resolveArchived*/true, remoteChanges, parseChildren);
    },
    onValueChanged: function(event)
    {
        if (event.isLocal && !tracker.undoRedoActive)
        {
            return;
        }

        var mappedProperty = g.translateFieldOut(event.property);

        //log('Value Changed: ' + this.id + ' || ' + event.target.id);
        //try{g.PAssert(1123,this.id === event.target.id, 'When changing values the correct item should always be handling itself');}catch(PERR){g.reportError(PERR);}

        // When creating new items, properties may be set on the realtime object before the object has been inserted into the
        // tree. Due to the way that the realtime API handles these changes we will still receive the object changed event which
        // should be ignored.
        if (this.id !== event.target.id)
        {
            return;
        }

        if (DEBUG) log(this.id, ': Changing remote property [', mappedProperty, ']: ', event.oldValue, ' -> ', event.newValue);

        switch (mappedProperty)
        {
            case 'version':
            case 'versionText':
                var remoteVersion = gdata.get(this.data, mappedProperty);

                // TODO: We get a nasty ping-pong effect here if you undo/redo the same action a bunch of times.
                //       How bad is it for the version to revert when undoing a change? Is there something else
                //       we can do here instead that doesn't cause this?
                if (remoteVersion > this[mappedProperty] || this[mappedProperty] === undefined)
                {
                    var changes = {
                        modifiedOffline: false
                    };

                    changes[mappedProperty] = this[mappedProperty] = remoteVersion;

                    this.save(changes, SaveFlag.None);
                }
                else
                {
                    // TODO: How terrible is not doing this?

                    // if (event.isLocal && tracker.undoRedoActive)
                    // {
                    //     if (this[mappedProperty] !== remoteVersion)
                    //     {
                    //         this[mappedProperty] = (this[mappedProperty] ? this[mappedProperty] + 1 : 1);

                    //         if (DEBUG) log('Setting Version During Undo:', this[mappedProperty]);

                    //         gdata.set(this.data, mappedProperty, this[mappedProperty]);
                    //         this.save({version: this[mappedProperty]}, /*toModify*/false);
                    //     }
                    // }
                }

                break;
            case 'formats':
                var formats = JSON.parse(JSON.stringify(gdata.get(this.data, 'formats')));

                if (!formats)
                {
                    formats = [ ];
                }

                this.setFormats(formats, /*isFromRemote*/true);

                break;
            case 'priority':
                this.priority(event.newValue, /*fromRemote*/true);

                break;
            case 'isComplete':
                this.isComplete(event.newValue, /*fromRemote*/true);

                break;

            case 'isFlagged':
                this.isFlagged(event.newValue, /*fromRemote*/true);

                break;
            case 'dateCompleted':
                this.dateCompleted = new Date(event.newValue);
                this.save({'dateCompleted': event.newValue}, SaveFlag.None);

                break;
            case 'isArchived':
                this.isArchived(event.newValue);
                this.save({'isArchived': event.newValue}, SaveFlag.None);

                break;
            case 'date':
                this.date(new Date(event.newValue));
                this.save({'date': event.newValue}, SaveFlag.None);

                break;
            case 'allDayDate':
                this.allDayDate = event.newValue;
                this.save({'allDayDate': event.newValue}, SaveFlag.None);

                break;
            case 'items':
            case 'archivedItems':
                var isArchived = !(mappedProperty === 'items');

                if (event.newValue)
                {
                    for (var i = 0; i < event.newValue.length; ++i)
                    {
                        this.addRemoteItem(event.newValue.get(i), i, isArchived);
                    }

                    gdata.attachEventsToItem(this);
                }
                else
                {
                    try{g.PAssert(1124,event.oldValue.length === 0, 'Expected no items in list');}catch(PERR){g.reportError(PERR);}

                    this.removeAllRemoteItems(isArchived);

                    // for (var i = 0; i < event.oldValue.length; ++i)
                    // {
                    //     this.removeRemoteItem(event.oldValue.get(i), i, isArchived);
                    // }
                }

                this.save({ 'items': this.getItemsArrayForSave() }, SaveFlag.None);

                break;
            case 'dateCreated':
                // Do Nothing
                break;
            case 'text':
                this.onTextChanged(event);

                break;
            default:
                if (ShouldLog(LogLevels.Error)) log('Unhandled value changed remotely: ', mappedProperty, event);
                break;
        }
    },
    onTextChanged: function(event)
    {
        if (event.isLocal && !tracker.undoRedoActive)
        {
            return;
        }

        var remoteText = gdata.get(this.data, 'text');

        // TODO: Avoid doing full string compare
        if (remoteText != this.getRawText())
        {
            if (DEBUG) log('Text modified remotely', remoteText);

            this.setRawText(remoteText);
            this.parseText(/*immediateParse*/true);

            this.save({text: this.getRawText()}, SaveFlag.None);

            if (tracker.undoRedoActive)
            {
                //var evType = (event.type === "text_deleted" ? TrackerType.Remove : TrackerType.Insert);
                var evType = TrackerType.SetText;
                tracker.notifyStateChangedFromUndoRedo(evType,
                {
                    'itemID': this.id,
                    'position': g.findFirstDiff(event.newValue, event.oldValue),
                    'text': event.newValue
                });
            }
        }
    },
    addRemoteItem: function (remoteItem, index, isArchived)
    {
        var remoteID = remoteItem.id;
        gdata.cacheItem(remoteItem);

        var localList = (isArchived ? this.archivedItems() : this.items);

        if (index < localList().length && localList()[index].id === remoteID)
        {
            return;
        }

        if (DEBUG) log('Remote List Add [', index, ']: ', remoteID, gdata.get(remoteItem, 'text'));

        var vmli = d.getModel(remoteID);
        if (vmli)
        {
            //log('%cClear delete timeout', 'color: green', remoteID, d.models[remoteID].text);
            clearTimeout(vmli.deleteTimeout);
            vmli.deleteTimeout = undefined;
        }
        else
        {
            vmli = new VMLI({data: remoteItem}, {parent: this, parse: true});
        }

        // TODO: We have the potential to do two saves below for isDeleted and isArchived

        // Adding a deleted item to a list, ensure that we undelete it and properly re-add it to the local cache.
        if (vmli.isDeleted)
        {
            vmli.isDeleted = false;
            vmli.save(null, SaveFlag.None);
        }

        if (vmli.isArchived() !== isArchived)
        {
            vmli.isArchived(isArchived);
            vmli.save({ 'isArchived': isArchived }, SaveFlag.None);
        }

        vmli.parent(this);

        localList.splice(index, 0, vmli);

        //g.events.emit('itemAdded', vmli, /*force*/true);
        g.fireCustomEvent('itemAdded', { item: vmli, force: true });

        g.timeline.notifyStateChanged(vmli);

        if (tracker.undoRedoActive)
        {
            tracker.notifyStateChangedFromUndoRedo(TrackerType.Create,
            {
                'itemID': vmli.id,
                'parent': this.id,
                'position': index,
                'text': gdata.get(remoteItem, 'text')
            });
        }

        //this.parseText(/*immediateParse*/true);
        // TODO: Put this back if parsing incoming items is slow. It was taken out because
        // of race conditions where onTextChanged would come in after onListAdded and set new text
        // before the parse worker completed and set it back to ''
        // g.vmMain.runParseWorker(vmli);
    },
    removeAllRemoteItems : function(isArchived)
    {
        if (isArchived)
        {
            if (this.hasArchivedChildren())
            {
                (this.archivedItems())([]);
            }
        }
        else
        {
            this.items([]);
        }
    },
    removeRemoteItem: function(remoteItem, index, isArchived, deleteLocal)
    {
        var localList;
        if (isArchived)
        {
            try{g.PAssert(1125,this.hasArchivedChildren(), 'Must have a valid item list to remove from');}catch(PERR){g.reportError(PERR);}
            
            localList = this.archivedItems();
        }
        else
        {
            localList = this.items;
        }

        var localItem = localList()[index];

        if (!localItem || localItem.id !== remoteItem.id)
        {
            return;
        }

        try{g.PAssert(1126,localItem, 'Expect a valid local item to remove');}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(1127,remoteItem.id == localItem.id, 'Remote item should match local items');}catch(PERR){g.reportError(PERR);}

        if (tracker.undoRedoActive)
        {
            tracker.notifyStateChangedFromUndoRedo(TrackerType.Delete,
            {
                'itemID': localItem.id,
                'parent': this.id,
                'position': index,
                'previous': g.getPreviousItem(localItem).id
            });
        }

        localList.splice(index, 1);

        if (deleteLocal)
        {
            localItem.deleteTimeout = setTimeout(localItem.deleteFromServer.bind(localItem), 2000);
        }
        else
        {
            localItem.deleteTimeout = setTimeout(localItem.markDeleted.bind(localItem), 2000);
        }
    },
    onListAdded: function(event)
    {
        if (event.isLocal && !tracker.undoRedoActive)
        {
            return;
        }

        var areItemsArchived = false;

        var remoteItemList = gdata.get(this.data, 'items');
        if (!remoteItemList || event.target.id !== remoteItemList.id)
        {
            var remoteArchivedList = gdata.get(this.data, 'archivedItems');
            if (remoteArchivedList && event.target.id === remoteArchivedList.id || !remoteItemList)
            {
                areItemsArchived = true;
            }
        }

        // When creating a new item list, the values will be empty however the list will contain entries.

        var getRemoteFn;
        var startIndex;
        var endIndex;
        if (event.values.length > 0)
        {
            startIndex = event.index;
            endIndex = startIndex + event.values.length;

            getRemoteFn = function(index)
            {
                return event.values[index];
            }
        }
        else
        {
            startIndex = 0;
            endIndex = event.target.length;

            getRemoteFn = function(index)
            {
                return event.target.get(index);
            }
        }

        for (var i = startIndex; i < endIndex; ++i)
        {
            var eventIndex = i - startIndex;
            var remoteItem = getRemoteFn(eventIndex);

            try{g.PAssert(1128,remoteItem, 'Always expect a valid item at each index');}catch(PERR){g.reportError(PERR);}

            this.addRemoteItem(remoteItem, i, areItemsArchived);
        }

        this.save({ 'items': this.getItemsArrayForSave() }, SaveFlag.None);
    },
    onListRemoved: function(event)
    {
        var localItem;
        if (event.isLocal && !tracker.undoRedoActive)
        {
            return;
        }

        if (DEBUG)
        {
            log('%cOn removed:', 'color: red', this.id, d.getModel(this.id).getParsedText());

            for (var idx = 0; idx < event.values.length; ++idx)
            {
                var remoteItem = event.values[idx];

                var debugLocalItem = this.items()[event.index + idx];

                if (debugLocalItem)
                {
                    var localText = debugLocalItem.getParsedText();

                    log('Remote List Remove [', event.index, ']: ', remoteItem.id, localText);
                }
                else
                {
                    try{g.PAssert(1129,false, 'Should always have a valid item to be removing');}catch(PERR){g.reportError(PERR);}
                }
            }
        }

        var areItemsArchived = false;

        var remoteItemList = gdata.get(this.data, 'items');
        if (!remoteItemList || event.target.id !== remoteItemList.id)
        {
            var remoteArchivedList = gdata.get(this.data, 'archivedItems');
            if (remoteArchivedList && event.target.id === remoteArchivedList.id)
            {
                areItemsArchived = true;
            }
        }

        var startIndex = event.index;
        var endIndex = startIndex + event.values.length;

        for (var i = endIndex - 1; i >= startIndex; i--)
        {
            var eventIndex = i - startIndex;

            try{g.PAssert(1130,!!event.values[eventIndex], 'Always expect a valid item at each index');}catch(PERR){g.reportError(PERR);}

            var remoteItem = event.values[eventIndex];

            // Until there is a better solution for determining when a remove is actually a deletion,
            // and not a move, only mark items as deleted when removing them remotely. When doing a local
            // undo action, don't mark the item deleted.
            var deleteLocal = event.isLocal && tracker.undoRedoActive;
            this.removeRemoteItem(remoteItem, i, areItemsArchived, deleteLocal);
        }

        this.save({ 'items': this.getItemsArrayForSave() }, SaveFlag.None);
    },
    onListSet: function(event)
    {
        try{g.PAssert(1131,false, 'What? This shouldn\'t happen!', event);}catch(PERR){g.reportError(PERR);}
    },
    onObjectChanged: function(event)
    {
        if (event.ocHandled)
        {
            //log('Event Already Handled: ', event.ocHandled, '-', this.id, event.events[0].type);

            return false;
        }

        event.ocHandled = this.id;

        event.stopPropagation();

        // var pre = (event.isLocal ? 'L' : '');
        // log(pre + this.id + ': ', event.events[0]);

        // if (event.events[0].type == 'values_added')
        // {
        //     if (g.isString(event.events[0].values[0]))
        //     {
        //         debugger;
        //     }
        // }

        if (event.isLocal && !tracker.undoRedoActive)
        {
            return false;
        }

        //log('[' + this.id + '] Object Changed Event: ', event);

        for (var i = 0; i < event.events.length; ++i)
        {
            //console.log(' Type: '  + event.events[i].type);

            switch (event.events[i].type)
            {
                case gapi.drive.realtime.EventType.TEXT_DELETED:
                    this.onTextChanged(event.events[i]);
                    break;
                case gapi.drive.realtime.EventType.TEXT_INSERTED:
                    this.onTextChanged(event.events[i]);
                    break;
                case gapi.drive.realtime.EventType.VALUES_ADDED:
                    this.onListAdded(event.events[i]);
                    break;
                case gapi.drive.realtime.EventType.VALUES_REMOVED:
                    this.onListRemoved(event.events[i]);
                    break;
                case gapi.drive.realtime.EventType.VALUE_CHANGED:
                    this.onValueChanged(event.events[i]);
                    break;
                case gapi.drive.realtime.EventType.VALUES_SET:
                    try{g.PAssert(1132,false, 'VALUES_SET should never be triggered');}catch(PERR){g.reportError(PERR);}
                    break;
                default:
                    try{g.PAssert(1133,false, 'Unexpected event type');}catch(PERR){g.reportError(PERR);}
                    break;
            }
        }

        return false;
    },
    deleteFromServer: function()
    {
        if (DEBUG) log('%cDB delete item', 'color: red', this.id, this.getParsedText());

        d.dbDeleteItem(this.id);
        // TODO: Remove this VMLI
    },
    markDeleted: function()
    {
        d.deleteItem(this);

        g.timeline.notifyStateChanged(this);
    }
};
};
return VMLI_Drive;
});
