    createScript: function (name, repeat)
    {
        var script =
        {
            owner: this,
            _name: name,
            index: 0,
            isResetting: false,
            _repeat: false,
            noDelay: false,

            runQueue: [ ],
            resetQueue: [ ],

            _activeQueue: function()
            {
                return this.isResetting ? this.resetQueue : this.runQueue;
            },

            queue: function(type, fnName)
            {
                var args = Array.prototype.slice.call(arguments, 2);

                var arr = (type == ActionType.Run) ? this.runQueue : this.resetQueue;

                arr.push({
                    name: fnName,
                    args: args
                });

                return arr;
            },
            repeat: function(val)
            {
                this._repeat = val;
            },
            reset: function()
            {
                this.index = 0;

                for (var i = 0; i < this.resetQueue.length; i ++)
                {
                    var next = this.resetQueue[i];
                    this.owner[next.name].apply(this.owner, next.args);
                }
            },
            nextStep: function()
            {
                var queue = this.runQueue;

                //try{g.PAssert(1288,this.index < queue.length);}catch(PERR){g.reportError(PERR);}

                return queue[this.index++];
            },
            getName: function()
            {
                return this._name;
            }
        };

        return script;
    },
    reset: function ()
    {
        this.index = 0;

        for (var i = 0; i < this._scriptQueue.length; i ++)
        {
            this._scriptQueue[i].reset();
        }
    },
    run: function ()
    {
        if (this.isDisabled) { return; }

        try{g.PAssert(1289,this.activeScript, 'Must have a script active to be running');}catch(PERR){g.reportError(PERR);}

        var next = this.activeScript.nextStep();

        // If there is no next step of the active script, check to see if we should continue
        // on to the next script in the queue.
        if (!next)
        {
            this.index++;

            // If the end of the queue has been reached, determine if we should repeat the scripts
            // in the queue or if no repeat is necessary.
            if (this.index >= this._scriptQueue.length)
            {
                // If no repeating is necessary, stop running scripts and notify that the script is complete.
                if (!this.repeat)
                {
                    this.isRunning = false;

                    if (this.onScriptDone)
                    {
                        this.onScriptDone();
                    }

                    return;
                }

                this.reset();
            }

            // Start the next script in the queue
            this.activeScript = this._scriptQueue[this.index];
            this.activeScript.reset();

            this.onScriptStart(this.activeScript);

            next = this.activeScript.nextStep();
        }

        var args = next.args;
        args.push(this.run.bind(this));
        args.push(this.activeScript);

        this.isRunning = true;

        setTimeout(function ()
        {
            try
            {
                this[next.name].apply(this, args);
            }
            catch (err)
            {
                g.reportError(err);

                this.onDisable();
            }
        }.bind(this), 0);
    },
    onDisable: function ()
    {
        this.isDisabled = true;
    },

