define('VMSearch',['ko', 'globals', 'platform', 'data', 'VMLI', 'tracker'],
function (ko, g, platform, d, VMLI, tracker) {

function VMSearch(pane, searchStr)
{
    this.elSearch = undefined;
    this.search = ko.observable(searchStr || '');
    this.isFocused = ko.observable(false);
    this.placeholderActive = ko.observable(true);
    this.searchLoaded = false;
    this.pane = pane;
    //this.prevNoPadNonHeader = undefined;
    this.searchArr = [ ];
    this.matches = { };
    this.hiddenItems = { };
    this.numMatches = ko.observable(0);
    this.previousSearch = '';
    this.titles = ko.observableArray();
    this.initialized = false;
    this.isPaneTimeline = (pane.mode() == PaneMode.Timeline);
    this.isSearchBoxSmall = ko.observable();
    this.onUpdated = ko.observable();

    if (DEBUG)
    {
        if (!window.itemChecks)
        {
            window.itemChecks = [ ];
        }

        window.itemChecks[this.pane.id] = { };
    }
}

VMSearch.prototype = {
    getSearchBox: function ()
    {
        return this.elSearch;
    },
    init: function ()
    {
        try{g.PAssert(1300,!this.initialized);}catch(PERR){g.reportError(PERR);}

        if(this.initialized)
            return;

        this.pane.showArchived.subscribe(function (newValue)
        {
            this.updateSearch();
        }.bind(this));

        this.pane.mode.subscribe(function (newValue)
        {
            this.isPaneTimeline = this.pane.isTimeline();
        }.bind(this));

        // g.events.on('itemAdded', function(item, force)
        // {
        //     if (force || g.focusedPane.id !== this.pane.id)
        //     {
        //         this.checkItem(item);
        //     }
        // }.bind(this));

        window.addEventListener('itemAdded', function(e)
        {
            if (e.data.force || g.focusedPane.id !== this.pane.id)
            {
                var match = this.checkItem(e.data.item);
                if(match === true)
                    this.updateNumMatches(e.data.item, true);
            }
            else if(g.focusedPane.id === this.pane.id)
            {
                // Update match count when an item is added
                this.updateNumMatches(e.data.item, true);
            }
        }.bind(this), true);

        // g.events.on('itemTextChanged', function(item, force)
        // {
        //     if (force || g.focusedPane.id !== this.pane.id)
        //     {
        //         this.checkItem(item);
        //         this.checkChildren(item);
        //     }
        // }.bind(this));

        window.addEventListener('itemTextChanged', function(e)
        {
            if (e.data.force || g.focusedPane.id !== this.pane.id)
            {
                // Check whether the new text matches the search
                var item = e.data.item;
                var oldValue = this.matches[item.id];
                var newValue = this.checkItem(item);
                if((newValue === true && oldValue !== true) ||
                    (newValue !== true && oldValue === true))
                {
                    this.updateNumMatches(item, newValue === true);
                }

                this.checkChildren(item, false, true);
            }
        }.bind(this), true);

        if (!platform.mobile)
        {
            window.addEventListener('windowResize', this.checkSearchBoxSize.bind(this));
        }

        if(this.search())
        {
            this.updateSearch();
        }
        else
        {
            // Set number of search results to be the number of items in the pane
            var num = 0;
            var includeArchived = this.isPaneTimeline && this.pane.showArchived();
            g.searchVMLIs({
                'item': this.pane.item(),
                'includeArchived': includeArchived,
                each: function(item)
                {
                    var newNum = this.amountIncrementNumMatches(item, true);
                    if(newNum)
                        num += newNum;
                }.bind(this)
            });

            this.setNumMatches(num);
        }

        this.initialized = true;
    },
    onLoad: function(element)
    {
        this.elSearch = element;
        this.elSearchWrap = element.parentNode;
        this.elAutocomplete = g.getElementByClassName('searchAutocomplete', this.elSearchWrap);

        // if (platform.phone)
        // {
            // this.elSearch.addEventListener('focus', g.vmMain.onfocus, false);
            this.elSearch.addEventListener('focus', this.onfocus.bind(this), false);
            // this.elSearch.addEventListener('blur', g.vmMain.onblur, false);
            this.elSearch.addEventListener('blur', this.onblur.bind(this), false);
        // }

        if (this.search())
        {
            setTimeout(function()
            {
                this.setDisplayText(this.search());
                this.searchLoaded = true;
            }.bind(this), 0);
        }
        else
        {
            setTimeout(function()
            {
                this.searchLoaded = true;
            }.bind(this), 0);
        }
    },
    checkSearchBoxSize: function()
    {
        try{g.PAssert(1301,!platform.mobile, 'Only update search box size on non-mobile platforms');}catch(PERR){g.reportError(PERR);}

        if(!this.isFocused())
            return;

        var autocompleteBox = this.elAutocomplete.getBoundingClientRect();
        var box = this.elSearchWrap.getBoundingClientRect();
        var paneBox = this.pane.scrollElement.getBoundingClientRect();
        if(paneBox.right - box.left - 10 < autocompleteBox.width)
        {
            // Right
            this.elAutocomplete.style.right = 0;
            this.elAutocomplete.style.left = 'auto';
        }
        else if(box.left < 40) // 40 for outline button
        {
            // Left
            this.elAutocomplete.style.left = 0;
            this.elAutocomplete.style.right = 'auto';
        }
        else
        {
            // Neither
            this.elAutocomplete.style.left = 'auto';
            this.elAutocomplete.style.right = 'auto';
        }
    },
    onfocus: function()
    {
        this.isFocused(true);

        g.vmMain.setFocusedPane(this.pane);

        if (platform.phone)
        {
            g.sendEvent('SearchFocus', 'Mobile');
        }
        else
        {
            this.clearPlaceholder();

            this.checkSearchBoxSize();

            g.sendEvent('SearchFocus', 'Desktop');
        }
    },
    onblur: function()
    {
        this.isFocused(false);
        g.fireCustomEvent('closeKeyboard');

        if(this.getDisplayText() === '')
        {
            this.setDisplayText(TextSpace);
        }
        else if (this.getDisplayText() === TextSpace)
        {
            this.setDisplayToPlaceholder();
        }

        g.clearSavedSelection('StoredSearch');
    },
    focusBox: function()
    {
        g.vmMain.setFocusedPane(this.pane);

        // The keyboard toolbar is only available on mobile devices
        if (g.keyboardToolbar)
        {
            g.keyboardToolbar.preShow();
        }

        this.getSearchBox().focus();
        g.enforceScroll();

        if (g.keyboardToolbar)
        {
            g.keyboardToolbar.show();
        }
    },
    blurBox: function()
    {
        this.getSearchBox().blur();
    },
    focusAndSelectEnd: function()
    {
        this.focusBox();
        g.selectElement(this.elSearch, /*selectEnd*/true);
    },
    resetSearch: function ()
    {
        this.search('');
        this.setDisplayText(TextSpace);

        for (var item in this.matches)
        {
            this.updateMatchState(d.getModel(item), /*newValue*/undefined, /*outline*/undefined);
        }

        this.matches[this.pane.item().id] = false;
        //this.hideItem(this.pane.item());

        this.updateSearch();
    },
    setSearch: function (text)
    {
        this.setDisplayText(text || TextSpace);
        this.search(text);

        this.updateSearch();
    },
    addToSearch: function (text)
    {
        if (text)
        {
            var newText = this.search() + ' ' + text;

            this.setDisplayText(newText || TextSpace);
            this.search(newText);

            this.updateSearch();
        }
    },
    isSearching: function()
    {
        return this.search().length > 0;
    },
    isShowing: function()
    {
        return this.isFocused() || this.isSearching();
    },
    isMatched: function (item)
    {
        // TODO: Document possible values for matches and make this better at detecting matches vs negated items
        return ((this.matches[item.id] === undefined) || !!this.matches[item.id]) && this.matches[item.id] !== -1;
    },
    isVisible: function (item)
    {
        return !this.hiddenItems[item.id];
    },
    hideItem: function (item, outline)
    {
        if(outline)
        {
            var outlineElement = item.getOutlineElement(this.pane.id);
            if(outlineElement)
            {
                if (DEBUG) log('[', this.pane.id, '] Hiding Outline Item (', this.search, '): ', item.getSearchText());
                g.addClass(outlineElement, 'hidden');
            }
        }
        else
        {
            var element = item.getElement(this.pane.id);

            if (element)
            {
                if (DEBUG) log('[', this.pane.id, '] Hiding Item (', this.search, '): ', item.getSearchText());

                g.addClass(element, 'hidden');
                this.hiddenItems[item.id] = true;
            }
            else
            {
                if (DEBUG) log('[', this.pane.id, '] Hiding Item MISSED (', this.search, '): ', item.getSearchText());
            }
        }

    },
    showItem: function (item, outline)
    {
        if(outline)
        {
            var outlineElement = item.getOutlineElement(this.pane.id);
            if(outlineElement)
            {
                if (DEBUG) log('[', this.pane.id, '] Showing Outline Item (', this.search, '): ', item.getSearchText());
                g.removeClass(outlineElement, 'hidden');
            }
        }
        else
        {
            var element = item.getElement(this.pane.id);

            if (element)
            {
                if (DEBUG) log('[', this.pane.id, '] Showing Item (', this.search, '): ', item.getSearchText());

                this.hiddenItems[item.id] = false;
                g.removeClass(element, 'hidden');
            }
            else
            {
                if (DEBUG) log('[', this.pane.id, '] Showing Item MISSED (', this.search, '): ', item.getSearchText());
            }
        }
    },
    updateMatchState: function (item, newValue, outline)
    {
        var wasChanged = false;
        if(item == this.pane.item.peek() || item == g.vmMain.root.peek())
            return;

        if (item)
        {
            var oldValue = this.matches[item.id];
            this.matches[item.id] = newValue;

            if (oldValue !== newValue && (oldValue !== undefined || newValue !== true))
            {
                wasChanged = true;

                if (this.isPaneTimeline)
                {
                    this.pane.notifySearchStateChanged(item, newValue);
                }
            }
        }

        return wasChanged;
    },
    amountIncrementNumMatches: function(item, newValue)
    {
        if (item == this.pane.item.peek() || item == g.vmMain.root.peek())
        {
            return 0;
        }

        return newValue ? 1 : -1;
    },
    updateNumMatches: function(item, newValue)
    {
        var newNum = this.amountIncrementNumMatches(item, newValue);
        if(newNum)
        {
            this.setNumMatches(this.numMatches() + newNum);
        }

        try{g.PAssert(1302,this.numMatches() >= 0);}catch(PERR){g.reportError(PERR);}
    },
    setNumMatches: function(val)
    {
        var oldValue = this.numMatches();

        if (oldValue !== val)
        {
            this.numMatches(val);

            if (this.pane.paneContent && !!oldValue !== !!val)
            {
                this.pane.setNoItems(!!val);
            }
        }
    },
    isMatch: function(item, lower, s)
    {
        var found = true;
        // Match priority
        if(s.indexOf('!') >= 0)
        {
            found = item.priority() >= VMLIFlag.P2;
            if(s.indexOf('!!') >= 0)
            {
                found = item.priority() >= VMLIFlag.P1;
                if(s.indexOf('!!!') >= 0)
                {
                    found = item.priority() >= VMLIFlag.P0;
                }
            }
            s = s.replace(/!/g, '');
        }

        // Match flagged
        if(s.indexOf('\'\'') >= 0 && found)
        {
            found = item.isFlagged() && !item.isComplete();
            s = s.replace(/\'/g, '');
        }

        // Match complete
        if(s.indexOf('//') >= 0 && found)
        {
            found = item.isComplete();
            s = s.replace(/\//g, '');
        }

        // Match text
        if(s.length > 0 && found)
        {
            found = lower.indexOf(s) >= 0;
        }
        return found;
    },
    checkItem: function(item, outline, onLoad)
    {
        var searchRoot = outline ? g.vmMain.root.peek() : this.pane.item.peek();
        if (DEBUG)
        {
            window.itemChecks[this.pane.id][item.id] = (window.itemChecks[this.pane.id][item.id] ? window.itemChecks[this.pane.id][item.id] + 1 : 1);
        }
        // if (!this.searchLoaded)
        // {
        //     return true;
        // }

        if (DEBUG) log('[', this.pane.id, '] Check Item (', this.search(), '): ', item.getSearchText());

        var isMatch = true;
        var parent;

        // TODO: Use different strategy for string compare and avoid toLowerCase calls
        var lower = item.getSearchText();

        // First search negations because it should definitely not match if negated and doesn't need to
        // go through parents
        for(var i = 0; i < this.searchArr.length; i ++)
        {
            var termEntry = this.searchArr[i];

            var negate = termEntry.isNegated;
            if(negate)
            {
                var s = termEntry.text;

                try{g.PAssert(1303,s, 'Each value in the search array should be valid at this point');}catch(PERR){g.reportError(PERR);}

                var found = this.isMatch(item, lower, s);

                if(found)
                {
                    isMatch = -1;
                    break;
                }
            }
        }

        // Search non-negations to find matches
        if(isMatch === true)
        {
            for(var i = 0; i < this.searchArr.length; i ++)
            {
                var termEntry = this.searchArr[i];

                var negate = termEntry.isNegated;
                var s = termEntry.text;

                try{g.PAssert(1304,s, 'Each value in the search array should be valid at this point');}catch(PERR){g.reportError(PERR);}

                if(!negate)
                {
                    var found = this.isMatch(item, lower, s);
                    // Not a match
                    if (!found)
                    {
                        // This word doesn't match this item so check whether this word matches a parent
                        parent = item;
                        while (parent)
                        {
                            parent = parent.parent();

                            // TODO: Cache whether the path up to the root matches so it doesn't have to be
                            // checked for each item
                            // if (!parent || parent == searchRoot)
                            if (!parent || parent == d.getRootModel())
                            {
                                isMatch = false;
                                break;
                            }
                            if (this.isMatch(parent, parent.getSearchText(), s))
                            {
                                break;
                            }

                        }
                    }
                }

                // Early out if this doesn't match
                if (isMatch !== true)
                {
                    break;
                }
            }
        }

        // Note: Elements can be undefined if search is opened on load or items are
        //       added while in timeline view
        // This item matches. Show it and all of its parents.
        if (isMatch === true && item.isLoaded)
        {
            var model = item;
            var oldMatchState = this.matches[item.id];

            var wasChanged = this.updateMatchState(item, /*newValue*/true, outline);

            //if (wasChanged)
            {
                // Early exit from the loop if a parent is explicitly hidden or is already shown.
                while (model)
                {
                    if(this.matches[model.id] === -1)
                    {
                        this.matches[item.id] = false;
                        break;
                    }
                    if (model.isLoaded)
                    {
                        this.showItem(model, outline);
                    }

                    if (model == searchRoot)
                    {
                        break;
                    }

                    model = model.parent();

                    if (model)
                    {
                        if (this.matches[model.id] === true)
                        {
                            break;
                        }

                        oldMatchState = this.matches[model.id];
                    }
                }
            }
            if(onLoad && !outline && this.search())
                this.updateNumMatches(item, true);
        }
        else if(item.isLoaded) // This item does not match
        {
            var wasChanged = this.updateMatchState(item, /*newValue*/isMatch, outline);
            //if (wasChanged)
            {
                this.hideItem(item, outline);
            }
        }

        return this.matches[item.id];
    },
    checkChildren: function(item, countItems, countChanged)
    {
        var num = this.numMatches();
        if(countItems)
        {
            num = 0;
        }
        var includeArchived = this.isPaneTimeline && this.pane.showArchived();
        g.searchVMLIs({
            'item': item,
            'includeArchived': includeArchived,
            each: function(item)
            {
                var oldValue = this.matches[item.id];
                var newValue = this.checkItem(item);
                if((countItems && newValue === true) ||
                   (countChanged &&
                        ((newValue === true && oldValue !== true) ||
                         (newValue !== true && oldValue === true))))
                {
                    var newNum = this.amountIncrementNumMatches(item, newValue === true);
                    if(newNum)
                        num += newNum;
                }
            }.bind(this)
        });
        this.setNumMatches(num);
    },
    checkChildrenOutline: function()
    {
        var includeArchived = this.isPaneTimeline && this.pane.showArchived();
        g.searchVMLIs({
            'headers': true,
            each: function(item)
            {
                this.checkItem(item, true);
            }.bind(this)
        });
    },
    updateSearch: function(force)
    {
        // TODO: Can this function be optimizaed?

        if (DEBUG) log('Updating Search [', this.pane.id, ']: ', this.search());

        if (force !== true && this.search() == this.previousSearch)
        {
            return;
        }

        if (this.searchLoaded)
        {
            g.vmMain.updatePaneState(this.pane);
        }

        // Remove extra whitespace from search string
        var textArray = this.search().toLowerCase().trim().split(/\s+/);

        textArray = textArray.filter(function (str)
        {
            return !!(str && str !== '-');
        });

        this.searchArr = [ ];
        for (var i = 0; i < textArray.length; ++i)
        {
            var isNegated = textArray[i].startsWith('-');
            var itemText = (isNegated ? textArray[i].substr(1) : textArray[i]);

            this.searchArr.push({
                text: itemText,
                isNegated: isNegated
            });
        }

        if(this.search())
        {
            this.pane.isSearching = true;
            if(platform.mobile)
            {
                this.clearPlaceholder();
            }
        }
        else
        {
            this.pane.isSearching = false;
            if(platform.mobile)
            {
                this.setDisplayToPlaceholder();
            }
        }

        // if(this.prevNoPadNonHeader)
        // {
        //     this.prevNoPadNonHeader.$elements[this.pane.id].removeClass('noPadNonHeader');
        //     this.prevNoPadNonHeader = undefined;
        // }

        this.previousSearch = this.search();

        // var firstItem;
        // var firstFoundItem;

        // Ensure the current pane item is always not matched
        this.matches[this.pane.item().id] = false;
        //this.hideItem(this.pane.item());

        if(this.initialized)
        {
            this.checkChildren(this.pane.item(), true);
            this.checkChildrenOutline();
        }

        // When searching, ensure that the itemOptions are reset to a valid position. This is to ensure that the current pane
        // doesn't stay scrolled down to the bottom with a bunch of blank content.
        //g.vmMain.hideOptions();

        // if (this.pane.mode() == PaneMode.Normal && firstFoundItem && !firstItem.isHeader() && firstFoundItem != firstItem)
        // {
        //     firstFoundItem.$elements[this.pane.id].addClass('noPadNonHeader');
        //     this.prevNoPadNonHeader = firstFoundItem;
        // }
        this.onUpdated.valueHasMutated();

        //g.fireCustomEvent('searchUpdated');
        //g.events.emit('searchUpdated');
    },
    keydown: function(item, e)
    {
        if (e.normCode === KeyCode.Enter)
        {
            return false;
        }

        // Don't let get text get set to empty string or it will make
        // the elements move up a few pixels
        if (e.normCode === KeyCode.BackSpace)
        {
            var text = this.getDisplayText();
            if (text === '' || text === TextSpace)
            {
                return false;
            }

            if (e.metaKey)
            {
                this.setDisplayText(TextSpace);
            }
            else
            {
                // Check if the entirety of the text is selected
                var range = g.getSelectionRange();
                if (range.startOffset === 0 && range.endOffset === text.length)
                {
                    this.setDisplayText(TextSpace);
                }
            }
        }

        return true;
    },
    keyup: function(item, e)
    {
        var text = this.getDisplayText();

        if (!platform.mobile && text === '')
        {
            this.setDisplayText(TextSpace);
        }

        if (e.normCode === KeyCode.Escape)
        {
            //this.resetSearch();
            g.selectElement(this.elSearch, /*selectEnd*/false);
        }
        else
        {
            var newText = text.replace(TextSpace, '').replace('\n', '');

            this.search(newText);

            tracker.miscAction({
                type: TrackerType.Misc,
                misc: TrackerMisc.SearchChanged,
                data: [
                {
                    pane: this.pane.id,
                    text: newText
                }]
            });

            var forceUpdate = (e.normCode === KeyCode.Enter);

            this.updateSearch(forceUpdate);
            if (!platform.mobile)
            {
                this.checkSearchBoxSize();
            }

            if (e.normCode === KeyCode.Enter)
            {
                if (platform.android && g.keyboardToolbar)
                {
                    g.clearSavedSelection('StoredSearch');

                    g.clearSelection();
                }
                else if (!g.restoreSavedSelection('StoredSearch', g.focusedPane))
                {
                    var firstChild = g.focusedItem.getFirstChild();

                    if (firstChild)
                    {
                        g.selectChildren(firstChild, 0, 0);

                        g.scrollIntoView(firstChild.getSpan(), 0);
                    }
                }
            }
        }

        return true;
    },
    insert: function(ch, withSpace)
    {
        var range = g.getSelectionRange();
        var start = range.startOffset;

        if(start === 1 && this.getDisplayText() === TextSpace)
        {
            start --; // To compensate for TextSpace
        }

        try{g.PAssert(1305,this.search().indexOf(TextSpace) < 0, 'TextSpace should never be in the search string');}catch(PERR){g.reportError(PERR);}
        var text = this.search().replace(TextSpace, '');

        if (withSpace && text.length > 0)
        {
            var prevChar = text[start - 1];
            if(prevChar !== ' ' & prevChar !== '-')
            {
                ch = ' ' + ch;
            }
        }

        var len = ch.length;

        var newSearch = text.substr(0, range.startOffset) + ch + text.substr(range.endOffset);
        this.search(newSearch);

        tracker.miscAction({
            type: TrackerType.Misc,
            misc: TrackerMisc.SearchChanged,
            data: [
            {
                pane: this.pane.id,
                text: newSearch
            }]
        });

        this.setDisplayText(newSearch);

        this.updateSearch();

        g.setSelection(this.elSearch, start + len, start + len);
    },
    onTapAutocomplete:
    {
        onStart: function(e)
        {
            // Prevent taking focus from search box
            e.preventDefault();
        }
    },
    onTapSearchInsert:
    {
        onStart: function(e)
        {
            // Prevent taking focus from search box
            e.preventDefault();
        },
        onClick: function(e)
        {
            var className = e.srcElement.className;
            if (className.indexOf('search') !== 0)
            {
                className = e.srcElement.parentNode.className;
            }

            switch (className)
            {
                case 'searchNegate':
                    var range = g.getSelectionRange();

                    if (range.startOffset !== range.endOffset)
                    {
                        var start = range.startOffset;
                        var end = range.endOffset;
                        if (this.getDisplayText[0] === TextSpace)
                        {
                            if (start > 0) { start--; }

                            end--;
                        }

                        var sText = this.search();

                        if (sText[start] !== '-')
                        {
                            var newText = sText.substring(0, start) + '-' + sText.substring(start);

                            this.setSearch(newText);
                            g.setSelection(this.elSearch, start, end + 1);
                        }
                        else
                        {
                            var newText = sText.substring(0, start) + sText.substring(start + 1);

                            this.setSearch(newText);
                            g.setSelection(this.elSearch, start, Math.max(0, end - 1));
                        }
                    }
                    else
                    {
                        this.insert('-', true);
                    }
                    break;
                case 'searchP2':
                    this.insert('!', true);
                    break;
                case 'searchP1':
                    this.insert('!!', true);
                    break;
                case 'searchP0':
                    this.insert('!!!', true);
                    break;
                case 'searchFlagged':
                    this.insert("''", true);
                    break;
                case 'searchComplete':
                    this.insert('//', true);
                    break;
            }

            g.sendEvent('Menu', className);
        }
    },
    requestFocusBoxForEvent: function(e)
    {
        var activeElement = document.activeElement;
        if (!activeElement || activeElement.id != 'searchPhone')
        {
            this.open();

            e.preventDefault();
        }
    },
    onTapBox: {
        onClick: function(e)
        {
            if(platform.mobile)
            {
                // log(e.srcElement);
                if(g.phoneMenu.activeScreen() !== Screens.Settings && g.phoneMenu.mode() == g.phoneMenu.Modes.Normal)
                {
                    if(this.open())
                    {
                        e.stopImmediatePropagation();
                    }
                }
                e.preventDefault();
            }
            else
            {
                this.requestFocusBoxForEvent(e);
            }
        }
    },
    onTapX: {
        onStart: function(e)
        {
            e.handled = true;
            e.preventDefault();
        },
        onClick: function(e)
        {
            if (this.isSearchActive())
            {
                this.resetSearch();
                e.preventDefault();

                g.sendEvent('Menu', 'ClearSearch');
            }
            else
            {
                this.requestFocusBoxForEvent(e);

                g.sendEvent('Menu', 'FocusSearch');
            }
        }
    },
    isSearchActive: function()
    {
        return (this.search().length !== 0);
    },
    getTitleText: function()
    {
        return this.isSearchActive() ? 'Clear Search' : 'Search';
    },
    getSearchIcon: function()
    {
        return this.isSearchActive() ? 'icon-remove' : 'icon-search';
    },
    open: function()
    {
        if(!this.isFocused())
        {
            if (platform.mobile)
            {
                this.focusAndSelectEnd();
            }
            else
            {
                this.focusBox();
            }
            return true;
        }
    },
    getDisplayText: function()
    {
        return this.elSearch.textContent;
    },
    setDisplayText: function(text)
    {
        try{g.PAssert(1306,text !== undefined && text !== '' || platform.mobile, 'Search text should always be defined, either with a TextSpace or actual text');}catch(PERR){g.reportError(PERR);}
        this.elSearch.textContent = text;
        if(text === TextSpace && platform.mobile)
        {
            this.placeholderActive(true);
        }
        else
        {
            this.placeholderActive(false);
        }
    },
    setDisplayToPlaceholder: function()
    {
        this.placeholderActive(true);
        this.elSearch.textContent = TextSpace;
    },
    clearPlaceholder: function()
    {
        this.placeholderActive(false);
    }
};

return VMSearch;
});

