define('VMMessage',['ko', 'globals', 'util', 'data'],
function (ko, g, util, d)
{

function VMMessage(info, queue)
{
    this.id = info.id;

    this.owner = queue;

    this.text = info.text;
    this.type = info.type || MessageType.Info;
    this.action = info.action || MessageAction.Default;
    this.block = info.block || false;
    this.bottom = info.bottom || false;

    this.onCloseFn = info.onClose;

    this.timeout = info.timeout;
    this.clearTimeout = undefined;

    this._isVisible = false;
}

VMMessage.prototype =
{
    show: function()
    {
        if (!this._isVisible)
        {
            this._isVisible = true;

            if (this.timeout)
            {
                this.clearTimeout = setTimeout(this.hide.bind(this), this.timeout);
            }

            if (this.block)
            {
                g.removeClass(document.getElementById('overlayBlocker'), 'none');
            }

            this.owner.onMessageShow(this);
        }
    },
    hide: function(noNotify)
    {
        if (this._isVisible)
        {
            this._isVisible = false;

            clearTimeout(this.clearTimeout);
            this.clearTimeout = undefined;

            if (this.onCloseFn)
            {
                this.onCloseFn(this);
            }

            if (!noNotify)
            {
                this.owner.onMessageHide(this);
            }
        }
    },
    isVisible: function()
    {
        return this._isVisible;
    },
    getClass: function()
    {
        var cls;

        switch (this.type)
        {
            case MessageType.Info:
                cls = 'messageInfo'; break;
            case MessageType.Intro:
                cls = 'messageIntro'; break;
            case MessageType.Warning:
                cls = 'messageWarning'; break;
            case MessageType.Error:
                cls = 'messageError'; break;
            case MessageType.Fatal:
                cls = 'messageFatal'; break;
            case MessageType.Subtitle:
                cls = 'messageSubtitle'; break;
            case MessageType.RequiredUpdate:
                cls = 'messageRequiredUpdate'; break;
            case MessageType.InfoGray:
                cls = 'messageInfoGray'; break;
                case MessageType.MobileOffline:
                cls = 'messageMobileOffline'; break;
            default:
                try{g.PAssert(1452,false, 'Invalid message type, add to globals.js');}catch(PERR){g.reportError(PERR);}
                cls = 'messageInfo'; break;
        }

        if (this.bottom)
        {
            cls += ' bottom';
        }

        return cls;
    },
    doAction: function(e)
    {
        if (this.isVisible())
        {
            if (g.isFunction(this.action))
            {
                this.action();
            }
            else
            {
                switch (this.action)
                {
                    case MessageAction.Default:
                        this.hide();
                        break;
                    case MessageAction.Reload:
                        g.reload();
                        this.hide();
                        break;
                    case MessageAction.Login:
                        util.removeURLParam('demo');
                        this.hide();
                        break;
                    case MessageAction.Demo:
                        util.insertURLParam('demo', 'true');
                        this.hide();
                        break;
                    case MessageAction.ResetClient:
                        d.clearData(function ()
                        {
                            g.reload();
                        });
                        this.hide();
                        break;
                    case MessageAction.None:
                        // Do Nothing
                        break;
                    default:
                        try{g.PAssert(1453,false, 'Invalid message action, add a new case here or pass in a function', this.action);}catch(PERR){g.reportError(PERR);}
                        this.hide();
                        break;
                }
            }

            e.preventDefault();
            e.stopImmediatePropagation();

            e.returnFalse = true;
        }
    },
    onTapIgnore:
    {
        onStart: g.handlerPreventStop,
        onClick: function(e)
        {
            if (this.isVisible())
            {
                this.hide();

                e.preventDefault();
                e.stopImmediatePropagation();

                e.returnFalse = true;
            }
        }
    }
};

return VMMessage;

});
