define('VMTooltip',['ko', 'globals', 'platform'],
function (ko, g, platform) {

var displayDelay = 300;
var extraDelay = 1000;

function VMTooltip()
{
    this.listenerElement = undefined;
    this.displayElement = undefined;
    this.element = undefined;

    this.displayTimeout = undefined;
    this.extraTimeout = undefined;

    this.displayText = ko.observable('');
    this.displayExtra = ko.observable('');

    this.overTooltip = ko.observable(false);

    this.tipExtras =
    {
        '#desktopMenuButton': 'Access your settings and documents, view help',
        '.addPaneNormal': 'Open another editable view of your data',
        '.addPaneTimeline': 'View your agenda',
    };

    g.applyBindings(this, 'tooltip');

    this.init();
}

VMTooltip.prototype =
{
    init: function()
    {

    },
    onLoad: function()
    {
        this.element = document.getElementById('tooltip');
        if (this.element)
        {
            this.element.addEventListener('mouseover', this.handleSelfOver.bind(this), true);
            this.element.addEventListener('mouseout', this.handleSelfOut.bind(this), true);
        }

        this.listenerElement = document.getElementById('outerWrapper');
        if (this.listenerElement)
        {
            this.listenerElement.addEventListener('mouseover', this.handleEleOver.bind(this), true);
            this.listenerElement.addEventListener('mouseout', this.handleEleOut.bind(this), true);
        }
    },
    register: function(elem, text)
    {
        //log('Tooltip Register: ' + text);

        // elem.addEventListener('mouseover', this.tipIn.bind(this), true);
        // elem.addEventListener('mouseout', this.tipOut.bind(this), true);

        // ko.utils.domNodeDisposal.addDisposeCallback(elem, function()
        // {
        //     log('Dispose');
        // });

        this.update(elem, text);
    },
    update: function(elem, text)
    {
        elem.setAttribute('tooltip', text);
    },
    tipIn: function(e)
    {
        if (this.displayElement !== e.srcElement)
        {
            this.displayElement = e.srcElement;

            clearTimeout(this.displayTimeout);
            this.displayTimeout = setTimeout(this.displayTip.bind(this), displayDelay);
        }
    },
    adjustBounds: function()
    {
        var maxTooltipWidth = 200;
        var horizAdjust = 0;

        var windowBounds = document.documentElement.getBoundingClientRect();
        var targetBounds = this.displayElement.getBoundingClientRect();

        var boundsDiff = (windowBounds.right - targetBounds.right);

        // Quick check to see if we may need to adjust the tooltip position based on its max size.
        var displayOnLeft = boundsDiff < maxTooltipWidth;

        // Ensure that the tooltip is kept on the screen
        if (displayOnLeft)
        {
            var actualWidth = g.width(this.element);

            if (boundsDiff < actualWidth)
            {
                horizAdjust += actualWidth - (windowBounds.right - targetBounds.right) + 10;
            }
        }

        var x = targetBounds.left + targetBounds.width / 2;

        g.setTransform(this.element, x - horizAdjust, targetBounds.bottom + 5);
    },
    displayTip: function()
    {
        this.displayTimeout = undefined;

        if (this.displayElement)
        {
            var infoText = this.displayElement.getAttribute('tooltip');

            try{g.PAssert(1476,infoText, 'When display a tooltip, elements should always have valid data set');}catch(PERR){g.reportError(PERR);}

            if (infoText)
            {
                this.displayText(infoText);

                this.adjustBounds();

                this.extraTimeout = setTimeout(this.showExtra.bind(this), extraDelay);
            }
        }
    },
    tipOut: function(e)
    {
        if (!e.srcElement)
        {
            e.srcElement = e.target;
        }

        var isAncestor = false;
        var newTarget = e.toElement || e.relatedTarget;

        if (newTarget)
        {
            isAncestor = g.isAncestor(newTarget, e.srcElement);
        }

        // If the new target is a descendant of the tooltip or the user is now mouseover on the tooltip, do not
        // hide the tooltip text.
        if (!isAncestor && e.srcElement !== newTarget && newTarget !== this.element)
        {
            this.hideTip();
        }
    },
    handleEleOver: function(e)
    {
        if (!e.srcElement)
        {
            e.srcElement = e.target;
        }

        var infoText = e.srcElement.getAttribute('tooltip');

        if (infoText)
        {
            this.tipIn(e);
        }
    },
    handleEleOut: function(e)
    {
        if (!e.srcElement)
        {
            e.srcElement = e.target;
        }

        var infoText = e.srcElement.getAttribute('tooltip');

        if (infoText)
        {
            this.tipOut(e);
        }
    },
    handleSelfOver: function(e)
    {
        if (this.displayElement)
        {
            this.overTooltip(true);

            this.showExtra();
        }
    },
    handleSelfOut: function(e)
    {
        if (!e.srcElement)
        {
            e.srcElement = e.target;
        }

        var isAncestor = false;
        var newTarget = e.toElement || e.relatedTarget;

        if (newTarget)
        {
            isAncestor = g.isAncestor(newTarget, e.srcElement);
        }

        if (!isAncestor && e.srcElement !== newTarget)
        {
            this.overTooltip(false);

            if (newTarget !== this.displayElement)
            {
                this.hideTip();
            }
        }
    },
    showExtra: function()
    {
        var extraString = undefined;

        try{g.PAssert(1477,this.displayElement, 'There should be a display element when showing extra information');}catch(PERR){g.reportError(PERR);}

        if(this.displayElement)
        {
            // First check if there is extra tooltip info associated with the element's ID
            if (this.displayElement.id)
            {
                var idString = '#' + this.displayElement.id;

                if (this.tipExtras[idString])
                {
                    extraString = idString;
                }
            }

            // Then check if any of the element's classes match.
            if (!extraString)
            {
                for (var i = 0; i < this.displayElement.classList.length; ++i)
                {
                    var classString = '.' + this.displayElement.classList[i];

                    if (this.tipExtras[classString])
                    {
                        extraString = classString;
                        break;
                    }
                }
            }

            if (extraString)
            {
                var extraInfo = this.tipExtras[extraString];

                if (extraInfo)
                {
                    this.displayExtra(extraInfo);

                    this.adjustBounds();
                }
            }

            clearTimeout(this.extraTimeout);
            this.extraTimeout = undefined;
        }
    },
    hideTip: function()
    {
        if (this.displayElement)
        {
            this.displayElement = undefined;

            clearTimeout(this.displayTimeout);
            this.displayTimeout = undefined;

            clearTimeout(this.extraTimeout);
            this.extraTimeout = undefined;

            this.displayText('');
            this.displayExtra('');
        }
    }
};

return VMTooltip;

});
