define('VMLI_TapHandlers',['ko', 'globals', 'platform', 'android', 'tracker'],
function (ko, g, platform, android, tracker)
{
var gestureThreshold = 80;
var gestureSelectFirstThreshold = 40;

var SPThresholdLeft = 18;
var SPThresholdRight = 36;

var dataTransfer = undefined;

var VMLI_TapHandlers =
{
    dragStart: function (e)
    {
        try{g.PAssert(1068,!platform.mobile || !g.focusedPane.isTimeline(), 'Should not be able to drag/drop on timeline on mobile');}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(1069,g.startDrag, 'Should always have a valid item to start dragging from');}catch(PERR){g.reportError(PERR);}

        // log('start drag');
        g.clearSelection();

        // When starting a new drag, ensure android input has been completed and hidden.
        if (android.isEnabled())
        {
            android.finishInput();
        }

        // $(document.body).addClass('noSelect');

        var target = g.startDrag.getElement();
        g.startDrag = undefined;

        try{g.PAssert(1070,g.dragging === undefined, 'Only one element may be dragged or dropped at a time');}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(1071,g.draggingHidden === undefined, 'Only one element may be dragged or dropped at a time');}catch(PERR){g.reportError(PERR);}

        try{g.PAssert(1072,!g.tempUL.hasChildNodes(), 'TempUL should never have children when starting a drag');}catch(PERR){g.reportError(PERR);}

        g.draggingHidden = target;

        // TODO: this clone is slow. Its purpose is to leave a white space when it moves.
        // Could it be optimized by setting the height of the item above instead?
        // Or is it ok to not leave a space?
        g.dragging = target.cloneNode(true);
        //g.$dragging = $target.clone();

        g.tempUL.appendChild(g.dragging);

        var scroll = document.getElementById('container').scrollTop;
        var y = e.eventY + scroll - (platform.mobile ? g.phoneMainY : 0);
        var width = g.width(target);

        g.dragStartInfo = {x: e.eventX, y: e.eventY};

        g.tempUL.style.display = 'block';
        if(!platform.mobile)
        {
            g.tempUL.style.left = (e.eventX - e.xFromTopLeft) + 'px';
        }
        g.tempUL.style.top = (y - e.yFromTopLeft) + 'px';
        g.tempUL.style.width = width + 'px';

        g.addClass(g.draggingHidden, 'itemBeingDragged');

        g.disableMouseMove = true;
    },
    dragMove: function (e)
    {
        // Prevent the default touchmove event, which is to pan with the finger
        e.preventDefault();

        var x = platform.mobile ? 0 : (e.eventX - g.dragStartInfo.x);
        var y = e.eventY - g.dragStartInfo.y;

        var translate = 'translate3d(' + x + 'px, ' + y + 'px, 0)';

        g.tempUL.style[platform.transform.style] = translate;

        var dragOver = document.elementFromPoint(e.eventX, e.eventY);

        if (!dataTransfer) { dataTransfer = { dropEffect: 'move', item: this }; }

        g.vmMain.handleDragOver({
            srcElement: dragOver,
            dataTransfer: dataTransfer,
            diffY: e.diffY,
            offsetY: e.eventY,
            preventDefault: g.emptyFn,
            stopPropagation: g.emptyFn,
            stopImmediatePropagation: g.emptyFn
        });
    },
    dragEnd: function (e)
    {
        // $(document.body).removeClass('noSelect');

        if (platform.ios)
        {
            g.preventClick();
        }

        g.disableMouseMove = false;

        var dragging = g.dragging;
        var draggingHidden = g.draggingHidden;

        g.dragging = undefined;
        g.draggingHidden = undefined;

        // TODO: This is revert:
        // $dragging.css({
        //     '-webkit-transition': '-webkit-transform ' + returnTime + 'ms',
        //     '-webkit-transform': 'translate3d(0,0,0)'
        // });
        // setTimeout(function(){
        g.removeClass(draggingHidden, 'itemBeingDragged');
        dragging.parentNode.removeChild(dragging);
        // }, 200);

        g.tempUL.style.display = 'none';

        if (!dataTransfer) { dataTransfer = { dropEffect: 'move', item: this }; }

        g.vmMain.handleDrop({
            srcElement: e.srcElement,
            dataTransfer: dataTransfer,
            preventDefault: g.emptyFn,
            stopPropagation: g.emptyFn,
            stopImmediatePropagation: g.emptyFn
        });

        dataTransfer = undefined;

        // iOS workaround where preventDefault on touchMove makes touchUp think
        // it's a click and then sets focus.
        if (platform.ios)
        {
            g.preventClick();
        }

        g.disableMouseMove = false;
    },
    onStart: function(e)
    {
        if (g.dragStartTimeout)
        {
            clearTimeout(g.dragStartTimeout);
            g.dragStartTimeout = undefined;
        }

        //g.touchPhase = g.TouchPhase.Start;
        g.scrollAtTouchStart = g.focusedPane.getScrollOffset();
        g.canRevealMenu = true;

        //log('Begin DragTouch: ', g.scrollAtTouchStart);

        if (g.isKeyboardOpen)
        {
            g.revealTimeout = setTimeout(function()
            {
                g.canRevealMenu = false;
            }, 400);
        }

        if (platform.mobile && !platform.ie && e.srcElement.tagName !== 'UL' && !g.isKeyboardOpen && !g.focusedPane.isTimeline())
        {
            g.dragStartTimeout = setTimeout(function()
            {
                g.clearSelection();

                g.startDrag = this;

                this.dragStart(e);
                this.dragMove(e);
            }.bind(this), 500);
        }
        else if (e.srcElement.tagName === 'SPAN' && e.offsetX <= SPThresholdLeft) // Clicks on the left of item
        {
            g.clearSelection();
            g.startDrag = this;

            e.preventDefault();
            e.stopImmediatePropagation();
        }
    },
    onMove: function(e)
    {
        // if(this.ignoreThisTap)
        //     return;

                // log(document.body.scrollTop, window.innerHeight, document.body.scrollHeight);

        if(g.revealTimeout)
        {
            clearTimeout(g.revealTimeout);
            g.revealTimeout = undefined;
        }

        //g.touchPhase = g.TouchPhase.Move;

        // if(g.isKeyboardOpen && platform.bodyscroll)
        // {
        //     if (g.phoneMenu.isSearchOut())
        //     {
        //         g.vmMain.elPhoneMenu.style.visibility = 'hidden';
        //         document.getElementById('searchDivPhone').style.visibility = 'hidden';
        //     }
        //     else
        //     {
        //         g.keyboardToolbar.hide();
        //     }
        // }

        if(this.isItemMenuOpen)
        {
            e.preventDefault();
        }
        else if (g.startDrag)
        {
            if(!platform.touch || Math.abs(e.distanceY) > g.ClickThreshold && Math.abs(e.distanceX) < g.ClickThreshold)
            {
                if (g.startDrag === this)
                {
                    this.dragStart(e);
                }
            }
        }

        if(g.dragging)
        {
            this.dragMove(e);
        }
        else
        {
            // TODO: Move this out of VMLI
            if (platform.touch && g.canRevealMenu && !g.focusedPane.isTimeline())
            {
                if (g.revealingMenu || g.revealingRightMenu || (e.distanceY < g.ClickThreshold && Math.abs(e.diffX) > g.ClickThreshold))
                {
                    // if(!g.isKeyboardOpen)
                    // {
                        if (!g.revealingMenu)
                        {
                            g.menus.revealGestureMenu(this, e);
                        }
                        // if(!g.menus.startedInMultiSelectMode)
                        // {
                            g.menus.updateRevealGestureMenu(e);
                        // }
                    // }

                    e.preventDefault();
                }
            }
        }
    },
    onClick: function(e)
    {
        //log('Ele: ' + e.srcElement.tagName + '  Offset: ' + e.offsetX + '  ReqOffset: ' + (g.focusedPane.getWidth() - SPThresholdRight));
        // if (this.ignoreThisTap)
        // {
        //     return;
        // }

        // if(g.vmMain.isOutlineOut())
        // {
        //     g.temp_closingOutline = true;
        //     g.outline.close();
        //     e.preventDefault();
        //     e.stopImmediatePropagation();
        // }
        if (platform.mobile && g.phoneMenu.multiSelectMode())
        {
            g.menus.multiSelect(this);
        }
        else if (e.srcElement.tagName === 'SPAN' && e.offsetX <= SPThresholdLeft) // Clicks on the left of item
        {
            // if (e.srcElement.className.startsWith('spComplete'))
            // {
            //     this.isComplete(false);
            // }

            // this.applyPrefix(CompletePrefix);
            // g.clearSelection();

            // var $clicked = $(e.srcElement);
            // var offset = $clicked.offset();
            // g.menus.setGestureMenu('format');
            // this.spanStart = g.offset(e.startSrc);
            // var y = this.spanStart.top + 10;
            // g.menus.openGestureMenu(this, this.spanStart.left, y);

            if (!this.isComplete())
            {
                g.messageQueue.pushMessage({
                    text: 'You completed an item, click again to undo',
                    type: MessageType.Info,
                    timeout: 3000
                });
            }

            // g.menus.openFormatMenu(this);
            this.isComplete(!this.isComplete());
        }
        else if (e.srcElement.tagName === 'LI' && this.isCollapsed() && e.offsetX >= (g.focusedPane.getWidth() - SPThresholdRight))
        {
            this.toggleCollapsed();
        }
        else if (g.hasClass(e.srcElement, 'tag') && !platform.mobile)
        {
            if (platform.mac && e.metaKey || !platform.mac && e.ctrlKey)
            {
                var text = e.srcElement.textContent;

                if (e.shiftKey)
                {
                    g.focusedPane.vmSearch.setSearch(text);
                }
                else
                {
                    g.focusedPane.vmSearch.addToSearch(text);
                }
            }
        }
        else if (g.hasClass(e.srcElement, 'contact'))
        {
            var offset = g.getOffsetOfSubNode(this.getSpan(), e.srcElement);

            g.autocomplete.update({item: this, start: offset + 1});
            g.autocomplete.show(this, e.srcElement);

            e.preventDefault();

            if (platform.ios)
            {
                g.preventClick();
            }
        }
        else if (e.startSrc.id === 'itemOptions')
        {
            // var $clicked = $(e.srcElement);
            // var offset = $clicked.offset();
            // g.menus.openItemMenu(this, offset.left, offset.top);
            if (g.itemMenuOpenedOn == this)
            {
                g.itemMenuOpenedOn = undefined;
            }
            // log('menu');
        }
        // // Clicks on switch view mode
        // else if(e.srcElement.tagName == 'LI' && e.yFromTopLeft < 0)
        // {
        //     this.togglePriorityView();
        // }
        else if (e.srcElement.tagName === 'A') // Link
        {
            if (platform.mac && e.metaKey || !platform.mac && e.ctrlKey)
            {
                var offset = g.offset(e.srcElement);

                open(e.srcElement.href, 'link' + e.srcElement.href);
            }
            else
            {
                // g.menus.openLinkMenu(this, e.srcElement.href, offset.left, offset.top + 20);
            }

            // e.ignoreThisTap = true;
            // e.handled = true;
            // e.preventDefault();
            // e.stopImmediatePropagation();
        }
        else
        {
            if (g.autocomplete)
            {
                g.autocomplete.hide();
            }

            if (platform.ios || platform.android || platform.mobileie)
            {
                var activeElement = document.activeElement;
                var scrollTopClick = g.focusedPane.getScrollOffset();
                var justCancelledInertialScroll = Math.abs(scrollTopClick - g.scrollAtTouchStart) > 0;

                if((!g.isKeyboardOpen || !activeElement || activeElement.id == 'searchPhone') &&
                    !justCancelledInertialScroll && !g.focusedPane.isTimeline())
                {
                    var performedScroll = false;

                    var item = this;

                    if (platform.mobile && this == g.focusedPane.item())
                    {
                        item = g.getLastVisibleItem(g.focusedPane.item());
                    }

                    try{g.PAssert(1073,item, 'Click on item should not happen');}catch(PERR){g.reportError(PERR);}

                    // if (item)
                    // {
                        if (platform.ios)
                        {
                            g.vmMain.setSelected( [ item ] );
                        }
                        g.openKeyboardOn(e, item.getSpan(), true, function()
                        {
                            g.selectChildren(item, item.getParsedText().length, 0);
                        });

                        // g.keyboardToolbar.preShow();

                        // if (!g.isKeyboardOpen)
                        // {
                        //     // Scroll item into view first
                        //     if(platform.app)
                        //     {
                        //         var callback = function()
                        //         {
                        //             setTimeout(function()
                        //             {
                        //                 g.keyboardToolbar.show();
                        //                 g.selectChildren(item, item.getParsedText().length, 0);

                        //                 if (!platform.bodyscroll)
                        //                 {
                        //                     g.enforceScroll();
                        //                 }
                        //                 g.removeClass(g.focusedPane.paneContent, 'padded');
                        //             }, platform.keyboardOpenTime);
                        //         };

                        //         g.addClass(g.focusedPane.paneContent, 'padded');
                        //         // Wait a frame to allow padding to take effect before scrolling
                        //         performedScroll = g.scrollIntoView(item.getSpan(), platform.keyboardOpenTime, callback, /*useAnimationFrame*/true);
                        //     }
                        //     else
                        //     {
                        //         performedScroll = g.scrollIntoView(item.getSpan(), platform.keyboardOpenTime);
                        //     }
                        // }

                        // if(!performedScroll || !platform.app)
                        // {
                        //     g.selectChildren(item, item.getParsedText().length, 0);
                        // }
                    // }

                    // // Prevent native tap handler since setting selection will bring up
                    // // the keyboard
                    // if (platform.ios)
                    // {
                    //     g.preventClick();
                    // }

                    // e.preventDefault();
                    // e.stopImmediatePropagation();
                    // e.returnFalse = true;

                    // if (!platform.bodyscroll)
                    // {
                    //     g.enforceScroll();
                    // }
                }
                else if (platform.mobileie)
                {
                    if (android.isEnabled())
                    {
                        android.updateInput(this);
                    }
                }
            }
            else (!platform.mobile)
            {
                // On desktop, single clicking on a timeline element will jump other panes to highlight that particular item.
                if (g.focusedPane.isTimeline() && g.hasClass(e.srcElement, 'text'))
                {
                    for (var i = 0; i < this.elements.length; ++i)
                    {
                        var spanEle = this.getSpan(i);

                        var pane = g.vmMain.panes()[i];

                        if (pane && pane.vmSearch.isVisible(this))
                        {
                            g.scrollIntoView(this.getSpan(i), 200);
                        }
                    }

                    this.highlight();

                    g.sendEvent('Menu', 'GoTo');
                }
            }
        }
    },
    onDblClick: function(e)
    {
        if (g.hasClass(e.srcElement, 'text') && platform.mobile)
        {
            // On mobile double tap, switch back to the main pane and zoom to the item
            // if (platform.mobile)
            // {
                try{g.PAssert(1074,g.phoneMenu, 'Phone menu should always be available on mobile');}catch(PERR){g.reportError(PERR);}
                g.phoneMenu.toggleMode();

                e.preventDefault();
            //}

            for (var i = 0; i < this.elements.length; ++i)
            {
                g.scrollIntoView(this.getSpan(i), 200);
            }

            this.highlight();

            g.sendEvent('PhoneMenu', 'GoTo');
        }
    },
    // onMiddleClick: function(e)
    // {
    //     // if (this.ignoreThisTap)
    //     // {
    //     //     return;
    //     // }
    //     if (g.hasClass(e.srcElement, 'num'))
    //     {
    //         var target = ko.dataFor(e.srcElement);
    //         if (target.hasChildren())
    //         {
    //             g.vmMain.addPane(target);

    //             e.srcElement.blur();
    //             e.ignoreThisTap = true;
    //             e.handled = true;
    //             e.preventDefault();
    //             e.stopImmediatePropagation();
    //         }
    //     }
    // },
    onEnd: function(e)
    {
        //g.touchPhase = g.TouchPhase.End;
        // if(this.ignoreThisTap)
        //     return;

        if (g.dragStartTimeout)
        {
            clearTimeout(g.dragStartTimeout);
            g.dragStartTimeout = undefined;
        }

        g.startDrag = undefined;

        // if(g.vmMain.isOutlineOut())
        // {
        //     if(g.vmMain.mainTransform < g.contextX() / 2)
        //     {
        //         g.outline.close();
        //     }
        //     else
        //     {
        //         g.vmMain.setTransform(g.contextX(), 150);
        //     }

        //     e.preventDefault();
        //     return false;
        // }
        // else if(g.temp_closingOutline)
        // {
        //     g.temp_closingOutline = false;
        //     e.preventDefault();
        //     return false;
        // }

        // if(this.transforming)
        // {
        //     if(g.vmMain.mainTransform > -160 && g.vmMain.mainTransform !== 0)
        //     {
        //         g.vmMain.setTransform(0, 150);
        //     }
        //     else if(g.vmMain.mainTransform < -160 && g.vmMain.mainTransform != -320)
        //     {
        //         g.vmMain.setTransform(-320, 150);
        //     }
        //     this.transforming = false;
        // }

        if (g.itemMenuOpenedOn == this)
        {
            g.menus.menuItemSelected(e.srcElement);
        }

        if (g.revealingMenu || (g.phoneMenu && g.phoneMenu.multiSelectMode()))
        {
            e.preventDefault();
            e.stopImmediatePropagation();
            e.returnFalse = true;
            // iOS workaround where preventDefault on touchMove makes touchUp think
            // it's a click and then sets focus.
            if (platform.ios)
            {
                g.preventClick();
            }
        }

        if (g.revealingMenu)
        {
            g.menus.stopRevealGestureMenu(e);
        }

        var returnTime = 200;

        if(g.dragging)
        {
            this.dragEnd(e);
        }
        // else if(!platform.touch)
        // {
        //     var target = g.getLIParent(e.srcElement);
        //     log('end', target);
        //     target.removeEventListener('dragstart', handleDragStart);
        //     target.removeEventListener('dragend', handleDragEnd);
        //     $(target).removeAttr('draggable');
        // }

        //g.events.emit('tapEnd', e);
    },
    // onCancel: function(e)
    // {
    //     if(touchTimeout)
    //         clearTimeout(touchTimeout);
    //     g.startDrag = undefined;
    // },
    onRightClick: function(e)
    {
        if (!e.stopPropagation || !e.preventDefault)
        {
            debugger;
        }

        g.menus.openItemMenu(this, e);
        e.preventDefault();
        e.stopPropagation();
    },
    onAutocompleteTapped: function(text, trackingTag)
    {
        var text = this.getParsedText().substr(0, trackingTag.index) + text + this.getParsedText().substr(trackingTag.index + trackingTag.length);

        this.setText(text, /*immediateParse*/true);
        g.selectChildren(this, trackingTag.index + text.length, 0);
    }
    // onTapTimeline: {
    //     onClick: function(e)
    //     {
    //         var span = g.getLIParent(e.srcElement);
    //         if(platform.touch || e.xFromTopLeft > span.clientWidth)
    //         {
    //             if(!g.smallMode)
    //             {
    //                 this.highlight();

    //                 g.selectChildren(this, 0, this.getParsedText().length);
    //             }
    //             else
    //             {
    //                 g.phoneMenu.goHome();
    //                 this.parent().zoomin({time: g.zoomTime});
    //                 setTimeout(this.highlight, 600);
    //                 e.preventDefault();
    //             }
    //         }
    //     }
    // }
};

return VMLI_TapHandlers;
});

