self.modifyItemId = function (id, newId, callback)
{
    if (DEBUG) log('modifying', id, 'into', newId);

    var item = self.getItem(id);
    var model = self.getModel(id);

    item.id = newId;
    model.id = newId;

    if (self.pendingRemoteUpdates[id])
    {
        self.pendingRemoteUpdates[id] = undefined;
        self.pendingRemoteUpdates[newId] = true;
    }

    // Reset the version to undefined to match the newly created remote item
    item.version = -1;
    item.versionText = -1;

    // Special case updating the ID for the root item - in this case also remove any unused children. This
    // optimization helps avoid issues when loading the app for the first time and the remote data needs to
    // be downloaded.
    if (id === 'root')
    {
        for (var i = item.items.length - 1; i >= 0; i--)
        {
            var rootChild = self.getItem(item.items[i]);

            if (g.isLocalItem(rootChild) && !rootChild.modifiedOffline && !rootChild.modifiedOfflineText)
            {
                item.items.removeAt(i);

                try{g.PAssert(1020,model.items()[i].id === rootChild.id, 'Item arrays should match');}catch(PERR){g.reportError(PERR);}

                model.items().removeAt(i);
            }
        }
    }

    self.itemsById[newId] = { data: item, model: self.itemsById[id].model };

    self.dbDeleteItem(id);
    self.dbUpdateItem({
        changes: item,
        callback: callback,
        skipLocalModification: true,
        saveFlags: SaveFlag.None
    });

    self.itemsById[id] = undefined;
    delete self.itemsById[id];
};

self.deleteItem = function (item)
{
    try{g.PAssert(1021,!item.isDeleted, 'Should never try to delete an item that has already been deleted');}catch(PERR){g.reportError(PERR);}

    if (DEBUG) log('deleting item', item.id, item.getParsedText());

    var localItem = self.getItem(item.id);
    if (localItem)
    {
        item.isDeleted = true;

        self.dbUpdateItem({
            id: item.id,
            changes: { isDeleted: true },
            saveFlags: SaveFlag.Prop
        });
    }
    else
    {
        try{g.PAssert(1022,'Expected local item, didnt find one', item.id);}catch(PERR){g.reportError(PERR);}
    }
};

self.undeleteItem = function (item)
{
    var localItem = self.getItem(item.id);
    if (localItem)
    {
        item.isDeleted = false;

        self.dbUpdateItem({
            id: item.id,
            changes: { isDeleted: undefined },
            saveFlags: SaveFlag.Prop
        });
    }
    else
    {
        try{g.PAssert(1023,'Expected local item, didnt find one', item.id);}catch(PERR){g.reportError(PERR);}
    }
};

//
// Take the incoming item, the changes to the item and save them locally then
// try to push them to the server.
//
self.saveChanges = function (item, changes, saveFlags)
{
    if (!item)
    {
        log('Incoming item to save has no item!', item, changes);

        try{g.PAssert(1024,false, 'Incoming item to save has no item!');}catch(PERR){g.reportError(PERR);}

        return;
    }

    if (!changes)
    {
        changes = { };
    }

    //if (DEBUG) log('[', item.id, '] Save Changes: ', changes, noVersion);

    self.dbUpdateItem({
        id: item.id,
        changes: changes,
        saveFlags: saveFlags
    });
};

self.addPendingUpdate = function(id)
{
    //var id = g.isString(item) ? item : item.id;
    try{g.PAssert(1025,id, 'Invalid ID');}catch(PERR){g.reportError(PERR);}
    try{g.PAssert(1026,g.isString(id), 'Must provide a valid id string');}catch(PERR){g.reportError(PERR);}

    if (DEBUG) log('Add Pending Update: ', id);

    if (!self.pendingRemoteUpdates[id])
    {
        self.pendingRemoteUpdates[id] = true;
    }
};

self.clearPendingUpdates = function()
{
    for (var itemID in self.pendingRemoteUpdates)
    {
        try{g.PAssert(1027,self.pendingRemoteUpdates.hasOwnProperty(itemID));}catch(PERR){g.reportError(PERR);}

        if (DEBUG || DEBUG) log('Clear Pending Update: ', itemID);

        var item = self.getModel(itemID);
        var itemData = self.getItem(itemID);

        if (itemData && itemData.isDeleted)
        {
            // Delete locally since we know it's been propagated to the server
            if (DEBUG) log('%cDB delete item', 'color: red', itemID, itemData.text);

            self.dbDeleteItem(itemID);
        }
        else if (item && item.data) // It's possible the item has been deleted so ignore it
        {
            var changes =
            {
                modifiedOffline: false,
                modifiedOfflineText: false
            };

            item.modifiedOffline = false;
            item.modifiedOfflineText = false;

            var newVersion = gdata.getVersion(item.data, VersionType.Structure);
            var newTextVersion = gdata.getVersion(item.data, VersionType.Text);

            if (newVersion != item.getVersion(VersionType.Structure))
            {
                changes.version = newVersion;

                item.setVersion(VersionType.Structure, newVersion);
            }

            if (newTextVersion != item.getVersion(VersionType.Text))
            {
                changes.versionText = newTextVersion;

                item.setVersion(VersionType.Text, newTextVersion);
            }

            self.dbUpdateItem({
                id: itemID,
                changes: changes,
                saveFlags: SaveFlag.None
            });
        }
    }

    self.pendingRemoteUpdates = { };
    self.printIndexedDB('id', 'text', 'version', 'versionText', 'modifiedOffline', 'isArchived');
};

var charMapHTML = {
    '&': "&amp;",
    '\"': "&quot;",
    '<': "&lt;",
    '>': "&gt;"
};

var charMapJSON = {
    '\"': '\\\"'
};

// TODO: Exporter functions should use only absolute dates!
self.toHtml = function(item, allowArchived)
{
    var str = '<ul>';

    if (item.items)
    {
        for(var i = 0; i < item.items.length; i ++)
        {
            var child = self.getItem(item.items[i]);
            var model = self.getModel(child.id);

            var text = String(model.getParsedText()).replace(/[&\"<>]/g, function (chr)
            {
                return charMapHTML[chr];
            });

            if (!child.isArchived || (child.isArchived && allowArchived))
            {
                str += '<li>' + text;
                if (child.items && child.items.length > 0)
                {
                    str += self.toHtml(child);
                }
                str += '</li>';
            }
        }
    }
    str += '</ul>';
    return str;
};

self.toPlainText = function(item, indent, allowArchived)
{
    if(isNaN(indent)) { indent = 0; }

    var str = '';

    if (item.items)
    {
        for(var i = 0; i < item.items.length; i ++)
        {
            var child = self.getItem(item.items[i]);
            var model = self.getModel(child.id);

            if (!child.isArchived || (child.isArchived && allowArchived))
            {
                for(var u = 0; u < indent; u ++)
                {
                    str += '\t';
                }

                str += model.getParsedText() + '\n';
                if (child.items && child.items.length > 0)
                {
                    str += self.toPlainText(child, indent + 1);
                }
            }
        }
    }
    return str;
};

function makePropString(item, field)
{
    var value = item[field];

    if (value)
    {
        return ',"' + field + '":' + value;
    }
    else
    {
        return '';
    }
}

function makeJSON(item, hasPrev, allowArchived)
{
    var str = '';
    if (hasPrev)
    {
        str += ',';
    }

    var model = self.getModel(item.id);

    var text = String(model.getParsedText()).replace(/[\"]/g, function (chr)
    {
        return charMapJSON[chr];
    });

    var item = self.getItem(item.id);

    var dateCompletedString = makePropString(item, 'dateCompleted');
    var priorityString = makePropString(item, 'priority');
    var isCompleteString = makePropString(item, 'isComplete');
    var isFlaggedString = makePropString(item, 'isFlagged');
    var isArchivedString = makePropString(item, 'isArchived');

    str += '{"text": "' + text + '"' + dateCompletedString + priorityString + isCompleteString + isFlaggedString + isArchivedString;

    if (item.items && item.items.length > 0)
    {
        str += ',"items":[';
        for(var i = 0; i < item.items.length; i ++)
        {
            var child = self.getItem(item.items[i]);

            if (!child.isArchived || (child.isArchived && allowArchived))
            {
                str += makeJSON(child, i > 0);
            }
        }
        str += ']';
    }
    str += '}';

    return str;
}

self.toJSON = function(item, hasPrev, allowArchived)
{
    var rootItem = makeJSON(item, hasPrev, allowArchived);

    return '{"moodoVer":' + g.settings.get(Settings.localDataVersion) + ',"moodoRoot":' + rootItem + '}';
};

