self.ensureLocalCache = function (callback)
{
    var root = self.getRootItem();
    if (!root)
    {
        var oldLocalData = self.hasLocalData;

        var tempID = g.generateTempId();

        self.dbUpdateItem({
            changes: {
                id: 'root',
                text: 'Home',
                items: [ ]
            },
            skipLocalModification: true,
            saveFlags: SaveFlag.None,
            callback: function(){
                self.setRoot('root');
                self.hasLocalData = oldLocalData;
                callback();
            }
        });
    }
    else
    {
        if (self.loadedItemCount > 2)
        {
            self.hasLocalData = true;
        }

        callback();
    }
};


self.printIndexedDB = function()
{
    if (DEBUG)
    {
        try{g.PAssert(1035,self.dbi);}catch(PERR){g.reportError(PERR);}

        var args = Array.prototype.slice.call(arguments);
        for(var i = 0; i < args.length; i ++)
        {
            args[i] = g.translateFieldIn(args[i]);
        }

        var printAll = args.length === 0;
        self.dbi.items.query().all().execute().done(function(results) {
            log('---- Local DB ----')
            for(var i = 0; i < results.length; i ++)
            {
                if(printAll)
                {
                    log(results[i]);
                }
                else
                {
                    var vals = [];
                    for(var u = 0; u < args.length; u ++)
                    {
                        vals[u] = results[i][args[u]];
                    }
                    log.apply(this, vals);
                }
            }
            log('------------------');
        });
    }
};

return self;
