define('VMIntroSection',['ko', 'globals', 'platform', 'data'],
function (ko, g, platform, d) {

function VMIntroSection(text, note)
{
    this.text = text;
    this.note = note;
    this.items = [];
    this.inOrder = false;

    this.owner = undefined;

    this.numComplete = 0;
    this.next = 0;
    this.isComplete = ko.observable(false);
}

VMIntroSection.prototype =
{
    getHash: function()
    {
        return g.introHash(this.text);
    },
    getStepHash: function(index)
    {
        return g.introHash(this.items[index].text);
    },
    loadFromSaveData: function(data)
    {
        if (data === true)
        {
            for (var i = 0; i < this.items.length; ++i)
            {
                this.items[i].isChecked(true);
            }

            this.numComplete = this.items.length;
            this.isComplete(true);
        }
        else
        {
            for (var i = 0; i < data.length; ++i)
            {
                for (var j = 0; j < this.items.length; ++j)
                {
                    if (data[i] === this.getStepHash(j))
                    {
                        this.items[j].isChecked(true);
                        this.numComplete++;
                    }
                }
            }

            if (this.numComplete === this.items.length)
            {
                this.isComplete(true);
            }
        }
    },
    addStep: function(item)
    {
        // TODO: Put in this assert after all help items are filled out
        // try{g.PAssert(1353,item.text && item.check, 'Invalid help step');}catch(PERR){g.reportError(PERR);}
        if(!item.check)
            item.check = function(){};

        if(!item.isChecked)
            item.isChecked = ko.observable(false);
        if(this.inOrder)
        {
            if(!item.isDisabled)
                item.isDisabled = ko.observable(this.items.length > 0);
        }
        else
        {
            item.isDisabled = g.fFalse;
        }
        this.items.push(item);
        return item;
    },
    complete: function(item)
    {
        item.isChecked(true);
        this.owner.notifyOfChange(item);
    },
    check: function(e)
    {
        for(var i = 0; i < this.items.length; i ++)
        {
            if (!this.items[i].isChecked() && this.items[i].check(e))
            {
                this.complete(this.items[i]);

                if(this.inOrder)
                {
                    this.numComplete ++;
                    if(i === this.next)
                    {
                        this.next ++;
                        if(this.next < this.items.length)
                        {
                            this.items[this.next].isDisabled(false);
                        }
                    }
                }
            }
        }
        if(this.numComplete === this.items.length)
        {
            this.isComplete(true);
        }
    }
};

return VMIntroSection;
});
