define('VMDocSharing',['ko', 'globals', 'platform', 'data', 'goog'],
function (ko, g, platform, d, goog) {

function VMDocSharing()
{
    this.doc = ko.observable({ isOwned: function () { return false }});
    this.isLoading = ko.observable(false);
    this.isCreating = ko.observable(false);
    this.permissions = ko.observableArray();
    this.permissionsNew = ko.observableArray();
    this.permissionsRemove = [ ];
    this.newEmail = ko.observable('');
    this.errorGlobal = ko.observable(false);
    this.errorName = ko.observable(false);
    this.errorInvite = ko.observable(false);
    this.isSaving = ko.observable(false);
    this.editing = ko.observable('');
    this.closeCallback = undefined;
    this.justCreated = false;
}

VMDocSharing.prototype =
{
    open: function(doc, callback)
    {
        this.doc(doc);
        this.closeCallback = callback;

        this.permissions.removeAll();
        this.permissionsNew.removeAll();
        this.permissionsRemove = [ ];
        this.newEmail('');
        this.editing('');
        this.isLoading(true);
        this.errorGlobal(false);
        this.errorName(false);
        this.errorInvite(false);
        this.isSaving(false);
        this.isCreating(!this.doc().file);
        this.justCreated = false;
        this.inputName = document.getElementById('shareNameInput');
        this.inputName.value = this.doc().title();

        this.shareLink = document.getElementById('shareLinkInput');
        if (this.shareLink)
        {
            this.shareLink.value = 'moo.do/#file=' + this.doc().id;
        }

        if (!platform.mobile)
        {
            setTimeout(function()
            {
                this.inputName.focus();
            }.bind(this), 0);
        }

        if (!this.isCreating())
        {
            if(DEBUG)
            {
                this.permissionHandler(g.test.permissions);
            }
            else
            {
                gapi.client.drive.permissions.list({
                    'fileId': this.doc().id
                }).execute(this.permissionHandler.bind(this));
            }
        }
        else
        {
            this.isLoading(false);
        }
    },
    permissionHandler: function(response)
    {
        if (!response.error)
        {
            this.isLoading(false);

            var permissions = [ ];
            for (var i = 0; i < response.items.length; ++i)
            {
                var item = response.items[i];
                if (item.emailAddress === g.getUserIdentifier())
                {
                    // Don't add self
                    continue;
                }

                item.onRemove = this.onRemove.bind(this, item);

                if (item.id != 'anyone')
                {
                    if (!item.emailAddress)
                    {
                        if (item.role === 'owner')
                        {
                            item.emailAddress = 'Owner';
                        }
                        else
                        {
                            item.emailAddress = '';
                        }
                    }

                    if (item.role === 'owner')
                    {
                        permissions.splice(0, 0, item);
                    }
                    else
                    {
                        permissions.push(item);
                    }
                }
                item.emailDisplay = '(' + item.emailAddress + ')';
            }

            this.permissions(permissions);
        }
    },
    isOwned: function()
    {
        return this.doc().isOwned && this.doc().isOwned();
    },
    saveText: function()
    {
        if (!this.isCreating())
        {
            if(platform.mobile && this.editing())
            {
                return 'Close';
            }
            else if(this.newEmail().length > 0)
            {
                return 'Invite';
            }
            else if(this.isSaving())
            {
                return 'Saving...';
            }
            else
            {
                return 'Save';
            }
        }
        else
        {
            if(this.isSaving())
            {
                return 'Creating...';
            }
            else
            {
                return 'Create';
            }
        }
    },
    titleText: function()
    {
        return (platform.mobile && this.editing()) ? this.editing() : (this.isCreating() ? 'Create Document' : 'Edit Document');
    },
    onkeydown: function(data, e)
    {
        platform.normalizeKeys(e);

        if (e.normCode == KeyCode.Escape)
        {
            this.newEmail('');

            e.preventDefault();
            e.stopImmediatePropagation();

            return false;
        }
        else if (e.normCode == KeyCode.Enter)
        {
            this.inviteUser();

            g.scrollIntoView(g.element('inputShareInvite'), 0);

            e.preventDefault();
            e.stopImmediatePropagation(); // Don't let it go up to the global event handler

            return false;
        }

        return true;
    },
    onKeyboardOpened: function(e)
    {
        this.editing(e.srcElement.title);
    },
    inviteUser: function()
    {
        if (this.newEmail().length === 0)
        {
            this.errorInvite('');
            return;
        }

        if(!g.isEmail(this.newEmail()))
        {
            this.errorInvite('Please enter a valid email address.');
            return false;
        }

        var user = { emailAddress: this.newEmail(), error: ko.observable(false) };
        var contact = d.findContactByEmail(user.emailAddress.toLowerCase());

        if (contact)
        {
            user.name = contact.text;
        }
        else
        {
            user.name = user.emailAddress;
        }

        user.onRemove = this.onRemoveNew.bind(this, user);
        this.permissionsNew.push(user);
        this.newEmail('');
        this.errorInvite(false);

        return true;
    },
    checkName: function()
    {
        var title = this.inputName.value.trim();

        if (title.length > 0 && title.length < 255)
        {
            this.errorName(false);

            return true;
        }

        this.errorName('Error: Document name must be >0 and <255 characters.');

        return false;
    },
    rename: function()
    {
        var title = this.inputName.value.trim();

        if (title != this.doc().title())
        {
            this.doc().title(title);

            gapi.client.drive.files.patch({
                'fileId': this.doc().id,
                'resource':
                {
                    'title': title
                }
            }).execute(function (response)
            {
                if (!response.error)
                {
                    log('File renamed to', title);
                }
                else
                {
                    this.errorName('Error: Unable to update name on remote server.');
                }
            }.bind(this));

        }
    },
    create: function(callback)
    {
        this.justCreated = true;
        this.isSaving(true);

        var title = this.inputName.value.trim();
        this.tooltip = title;

        this.isSaving(true);

        gapi.client.drive.files.insert({
            'resource':
            {
                'mimeType': goog.REALTIME_MIMETYPE,
                'title': title,
                'writersCanShare': false
            }
        }).execute(function (response)
        {
            if (!response.error)
            {
                this.doc().id = response.id;
                this.doc().file = response;

                this.isCreating(!this.doc().file);
                this.isSaving(false);

                if(callback)
                {
                    callback();
                }
            }
            else
            {
                this.errorGlobal('Error: Unable to create new document.');
            }
        }.bind(this));
    },
    save: function()
    {
        if(this.isSaving() && !this.justCreated)
            return;

        if(this.editing())
        {
            g.blurActiveElement();
            this.editing('');
            return;
        }

        if (!goog.isDriveAPILoaded())
        {
            this.errorGlobal('Error communicating with Google Drive. Please reconnect and try again.');
            return;
        }

        //
        // Check inputs
        //

        if (!this.checkName())
        {
            // If the doucment name is not valid, display error.
            return;
        }

        if (this.isCreating())
        {
            this.create(this.save.bind(this));
            return;
        }

        if (this.newEmail().length > 0)
        {
            if (!this.inviteUser())
            {
                // If the users being invited are not valid, display error.
                return;
            }
        }
        else
        {
            this.errorInvite(false);
        }

        // Assume inputs are valid after here

        this.rename();

        var count = 0;
        function handleResponse(index, response)
        {
            if (DEBUG) log('Permission Response: ', response);

            if (response.error)
            {
                console.error('UH OH', response);
                this.permissionsNew()[i].error(true);
                this.errorInvite('Some people could not be invited.');
                this.isSaving(false);
            }
            else
            {
                count --;
                if (count <= 0)
                {
                    g.menus.closeModal('shareDoc', false);
                }
            }
        }

        for (var i = 0; i < this.permissionsNew().length; i ++)
        {
            var newPermission = this.permissionsNew()[i];
            if(newPermission.error())
                continue;

            count ++;

            if (DEBUG) log('Adding Writer Permission: ', newPermission);

            var body =
            {
                'value': newPermission.emailAddress,
                'type': 'user',
                'role': 'writer',
                'withLink': false
            };

            var message = 'Come collaborate with me at Moo.do!\r\nhttp://www.moo.do/#login=true&file=' + this.doc().id;

            gapi.client.drive.permissions.insert({
                'fileId': this.doc().id,
                'emailMessage': message,
                'resource': body
            }).execute(handleResponse.bind(this, i));
        }

        for (var i = 0; i < this.permissionsRemove.length; i ++)
        {
            var permission = this.permissionsRemove[i];

            gapi.client.drive.permissions.delete(
            {
                'fileId': this.doc().id,
                'permissionId': permission.id
            }).execute(function (response)
            {
                // TODO: Error Handling
                if (DEBUG) log('Remove Permission Response: ', response);
            });
        }

        if (this.errorInvite() || this.errorName())
        {
            return;
        }
        else if (count === 0)
        {
            g.menus.closeModal('shareDoc', true);
        }
        else
        {
            this.isSaving(true);

            this.onClose(true);
        }
    },
    onClose: function (isSave)
    {
        if (this.closeCallback)
        {
            this.closeCallback(isSave);
            this.closeCallback = undefined;
        }
    },
    onTapInviteMessage: {
        onStart: g.handlerPreventStop,
        onClick: function(e)
        {
            var el = g.element('inputShareInvite');
            if(platform.mobile)
            {
                g.menus.focusModalInputMobile(e, el);
            }
            else
            {
                el.focus();
            }
        }
    },
    onTapSave: {
        onStart: g.handlerPreventStop,
        onClick: function(e)
        {
            this.save();

            g.sendEvent('Menu', 'SharingSave');
        }
    },
    onTapCancel: {
        onStart: g.handlerPreventStop,
        onClick: function(e)
        {
            g.menus.closeModal('shareDoc', false);

            g.sendEvent('Menu', 'SharingCancel');
        }
    },
    onTapDelete: {
        onStart: g.handlerPreventStop,
        onClick: function(e)
        {
            g.sendEvent('Menu', 'SharingDelete');

            // Do this in a timeout because prompt causes a touchstart with no touchend
            // on hitting cancel. This timeout can be removed when we have a custom confirmation
            // popup.
            setTimeout(function()
            {
                var result = prompt('This will delete the file. Type DELETE to confirm.');
                if(result && result.trim().toLowerCase() == 'delete')
                {
                    gapi.client.drive.files.delete({
                        'fileId': this.doc().id
                    }).execute(function (response)
                    {
                        log('Document Delete Response: ', response);

                        if (response.error)
                        {
                            g.messageQueue.pushMessage({
                                text: 'Unable to delete document on the remote server.',
                                type: MessageType.Error
                            });
                        }
                    }.bind(this));

                    g.vmMain.vmPicker.removeFile(this.doc());
                    g.menus.closeModal('shareDoc', false);
                }
            }.bind(this), 0);
        }
    },
    onRemove: function(user)
    {
        //log('remove', user);
        this.permissionsRemove.push(user);
        this.permissions.remove(user);

        g.sendEvent('Menu', 'SharingRemoveUser');
    },
    onRemoveNew: function(user)
    {
        this.permissionsNew.remove(user);
    }
};

return VMDocSharing;
});
