            var childModel = d.getModel(entry.itemID);
            try{g.PAssert(1200,childModel, "Expected model to exist: ", entry);}catch(PERR){g.reportError(PERR);}
            //try{g.PAssert(1201,childModel.parent() === parentModel, "Child has mismatching parent", childModel, parentModel);}catch(PERR){g.reportError(PERR);}

