    // Move Fields [Item]
    //   ItemID
    //   OldParent
    //   OldPosition
    //   NewParent
    //   NewPosition
    function undoMove (data)
    {
        //console.log('Undo [Move]: ', data);

        for (var i = 0; i < data.length; ++i)
        {
            var entry = data[i];

            var oldParent = d.getModel(entry.oldParent);
            try{g.PAssert(1204,oldParent, "Expected oldParent model to exist: ", entry);}catch(PERR){g.reportError(PERR);}

            var newParent = d.getModel(entry.newParent);
            try{g.PAssert(1205,newParent, "Expected newParent model to exist: ", entry);}catch(PERR){g.reportError(PERR);}

            var child = newParent.removeChildAtIndex(entry.newPosition);
            try{g.PAssert(1206,child.id === entry.itemID, "Expected data to match child: ", child, entry);}catch(PERR){g.reportError(PERR);}

            child.parent(oldParent);
            oldParent.items.splice(entry.oldPosition, 0, child);

            g.selectChildren(child, 0, 0);

            newParent.save({ 'items': newParent.getItemsArrayForSave() }, SaveFlag.None);
            oldParent.save({ 'items': oldParent.getItemsArrayForSave() }, SaveFlag.None);
        }

        // TODO: Ensure that parent models persist/exist properly
    }

    function redoMove (data)
    {
        //console.log('Redo [Move]: ', data);

        for (var i = 0; i < data.length; ++i)
        {
            var entry = data[i];

            var oldParent = d.getModel(entry.oldParent);
            try{g.PAssert(1207,oldParent, "Expected oldParent model to exist: ", entry);}catch(PERR){g.reportError(PERR);}

            var newParent = d.getModel(entry.newParent);
            try{g.PAssert(1208,newParent, "Expected newParent model to exist: ", entry);}catch(PERR){g.reportError(PERR);}

            var child = oldParent.removeChildAtIndex(entry.oldPosition);
            try{g.PAssert(1209,child.id === entry.itemID);}catch(PERR){g.reportError(PERR);}

            child.parent(newParent);
            newParent.items.splice(entry.newPosition, 0, child);

            g.selectChildren(child, 0, 0);

            newParent.save({ 'items': newParent.getItemsArrayForSave() }, SaveFlag.None);
            oldParent.save({ 'items': oldParent.getItemsArrayForSave() }, SaveFlag.None);
        }

        // TODO: Ensure that parent models persist/exist properly
    }

    // Update Fields [Property]
    //   ItemID
    //   Field
