        var item = d.getModel(entry.itemID);
        try{g.PAssert(1218,item, "Expected model to exist: ", entry);}catch(PERR){g.reportError(PERR);}

        if (entry.zoomIn)
        {
            item.zoomin({ 'time': entry.time });
        }
        else if (entry.zoomOut)
        {
            item.zoomOut(entry.time);
        }
