define('copypaste',['ko', 'globals', 'platform', 'edit', 'VMLI', 'tracker', 'parse'],
function (ko, g, platform, edit, VMLI, tracker, parse) {
    var self = { };

    self.init = function ()
    {
        try{g.PAssert(1290,document.readyState === 'complete' || document.readyState === 'interactive', 'Setting up copy/paste too early');}catch(PERR){g.reportError(PERR);}

        document.body.addEventListener("paste", handlepaste, false);
        document.body.addEventListener("cut", handlecut, false);
        document.body.addEventListener("copy", handlecopy, false);
    }

    function insertDummyItem(parentItem, e, text)
    {
        if (!text)
        {
            text = '';
        }

        var item = new VMLI({text: text}, {parent: parentItem, parse: true, forceSave: true});
        parentItem.items.unshift(item);

        tracker.performAction(TrackerType.Create, {
            'itemID': item.id,
            'parent': parentItem.id,
            'position': 0,
            'text': text
        });

        g.selectChildren(item, 0, 0);
        var selection = g.getSelectionInfo();
        var range = selection.selection.getRangeAt(0);

        return range;
    }

    function killEvent(e)
    {
        try{g.PAssert(1291,e.preventDefault, 'Should always be defined');}catch(PERR){g.reportError(PERR);}
        try{g.PAssert(1292,e.stopPropagation, 'Should always be defined');}catch(PERR){g.reportError(PERR);}

        if (e.preventDefault)
        {
            e.preventDefault();
        }

        if (e.stopPropagation)
        {
            e.stopPropagation();
        }
    }

    function handlepaste(e)
    {
        if (!e.srcElement)
        {
            e.srcElement = e.target;
        }

        var clipData = (e && e.clipboardData) ? e.clipboardData : window.clipboardData;
        var dataType = platform.ie ? 'Text' : 'text/plain';

        var clipboardBuffer = '';

        var selection = g.getSelectionInfo();
        if(!selection || !selection.selection)
            return;

        var range = selection.selection.getRangeAt(0);

        var pane = g.getPaneForElement(e.srcElement);

        if (g.hasClass(e.srcElement, 'search'))
        {
            if (/text\/plain/.test(clipData.types) || platform.ie)
            {
                e.srcElement.textContent = clipData.getData(dataType).replace(/[\r\n\t]/g, '');
                g.selectElement(e.srcElement, /*selectEnd*/true);
            }

            e.preventDefault();

            return false;
        }
        else if (!pane)
        {
            return;
        }

        if (pane.mode() !== PaneMode.Normal)
        {
            return false;
        }

        // Check for invalid paste targets
        var startSpan = g.getSpanParent(range.startContainer);

        var endSpan = g.getSpanParent(range.endContainer);
        if (!startSpan || !endSpan) // Nothing valid to paste into
        {
            // If pasting into an empty paneContent
            if (range.startContainer == pane.paneContent || range.endContainer == pane.paneContent)
            {
                if (DEBUG) log('Pasting into empty pane content');
                var item = pane.addItemToPane('', 0);

                startSpan = endSpan = item.getSpan();
                g.selectChildren(item, 0, 0);
            }
            else
            {
                try{g.PAssert(1293,false, 'There should always be something to paste into');}catch(PERR){g.reportError(PERR);}
                // TODO: Should there be an error report here? Or is this ok to happen?
                return;
            }
        }

        if (clipData && clipData.getData)
        {
            tracker.miscAction({
                type: TrackerType.Misc,
                misc: TrackerMisc.Paste,
                data: [
                {
                    value: true
                }]
            });
            killEvent(e);

            // Parse the incoming text
            var traversed;
            if (/text\/html/.test(clipData.types))
            {
                if (DEBUG) log(clipData.getData('text/html'));
                clipboardBuffer = clipData.getData('text/html');

                if (!clipboardBuffer)
                {
                    if (DEBUG) log('Paste buffer is empty');
                    return;
                }

                var node = document.createElement('div');
                var replacedText = clipboardBuffer.replace(/[\n\t]/g, ' ')
                                                  .replace(/[\r]|<head(.|\s)*?\/head>|<script(.|\s)*?\/script>|<style(.|\s)*?\/style>/g, '')
                                                  .replace(/\s+/g, ' ')
                                                  .replace(/onload/gi, 'never');

                var bodyRegex = /<body(.|\s)*?\/body>/g;
                var bodyMatch;
                if (bodyMatch = bodyRegex.exec(replacedText))
                {
                    replacedText = bodyMatch[0];
                }

                node.innerHTML = replacedText;

                traversed = parse.traverseDOM({root: node, shouldPrint: DEBUG, stripParents: true});
                if (traversed.items.length === 1 && traversed.items[0].text === '')
                {
                    traversed = traversed.items[0];
                }
            }
            else if (/text\/plain/.test(clipData.types) || platform.ie)
            {
                if (DEBUG) log(clipData.getData(dataType));
                clipboardBuffer = clipData.getData(dataType).trim();

                var isXML = clipboardBuffer.startsWith('<?xml ');
                var isOPML = clipboardBuffer.startsWith('<?opml ');
                var xml;
                if (isXML || isOPML)
                {
                    xml = g.loadXML(clipboardBuffer);
                }

                // If pasting XML/OPML content, be smarter about the data that we paste into the doc. If it
                // really is just plain text, do the simple thing.
                if (xml)
                {
                    if (isXML)
                    {
                        g.messageQueue.pushMessage({
                            text: 'We currently do not support pasting generic XML documents, try convering to OPML. If you need to import XML send us a message and we will work through your scenario with you.',
                            type: MessageType.Error
                        });

                        return false;
                    }
                    else if (isOPML)
                    {
                        parse.parseOPML(xml, 'OPML Import');

                        return false;
                    }
                }
                else
                {
                    var isJSON = false;

                    // If we might be pasting JSON, try parsing
                    if (clipboardBuffer.startsWith('{') && clipboardBuffer.endsWith('}'))
                    {
                        try
                        {
                            var jsonData = JSON.parse(clipboardBuffer);

                            var root = ko.dataFor(startSpan.parentElement);

                            var res = parse.parseJSON(jsonData, root);

                            // If we successfully parsed the JSON, return out, otherwise continue on
                            if (res)
                            {
                                return false;
                            }
                        }
                        catch (err)
                        {
                            // Do nothing, assume invalid JSON and paste normal text
                        }
                    }

                    var replacedText = clipboardBuffer.replace(/[\t]/g, ' ');

                    var traversedText = replacedText.split('\n');

                    traversed = { items: [ ] };
                    for (var i = 0; i < traversedText.length; i++)
                    {
                        if (traversedText[i].length === 0)
                        {
                            continue;
                        }

                        traversed.items.push({text: g.trimString(traversedText[i]), parent: traversed});
                    }
                }
            }

            if (!traversed || traversed.items.length === 0)
            {
                return false;
            }

            try{g.PAssert(1294,startSpan !== undefined && endSpan !== undefined, 'Must have a valid selection to perform paste');}catch(PERR){g.reportError(PERR);}

            tracker.beginAction(); // BEGIN TRACKER <----

            var itemStart = ko.dataFor(startSpan.parentElement);
            var itemEnd = ko.dataFor(endSpan.parentElement);

            var sOffset = g.getSelectOffsetsInSpan(itemStart, startSpan, range);
            var eOffset = g.getSelectOffsetsInSpan(itemEnd, endSpan, range);

            var itemParent = itemStart.parent();

            var itemIndex = itemStart.getIndex();
            var sText = itemStart.getParsedText();
            var eText = itemEnd.getParsedText();

            var selItemSpan;
            var selOffset;

            // Iterate through each item between the start and end, removing them.
            if (itemStart != itemEnd)
            {
                edit.handleMultiselect({}, startSpan, endSpan, range);
            }

            var lastItem = itemStart;

            if (traversed && traversed.items.length === 1 && !traversed.items[0].items)
            {
                var iText = traversed.items[0].text.trim();

                // Just one item so insert it into the middle of the current line
                var str = sText.substring(0, sOffset.startText) + iText + eText.substring(eOffset.endText);
                selItemSpan = itemStart.getSpan();

                tracker.performAction(TrackerType.Remove, {
                    'itemID': itemStart.id,
                    'position': 0,
                    'text': itemStart.getParsedText()
                });

                //itemStart.transformFormats(sOffset.start, iText.length);
                itemStart.setText(str, true);

                tracker.performAction(TrackerType.Insert, {
                    'itemID': itemStart.id,
                    'position': 0,
                    'text': str
                });
                selOffset = sOffset.start + iText.length;
                g.selectChildren(itemStart, selOffset, 0);
            }
            else
            {
                if(DEBUG) console.time('paste');
                var parentItems = itemParent.items();
                //var newFormats;
                var mutations = [];

                for (var i = 0; traversed && i < traversed.items.length; ++i)
                {
                    // Add each item
                    var itemInfo = traversed.items[i];

                    itemInfo.text = itemInfo.text.trim();

                    if (itemInfo.text === '' && !itemInfo.items)
                    {
                        continue;
                    }

                    if (i === 0)
                    {
                        // If the first item == incoming text's formatter, don't include it twice
                        if (itemInfo.text[0] == sText)
                        {
                            itemInfo.text = itemInfo.text.substr(1);
                        }

                        // First item's text = text + incoming first item
                        itemInfo.text = sText.substring(0, sOffset.startText) + itemInfo.text;

                        // newFormats = itemStart.getFormatsAfter(sOffset.start);
                        // itemStart.removeFormatsAfter(sOffset.start);

                        itemStart.setText(itemInfo.text, true);

                        tracker.performAction(TrackerType.Insert, {
                            'itemID': itemStart.id,
                            'position': 0,
                            'text': itemInfo.text
                        });

                        itemIndex ++;

                        if(itemInfo.items)
                        {
                            for(var u = 0; u < itemInfo.items.length; u ++)
                            {
                                var child = itemInfo.items[u];

                                if (child.text === '' && !child.items)
                                {
                                    continue;
                                }

                                itemStart.insertItem(new VMLI(child, {parent: itemStart, parse: true, forceSave: true}));
                            }
                            if(mutations.indexOf(itemStart) == -1)
                                mutations.push(itemStart);
                        }
                    }
                    else
                    {
                        var newItem = new VMLI(itemInfo, {parent: itemParent, parse: true, forceSave: true});

                        // TODO: what if UL?

                        itemParent.insertItem(newItem, itemIndex++, true);
                        if(mutations.indexOf(itemParent) == -1)
                            mutations.push(itemParent);

                        tracker.performAction(TrackerType.Create, {
                            'itemID':  newItem.id,
                            'parent': itemParent.id,
                            'position': itemIndex - 1,
                            'text': newItem.getParsedText()
                        });
                    }
                }

                for(i = 0; i < mutations.length; i ++)
                {
                    mutations[i].items.valueHasMutated();
                }

                // Find the last item so it can be selected
                lastItem = itemParent.getChild(itemIndex - 1);
                while (lastItem.hasChildren())
                {
                    lastItem = lastItem.getLastChild();
                }

                // Select the end of the last item
                selOffset = lastItem.getParsedText().length;

                lastItem.setRawText(lastItem.getParsedText() + eText.substring(eOffset.endText));

                g.vmMain.runParseWorker(itemParent, function()
                {
                    setTimeout(function(){
                        g.selectChildren(lastItem, selOffset, 0);
                    }, 0);
                });
            }
            if(DEBUG) console.timeEnd('paste');
        }

        // console.log('Types', clipData.types);

        // console.log(clipboardBuffer);
        // waitForPasteData(clipboardBuffer);

        tracker.endAction(); // END TRACKER <----

        g.scrollIntoView(lastItem.getSpan(), 200);

        return false;
    }

    function handlecut (e)
    {
        var copySuccess = !handlecopy(e);
        if (copySuccess)
        {
            edit.keydown(undefined, {normCode: KeyCode.BackSpace});

            killEvent(e);
            return false;
        }

        return true;
    }

    function handlecopy (e)
    {
        var clipData = (e && e.clipboardData) ? e.clipboardData : window.clipboardData;

        if (clipData)
        {
            if (!e.srcElement)
            {
                e.srcElement = e.target;
            }

            var selection = g.getSelectionInfo();

            if (!selection)
            {
                return true;
            }

            var currentItem = selection.selection.getRangeAt(0).startContainer;
            if (currentItem.nodeName == '#text')
            {
                currentItem = currentItem.parentNode;
            }

            // Short-circuit logic for export data
            if (e.srcElement.id === 'exportedArea' || e.srcElement.id === 'exportedContent')
            {
                var dataType = platform.ie ? 'Text' : 'text/plain';
                clipData.setData(dataType, e.srcElement.textContent);
            }
            else
            {
                var inPane = g.elementInPane(currentItem);

                if (!inPane || selection.node.id === 'searchDiv' || g.hasClass(selection.node, 'search'))
                {
                    return true;
                }

                g.restrictSelectionToText();

                selection = g.getSelectionInfo();
                var range = selection.selection.getRangeAt(0);

                try{g.PAssert(1295,range.startContainer.nodeName == '#text', 'Expected #text, Actual: ' + range.startContainer.tagName);}catch(PERR){g.reportError(PERR);}
                var startSpan = g.getSpanParent(range.startContainer);

                try{g.PAssert(1296,range.endContainer.nodeName == '#text' || range.endContainer.nodeName == 'SPAN', 'Expected #text or SPAN, Actual: ' + range.endContainer.tagName);}catch(PERR){g.reportError(PERR);}
                var endSpan = g.getSpanParent(range.endContainer);

                // #text -> span -> li
                var startLI = startSpan.parentElement;
                try{g.PAssert(1297,startLI.tagName == 'LI', 'Expected LI, Actual: ', startLI);}catch(PERR){g.reportError(PERR);}

                var endLI = endSpan.parentElement;
                try{g.PAssert(1298,endLI.tagName == 'LI', 'Expected LI, Actual: ', endLI);}catch(PERR){g.reportError(PERR);}

                var item = ko.dataFor(startLI);
                var endItem = ko.dataFor(endLI);
                var offsets = g.getSelectOffsetsInSpan(item, startSpan, range);

                var nextLI = startLI;

                try{g.PAssert(1299,item);}catch(PERR){g.reportError(PERR);}

                var createHTML = !platform.ie;

                var html;
                var plain;
                var text = item.getParsedText().substring(offsets.startText);
                if (startLI == endLI)
                {
                    if (offsets.start == offsets.end)
                    {
                        text = item.getParsedText();
                    }
                    else
                    {
                        text = item.getParsedText().substr(offsets.startText, offsets.endText - offsets.startText);
                    }

                    if (createHTML) { html = text; }
                    plain = text;
                }
                else
                {
                    var deep = 0;

                    // if (item.parent().isFullscreen())
                    // {
                    //     debugger;
                    //     html = text;
                    //     plain = text;
                    // }
                    // else
                    {
                        deep++;

                        if (createHTML) { html = '<ul><li>' + text + '</li>'; }
                        plain = text;
                    }

                    do
                    {
                        var nextOptions = {numLevels: 0};

                        item = g.getNextItem(item, g.focusedPane, nextOptions);

                        if (nextOptions.numLevels > 0) // Next item is a child
                        {
                            if (createHTML) { html += '<ul>'; }
                            deep++;
                        }

                        while (nextOptions.numLevels < 0 && deep >= 0) // Next item is in an ancestor
                        {
                            if (createHTML) { html += '</ul>'; }
                            deep--;

                            nextOptions.numLevels++;
                        }

                        // Handle the case where the first node selected is at a higher indentation level that other nodes
                        // in the selection. In this case, we need to insert extra lists to ensure that the less indented
                        // nodes get inserted in the proper order.
                        if (deep <= 0)
                        {
                            if (createHTML) { html += '<ul>'; }
                            deep++;
                        }

                        if (item !== endItem) // Middle item
                        {
                            text = item.getParsedText();
                            if (createHTML) { html += '<li>' + text + '</li>'; }
                        }
                        else // End item
                        {
                            var endOffset = g.getSelectOffsetsInSpan(item, item.getSpan(), range);

                            text = item.getParsedText().substring(0, endOffset.endText);

                            if (createHTML) { html += '<li>' + text + '</li></ul>'; }
                            deep--;
                        }

                        plain += '\r\n' + text;
                    } while (item !== endItem);

                    while (deep > 0)
                    {
                        if (createHTML) { html += '</ul>'; }
                        deep--;
                    }
                }

                //log(html);
                //log(plain);

                if (createHTML && html)
                {
                    clipData.setData("text/html", html);
                }

                var dataType = platform.ie ? 'Text' : 'text/plain';
                clipData.setData(dataType, plain);
            }
        }

        killEvent(e);

        tracker.miscAction({
            type: TrackerType.Misc,
            misc: TrackerMisc.Copy,
            data: [
            {
                value: true
            }]
        });

        return false;
    }

    return self;
});
