//
// Google API callback
//
var gapiCalled = false;
var gapiLoadError = false;
function GoogleApiLoaded()
{
    if (gapiCalled)
    {
        //console.error('GAPI Init function expection to be called exactly once');
        return;
    }

    gapiCalled = true;
    gapi.auth.init(); // Always call init on load so popup doesn't get blocked later

    if (window.location.hash.indexOf('offline=true') >= 0 ||
        window.location.hash.indexOf('offline%22:true') >= 0 ||
        window.location.hash.indexOf('offline":true') >= 0 ||
        window.location.hash.indexOf('demo=true') >= 0 ||
        (DEBUG && DEBUG))
    {
        log('%cOffline', 'color: pink');

        return;
    }

    if (DEBUG) log('GAPI Callback - Loaded:', log.now());

    gapi.client.setApiKey(window.gapiKey);

    // Simulate bad/slow internet connection by delaying the loading of gapi.
    // setTimeout(function ()
    // {
        gapi.load('drive-realtime', function()
        {
            if (DEBUG) log('Drive-Realtime API - Loaded: ', window.performance.now());

            // We received a couple error reports of crashing after the drive api was not loaded properly. Try to harden
            // calls so we don't crash and instead go offline and collect additional data.
            if (!gapi || !gapi.drive || !gapi.drive.realtime)
            {
                var loadArgs = arguments;

                require(['globals', 'platform'], function (g, platform)
                {
                    var errorString;
                    if (gapi)
                    {
                        if (gapi.drive)
                        {
                            if (gapi.drive.realtime)
                            {
                                errorString = 'all exist';
                            }
                            else
                            {
                                errorString = 'gapi.drive.realtime';
                            }
                        }
                        else
                        {
                            errorString = 'gapi.drive';
                        }
                    }
                    else
                    {
                        errorString = 'gapi';
                    }

                    // Reset gapi status
                    gapiCalled = false;
                    gapiLoadError = true;

                    g.reportError(new Error('Drive returned load, but unable to load ' + errorString));

                    var msgEle = document.getElementById('blockedMessage');
                    if (platform.app)
                    {
                        msgEle.innerText = 'There was an error loading the Google APIs, ' + platform.actionVerb() + ' to try again.';
                    }
                    else
                    {
                        msgEle.innerText = 'You are running an extension that blocked drive.google.com from loading, Moo.do requires access to function correctly.';
                    }

                    document.getElementById('blockedError').classList.remove('none');

                    if (g.vmMain)
                    {
                        g.vmMain.gdriveStatus(DriveStatus.Offline);
                    }
                });
            }
            else
            {
                require(['globals', 'goog'], function (g, goog)
                {
                    goog.loadClientDefaultFile();

                    g.removeDOM('blockedError');
                });
            }
        });

        require(['globals', 'util', 'platform', 'goog', 'VMSetup'], function (g, util, platform, goog, VMSetup)
        {
            if (DEBUG) log('Goog Callback - Loaded: ', log.now());

            var setTokenFromHash = false;
            if (!platform.ie || platform.mobile)
            {
                setTokenFromHash = goog.checkTokenInHash();
            }

            var doLogin = setTokenFromHash || util.hasURLParam('login', 'true') || g.getLoginState() == LoginState.LOGGED_IN;

            // Request scopes that have been previously authorized for the user, if these scopes are not authenticated,
            // show the login screen to get the user to re-authenticate with the required set of OAuth scopes.
            goog.runAuthenticate(goog.requestScopes, /*immediate*/true, /*retry*/false, function (token)
            {
                if (DEBUG) log('Goog Authorized - Callback: ', log.now());

                util.removeURLParam('login', true);

                if (token && !token.error)
                {
                    if (doLogin)
                    {
                        g.vmSetup ? g.vmSetup.setLoginState(LoginState.LOGGED_IN) : g.setLoginState(LoginState.LOGGED_IN);

                        goog.loadClientDefaultFile();

                        g.openMainPage();
                    }

                    goog.ensureRequiredScopesAreRequested();
                }
                else
                {
                    if (goog.requiredScopes !== goog.requestScopes)
                    {
                        g.settings.reset(Settings.authScopes);

                        goog.runAuthenticate(goog.requiredScopes, /*immediate*/true, /*retry*/false, function (token2)
                        {
                            if (token2 && !token2.error)
                            {
                                if (doLogin)
                                {
                                    g.vmSetup ? g.vmSetup.setLoginState(LoginState.LOGGED_IN) : g.setLoginState(LoginState.LOGGED_IN);

                                    goog.loadClientDefaultFile();

                                    g.openMainPage();
                                }
                            }
                            else
                            {
                                g.vmSetup ? g.vmSetup.setLoginState(LoginState.LOGGED_OUT) : g.setLoginState(LoginState.LOGGED_OUT);

                                g.openSetupPage();
                            }
                        });
                    }
                    else
                    {
                        g.vmSetup ? g.vmSetup.setLoginState(LoginState.LOGGED_OUT) : g.setLoginState(LoginState.LOGGED_OUT);

                        g.openSetupPage();
                    }
                }
            });
        });
    // }, 3000);
}

