    var windowResize = function (e)
    {
        var w = window.innerWidth;
        var h = window.innerHeight;

        var oldWidth = platform.windowWidth();
        var oldHeight = platform.windowHeight();

        if (DEBUG) { log('Old Height: ' + oldHeight + '  New Height: ' + h); }

        if (w == oldWidth && (h == oldHeight || h == oldHeight + 60))
        {
            return;
        }

        g.smallMode = (w <= 480);

        platform.windowWidth(w);
        platform.windowHeight(h);

        // if(!g.windowHeightStart)
        // {
        //     g.windowHeightStart = h;
        // }

        g.documentHeight = h;

        // if (g.menus && !platform.mobile)
        // {
        //     g.menus.updateHoverMenu();
        // }

        // In android and IE< if the new height is larger than the old height, it means that the
        // software keyboard has closed. Due to the ways that the user can close the software keyboard
        // this end sup being the only indication that it was closed and the keyboard toolbar needs
        // to be hidden and editing stopped.
        if ((platform.android || platform.mobileie) && h > oldHeight && g.vmMain && g.vmMain.isLoaded)
        {
            g.blurActiveElement();
        }

        // Ensure address bar is hidden on iphones.
        if (platform.iphone && !platform.bodyscroll && !platform.standalone && !platform.app && !platform.ios7)
        {
            var height = document.documentElement.clientHeight + 60;
            document.documentElement.style.height = height + 'px';
            setTimeout(g.enforceScroll, 0);
        }

        if (g.autocomplete)
        {
            g.autocomplete.hide();
        }

        // On a page resize, ensure that snap points are recomputed
        // e is to check that it's on an event, not from first load
        if (oldWidth !== w && e && platform.mobile)
        {
            if (g.ScreenManager)
            {
                g.ScreenManager.onResize();
            }

            if (g.menus)
            {
                g.menus.onResize();
            }
        }

        if (!platform.mobile)
        {
            g.fireCustomEvent('windowResize');
        }
    };

