        hotkeys[KeyCode.RBracket] = [{mods: {0: true}, call: hotkeyIndent},
                                     {mods: {2: true}, call: hotkeyIndent}];

        hotkeys[KeyCode.Escape] = [{call: hotkeyFireEvent}];

        hotkeys[KeyCode.BackSpace] = [{call: hotkeyBackspace}];

        function ignoreKeyOnBody(e)
        {
            return (((platform.mac && e.metaKey) || (!platform.mac && e.ctrlKey)) && e.normCode == KeyCode.A); // Select all
        }

        g.globalKeyDown = function(e)
        {
            var result = hotkeys[e.normCode];
            if (result)
            {
                if (g.isFunction(result))
                {
                    result(e);
                }
                else
                {
                    var valid;
                    for (var i = 0; i < result.length; i ++)
                    {
                        if( !result[i].mods ||
                          ((!result[i].mods[0] || e.ctrlKey) &&
                           (!result[i].mods[1] || e.optKey) &&
                           (!result[i].mods[2] || e.metaKey) &&
                           (!result[i].mods[3] || e.altKey) &&
                           (!result[i].mods[4] || e.shiftKey)))
                        {
                            valid = result[i];
                            break;
                        }
                    }
                    if (valid)
                    {
                        var ret = valid.call(e);

                        if (ret !== false)
                        {
                            e.handled = true;
                            e.preventDefault();

                            return false;
                        }
                    }
                }
            }
            else if (e.normCode === KeyCode.Ctrl)
            {
                g.vmMain.updateCursor(true)
            }

            if (platform.android && e.srcElement === document.body)
            {
                if (ignoreKeyOnBody(e))
                {
                    e.preventDefault();

                    return false;
                }
                else
                {
                    edit.keydown(null, e);
                }
            }
        };

        g.globalKeyUp = function(e)
        {
            platform.normalizeKeys(e);

            if (e.normCode === KeyCode.Ctrl)
            {
                g.vmMain.updateCursor(false);
            }
        };

