define('VMPane_Timeline',['ko', 'globals', 'data', 'VMTimelineBlock'],
function (ko, g, d, VMTimelineBlock) {
var VMPane_Timeline =
{
    initTimeline: function()
    {
        this.blocks = ko.observableArray();
        this.blockImportant = undefined;
        this.blockStarred = undefined;
        this.days = { };
        this.updateNextTimeout = 0;
        this.datesByItem = { };
        this.flaggedItems = { };
        this.priorityItems = { };
        this.isLoading = false;
        this.deferModelUpdates = 0;

        var self = this;
        this.showArchived.subscribe(function(newValue)
        {
            if (DEBUG) log('Timeline Changed Archived Mode: ', newValue);

            self.reloadTimeline();
        });

        if (DEBUG)
        {
            window.dumpBlocks = this.dumpBlocks.bind(this);
        }
    },
    hasBlocks: function()
    {
        var count = 0;
        for(var i = 0; i < this.blocks().length; i ++)
        {
            count += this.blocks()[i].searchCount();
        }

        return count > 0;
    },
    reasonEmpty: function()
    {
        if(this.vmSearch && this.vmSearch.search().length > 0)
        {
            return 'No search results';
        }
        else
        {
            return 'Nothing on the agenda';
        }
    },
    blockCompare: function(left, right)
    {
        if(left.isNow && right.isToday)
            return -1;
        if(left.isToday && right.isNow)
            return 1;
        return Date.compare(left.date, right.date);
    },
    // sortDates: function()
    // {
    //     this.blocks.sort(blockCompare);
    // },
    removeDate: function(item)
    {
        //console.log('Remove Date: ', item.text);

        var vmDate = this.datesByItem[item.id];
        vmDate.removeItem(item);

        this.datesByItem[item.id] = undefined;

        // TODO: Re-enable retirement of old blocks when they are no longer needed
        // if (vmDate.items().length === 0)
        // {
        //     this.blocks.remove(vmDate);

        //     var dateKey = vmDate.dateKey;
        //     this.days[dateKey] = undefined;
        // }
    },
    addBlock: function(block)
    {
        block.setSearchProvider(this.vmSearch);

        //log('Add Block: ', this.blocks().length);

        if (this.deferModelUpdates)
        {
            this.blocks().insertSorted(block, this.blockCompare);
        }
        else
        {
            this.blocks.insertSorted(block, this.blockCompare);
        }
    },
    dumpBlocks: function()
    {
        log('Dump Timeline Blocks:');

        for (var i = 0; i < this.blocks().length; ++i)
        {
            var block = this.blocks()[i];

            log('  Block: ', block.text);
            log('  Count: ', block.searchCount);

            for (var j = 0; j < block.items().length; ++j)
            {
                var item = block.items()[j];

                log('    Item: ', item.getParsedText());
            }
        }
    },
    findNextBlock: function()
    {
        var now = new Date();
        for(var i = 0; i < this.blocks().length; i ++)
        {
            var block = this.blocks()[i];

            if(block.isToday || block.isFlagged || block.isImportant || block.date > now)
            {
                return block;
            }
        }
    },
    hideDescendants: function(parent)
    {
        for(var i = 0; i < parent.items().length; i ++)
        {
            var item = parent.items()[i];
            if (this.datesByItem[item.id])
            {
                this.removeDate(item);
            }

            if(this.flaggedItems[item.id])
            {
                this.blockStarred.removeItem(item);
                this.flaggedItems[item.id] = undefined;
            }

            if(this.priorityItems[item.id])
            {
                this.blockImportant.removeItem(item);
                this.priorityItems[item.id] = undefined;
            }

            if(item.items().length > 0)
            {
                this.hideDescendants(item);
            }
        }
    },
    notifyDescendants: function(parent)
    {
        for(var i = 0; i < parent.items().length; i ++)
        {
            var item = parent.items()[i];

            this.notifyStateChanged(item);

            if(item.items().length > 0)
            {
                this.notifyDescendants(item);
            }
        }
    },
    // TODO: Is this not actually used?
    notifyPositionChanged: function(item)
    {
        if (this.mode() !== PaneMode.Timeline)
        {
            return;
        }

        if (this.isLoading)
        {
            // Ignore event until load is complete
            return;
        }

        if (this.datesByItem[item.id])
            this.datesByItem[item.id].updateItem(item);

        if(this.flaggedItems[item.id])
            this.blockStarred.updateItem(item);

        if(this.priorityItems[item.id])
            this.blockImportant.updateItem(item);
    },
    notifySearchStateChanged: function(item, newValue)
    {
        if (this.mode() !== PaneMode.Timeline)
        {
            return;
        }

        if (this.isLoading)
        {
            // Ignore event until load is complete
            return;
        }

        if (this.datesByItem[item.id])
            this.datesByItem[item.id].notifySearchStateChanged(item, newValue);

        if(this.flaggedItems[item.id])
            this.blockStarred.notifySearchStateChanged(item, newValue);

        if(this.priorityItems[item.id])
            this.blockImportant.notifySearchStateChanged(item, newValue);
    },
    notifyStateChanged: function(item, changeType)
    {
        if (this.mode() !== PaneMode.Timeline)
        {
            return;
        }

        if (this.isLoading)
        {
            // Ignore event until load is complete
            return;
        }

        if(item.hasCompletedAncestor())
        {
            return;
        }

        // console.log('Timeline Item State Changed: ', item._rawText);

        //log(item._rawText);

        var dataItem = d.getItem(item.id);
        try{g.PAssert(1311,!dataItem || !!dataItem.isDeleted === !!item.isDeleted || g.isDemoMode(), 'isDeleted should match loaded item and item info');}catch(PERR){g.reportError(PERR);}
        this.vmSearch.checkItem(item);

        if (dataItem && dataItem.isDeleted)
        {
            var block = this.datesByItem[item.id];

            if (block)
            {
                block.removeItem(item);
            }

            if (item.isImportant())
            {
                this.updateSpecialBlock(item, this.priorityItems, this.blockImportant, /*matches*/false);
            }

            if (item.isFlagged())
            {
                this.updateSpecialBlock(item, this.flaggedItems, this.blockStarred, /*matches*/false);
            }
        }
        else
        {
            // Update date if the date or completed state has changed
            if (!changeType || g.isAtLeastOneFlagSet(changeType, PropChangeType.Date | PropChangeType.Complete | PropChangeType.Archive))
            {
                if (item.date() || item.isComplete())
                {
                    var date = (item.date() ? item.date() : item.dateCompleted);

                    this.dateUpdated(item, date);
                }
                else
                {
                    var oldBlock = this.datesByItem[item.id];

                    if (oldBlock)
                    {
                        this.removeDate(item);
                    }
                }
            }

            // Notify all children if isComplete or isArchived has changed
            if (g.isAtLeastOneFlagSet(changeType, PropChangeType.Complete | PropChangeType.Archive))
            {
                if (item.isComplete() || item.isArchived())
                {
                    this.hideDescendants(item);
                }
                else
                {
                    this.notifyDescendants(item);
                }
            }

            // Update Important block
            if (!changeType || g.isAtLeastOneFlagSet(changeType, PropChangeType.Priority | PropChangeType.Complete | PropChangeType.Archive))
            {
                this.updateSpecialBlock(item, this.priorityItems, this.blockImportant, item.isImportant() && !item.isComplete());
            }

            // Update Flagged block
            if (!changeType || g.isAtLeastOneFlagSet(changeType, PropChangeType.Flagged | PropChangeType.Complete | PropChangeType.Archive))
            {
                this.updateSpecialBlock(item, this.flaggedItems, this.blockStarred, item.isFlagged() && !item.isComplete());
            }
        }
    },
    updateSpecialBlock: function(item, items, block, matches)
    {
        if(matches && !item.isDeleted && !(item.isArchived() && !this.showArchived()))
        {
            // New item
            if(!items[item.id])
            {
                block.addItem(item);
                items[item.id] = item;
            }
            // Existing item's state changed
            else
            {
                block.updateItem(item);
            }
        }
        // Item no longer matches
        else if(items[item.id])
        {
            block.removeItem(item);
            items[item.id] = undefined;
        }
    },
    dateUpdated: function(item, date, skipSort)
    {
        if (this.mode() !== PaneMode.Timeline)
        {
            return;
        }

        if (this.isLoading)
        {
            // Ignore event until load is complete
            return;
        }

        if (item.isArchived() && !this.showArchived())
        {
            if (this.datesByItem[item.id])
            {
                this.removeDate(item);
            }

            return;
        }

        // if (item.isComplete() && date)
        // {
        //     console.log('Item Completed [', date, ']: ', item.text;
        // }
        // else if (!item.isComplete())
        // {
            // console.log('Timeline Updating Date: ', date);
        // }

        this.vmSearch.checkItem(item);

        // The date may be either updated or removed, handle each case separately.
        if (date /*&& (date >= Date.today())*/)
        {
            var dateKey;
            var isSoft = g.isDateSoft(item.dateText);
            if(isSoft)
            {
                dateKey = item.dateText.toUpperCase();
            }
            else
            {
                dateKey = date.clone().clearTime().getTime();
            }

            var vmDate = this.days[dateKey];
            if (!vmDate && !this.days[dateKey])
            {
                var options = {
                    'owner': this,
                    'date': date.clone().clearTime(),
                    'dateKey': dateKey
                };
                if(isSoft)
                {
                    options.name = dateKey;
                }
                var vmDate = new VMTimelineBlock(options);

                this.days[dateKey] = vmDate;

                this.addBlock(vmDate, skipSort);
            }

            if (vmDate)
            {
                var oldEntry = this.datesByItem[item.id];

                // If this item hass previously been inserted into the dates array it needs to be removed
                if (oldEntry)
                {
                    if (oldEntry.dateKey !== dateKey)
                    {
                        this.removeDate(item);
                        this.datesByItem[item.id] = vmDate;
                    }
                }
                else
                {
                    this.datesByItem[item.id] = vmDate;
                }

                this.days[dateKey].addItem(item);
            }
        }
        else
        {
            if (this.datesByItem[item.id])
            {
                this.removeDate(item);
            }
        }
    },
    ensureSpecialBlocks: function()
    {
        if (!this.blockStarred)
        {
            this.blockStarred = new VMTimelineBlock({owner: this, name: 'Flagged', date: Date.today().add({hour: 1}), isFlagged: true});
            this.addBlock(this.blockStarred);
        }

        if (!this.blockImportant)
        {
            this.blockImportant = new VMTimelineBlock({owner: this, name: 'Important', date: Date.today().add({hour: 2}), isImportant: true});
            this.addBlock(this.blockImportant);
        }
    },
    loadTimeline: function()
    {
        if (DEBUG) log('Load Timeline');

        try{g.PAssert(1312,this.mode() == PaneMode.Timeline);}catch(PERR){g.reportError(PERR);}

        try{g.PAssert(1313,this.blocks().length === 0, 'When loading the timeline we expect it to be empty');}catch(PERR){g.reportError(PERR);}

        this.setDeferUpdates(true);

        this.ensureSpecialBlocks();

        g.searchVMLIs({
            item: this.item(),
            includeArchived: this.showArchived(),
            each: this.notifyStateChanged.bind(this)
        });

        this.setDeferUpdates(false);
        this.vmSearch.onUpdated.valueHasMutated();

        // setTimeout(function ()
        // {
        //     var idx = self.blocks().findInsertIndex({'date': Date.today()}, self.blockCompare);

        //     var focusBlock = self.blocks()[idx];

        //     //log('FOCUS BLOCK: ', focusBlock);

        //     if (focusBlock && focusBlock.element)
        //     {
        //         g.scrollIntoView(focusBlock.element);
        //     }

        //     // TODO: Infinite Scrolling
        // }, 0);

        //dumpBlocks();
    },
    unloadTimeline: function ()
    {
        // try{g.PAssert(1314,this.mode() == PaneMode.Timeline);}catch(PERR){g.reportError(PERR);}

        if (DEBUG) log('Unload Timeline');

        this.datesByItem = { };
        this.flaggedItems = { };
        this.priorityItems = { };
        this.days = { };

        if (this.deferModelUpdates)
        {
            this.blocks().splice(0, this.blocks().length);
        }
        else
        {
            this.blocks([ ]);
        }

        this.blockStarred = undefined;
        this.blockImportant = undefined;
    },
    reloadTimeline: function ()
    {
        if (this.mode() !== PaneMode.Timeline)
        {
            return;
        }

        if (DEBUG) log('[', this.id, '] Reload Timeline');

        this.reloading = true;

        this.unloadTimeline();

        this.loadTimeline();

        this.reloading = false;
    },
    setDeferUpdates: function (value)
    {
        try{g.PAssert(1315,this.mode() == PaneMode.Timeline);}catch(PERR){g.reportError(PERR);}

        this.deferModelUpdates += (value ? 1 : -1);
        try{g.PAssert(1316,this.deferModelUpdates >= 0, 'Mismatching defer update calls');}catch(PERR){g.reportError(PERR);}

        if (this.deferModelUpdates === 0)
        {
            //log('Processing Deferred Timeline Updates');

            var numBlocks = this.blocks().length;
            for (var i = 0; i < numBlocks; ++i)
            {
                this.blocks()[i].processDeferredUpdates();
            }

            this.blocks.valueHasMutated();
        }
    },
    startLoading: function ()
    {
        if (this.mode() !== PaneMode.Timeline)
        {
            return;
        }

        try{g.PAssert(1317,!this.isLoading, 'Should only notify the timeline a single time when loading starts');}catch(PERR){g.reportError(PERR);}

        this.isLoading = true;
    },
    endLoading: function ()
    {
        if (this.mode() !== PaneMode.Timeline)
        {
            return;
        }

        try{g.PAssert(1318,this.isLoading, 'Must have started loading the timeline to stop loading it');}catch(PERR){g.reportError(PERR);}

        this.isLoading = false;

        this.setDeferUpdates(true);

        this.reloadTimeline();

        this.setDeferUpdates(false);
    }
};

return VMPane_Timeline;
});

