    // onTapMenu: {
    //     onClick: function(e)
    //     {
    //         g.vmSettings.openPhone();
    //         e.preventDefault();
    //     }
    // },
    // onTapMenuRight: {
    //     onClick: function(e)
    //     {
    //         try{g.PAssert(1352,!g.ScreenManager.isScreenOpen(Screens.List), "Shouldn't be able to hit right button when not in menu");}catch(PERR){g.reportError(PERR);}

