define('VMIntro',['ko', 'globals', 'util', 'platform', 'data', 'VMIntroSection'],
function (ko, g, util, platform, d, VMIntroSection)
{

window.HelpMode =
{
    None:   0,
    Normal: 1,
    Intro:  2,
};

function VMIntro(initState)
{
    try{g.PAssert(1354,initState !== undefined, 'Must pass in a valid display state');}catch(PERR){g.reportError(PERR);}
    this.mode = ko.observable(initState);

    this.sections = ko.observableArray();
    this.introSection = undefined;

    this.init();

    g.intro = this;
}

VMIntro.prototype =
{
    init: function()
    {
        if (this.isIntroMode())
        {
            this.initIntro();
        }
        else
        {
            this.initHelp();
        }

        this.loadState();
    },
    checkContactsEnable: function()
    {
        this.itemContactsEnable.isChecked(true);
        this.saveState();
    },
    setMode: function(mode)
    {
        this.mode(mode);

        g.settings.set(Settings.helpPaneState, mode);

        g.vmMain.helpButtonHidden(mode === HelpMode.Intro);
    },
    isIntroMode: function()
    {
        return this.mode() === HelpMode.Intro;
    },
    modeClass: function()
    {
        return this.isIntroMode() ? 'helpIntro' : 'helpNormal';
    },
    loadState: function()
    {
        if (g.shouldWriteLocal())
        {
            try
            {
                var stateString = util.storage.getItem(this.modeClass());

                if (stateString)
                {
                    var state = JSON.parse(stateString);

                    if (DEBUG) log('StateLoad: ', state);

                    if (state)
                    {
                        for (var i = 0; i < this.sections().length; ++i)
                        {
                            var section = this.sections()[i];

                            var secHash = section.getHash();

                            if (state[secHash])
                            {
                                section.loadFromSaveData(state[secHash]);
                            }
                        }
                    }
                }
            }
            catch (err)
            {
                g.reportError(err);
            }
        }
    },
    saveState: function()
    {
        if (g.shouldWriteLocal())
        {
            try
            {
                var state = this.getState();

                if (DEBUG) log('StateSave: ', state);

                util.storage.setItem(this.modeClass(), JSON.stringify(state));
            }
            catch (err)
            {
                g.reportError(err);
            }
        }
    },
    getState: function()
    {
        var saveData = { };
        for (var i = 0; i < this.sections().length; ++i)
        {
            var section = this.sections()[i];

            if (!section.isComplete())
            {
                var sectionData = [ ];

                for (var j = 0; j < section.items.length; ++j)
                {
                    try{g.PAssert(1355,section.items[j], 'Item entries must exist');}catch(PERR){g.reportError(PERR);}

                    if (section.items[j].isChecked())
                    {
                        var entryData = section.getStepHash(j);

                        sectionData.push(entryData);
                    }
                }

                if (sectionData.length > 0)
                {
                    saveData[section.getHash()] = sectionData;
                }
            }
            else
            {
                saveData[section.getHash()] = true;
            }
        }

        return saveData;
    },
    notifyOfChange: function(change)
    {
        this.saveState();
    },
    addSection: function(section)
    {
        section.owner = this;

        this.sections.push(section);
    },
    initIntro: function()
    {
        this.introSection = new VMIntroSection('Welcome to Moo.do!', 'We\'ll walk you through the basics to get you started.');

        this.introSection.inOrder = true;

        this.introSection.addStep(this.itemAddItem);
        this.introSection.addStep(this.itemIndent);
        this.introSection.addStep(this.itemOutdent);
        this.introSection.addStep(this.itemDateTime);
        this.introSection.addStep(this.itemRightClick);
        this.introSection.addStep(this.itemAddAgenda);

        this.addSection(this.introSection);
    },
    itemAddItem: {
        text: 'Write a list header and press enter to add a new item',
        check: function(e)
        {
            if(e.type === TrackerType.Create)
            {
                // As long as it's not the + button it must have been enter
                var item = d.itemsById[e.data[0].itemID].model;
                if(!(item.parent() == d.getRootModel() && e.data[0].position === 0))
                {
                    return true;
                }

                return false;
            }
        }
    },
    itemIndent: {
        text: 'Press the Tab key to add an item into a list',
        check: function(e)
        {
            if(e.type === TrackerType.Move)
            {
                // Old parent has new parent as a child
                var oldParentItem = d.itemsById[e.data[0].oldParent].data;
                if(oldParentItem.items.indexOf(e.data[0].newParent) >= 0)
                {
                    return true;
                }
            }
        }
    },
    itemOutdent: {
        text: 'Press Shift + Tab to remove an item out of a list',
        check: function(e)
        {
            if(e.type === TrackerType.Move)
            {
                // New parent has old parent as a child
                var newParentItem = d.itemsById[e.data[0].newParent].data;

                if(newParentItem.items.indexOf(e.data[0].oldParent) >= 0)
                {
                   return true;
                }
            }
        }
    },
    itemDateTime: {
        text: 'Add a date/time by typing @ in the text of an item',
        check: function(e)
        {
            return e.type === TrackerType.Insert && e.data[0].text === '@';
        }
    },
    itemRightClick: {
        text: 'Right click an item to explore more options',
        check: function(e)
        {
            return e.type === TrackerType.Misc && e.misc === TrackerMisc.RightClick;
        }
    },
    itemAddAgenda: {
        text: 'Click <i class="icon-calendar2 f22 gd"></i> in the top right to add an Agenda pane',
        check: function(e)
        {
            if(e.type === TrackerType.UIAction && e.data[0].type === TrackerUIAction.AddPane)
            {
                var pane = g.vmMain.panes()[e.data[0].data];
                if(pane.mode() === PaneMode.Timeline)
                {
                    return true;
                }
            }
        }
    },
    itemContactsEnable: {
        text: '<div id="buttonHelpEnableContacts" class="button bBlue" style="margin-right: 3px;">Enable</div> syncing contacts with your Google account',
        check: function(e)
        {
            return e.type === TrackerType.Misc && e.misc === TrackerMisc.SyncContacts && e.data[0].value;
        }
    },
    initHelp: function()
    {
        var org = new VMIntroSection('Organization', 'Moo.do works like a text editor so you can organize like you\'re writing a document.');
        org.addStep(this.itemAddItem);
        org.addStep(this.itemIndent);
        org.addStep(this.itemOutdent);
        org.addStep({
            text: 'Click and drag on the left edge of an item to move it around',
            check: function(e)
            {
                if (e.type === TrackerType.Misc && e.misc === TrackerMisc.DropItem && g.vmMain.isLoaded)
                {
                    return true;
                }
            }
        });
        org.addStep({
            text: 'Press ' + cmdChar + ' + Z to undo and ' + cmdChar + ' + Y to redo',
            check: function(e)
            {
                if (e.type === TrackerType.Misc && e.misc === TrackerMisc.UndoRedo)
                {
                    return true;
                }
            }
        });
        org.addStep({
            text: 'To collapse an item and hide its children press ' + cmdChar + ' + , or select collapse from the right click menu',
            check: function(e)
            {
                var v = e.data[0];
                return e.type === TrackerType.Update && v.field === 'isCollapsed' && v.newValue;
            }
        });
        this.addSection(org);

        var dates = new VMIntroSection('Dates', 'Scheduling items automatically puts them on your Agenda.');
        dates.addStep(this.itemDateTime);
        dates.addStep({
            text: 'Write dates in plain English like <span class="date">@tomorrow</span>',
            check: function(e)
            {
                if (e.type === TrackerType.Misc && e.misc === TrackerMisc.ChangeDate && g.vmMain.isLoaded)
                {
                    var date = e.data[0].date;
                    return !!date;
                }
            }
        });
        dates.addStep({
            text: 'Schedule a full date and time like <span class="date">@today 2:30pm</span> or <span class="date">@5/25 6:00</span>',
            check: function(e)
            {
                if (e.type === TrackerType.Misc && e.misc === TrackerMisc.ChangeDate && g.vmMain.isLoaded)
                {
                    var date = e.data[0].date;
                    if(date)
                    {
                        return date._explicitTime;
                    }
                }
            }
        });
        dates.addStep({
            text: 'Describe urgency with <span class="date">@now</span>, <span class="date">@soon</span>, or <span class="date">@later</span>',
            check: function(e)
            {
                if (e.type === TrackerType.Misc && e.misc === TrackerMisc.ChangeDate && g.vmMain.isLoaded)
                {
                    var item = d.itemsById[e.data[0].itemID].model;
                    return item ? g.isDateSoft(item.dateText) : false;
                }
            }
        });
        this.addSection(dates);

        var priority = new VMIntroSection('Priority', 'Prioritizing items automatically puts them on your agenda and lets you manage what to do next.');
        priority.addStep(this.itemRightClick);
        priority.addStep({
            text: 'Set a priority with the right click menu or ' + cmdChar + ' + 1/2/3',
            check: function(e)
            {
                var v = e.data[0];
                return e.type === TrackerType.Update && v.field === 'priority' && v.newValue > 0;
            }
        });
        priority.addStep({
            text: 'Click the left of an item or ' + cmdChar + ' + / to mark it complete',
            check: function(e)
            {
                var v = e.data[0];
                return e.type === TrackerType.Update && v.field === 'isComplete' && v.newValue;
            }
        });
        priority.addStep({
            text: 'Press ' + cmdChar + ' + \' to flag an item which highlights it in yellow',
            check: function(e)
            {
                var v = e.data[0];
                return e.type === TrackerType.Update && v.field === 'isFlagged' && v.newValue;
            }
        });
        this.addSection(priority);

        var contacts = new VMIntroSection('Contacts', 'Add a contact to an item to easily start a call or email.');
        contacts.addStep(this.itemContactsEnable);
        g.waitForElement('buttonHelpEnableContacts', function(el)
        {
            el.onclick = function()
            {
                g.vmSettings.enableSyncContacts();
            };
        }, 100);

        contacts.addStep({
            text: 'Add a contact by typing + in the text of an item',
            check: function(e)
            {
                return e.type === TrackerType.Insert && e.data[0].text === '+';
            }
        });
        contacts.addStep({
            text: 'Select a contact\'s name from the list or start typing a name to narrow the search',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.AddContact;
            }
        });
        contacts.addStep({
            text: 'Click on a <span class="contact">+contact</span> to start a call or email',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.OpenContact;
            }
        });
        this.addSection(contacts);

        var panes = new VMIntroSection('Panes', 'There are two types of panes: List and Agenda. The List pane is where you write and organize while the Agenda pane is organized automatically by time and priority.');
        panes.addStep({
            text: 'Click <i class="icon-align-left f22 gd"></i> in the top right to add a List pane',
            check: function(e)
            {
                if(e.type === TrackerType.UIAction && e.data[0].type === TrackerUIAction.AddPane)
                {
                    var pane = g.vmMain.panes()[e.data[0].data];
                    if(pane.mode() === PaneMode.Normal)
                    {
                        return true;
                    }
                }
            }
        });
        panes.addStep(this.itemAddAgenda);
        panes.addStep({
            text: 'Click <i class="icon-remove gd"></i> in the top right of a pane to close it',
            check: function(e)
            {
                return e.type === TrackerType.UIAction && e.data[0].type === TrackerUIAction.RemovePane;
            }
        });
        this.addSection(panes);

        var focus = new VMIntroSection('Focus', 'Focus on an item to show only the items under it');
        focus.addStep({
            text: 'To focus on an item, right click on it and select zoom or press ' + cmdChar + ' + Enter',
            check: function(e)
            {
                return e.type === TrackerType.Zoom && !e.data[0].zoomOut;
            }
        });
        focus.addStep({
            text: 'To bring focus back out, click the <span class="titleWrapper"><span class="titleOpen">title of the list</span></span> you want to focus on',
            check: function(e)
            {
                return e.type === TrackerType.Zoom && e.data[0].zoomOut;
            }
        });
        focus.addStep({
            text: 'Press <i class="outlineButtonNormal icon-list"></i> in the top left of a pane to open the outline',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.ToggleOutline;
            }
        });
        focus.addStep({
            text: 'Click the title of a list in the outline to focus on it',
            check: function(e)
            {
                return e.type === TrackerType.Zoom && !e.data[0].zoomOut && g.focusedPane.outline.isVisible();
            }
        });
        this.addSection(focus);

        var search = new VMIntroSection('Search', 'Filter for only the items you want to see right now');
        search.addStep({
            text: 'Filter for a keyword by searching for that word',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.SearchChanged;
            }
        });
        search.addStep({
            text: 'Mark similar items with a <span class="tag">#tag</span> to easily search for all items matching that tag',
            check: function(e)
            {
                return e.type === TrackerType.Insert && e.data[0].text === '#';
            }
        });
        // search.addStep({
        //     text: 'Search for a <span class="tag">#tag</span> to focus on only items that match that <span class="tag">#tag</span>',
        //     check: function(e)
        //     {
        //         return e.type === TrackerType.Misc && e.misc === TrackerMisc.SearchChanged && e.data[0].text.indexOf('#') >= 0;
        //     }
        // });
        search.addStep({
            text: 'Filter by priority by searching for !, !!, or !!!',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.SearchChanged && e.data[0].text.indexOf('!') >= 0;
            }
        });
        search.addStep({
            text: 'Show only flagged items by searching for \'\' (two apostrophes)',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.SearchChanged && e.data[0].text.indexOf('\'\'') >= 0;
            }
        });
        search.addStep({
            text: 'Hide items you\'re not interested in by starting a search with - (dash)',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.SearchChanged && e.data[0].text.indexOf('-') >= 0;
            }
        });
        search.addStep({
            text: 'Hide completed items by searching for -// (dash, two slashes)',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.SearchChanged && e.data[0].text.indexOf('-//') >= 0;
            }
        });
        this.addSection(search);

        var documents = new VMIntroSection('Collaboration', 'Collaborate with others by sharing documents with different groups of people');
        documents.addStep({
            text: 'Click <i class="icon-reorder f22 gd"></i> in the top left to open the sidebar',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.ToggleSidebar && e.data[0].value;
            }
        });
        documents.addStep({
            text: 'Click <i class="icon-plus f18 g"></i> to create a new document',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.AddDocument && e.data[0].value;
            }
        });
        documents.addStep({
            text: 'Open your new document by clicking on it in the list of documents',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.OpenDocument && e.data[0].value;
            }
        });
        documents.addStep({
            text: 'Click <i class="icon-user f18 c g"></i> to change the sharing settings',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.OpenDocSharing && e.data[0].value;
            }
        });
        this.addSection(documents);

        var data = new VMIntroSection('Data', 'Import data from another app to use in Moo.do or export your data out of Moo.do');
        data.addStep({
            text: 'Paste into Moo.do by pressing ' + cmdChar + ' + V or finding Paste in your browser\s edit menu',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.Paste && e.data[0].value;
            }
        });
        data.addStep({
            text: 'Copy the selected text by pressing ' + cmdChar + ' + C or finding Copy in your browser\s edit menu',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.Copy && e.data[0].value;
            }
        });
        data.addStep({
            text: 'Click on Import Data in the sidebar, then choose the app to import from and follow the instructions. If you do not see your app, try using copy and paste to paste it in to Moo.do. If that does not work, please <a href="mailto:support@moo.do?subject=Import Request">contact us</a> and tell us what app you want to import from.',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.OpenImport && e.data[0].value;
            }
        });
        data.addStep({
            text: 'Click on Export Data in the sidebar, then choose the format you\'d like. You can see your data in a new tab so you may copy or download it.',
            check: function(e)
            {
                return e.type === TrackerType.Misc && e.misc === TrackerMisc.OpenExport && e.data[0].value;
            }
        });
        this.addSection(data);

        if(!this.itemContactsEnable.isChecked() && g.settings.get(Settings.syncContacts))
        {
            this.checkContactsEnable();
        }
        window.addEventListener('settingsChange', function(e)
        {
            if(e.data.property == 'syncContacts')
            {
                this.checkContactsEnable();
            }
        }.bind(this));
    },
    onAction: function(e)
    {
        try
        {
            for (var i = 0; i < this.sections().length; i ++)
            {
                this.sections()[i].check(e);
            }
        }
        catch (err)
        {
            g.reportError(err);
        }
    },
    onClosePane: function(e)
    {
        g.tooltip.hideTip();
        g.vmMain.closeHelp();
    },
    introComplete: function()
    {
        return this.introSection && this.introSection.isComplete();
    },
    switchToHelp: function()
    {
        this.initHelp();

        this.setMode(HelpMode.Normal);

        this.sections.splice(0, 1);
        delete this.introSection;

        this.saveState();
    }
};

return VMIntro;
});

