/*
text: '<div class="header">Welcome to Moo.do!</div><p>We\'re going to walk through a few common operations before you get started. You can click these messages to hide them, they will reappear if you get stuck.<br/>Click here to continue.</p>',
text: '<div class="header">Introduction</div><p>Create a new list header by typing some text. [Example: <i>Things I need to do</i>]</p>',
text: '<p>Press <i>Enter</i> to add another item.</p>',
text: '<p>Press <i>tab</i> to indent the item you just created.</p>',

text: '<div class="header">Dates</div><p>Add an item with a date by having \'@today\' in the text. [Example: <i>Pick up Bob at the airport @today 3:00pm</i>]</p>',
text: '<div class="header">Priority</div><p>Mark the item you just created as high priority by right-clicking it and selecting <i>!!</i>.</p>',
text: '<p>Press <i>Enter</i> at the end of the item you just marked as high priority.</p>',
text: '<p>Add another list header by creating a new item. [Example: <i>Places to Visit</i>]</p>',
text: '<p>Add a few more items under the header you just created by pressing enter and then tab.</p>',
text: '<div class="header">Hierarchy</div><p>Un-indent the list header that you created previously by clicking on it then pressing <i>shift+tab</i>.</p>',
text: '<div class="header">Navigation</div><p>Zoom into the list header you just un-indented by right-clicking it and selecting <i>Zoom</i>.</p>',
text: '<div class="header">Completing Items</div><p>Complete an item by right-clicking one and selecting <i>Complete</i>.</p>',
text: '<div class="header">The Outline</div><p>Zoom back out by clicking the <i>Outline</i> button and clicking <i>Home</i>.</p>',
*/;
