            try{g.PAssert(1174,child.isArchived(), 'Child must be archived to be unarchived');}catch(PERR){g.reportError(PERR);}
            try{g.PAssert(1175,this.hasArchivedChildren(), 'Parent must have archived children to unarchive a child');}catch(PERR){g.reportError(PERR);}

